import React, {FC} from "react";
import Grid from "@material-ui/core/Grid";
import CircularProgress from "@material-ui/core/CircularProgress";

export interface WaitSpinnerProps extends React.HTMLAttributes<HTMLDivElement> {}

export const WaitSpinner: FC<WaitSpinnerProps> = ({children, ...rest}) => {
    return (
        <div {...rest}>
            <Grid container direction="column" alignItems="center" justifyContent="center">
                <Grid item xs={12}>
                    <CircularProgress />
                </Grid>
                {children && (
                    <Grid item xs={12}>
                        {children}
                    </Grid>
                )}
            </Grid>
        </div>
    );
};
