import {useCallback} from "react";
import {isPresent} from "ts-is-present";

import {useNimflow} from "./nimflow-api-provider";
import {Stages} from "./nimflow-api";
import {CancelToken} from "axios";

export interface Localization {
    id?: string;
    language: string;
    translations: Record<string, string>;
}

export const localizationCollectionName = "NimflowLocalizations";

export const useNimflowLocalization = () => {
    const {collections_Aggregate, collections_InsertOne, collections_DeleteOne, collections_UpdateOne} = useNimflow();

    const localizations_GetSupportedLanguages = useCallback(
        async (cancelToken?: CancelToken): Promise<string[]> => {
            const result: Localization[] = await collections_Aggregate(
                {
                    collectionName: localizationCollectionName,
                    stages: [
                        {
                            $project: {
                                _id: false,
                                language: 1,
                            },
                        },
                        {
                            $sort: {
                                language: 1,
                            },
                        },
                    ],
                },
                cancelToken
            );
            return result.map(s => s.language);
        },
        [collections_Aggregate]
    );

    const localizations_GetAll = useCallback(
        async (cancelToken?: CancelToken) => {
            const result: Localization[] = await collections_Aggregate(
                {
                    collectionName: localizationCollectionName,
                    stages: [
                        {
                            $project: {
                                _id: false,
                                id: "$_id",
                                language: 1,
                                translations: 1,
                            },
                        },
                        {
                            $sort: {
                                language: 1,
                            },
                        },
                    ],
                },
                cancelToken
            );
            return result;
        },
        [collections_Aggregate]
    );

    const localizations_FindByLanguage = useCallback(
        async (language: string, cancelToken?: CancelToken) => {
            let match: any = {
                language,
            };
            if (language.length > 2 && language[2] === "-") {
                match = {
                    $or: [
                        match,
                        {
                            language: language.substr(0, 2),
                        },
                    ],
                };
            }
            let stages: Stages[] = [
                {
                    $match: match,
                },
            ];
            const result: Localization[] = await collections_Aggregate(
                {
                    collectionName: localizationCollectionName,
                    stages,
                },
                cancelToken
            );
            if (result.length > 1) {
                result.sort((a, b) => b.language.length - a.language.length);
            }
            return result;
        },
        [collections_Aggregate]
    );

    const localizations_Add = useCallback(
        async (localization: Localization, cancelToken?: CancelToken) => {
            const result = await collections_InsertOne(
                {
                    collectionName: localizationCollectionName,
                    document: localization,
                },
                cancelToken
            );
            return result.id;
        },
        [collections_InsertOne]
    );

    const localizations_Remove = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result = await collections_DeleteOne(
                {
                    collectionName: localizationCollectionName,
                    id,
                },
                cancelToken
            );
            return result;
        },
        [collections_DeleteOne]
    );

    const localizations_UpdateTranslation = useCallback(
        async (language: string, key: string, value?: string, cancelToken?: CancelToken) => {
            const update: any = isPresent(value)
                ? {
                      $set: {[`translations.${key}`]: value},
                  }
                : {
                      $unset: {[`translations.${key}`]: ""},
                  };
            const result = await collections_UpdateOne(
                {
                    collectionName: localizationCollectionName,
                    filter: {
                        language,
                    },
                    update,
                    isUpsert: true,
                },
                cancelToken
            );
            return result;
        },
        [collections_UpdateOne]
    );

    return {
        localizations_GetSupportedLanguages,
        localizations_GetAll,
        localizations_FindByLanguage,
        localizations_Add,
        localizations_Remove,
        localizations_UpdateTranslation,
    };
};
