import {FC, useMemo} from "react";

import {TasksView, TaskViewColumn, TasksViewState, FilterOf} from "../components/tasks-view/tasks-view";
import {ContextTask} from "../api/nimflow-api";
import {useAuthenticationService} from "./auth/authentication-service";

const pageSizeOpts = [5, 10, 20, 100];

const cols: TaskViewColumn[] = [
    {field: "title"},
    {field: "typeName"},
    {field: "contextTypeName"},
    {field: "contextReference"},
    {field: "dueDate"},
    {field: "status"},
    {field: "dateCreated"},
    {field: "focused", hidden: true},
];

const initialState: TasksViewState = {
    currentPage: 0,
    pageSize: 10,
};

export const TasksViewer: FC<{}> = () => {
    const {authenticatedUser} = useAuthenticationService();
    const enforcedFilter = useMemo(() => {
        const result: FilterOf<ContextTask> = {
            archived: {
                type: "boolean",
                value: false,
            },
        };
        result.assignedTo = {
            type: "textMatch",
            value: "*",
        };
        const email = authenticatedUser?.email;
        if (email) {
            result.assignedTo = {
                type: "or",
                filters: [
                    result.assignedTo,
                    {
                        type: "prefixTextMatch",
                        prefix: email,
                    },
                ],
            };
        }
        return result;
    }, [authenticatedUser]);

    return (
        <TasksView
            columns={cols}
            initialState={initialState}
            pageSizeOptions={pageSizeOpts}
            allowResponseCreation={true}
            allowFocusing={true}
            enforcedFilter={enforcedFilter}
        />
    );
};
