import React from "react";

import {useTheme} from "@material-ui/core/styles";
import JSONTree from "react-json-tree";

///https://github.com/reduxjs/redux-devtools/tree/75322b15ee7ba03fddf10ac3399881e302848874/src/react/themes

const lightTheme = {
    scheme: "grayscale",
    author: "alexandre gavioli (https://github.com/alexx2/)",
    base00: "#101010",
    base01: "#252525",
    base02: "#464646",
    base03: "#525252",
    base04: "#ababab",
    base05: "#b9b9b9",
    base06: "#e3e3e3",
    base07: "#f7f7f7",
    base08: "#7c7c7c",
    base09: "#999999",
    base0A: "#a0a0a0",
    base0B: "#8e8e8e",
    base0C: "#868686",
    base0D: "#686868",
    base0E: "#747474",
    base0F: "#5e5e5e",
};

type JSONTreeProps = Partial<ConstructorParameters<typeof JSONTree>[0]>;

const JsonViewer: React.FC<JSONTreeProps> = props => {
    const theme = useTheme();
    const options: JSONTreeProps = {
        theme: lightTheme,
        hideRoot: true,
        invertTheme: theme.palette.type === "light",
        ...props,
    };
    return <JSONTree {...options} />;
};

export default JsonViewer;
