import React, {FC, useCallback, useMemo} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import useSWR from "swr";
import Grid from "@material-ui/core/Grid";
import Alert from "@material-ui/lab/Alert";
import Button from "@material-ui/core/Button";
import {isPresent} from "ts-is-present";

import {DispatchActionStep} from "./dispatch-action-stepper";
import {useNimflow} from "../../api/nimflow-api-provider";
import {StepContainer} from "./step-container";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            width: "100%",
            marginTop: "20px",
            marginBottom: "20px",
        },
    })
);

const useContextType = (contextTypeName?: string) => {
    const {contextTypes_FindOne} = useNimflow();
    const {data, error, isValidating, mutate} = useSWR(
        `/ContextType/${contextTypeName}`,
        async () => {
            return await contextTypes_FindOne({name: contextTypeName});
        },
        {revalidateOnFocus: false}
    );
    return useMemo(() => {
        return {
            contextType: data,
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export interface ActionTypeSelectorStepProps extends DispatchActionStep {}

export const ActionTypeSelectorStep: FC<ActionTypeSelectorStepProps> = props => {
    const {value, onBack, onNext} = props;
    const classes = useStyles();
    const {contextType, error, isValidating} = useContextType(value.contextTypeName);

    const candidateActions = useMemo(() => {
        if (!contextType) {
            return [];
        }
        // Only initial actions at the moment
        return contextType.initialActions.map(s => contextType.actions.find(a => a.name === s)).filter(isPresent);
    }, [contextType]);

    const handleActionSelected = useCallback(
        (name: string) => {
            if (value.action === name) {
                onNext(value);
            } else {
                onNext({
                    contextTypeName: value.contextTypeName,
                    action: name,
                });
            }
        },
        [value, onNext]
    );

    if (!isValidating && !!error) {
        return <Alert severity="error">{error.toString()}</Alert>;
    }
    if (isValidating || !contextType) {
        return null;
    }

    return (
        <StepContainer onBack={onBack}>
            <Grid container direction="column" alignItems="center" spacing={3} className={classes.root}>
                {candidateActions.map(s => (
                    <Grid item key={s.name} xs={10}>
                        <Button variant="outlined" onClick={() => handleActionSelected(s.name)}>
                            {s.name}
                        </Button>
                    </Grid>
                ))}
            </Grid>
        </StepContainer>
    );
};
