import React, {FC, useCallback} from "react";
import {useTheme} from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import Brightness4Icon from "@material-ui/icons/Brightness4";
import Brightness7Icon from "@material-ui/icons/Brightness7";
import {useTranslation} from "react-i18next";

import {ReactComponentProps} from "../../utils/types";
import {useSetThemePaletteType} from "../../components/theme-provider";

export type TogglePaletteTypeButtonProps = Omit<ReactComponentProps<typeof IconButton>, "onClick">;

export const TogglePaletteTypeButton: FC<TogglePaletteTypeButtonProps> = props => {
    const {title, ...rest} = props;
    const theme = useTheme();
    const {t} = useTranslation();
    const setThemePaletteType = useSetThemePaletteType();
    const handleTogglePaletteType = useCallback(() => {
        const paletteType = theme.palette.type === "light" ? "dark" : "light";
        setThemePaletteType(paletteType);
    }, [theme.palette.type, setThemePaletteType]);

    return (
        <Tooltip title={title ?? (t("ToggleTheme") as string)} enterDelay={300}>
            <IconButton onClick={handleTogglePaletteType} {...rest}>
                {theme.palette.type === "light" ? <Brightness4Icon /> : <Brightness7Icon />}
            </IconButton>
        </Tooltip>
    );
};
