import {useCallback} from "react";
import {uniq, fromPairs} from "ramda";

import {
    timeGranularities,
    FilterTypeDescriptor,
    contextFilterDescriptor,
    contextTaskFilterDescriptor,
    eventFilterDescriptor,
    TimeGranularity,
} from "./../../api/nimflow-dashboard-widgets-api";
import {useNimflow} from "./../../api/nimflow-api-provider";
import {
    DashboardWidgetQueryDataSource,
    DashboardWidgetAggregateDataSourceSourceCollection,
    ExpressionPropertyAggregator,
    CalculateExpression,
    CalculateTableExpression,
    SummarizeExpression,
} from "../../api/nimflow-dashboard-widgets-api";
import {isPlainObject} from "../../utils/types";
import {ContextType, SortDirection} from "../../api/nimflow-api";
import {evaluateScript} from "../../utils/scripting";
import {isPresent} from "ts-is-present";

const filterKeyMap: Record<string, string> = {
    milestoneValue: "value",
};

const transformValue = (value: any, type: FilterTypeDescriptor<any>[string]) => {
    if (value instanceof Date) {
        value = value.toISOString();
    }
    if (type === "Date" && value) {
        value = {
            $dateFromString: {
                dateString: value,
            },
        };
    }
    return value;
};

const createInterval = (key: string, from: any, to: any, type: FilterTypeDescriptor<any>[string]) => {
    const conds: any[] = [];
    if (from) {
        conds.push({$gte: [`$${key}`, transformValue(from, type)]});
    }
    if (to) {
        conds.push({$lt: [`$${key}`, transformValue(to, type)]});
    }
    return conds.length === 2 ? {$and: conds} : conds[0];
};

const createDefaultFilter = (key: string, value: any, type: FilterTypeDescriptor<any>[string]) => {
    switch (type) {
        case "string":
            if (Array.isArray(value)) {
                return {$in: [`$${filterKeyMap[key] ?? key}`, value]};
            }
            break;
        case "Date":
            if (Array.isArray(value)) {
                return createInterval(key, value[0], value[1], type);
            }
            break;
        case "number":
            if (Array.isArray(value)) {
                return createInterval(key, value[0], value[1], type);
            }
            break;
    }
    return {$eq: [`$${filterKeyMap[key] ?? key}`, transformValue(value, type)]};
};

const createMilestonesFilter = (value: Record<string, any>) => {
    const result: Record<string, any>[] = [];
    for (const [msname, msvalue] of Object.entries(value)) {
        if (!msvalue) {
            continue;
        }
        const filteredMilestones = {
            $filter: {
                input: {$ifNull: ["$milestones", []]},
                as: "ms",
                cond: {
                    $and: [
                        {
                            $eq: ["$$ms.name", msname],
                        },
                        createDefaultFilter("$ms.lastReached", msvalue, "Date"),
                    ],
                },
            },
        };
        result.push({$gt: [{$size: filteredMilestones}, 0]});
    }
    return result;
};

const createTagsFilter = (value: Record<string, any>) => {
    const result: Record<string, any>[] = [];
    for (const [tagName, tagValue] of Object.entries(value)) {
        if (!tagValue) {
            continue;
        }
        const filteredTags = {
            $filter: {
                input: {$ifNull: ["$tags", []]},
                as: "tag",
                cond: {
                    $and: [
                        {
                            $eq: ["$$tag.name", tagName],
                        },
                        createDefaultFilter("$tag.value", tagValue, "string"),
                    ],
                },
            },
        };
        result.push({$gt: [{$size: filteredTags}, 0]});
    }
    return result;
};

const createMetricsFilter = (value: Record<string, any>) => {
    const result: Record<string, any>[] = [];
    for (const [metricName, metricValue] of Object.entries(value)) {
        if (!isPresent(metricValue)) {
            continue;
        }
        const filteredMetrics = {
            $filter: {
                input: {$ifNull: ["$metrics", []]},
                as: "metric",
                cond: {
                    $and: [
                        {
                            $eq: ["$$metric.name", metricName],
                        },
                        createDefaultFilter("$metric.value", metricValue, "number"),
                    ],
                },
            },
        };
        result.push({$gt: [{$size: filteredMetrics}, 0]});
    }
    return result;
};

const transformQueryFilterToMatchStage = async (
    queryFilter: Record<string, any>,
    sourceCollection: DashboardWidgetAggregateDataSourceSourceCollection,
    contextTypeName: string | undefined,
    contextLocator: (contextTypeName: string) => Promise<ContextType>
): Promise<any> => {
    let result: Record<string, any>[] = [];
    switch (sourceCollection) {
        case "contexts":
            if (contextTypeName) {
                result.push(createDefaultFilter("typeName", contextTypeName, "string"));
            }
            for (const [key, value] of Object.entries(queryFilter)) {
                switch (key) {
                    case "excludeContextTypeVersion":
                        result.push({$ne: ["$contextTypeVersion", value]});
                        break;
                    case "created":
                        result.push(createDefaultFilter("dateCreated", value, contextFilterDescriptor.created));
                        break;
                    case "modified":
                        result.push(createDefaultFilter("dateModified", value, contextFilterDescriptor.modified));
                        break;
                    case "reachedMilestones":
                    case "active":
                        let milestoneNames = undefined;
                        if (key === "active") {
                            if (queryFilter.typeName) {
                                const contextType = await contextLocator(queryFilter.typeName).catch(() => undefined);
                                if (contextType) {
                                    milestoneNames = contextType.milestones.filter(s => s.final).map(s => s.name);
                                }
                            }
                        } else {
                            milestoneNames = value;
                        }
                        if (Array.isArray(milestoneNames) && milestoneNames.length > 0) {
                            const filteredMilestones = {
                                $filter: {
                                    input: "$milestones",
                                    as: "ms",
                                    cond: {$ne: ["$$ms.lastReached", null]},
                                },
                            };
                            const sizeProjection = {
                                $size: {
                                    $setIntersection: [
                                        {
                                            $map: {
                                                input: filteredMilestones,
                                                as: "ms",
                                                in: "$$ms.name",
                                            },
                                        },
                                        uniq(milestoneNames),
                                    ],
                                },
                            };
                            result.push(value === true ? {$eq: [sizeProjection, 0]} : {$gt: [sizeProjection, 0]});
                        }
                        break;
                    case "milestones": {
                        result.push(...createMilestonesFilter(value));
                        break;
                    }
                    case "tags": {
                        result.push(...createTagsFilter(value));
                        break;
                    }
                    case "metrics": {
                        result.push(...createMetricsFilter(value));
                        break;
                    }
                    default:
                        result.push(createDefaultFilter(key, value, (contextFilterDescriptor as any)[key]));
                }
            }
            break;
        case "tasks":
            for (const [key, value] of Object.entries(queryFilter)) {
                switch (key) {
                    case "unassigned":
                        let unassignedCond: any = {
                            $in: [
                                {
                                    $type: "$assignedTo",
                                },
                                ["missing", "null"],
                            ],
                        };
                        if (!value) {
                            unassignedCond = {$not: unassignedCond};
                        }
                        result.push(unassignedCond);
                        break;
                    case "labels":
                        result.push({
                            $eq: [
                                {
                                    $size: {
                                        $setIntersection: ["$labels", uniq(value)],
                                    },
                                },
                                uniq(value).length,
                            ],
                        });
                        break;
                    default:
                        result.push(createDefaultFilter(key, value, (contextTaskFilterDescriptor as any)[key]));
                }
            }
            break;
        case "events":
            for (const [key, value] of Object.entries(queryFilter)) {
                switch (key) {
                    case "tags": {
                        result.push(...createTagsFilter(value));
                        break;
                    }
                    case "metrics": {
                        result.push(...createMetricsFilter(value));
                        break;
                    }
                    default:
                        result.push(createDefaultFilter(key, value, (eventFilterDescriptor as any)[key]));
                }
            }
            break;
    }
    return {
        $expr: {
            $and: result,
        },
    };
};

const getAggregation = (op: ExpressionPropertyAggregator, operand: any) => {
    switch (op) {
        case "average":
            return {$avg: operand};
        case "count":
            return {$sum: 1};
        case "max":
            return {$max: operand};
        case "min":
            return {$min: operand};
        case "sum":
            return {$sum: operand};
    }
};

const getCalculateStages = (query: CalculateExpression) => {
    const stages: any[] = [];
    let aggregationPath = `$${query.property}`;
    if (query.value) {
        stages.push(
            {
                $project: {_id: 0, value: aggregationPath},
            },
            {
                $unwind: `$value`,
            },
            {
                $match: {
                    $expr: {$eq: ["$value.name", query.value]},
                },
            }
        );
        aggregationPath = `$value.${query.property === "milestones" ? "lastReached" : "value"}`;
    }
    stages.push({
        $group: {
            _id: null,
            value: getAggregation(query.aggregation, aggregationPath),
        },
    });
    return stages;
};

const getCalculateTableStages = (query: CalculateTableExpression) => {
    const stages: any[] = [];
    if (query.sortProperty) {
        stages.push({
            $sort: {
                [query.sortProperty]: query.sortDirection === SortDirection.Descending ? -1 : 1,
            },
        });
    }
    if (query.skip) {
        stages.push({
            $skip: query.skip,
        });
    }
    if (query.limit) {
        stages.push({
            $limit: query.limit,
        });
    }
    return stages;
};

const getTimeGranularityFormat = (timeGranularity: TimeGranularity) => {
    let index = timeGranularities.indexOf(timeGranularity);
    if (index === -1) {
        index = 5;
    }
    let format = "";
    if (index === 2) {
        format = "%G-%V";
    } else {
        let formatters = ["%Y", "-%m", null, "-%d", "T%H", ":%M", ":%S"];
        for (let i = 0; i < formatters.length; i++) {
            if (i === 2) {
                continue;
            }
            format += i <= index ? formatters[i] : i < 4 ? "-01" : i === 4 ? "T00" : ":00";
        }
        format += "%z";
    }
    return format;
};

const getSummarizeStages = (query: SummarizeExpression, timezone: string) => {
    const stages: any[] = [];
    const proj: Record<string, any> = {
        _id: 0,
    };

    const spreadSet: Record<string, any> = {};
    for (const g of query.groupings.filter(s => s.value)) {
        if (!(g.property in spreadSet)) {
            spreadSet[g.property] = "$$REMOVE";
        }
        spreadSet[`${g.property}_${g.value}`] = {
            $first: {
                $map: {
                    input: {
                        $filter: {
                            input: `$${g.property}`,
                            as: "item",
                            cond: {
                                $eq: ["$$item.name", g.value],
                            },
                        },
                    },
                    as: "item",
                    in: `$$item.${g.property === "milestones" ? "lastReached" : "value"}`,
                },
            },
        };
        proj[`${g.property}_${g.value}`] = `$${g.property}_${g.value}`;
    }
    if (Object.keys(spreadSet).length > 0) {
        stages.push({
            $set: spreadSet,
        });
    }

    const transformations: any[] = [];
    const groupIds: string[] = [];
    const aggregates: Record<string, any> = {};
    for (const g of query.groupings) {
        proj[g.property] = `$${g.property}`;
        let fieldName = g.property;
        if (g.value) {
            fieldName = `${g.property}_${g.value}`;
        }
        if (g.timeGranularity) {
            let format = getTimeGranularityFormat(g.timeGranularity);
            transformations.push({
                $set: {
                    [fieldName]: {
                        $dateToString: {
                            date: `$${fieldName}`,
                            format,
                            timezone,
                        },
                    },
                },
            });
        }
        groupIds.push(fieldName);
    }
    if (query.aggregates) {
        for (const g of query.aggregates) {
            if (g.property) {
                proj[g.property] = `$${g.property}`;
            }
            let fieldName = g.property;
            if (g.value) {
                fieldName = `${g.property}_${g.value}`;
                transformations.push(
                    {
                        $unwind: {
                            path: `$${g.property}`,
                            preserveNullAndEmptyArrays: true,
                        },
                    },
                    {
                        $match: {
                            $expr: {
                                $or: [
                                    {
                                        $eq: [`$${g.property}.name`, g.value],
                                    },
                                    {
                                        $eq: [`$${g.property}`, null],
                                    },
                                ],
                            },
                        },
                    },
                    {
                        $set: {
                            [fieldName]: {
                                $cond: {
                                    if: {$eq: [`$${g.property}`, null]},
                                    then: null,
                                    else: `$${g.property}.${g.property === "milestones" ? "lastReached" : "value"}`,
                                },
                            },
                        },
                    }
                );
            }
            aggregates[g.name] = getAggregation(g.aggregation, `$${fieldName}`);
        }
    }
    if (transformations.length > 0) {
        stages.push(
            {
                $project: proj,
            },
            ...transformations
        );
    }
    stages.push(
        {
            $group: {
                _id: fromPairs(groupIds.map(s => [s, `$${s}`])),
                ...aggregates,
            },
        },
        {
            $set: {
                _id: "$$REMOVE",
                ...fromPairs(groupIds.map(s => [s, `$_id.${s}`])),
            },
        }
    );
    return stages;
};

export const useQueryDataSourceEvaluator = () => {
    const {contexts_Aggregate, tasks_Aggregate, events_AggregateEvents, contextTypes_FindOne} = useNimflow();

    const evaluateQueryFilter = useCallback(
        async (dataSource: DashboardWidgetQueryDataSource, args: Record<string, any>) => {
            const filterScript = dataSource.query.filter;
            if (!filterScript) {
                return undefined;
            }
            const outputValues = {
                queryFilter: undefined,
            };
            evaluateScript(filterScript, {
                inputValues: args,
                outputValues,
            });
            return outputValues.queryFilter;
        },
        []
    );

    const evaluateQueryDataSource = useCallback(
        async (
            dataSource: DashboardWidgetQueryDataSource,
            args: Record<string, any>,
            timezone: string
        ): Promise<any> => {
            let stages: any[] = [];
            const queryFilter = await evaluateQueryFilter(dataSource, {params: args});

            if (isPlainObject(queryFilter)) {
                stages.push({
                    $match: await transformQueryFilterToMatchStage(
                        queryFilter,
                        dataSource.sourceCollection,
                        dataSource.contextTypeName,
                        name =>
                            contextTypes_FindOne({
                                name,
                            })
                    ),
                });
            }

            const query = dataSource.query;
            switch (query.type) {
                case "Calculate":
                    stages.push(...getCalculateStages(query));
                    break;
                case "CalculateTable":
                    stages.push(...getCalculateTableStages(query));
                    break;
                case "Summarize":
                    stages.push(...getSummarizeStages(query, timezone));
                    break;
            }

            let result;
            switch (dataSource.sourceCollection) {
                case "contexts": {
                    result = await contexts_Aggregate({
                        stages,
                    });
                    break;
                }
                case "tasks": {
                    result = await tasks_Aggregate({
                        stages,
                    });
                    break;
                }
                case "events": {
                    result = await events_AggregateEvents({
                        stages,
                    });
                    break;
                }
            }
            if (query.type === "Calculate" && Array.isArray(result) && result.length > 0) {
                result = result[0].value;
            }
            return result;
        },
        [contextTypes_FindOne, contexts_Aggregate, evaluateQueryFilter, events_AggregateEvents, tasks_Aggregate]
    );

    return {
        evaluateQueryDataSource,
    };
};
