import {FC, useEffect} from "react";
import {Route} from "react-router-dom";
import {RouteProps} from "react-router";

import {useAuthenticationService} from "../features/auth/authentication-service";

const defaultOnRedirecting = (): JSX.Element => <div />;

export interface AuthenticatedRouteProps extends RouteProps {
    /**
     * ```js
     * withAuthenticationRequired(Profile, {
     *   onRedirecting: () => <div>Redirecting you to the login...</div>
     * })
     * ```
     *
     * Render a message to show that the user is being redirected to the login.
     */
    onRedirecting?: () => JSX.Element;
}

export const AuthenticatedRoute: FC<AuthenticatedRouteProps> = props => {
    const {onRedirecting = defaultOnRedirecting, ...rest} = props;
    const {isAuthenticated, login} = useAuthenticationService();

    useEffect(() => {
        if (isAuthenticated) {
            return;
        }
        login();
    }, [isAuthenticated, login]);

    if (!isAuthenticated) {
        return onRedirecting();
    }

    return <Route {...rest} />;
};
