import {useCallback} from "react";
import useSWRImmutable from "swr/immutable";

import {useNimflow} from "../api/nimflow-api-provider";
import {useCurrentUnitInfo} from "../api/nimflow-business-directory-provider";
import {useAuthenticationService} from "../features/auth/authentication-service";
export interface AuthorizationQueries {
    can: (requestedPermissions: string[]) => boolean;
    canAny: (requestedPermissions: string[]) => boolean;
    getGrantedPermission: () => string[];
    checkPermissions: (requestedPermissions: Array<string | string[]>) => Array<string | string[]>;
    permissions: string[] | undefined;
}

export const useAuthorization = () => {
    const {unit: currentUnit} = useCurrentUnitInfo();
    const {users_GetCurrentUserPermissions} = useNimflow();
    const {isAuthenticated} = useAuthenticationService();
    const {data: permissions} = useSWRImmutable([`permissions/${currentUnit?.id}`, isAuthenticated], async () => {
        if (!currentUnit || !isAuthenticated) {
            return [];
        }
        try {
            return await users_GetCurrentUserPermissions({
                pageNumber: 0,
                pageSize: 100,
            });
        } catch {
            return [];
        }
    });

    const getGrantedPermission = useCallback(() => {
        return permissions ?? [];
    }, [permissions]);

    // Returns missing permissions
    const checkPermissions = useCallback(
        (requestedPermissions: Array<string | string[]>): Array<string | string[]> => {
            const granted = getGrantedPermission();
            return requestedPermissions.filter(s => {
                if (typeof s === "string") {
                    return !granted.includes(s);
                }
                return s.every(p => !granted.includes(p));
            });
        },
        [getGrantedPermission]
    );

    const can = useCallback(
        (requestedPermissions: string[]): boolean => {
            return checkPermissions(requestedPermissions).length === 0;
        },
        [checkPermissions]
    );

    const canAny = useCallback(
        (requestedPermissions: string[]): boolean => {
            return checkPermissions(requestedPermissions).length < requestedPermissions.length;
        },
        [checkPermissions]
    );

    const api: AuthorizationQueries = {
        can,
        canAny,
        getGrantedPermission,
        checkPermissions,
        permissions,
    };
    return api;
};
