import {FC, useCallback, useMemo, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Hidden from "@material-ui/core/Hidden";
import {
    rankWith,
    uiTypeIs,
    RankedTester,
    UISchemaElement,
    JsonFormsRendererRegistryEntry,
    mapStateToControlProps,
    OwnPropsOfControl,
    JsonSchema,
    update,
} from "@jsonforms/core";
import {compose} from "@jsonforms/core/lib/util/path";
import {useJsonForms} from "@jsonforms/react";
import {useTranslation} from "react-i18next";
import {isPresent} from "ts-is-present";

import {ImageBatchRecognitionElement} from "./image-batch-recognition-element";
import {ImageList} from "./image-list";
import {BatchProcessorContextProvider, useBatchProcessorService} from "./image-batch-recognition-service";
import {WaitSpinner} from "../../../components/wait-spinner";
import {DocumentTree} from "./document-tree";
import {DocumentEditor} from "./document-editor";
import {ImageExtractorModal} from "./image-extractor-modal";
import { equals } from "ramda";
import { RecognizedDocument } from "./recognized-document";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            height: "75vh",
            minWidth: 900,
            display: "flex",
            flexDirection: "row",
        },
        treePane: {
            flexGrow: 1,
            overflow: "auto",
            width: "25%",
        },
        imageListPane: {
            flexGrow: 1,
            width: "41%",
            overflowY: "auto",
            borderLeft: `2px solid ${theme.palette.divider}`,
            paddingLeft: 10,
        },
        editorPane: {
            width: "33%",
            flexGrow: 1,
            overflow: "auto",
            borderLeft: `2px solid ${theme.palette.divider}`,
            paddingLeft: 10,
        },
    })
);

const isImageBatchRecognitionElement = (element: UISchemaElement): element is ImageBatchRecognitionElement => {
    return element.type === "ImageBatchRecognition";
};
interface ImageBatchRecognitionEditorProps {
    schema: JsonSchema;
    path: string;
    renderers?: JsonFormsRendererRegistryEntry[];
    data: any;
}

const ImageBatchRecognitionEditor: FC<ImageBatchRecognitionEditorProps> = props => {
    const classes = useStyles();
    const {t} = useTranslation();
    const {loadingImages, documentSchemas} = useBatchProcessorService();
    const [pageIndexForExtraction, setPageIndexForExtraction] = useState<number>();

    const handleImageExtractionRequested = useCallback((pageIndex: number) => {
        setPageIndexForExtraction(pageIndex);
    }, []);

    const handleClose = useCallback(() => {
        setPageIndexForExtraction(undefined);
    }, []);

    if (!documentSchemas) {
        return null;
    }

    if (loadingImages) {
        return <WaitSpinner>{t("LoadingImages")}</WaitSpinner>;
    }

    return (
        <div className={classes.content}>
            <div className={classes.treePane}>
                <DocumentTree />
            </div>
            <div className={classes.imageListPane}>
                <ImageList onImageExtractionRequested={handleImageExtractionRequested} />
            </div>
            <div className={classes.editorPane}>
                <DocumentEditor />
                {/* {JSON.stringify(currentDocument)} */}
                {/* {JSON.stringify(fileInfos)} */}
            </div>
            {isPresent(pageIndexForExtraction) && (
                <ImageExtractorModal
                    open={isPresent(pageIndexForExtraction)}
                    pageIndex={pageIndexForExtraction}
                    onClose={handleClose}
                />
            )}
        </div>
    );
};

const ImageBatchRecognitionRenderer: FC<OwnPropsOfControl> = props => {
    const ctx = useJsonForms();
    const {data, path, schema, visible, uischema} = mapStateToControlProps({jsonforms: {...ctx}}, props);
    const docs = useMemo(() => {
        if (!Array.isArray(data.documents)) {
            return [];
        }
        let result = data.documents as Partial<RecognizedDocument>[];
        for(let i = 0; i < result.length; i++) {
            const doc = result[i];
            if (!doc.action) {
                const newDoc: Partial<RecognizedDocument> = {
                    ...doc,
                    action: "saveAsDraft",
                }
                if (result === data.documents) {
                    result = [...data.documents];
                }
                result[i] = newDoc;
            }
        }
        return result as RecognizedDocument[];
    }, [data.documents]);

    if (docs !== data.documents && !equals(docs, data.documents)) {
        ctx.dispatch?.(update(compose(path, "documents"), () => docs));
        return null;
    }

    if (!isImageBatchRecognitionElement(uischema as any)) {
        return null;
    }
    const imageBatchRecognitionElement = uischema as unknown as ImageBatchRecognitionElement;
    return (
        <Hidden xsUp={!visible}>
            <BatchProcessorContextProvider
                basePath={path}
                batch={data}
                documentSchemasSource={imageBatchRecognitionElement.documentSchemasSource}
            >
                <ImageBatchRecognitionEditor path={path} schema={schema} renderers={props.renderers} data={data} />
            </BatchProcessorContextProvider>
        </Hidden>
    );
};

export default ImageBatchRecognitionRenderer;

export const imageBatchRecognitionRendererTester: RankedTester = rankWith(
    3, //increase rank as needed
    uiTypeIs("ImageBatchRecognition")
);
