import {useCallback} from "react";
import {isPresent} from "ts-is-present";

import {useNimflow} from "./nimflow-api-provider";
import {Stages, SortDirection} from "./nimflow-api";
import {
    contextScalarPropertyNames,
    eventScalarPropertyNames,
    contextTaskScalarPropertyNames,
    contextTypeDescriptor,
    contextTaskTypeDescriptor,
    eventTypeDescriptor,
} from "./nimflow-api-extensions";
import {isPlainObject} from "../utils/types";
import {ThemeTypes} from "@material-vega/core";
import {evaluateScript} from "../utils/scripting";
import { CancelToken } from "axios";

export interface DashboardWidgetDefinitionInfo {
    id?: string;
    name: string;
    title?: string;
}

export const dashboardWidgetParameterTypes = ["number", "string", "boolean", "datetime", "stringList"] as const;
Object.freeze(dashboardWidgetParameterTypes);

export type DashboardWidgetParameterType = typeof dashboardWidgetParameterTypes[number];
export interface DashboardWidgetParameter {
    name: string;
    type: DashboardWidgetParameterType;
    required: boolean;
}

export const dashboardWidgetAggregateDataSourceSourceCollections = ["contexts", "events", "tasks"] as const;
Object.freeze(dashboardWidgetAggregateDataSourceSourceCollections);

export type DashboardWidgetAggregateDataSourceSourceCollection =
    typeof dashboardWidgetAggregateDataSourceSourceCollections[number];

export const expressionPropertyAggregators = ["count", "sum", "average", "max", "min"] as const;
Object.freeze(expressionPropertyAggregators);

export type ExpressionPropertyAggregator = typeof expressionPropertyAggregators[number];

export const timeGranularities = ["years", "months", "weeks", "days", "hours", "minutes", "seconds"] as const;
Object.freeze(timeGranularities);

export type TimeGranularity = typeof timeGranularities[number];

type BaseDateFilterValue = Date | string;
type IntervalFilterValue<T> = [T | undefined, T | undefined];
export type StringPropertyFilter = string | string[] | undefined;
export type StringArrayPropertyFilter = string[] | undefined;
export type NumberPropertyFilter = number | IntervalFilterValue<number> | undefined;
export type BooleanPropertyFilter = boolean | undefined;
export type DatePropertyFilter = BaseDateFilterValue | IntervalFilterValue<BaseDateFilterValue> | undefined;
export type DateMapPropertyFilter = Record<string, DatePropertyFilter>;
export type StringMapPropertyFilter = Record<string, StringPropertyFilter>;
export type NumberMapPropertyFilter = Record<string, NumberPropertyFilter>;

export interface ContextFilter {
    id?: StringPropertyFilter;
    typeName?: StringPropertyFilter;
    reference?: StringPropertyFilter;
    active?: BooleanPropertyFilter;
    excludeContextTypeVersion?: NumberPropertyFilter;
    created?: DatePropertyFilter;
    modified?: DatePropertyFilter;
    reachedMilestones?: StringArrayPropertyFilter;
    milestones?: DateMapPropertyFilter;
    tags?: StringMapPropertyFilter;
    metrics?: NumberMapPropertyFilter;
}

export interface ContextTaskFilter {
    contextId?: StringPropertyFilter;
    contextTypeName?: StringPropertyFilter;
    contextReference?: StringPropertyFilter;
    typeName?: StringPropertyFilter;
    title?: StringPropertyFilter;
    assignedTo?: StringPropertyFilter;
    unassigned?: BooleanPropertyFilter;
    status?: StringPropertyFilter;
    archived?: BooleanPropertyFilter;
    focused?: BooleanPropertyFilter;
    importance?: NumberPropertyFilter;
    dueDate?: DatePropertyFilter;
    labels?: StringArrayPropertyFilter;
}

export interface EventFilter {
    event?: StringPropertyFilter;
    timestamp?: DatePropertyFilter;
    requestTypeName?: StringPropertyFilter;
    contextTypeName?: StringPropertyFilter;
    sentBy?: StringPropertyFilter;
    reference?: StringPropertyFilter;
    subject?: StringPropertyFilter;
    //duration?: StringPropertyFilter;
    contextIsNew?: BooleanPropertyFilter;
    action?: StringPropertyFilter;
    message?: StringPropertyFilter;
    dueDate?: DatePropertyFilter;
    taskTypeName?: StringPropertyFilter;
    assignedTo?: StringPropertyFilter;
    importance?: NumberPropertyFilter;
    responseTypeName?: StringPropertyFilter;
    oldImportance?: NumberPropertyFilter;
    oldLabels?: StringArrayPropertyFilter;
    oldDueDate?: DatePropertyFilter;
    oldAssignedTo?: StringPropertyFilter;
    milestoneName?: StringPropertyFilter;
    milestoneValue?: DatePropertyFilter;
    eventName?: StringPropertyFilter;
    tags?: StringMapPropertyFilter;
    metrics?: NumberMapPropertyFilter;
}

export type EntityFilter<T extends DashboardWidgetAggregateDataSourceSourceCollection> = T extends "contexts"
    ? ContextFilter
    : T extends "tasks"
    ? ContextTaskFilter
    : T extends "events"
    ? EventFilter
    : {};

export interface CalculateTableExpression {
    type: "CalculateTable";
    filter?: string; // Script evaluating to EntityFilter<T>;
    sortProperty?: string;
    sortDirection?: SortDirection;
    skip?: number;
    limit?: number;
}

export interface CalculateExpression {
    type: "Calculate";
    filter?: string; // Script evaluating to EntityFilter<T>;
    property?: string;
    value?: string; //When property is milestones/tags/metrics
    aggregation: ExpressionPropertyAggregator;
}

export interface SummarizeExpressionGrouping {
    property: string;
    value?: string; //When property is milestones/tags/metrics
    timeGranularity?: TimeGranularity;
}

export interface SummarizeExpressionAggregate {
    name: string;
    aggregation: ExpressionPropertyAggregator;
    property?: string;
    value?: string; //When property is milestones/tags/metrics
}

export interface SummarizeExpression {
    type: "Summarize";
    filter?: string; // Script evaluating to EntityFilter<T>;
    groupings: SummarizeExpressionGrouping[];
    aggregates?: SummarizeExpressionAggregate[];
}

export type QueryExpression = CalculateExpression | CalculateTableExpression | SummarizeExpression;

export interface DashboardWidgetQueryDataSource {
    type: "query";
    name: string;
    sourceCollection: DashboardWidgetAggregateDataSourceSourceCollection;
    contextTypeName?: string;
    query: QueryExpression;
}

export interface DashboardWidgetFunctionDataSource {
    type: "function";
    name: string;
    moduleName: string;
    functionName: string;
    jsArguments?: string;
}

export type DashboardWidgetDataSource = DashboardWidgetQueryDataSource | DashboardWidgetFunctionDataSource;

export interface DashboardWidgetVegaLiteVisualization {
    type: "vega-lite";
    jsSpecification: string;
    variant?: ThemeTypes;
}

export interface DashboardWidgetMarkdownVisualization {
    type: "markdown";
    template: string;
}

export interface DashboardWidgetTabularVisualization {
    type: "tabular";
    jsConfiguration: string;
}

export type DashboardWidgetVisualization =
    | DashboardWidgetVegaLiteVisualization
    | DashboardWidgetMarkdownVisualization
    | DashboardWidgetTabularVisualization;

export interface DashboardWidgetDefinition {
    id?: string;
    name: string;
    title: string;
    description?: string;
    parameters?: DashboardWidgetParameter[];
    dataSources?: DashboardWidgetDataSource[];
    visualization: DashboardWidgetVisualization;
}

export interface DashboardWidgetJsSpec {
    id?: string;
    name: string;
    title: string;
    description?: string;
    widgetJsSpec: string;
}

export type DashboardWidgetDefOrJsSpec = DashboardWidgetDefinition | DashboardWidgetJsSpec;

export interface DashboardWidgetsGetInfos {
    name?: string;
}

export const dashboardWidgetsCollectionName = "NimflowDashboardWidgets";

const stringifyAndInterpolate = (value: any): string | undefined => {
    if (value === undefined) {
        return undefined;
    }
    if (value === null) {
        return "null";
    }
    if (Array.isArray(value)) {
        const elems = value.map(stringifyAndInterpolate).join(", ");
        return `[${elems}]`;
    }
    if (typeof value === "object") {
        if ("$parameter" in value && Object.keys(value).length === 1) {
            return `params.${value["$parameter"]}`;
        } else {
            const fields = Object.entries(value)
                .filter(([, val]) => val !== undefined)
                .map(([key, val]) => `${key}: ${stringifyAndInterpolate(val)}`)
                .join(",");
            return `{${fields}}`;
        }
    }
    if (value instanceof Date) {
        value = value.toISOString();
    }
    if (typeof value === "string" || typeof value === "number" || typeof value === "boolean") {
        return JSON.stringify(value);
    }
    return undefined;
};

const getQueryExpressionFilter = (value: any) => {
    if (!isPlainObject(value)) {
        return undefined;
    }
    const filterEntries: string[] = [];
    for (const [key, val] of Object.entries(value)) {
        const entryValue = stringifyAndInterpolate(val);
        if (entryValue) {
            filterEntries.push(`${key}: ${entryValue}`);
        }
    }
    if (filterEntries.length === 0) {
        return undefined;
    }
    const filter = `queryFilter = {
        ${filterEntries.join(",\n")}
    }`;
    return filter;
};

const getCalculateTableExpression = (value: any) => {
    const expr: CalculateTableExpression = {
        type: "CalculateTable",
    };
    const filter = getQueryExpressionFilter(value.filter);
    if (filter) {
        expr.filter = filter; // Script evaluating to EntityFilter<T>;
    }
    if (typeof value.sortProperty === "string") {
        expr.sortProperty = value.sortProperty;
        if (value.sortDirection === "asc") {
            expr.sortDirection = SortDirection.Ascending;
        } else if (value.sortDirection === "desc") {
            expr.sortDirection = SortDirection.Descending;
        }
    }
    if (typeof value.skip === "number") {
        expr.skip = value.skip;
    }
    if (typeof value.limit === "number") {
        expr.limit = value.limit;
    }
    return expr;
};

const getCalculateExpression = (value: any) => {
    if (!expressionPropertyAggregators.includes(value.aggregation)) {
        return undefined;
    }
    const expr: CalculateExpression = {
        type: "Calculate",
        aggregation: value.aggregation,
    };
    const filter = getQueryExpressionFilter(value.filter);
    if (filter) {
        expr.filter = filter; // Script evaluating to EntityFilter<T>;
    }
    if (typeof value.property === "string") {
        expr.property = value.property;
        if (typeof value.value === "string") {
            expr.value = value.value; //When property is milestones/tags/metrics
        }
    }
    return expr;
};

const getSummarizeExpression = (value: any) => {
    if (!Array.isArray(value.groupings)) {
        return undefined;
    }
    const groupings: SummarizeExpressionGrouping[] = [];
    for (const grouping of value.groupings) {
        if (typeof grouping.property !== "string") {
            return undefined;
        }
        const gp: SummarizeExpressionGrouping = {
            property: grouping.property,
        };
        if (typeof grouping.value === "string") {
            gp.value = grouping.value; //When property is milestones/tags/metrics
        }
        if (timeGranularities.includes(grouping.timeGranularity)) {
            gp.timeGranularity = grouping.timeGranularity;
        }
        groupings.push(gp);
    }
    const expr: SummarizeExpression = {
        type: "Summarize",
        groupings,
    };
    const filter = getQueryExpressionFilter(value.filter);
    if (filter) {
        expr.filter = filter; // Script evaluating to EntityFilter<T>;
    }
    if (Array.isArray(value.aggregates)) {
        const aggregates: SummarizeExpressionAggregate[] = [];
        for (const aggregate of value.aggregates) {
            if (typeof aggregate.name !== "string" || !expressionPropertyAggregators.includes(aggregate.aggregation)) {
                continue;
            }
            const agg: SummarizeExpressionAggregate = {
                name: aggregate.name,
                aggregation: aggregate.aggregation,
            };
            if (typeof aggregate.property === "string") {
                agg.property = aggregate.property;
                if (typeof aggregate.value === "string") {
                    agg.value = aggregate.value; //When property is milestones/tags/metrics
                }
            }
            aggregates.push(agg);
        }
        expr.aggregates = aggregates;
    }
    return expr;
};

const getQueryExpression = (value: any) => {
    switch (value.type) {
        case "CalculateTable": {
            return getCalculateTableExpression(value);
        }
        case "Calculate": {
            return getCalculateExpression(value);
        }
        case "Summarize": {
            return getSummarizeExpression(value);
        }
    }
    return undefined;
};

const getDashboardWidgetQueryDataSource = (name: string, value: any) => {
    if (
        !dashboardWidgetAggregateDataSourceSourceCollections.includes(value.sourceCollection) ||
        !isPlainObject(value.query) ||
        typeof value.query.type !== "string"
    ) {
        return undefined;
    }
    const query = getQueryExpression(value.query);
    if (!query) {
        return undefined;
    }
    const dataSource: DashboardWidgetQueryDataSource = {
        type: "query",
        name,
        sourceCollection: value.sourceCollection,
        query,
    };
    if (typeof value.contextTypeName === "string") {
        dataSource.contextTypeName = value.contextTypeName;
    }
    return dataSource;
};

const getDashboardWidgetFunctionDataSource = (name: string, value: any) => {
    if (!isPlainObject(value) || typeof value.functionName !== "string") {
        return undefined;
    }
    const nameParts = (value.functionName as string).split("/");
    if (nameParts.length !== 2) {
        return undefined;
    }
    const dataSource: DashboardWidgetFunctionDataSource = {
        type: "function",
        name,
        moduleName: nameParts[0],
        functionName: nameParts[1],
    };
    if (isPlainObject(value.args)) {
        const argsEntries: string[] = [];
        for (const [key, val] of Object.entries(value.args)) {
            const entryValue = stringifyAndInterpolate(val);
            if (entryValue) {
                argsEntries.push(`${key}: ${entryValue}`);
            }
        }
        if (argsEntries.length > 0) {
            dataSource.jsArguments = `args = {
                ${argsEntries.join(",\n")}
            }`;
        }
    }
    return dataSource;
};

export const useNimflowDashboardWidgets = () => {
    const {
        collections_Aggregate,
        collections_FindById,
        collections_InsertOne,
        collections_DeleteOne,
        collections_Replace,
        collections_Find,
    } = useNimflow();

    const dashboardWidgets_GetInfos = useCallback(
        async (query?: DashboardWidgetsGetInfos, cancelToken?: CancelToken) => {
            let stages: Stages[] = [
                {
                    $project: {
                        _id: false,
                        id: "$_id",
                        name: 1,
                        title: 1,
                    },
                },
                {
                    $sort: {
                        name: 1,
                    },
                },
            ];
            const name = query?.name;
            if (name) {
                stages = [
                    {
                        $match: {
                            name,
                        },
                    },
                    ...stages,
                ];
            }
            const result: DashboardWidgetDefinitionInfo[] = await collections_Aggregate({
                collectionName: dashboardWidgetsCollectionName,
                stages,
            }, cancelToken);
            return result;
        },
        [collections_Aggregate]
    );

    const dashboardWidgets_FindOne = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result: DashboardWidgetDefOrJsSpec = await collections_FindById({
                collectionName: dashboardWidgetsCollectionName,
                id,
            }, cancelToken);
            if (!result.id) {
                result.id = (result as any)._id;
            }
            return result;
        },
        [collections_FindById]
    );

    const dashboardWidgets_FindOneByName = useCallback(
        async (name: string, cancelToken?: CancelToken) => {
            const result: DashboardWidgetDefOrJsSpec[] = await collections_Find({
                collectionName: dashboardWidgetsCollectionName,
                filter: {
                    name,
                },
            }, cancelToken);
            for (const widget of result) {
                if (!widget.id) {
                    widget.id = (widget as any)._id;
                }
            }
            return result[0];
        },
        [collections_Find]
    );

    const dashboardWidgets_FindAllById = useCallback(
        async (ids: string[], cancelToken?: CancelToken) => {
            const result: DashboardWidgetDefOrJsSpec[] = await collections_Find({
                collectionName: dashboardWidgetsCollectionName,
                filter: {
                    _id: {$in: ids},
                },
            }, cancelToken);
            for (const widget of result) {
                if (!widget.id) {
                    widget.id = (widget as any)._id;
                }
            }
            return result;
        },
        [collections_Find]
    );

    const dashboardWidgets_FindAllByName = useCallback(
        async (names: string[], cancelToken?: CancelToken) => {
            const result: DashboardWidgetDefOrJsSpec[] = await collections_Find({
                collectionName: dashboardWidgetsCollectionName,
                filter: {
                    name: {$in: names},
                },
            }, cancelToken);
            for (const widget of result) {
                if (!widget.id) {
                    widget.id = (widget as any)._id;
                }
            }
            return result;
        },
        [collections_Find]
    );

    const dashboardWidgets_InsertOne = useCallback(
        async (widgetDefinition: DashboardWidgetDefOrJsSpec, cancelToken?: CancelToken) => {
            const result = await collections_InsertOne({
                collectionName: dashboardWidgetsCollectionName,
                document: widgetDefinition,
            }, cancelToken);
            return result.id;
        },
        [collections_InsertOne]
    );

    const dashboardWidgets_Replace = useCallback(
        async (widgetDefinition: DashboardWidgetDefOrJsSpec, cancelToken?: CancelToken) => {
            const result = await collections_Replace({
                id: widgetDefinition.id ?? (widgetDefinition as any)._id,
                collectionName: dashboardWidgetsCollectionName,
                replacement: widgetDefinition,
            }, cancelToken);
            return result;
        },
        [collections_Replace]
    );

    const dashboardWidgets_Delete = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result = await collections_DeleteOne({
                collectionName: dashboardWidgetsCollectionName,
                id,
            }, cancelToken);
            return result;
        },
        [collections_DeleteOne]
    );

    const getWidgetDefinition = useCallback(
        async (defOrSpec: DashboardWidgetDefOrJsSpec): Promise<DashboardWidgetDefinition> => {
            if (!isDashboardWidgetJsSpec(defOrSpec)) {
                return defOrSpec;
            }

            const outputValues = {
                parameters: undefined as any,
                dataSources: undefined as any,
                visualization: undefined as any,
            };
            evaluateScript(defOrSpec.widgetJsSpec, {
                outputValues,
            });

            const parameters: DashboardWidgetParameter[] = [];
            if (isPlainObject(outputValues.parameters)) {
                for (const [name, value] of Object.entries(outputValues.parameters)) {
                    if (!isPlainObject(value) || !dashboardWidgetParameterTypes.includes(value.type)) {
                        continue;
                    }
                    const param: DashboardWidgetParameter = {
                        name,
                        type: value.type,
                        required: typeof value.required === "boolean" ? value.required : false,
                    };
                    parameters.push(param);
                }
            }

            const dataSources: DashboardWidgetDataSource[] = [];
            if (isPlainObject(outputValues.dataSources)) {
                for (const [name, value] of Object.entries(outputValues.dataSources)) {
                    if (!isPlainObject(value) || typeof value.type !== "string") {
                        continue;
                    }
                    switch (value.type) {
                        case "query": {
                            const dataSource = getDashboardWidgetQueryDataSource(name, value);
                            if (dataSource) {
                                dataSources.push(dataSource);
                            }
                            break;
                        }
                        case "function": {
                            const dataSource = getDashboardWidgetFunctionDataSource(name, value);
                            if (dataSource) {
                                dataSources.push(dataSource);
                            }
                            break;
                        }
                    }
                }
            }

            const jsVisualization = outputValues.visualization;
            let visualization: DashboardWidgetVisualization = {
                type: "markdown",
                template: "VISUALIZATION NOT DEFINED",
            };
            if (isPlainObject(jsVisualization) && typeof jsVisualization.type === "string") {
                switch (jsVisualization.type) {
                    case "markdown": {
                        visualization.template = jsVisualization.template;
                        break;
                    }
                    case "vega-lite": {
                        let jsSpecification = "vegaLiteSpec = ";
                        jsSpecification += stringifyAndInterpolate(
                            isPlainObject(jsVisualization.specification) ? jsVisualization.specification : {}
                        );
                        jsSpecification += ";";
                        visualization = {
                            type: "vega-lite",
                            jsSpecification,
                            variant: jsVisualization.variant,
                        };
                        break;
                    }
                    case "tabular": {
                        let jsConfiguration = "tabularConfig = ";
                        jsConfiguration += stringifyAndInterpolate(
                            isPlainObject(jsVisualization.configuration) ? jsVisualization.configuration : {}
                        );
                        jsConfiguration += ";";
                        visualization = {
                            type: "tabular",
                            jsConfiguration,
                        };
                        break;
                    }
                }
            }

            const def: DashboardWidgetDefinition = {
                id: defOrSpec.id,
                name: defOrSpec.name,
                title: defOrSpec.title,
                description: defOrSpec.description,
                parameters,
                dataSources,
                visualization,
            };

            return def;
        },
        []
    );

    return {
        dashboardWidgets_GetInfos,
        dashboardWidgets_FindOne,
        dashboardWidgets_FindOneByName,
        dashboardWidgets_InsertOne,
        dashboardWidgets_Replace,
        dashboardWidgets_Delete,
        dashboardWidgets_FindAllById,
        dashboardWidgets_FindAllByName,
        getWidgetDefinition,
    };
};

export const isDashboardWidgetJsSpec = (defOrSpec: DashboardWidgetDefOrJsSpec): defOrSpec is DashboardWidgetJsSpec => {
    return "widgetJsSpec" in defOrSpec;
};

export const isListParameter = (param: DashboardWidgetParameter) => {
    return param.type === "stringList";
};

export const getScalarPropertyNamesFor = (collection: DashboardWidgetAggregateDataSourceSourceCollection) => {
    switch (collection) {
        case "contexts":
            return contextScalarPropertyNames;
        case "tasks":
            return contextTaskScalarPropertyNames;
        case "events":
            return eventScalarPropertyNames;
    }
};

export const getScalarPropertyTypeFor = (
    collection: DashboardWidgetAggregateDataSourceSourceCollection,
    name: string
): "string" | "number" | "boolean" | "dateTime" => {
    switch (collection) {
        case "contexts":
            switch (name) {
                case "milestones":
                    return "dateTime";
                case "tags":
                    return "string";
                case "metrics":
                    return "number";
                default:
                    return (contextTypeDescriptor as any)[name] as any;
            }
        case "tasks":
            return (contextTaskTypeDescriptor as any)[name] as any;
        case "events":
            return (eventTypeDescriptor as any)[name] as any;
    }
};

export const getItemPropertyTypeFor = (
    collection: DashboardWidgetAggregateDataSourceSourceCollection,
    name: string
): "string" | "number" | "boolean" | "dateTime" => {
    switch (collection) {
        case "contexts":
            return (contextTypeDescriptor as any)[name] as any;
        case "tasks":
            return (contextTaskTypeDescriptor as any)[name] as any;
        case "events":
            return (eventTypeDescriptor as any)[name] as any;
    }
};

export const getValidAggregatorsForType = (propertyType?: string) => {
    const result: ExpressionPropertyAggregator[] = [];
    switch (propertyType) {
        case "string":
        case "dateTime":
            result.push("count", "max", "min");
            break;
        case "number":
            result.push(...expressionPropertyAggregators);
            break;
        default:
            result.push("count");
            break;
    }
    return result;
};

export const getAggregatorsForProperty = (
    collection: DashboardWidgetAggregateDataSourceSourceCollection,
    propertyName?: string
) => {
    const type = isPresent(propertyName) ? getScalarPropertyTypeFor(collection, propertyName) : undefined;
    return getValidAggregatorsForType(type);
};

export const getValidTransformationsForType = (propertyType?: string) => {
    const result: string[] = [];
    switch (propertyType) {
        case "dateTime":
            const fn = "timeGranularity:";
            result.push(...timeGranularities.map(s => fn + s));
            break;
    }
    return result;
};

export const getTransformationsForProperty = (
    collection: DashboardWidgetAggregateDataSourceSourceCollection,
    propertyName: string
) => {
    const type = getScalarPropertyTypeFor(collection, propertyName);
    return getValidTransformationsForType(type);
};

type TypeName<T> = T extends StringArrayPropertyFilter
    ? "string[]"
    : T extends StringMapPropertyFilter
    ? "StringMap"
    : T extends NumberMapPropertyFilter
    ? "NumberMap"
    : T extends DateMapPropertyFilter
    ? "DateMap"
    : T extends StringPropertyFilter
    ? "string"
    : T extends NumberPropertyFilter
    ? "number"
    : T extends BooleanPropertyFilter
    ? "boolean"
    : T extends DatePropertyFilter
    ? "Date"
    : never;

export type FilterTypeDescriptor<T extends object> = {
    [K in keyof T]-?: TypeName<T[K]>;
};

export const contextFilterDescriptor: FilterTypeDescriptor<ContextFilter> = {
    id: "string",
    typeName: "string",
    reference: "string",
    active: "boolean",
    excludeContextTypeVersion: "number",
    created: "Date",
    modified: "Date",
    reachedMilestones: "string[]",
    milestones: "DateMap",
    tags: "StringMap",
    metrics: "NumberMap",
};

export const contextTaskFilterDescriptor: FilterTypeDescriptor<ContextTaskFilter> = {
    contextId: "string",
    contextTypeName: "string",
    contextReference: "string",
    typeName: "string",
    title: "string",
    assignedTo: "string",
    unassigned: "boolean",
    status: "string",
    archived: "boolean",
    focused: "boolean",
    importance: "number",
    dueDate: "Date",
    labels: "string[]",
};

export const eventFilterDescriptor: FilterTypeDescriptor<EventFilter> = {
    event: "string",
    timestamp: "Date",
    requestTypeName: "string",
    contextTypeName: "string",
    sentBy: "string",
    reference: "string",
    subject: "string",
    //duration: "string",
    contextIsNew: "boolean",
    action: "string",
    message: "string",
    dueDate: "Date",
    taskTypeName: "string",
    assignedTo: "string",
    importance: "number",
    responseTypeName: "string",
    oldImportance: "number",
    oldLabels: "string[]",
    oldDueDate: "Date",
    oldAssignedTo: "string",
    milestoneName: "string",
    milestoneValue: "Date",
    eventName: "string",
    tags: "StringMap",
    metrics: "NumberMap",
};
