import {generateDefaultUISchema, UISchemaElement} from "@jsonforms/core";
import {DialogProps} from "@material-ui/core/Dialog";

import {ContextType} from "../api/nimflow-api";
import {getSettings} from "../api/nimflow-api-extensions";
import {countLayoutColumns} from "../libs/jsonforms/layout-column-counter";
import {getActionJsonSchema, getResponseJsonSchema} from "./json-schema";

const getDialogMaxWidthForUiSchema = (
    uischema: UISchemaElement | undefined,
    contextType: ContextType
): DialogProps["maxWidth"] => {
    const uischemaCommonStr = getSettings(contextType)?.uiSchemaCommon;
    const uischemaCommon = !!uischemaCommonStr ? JSON.parse(uischemaCommonStr) : undefined;
    const cols = uischema ? countLayoutColumns(uischema, uischemaCommon) : 1;
    if (cols >= 3) {
        return "xl";
    }
    if (cols === 2) {
        return "lg";
    }
    return "md";
};

export const getDialogMaxWidthForActionType = (
    contextType: ContextType,
    actionName: string
): DialogProps["maxWidth"] => {
    const actionType = contextType.actions.find(s => s.name === actionName);
    const schema = actionType ? getSettings(actionType)?.uiSchema : undefined;
    let uischema = schema ? (JSON.parse(schema) as UISchemaElement) : undefined;
    if (!uischema && actionType) {
        const jsonSchema = getActionJsonSchema(contextType, actionName);
        if (jsonSchema) {
            uischema = generateDefaultUISchema(jsonSchema as any);
        }
    }
    return getDialogMaxWidthForUiSchema(uischema, contextType);
};

export const getDialogMaxWidthForResponseType = (
    contextType: ContextType,
    taskTypeName: string,
    responseTypeName: string
): DialogProps["maxWidth"] => {
    const taskType = contextType.tasks.find(s => s.name === taskTypeName);
    const responseType = taskType?.validResponses.find(s => s.name === responseTypeName);
    const schema = responseType ? getSettings(responseType)?.uiSchema : undefined;
    let uischema = schema ? (JSON.parse(schema) as UISchemaElement) : undefined;
    if (!uischema && responseType) {
        const jsonSchema = getResponseJsonSchema(contextType, taskTypeName, responseTypeName);
        if (jsonSchema) {
            uischema = generateDefaultUISchema(jsonSchema as any);
        }
    }
    return getDialogMaxWidthForUiSchema(uischema, contextType);
};
