import {FC} from "react";
import {JsonSchema7, UISchemaElement} from "@jsonforms/core";
import {JsonFormsInitStateProps, JsonFormsReactProps} from "@jsonforms/react";
import {JsonForms as OriginalJsonForms} from "@jsonforms/react";

import {renderers, cells} from "./index";
import {JsonFormsContextProvider} from "./jsonforms-context";
import {useAsync} from "react-use";
import {dereferenceSchema} from "../json-schema-ref-parser";
import {WaitSpinner} from "../../components/wait-spinner";

export type JsonFormsProps = {
    context?: Record<string, any>;
    bindContext?: boolean;
    uiSchemaLibrary?: Record<string, UISchemaElement>;
} & Omit<JsonFormsInitStateProps, "cells" | "renderers"> &
    JsonFormsReactProps;

export const JsonForms: FC<JsonFormsProps> = props => {
    const {context, bindContext, uiSchemaLibrary, schema, ...rest} = props;
    const {
        value: resolvedSchema,
        loading,
        error,
    } = useAsync(async () => {
        if (!schema) {
            return undefined;
        }
        const result = await dereferenceSchema(schema as any);
        return result as JsonSchema7;
    }, [schema]);

    if (loading) {
        return <WaitSpinner />;
    }
    if (error) {
        throw error;
    }
    return (
        <JsonFormsContextProvider context={context} bindContext={bindContext} uiSchemaLibrary={uiSchemaLibrary}>
            <OriginalJsonForms renderers={renderers} cells={cells} schema={resolvedSchema} {...rest} />
        </JsonFormsContextProvider>
    );
};
