import {UISchemaElement, HorizontalLayout, Layout} from "@jsonforms/core";

import {ImageBatchRecognitionElement} from "./image-batch-recognition/image-batch-recognition-element";
import {UiSchemaVisitor} from "./uischema-visitor";

class LayoutColumnCounter extends UiSchemaVisitor {
    public override visit(schema: UISchemaElement | undefined): UISchemaElement | undefined {
        this.columnCount = 1;
        return super.visit(schema);
    }

    protected override processLayoutElements(layout: Layout) {
        this.columnCount = layout.elements.reduce((cnt, elem) => {
            this.visit(elem);
            return Math.max(cnt, this.columnCount);
        }, 0);
    }

    protected override visitHorizontalLayout(schema: HorizontalLayout): UISchemaElement | undefined {
        this.columnCount = schema.elements.reduce((cnt, elem) => {
            this.visit(elem);
            return cnt + this.columnCount;
        }, 0);
        return schema;
    }

    protected override visitImageBatchRecognitionElement(schema: ImageBatchRecognitionElement) {
        this.columnCount = 10; // Try to force maximum width to make room for this editor
        return schema;
    }

    public columnCount = 0;
}

export const countLayoutColumns = (
    schema: UISchemaElement,
    schemaLibrary?: Record<string, UISchemaElement>
): number => {
    const counter = new LayoutColumnCounter(schemaLibrary);
    counter.visit(schema);
    return counter.columnCount;
};
