import {createAjv} from "@jsonforms/core";

//Use JsonForms internal Ajv (v6).
export const jsonFormsAjv = createAjv({
    allErrors: true,
    addUsedSchema: false,
});

jsonFormsAjv.addFormat("image", () => true);
jsonFormsAjv.addKeyword("patternMask", {
    type: "string",
    valid: true,
    errors: false,
});
