import {JSONSchema7} from "json-schema";
import {isPresent} from "ts-is-present";
import {clone} from "ramda";

import {ContextType} from "../api/nimflow-api";

export const buildDefinitionSchema = (schema: JSONSchema7, definitionName: string): JSONSchema7 => {
    const def = schema.definitions?.[definitionName];
    if (!isPresent(def)) {
        throw new Error(`Invalid definition ${definitionName}`);
    }
    if (typeof def === "boolean") {
        throw new Error(`Invalid definition ${definitionName}`);
    }
    const clonedDef = clone(def);
    delete clonedDef.definitions;
    schema = {
        ...schema,
        ...clonedDef,
    };
    return schema;
};

export const getResponseJsonSchema = (
    contextType: ContextType,
    taskTypeName: string,
    responseTypeName: string
): JSONSchema7 | undefined => {
    if (!contextType.jsonSchema) {
        return undefined;
    }
    const jsonSchema = JSON.parse(contextType.jsonSchema);
    const taskType = contextType.tasks.find(s => s.name === taskTypeName);
    const responseType = taskType?.validResponses.find(s => s.name === responseTypeName);
    const schemaName = responseType?.payloadSchemaDefinitionName;
    if (!schemaName) {
        return undefined;
    }
    const schema = buildDefinitionSchema(jsonSchema, schemaName);
    return schema;
};

export const getActionJsonSchema = (contextType: ContextType, actionTypeName: string): JSONSchema7 | undefined => {
    if (!contextType.jsonSchema) {
        return undefined;
    }
    const jsonSchema = JSON.parse(contextType.jsonSchema);
    const actionType = contextType.actions.find(s => s.name === actionTypeName);
    const schemaName = actionType?.payloadSchemaDefinitionName;
    if (!schemaName) {
        return undefined;
    }
    const schema = buildDefinitionSchema(jsonSchema, schemaName);
    return schema;
};
