import {FC, useMemo} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import Hidden from "@material-ui/core/Hidden";
import {rankWith, uiTypeIs, RankedTester, LayoutProps} from "@jsonforms/core";
import {withJsonFormsLayoutProps} from "@jsonforms/react";

import {useJsonForms} from "./jsonforms-context";
import {evaluateHandlebars} from "../handlebars";
import {PdfElement} from "./pdf-element";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        viewer: {
            width: "100%",
            minHeight: "800px"
        },
    })
);

const PdfRenderer: FC<LayoutProps> = props => {
    const classes = useStyles();
    const {uischema, visible, data, path} = props;
    const {bindContext, context} = useJsonForms();
    const url = useMemo(() => {
        const pdfElement = uischema as PdfElement;
        if (!pdfElement && !("url" in pdfElement)) {
            return undefined;
        }
        let ctx: Record<string, any> = {    
            data,
            path,
        };
        if (bindContext) {
            Object.assign(ctx, context);
        }
        return evaluateHandlebars(pdfElement.url, ctx);
    }, [uischema, data, path, bindContext, context]);
    if (!url) {
        return null;
    }
    return (
        <Hidden xsUp={!visible || !url}>
            <object data={url} type="application/pdf" className={classes.viewer}>
                <embed src={url} type="application/pdf" />
            </object>
        </Hidden>
    );
};

export default withJsonFormsLayoutProps(PdfRenderer);

export const pdfRendererTester: RankedTester = rankWith(
    3, //increase rank as needed
    uiTypeIs("Pdf")
);
