import React, {FC, useMemo} from "react";
import ReactMarkdown from "react-markdown";

import {DashboardWidgetMarkdownVisualization} from "../../api/nimflow-dashboard-widgets-api";
import {evaluateHandlebars} from "../../libs/handlebars";
import {DashboardWidgetRendererProps} from "./dashboard-widget-viewer";

export interface MarkdownRendererProps extends DashboardWidgetRendererProps<DashboardWidgetMarkdownVisualization> {}

export const MarkdownRenderer: FC<MarkdownRendererProps> = props => {
    const {visualization, params, data} = props;

    const template = useMemo(() => {
        //return evaluateHandlebars(visualization.template + "\n\nContext: {{debug}}", {params, data, debug: JSON.stringify({params, data})});
        return evaluateHandlebars(visualization.template, {params, data});
    }, [data, params, visualization.template]);

    return <ReactMarkdown>{template}</ReactMarkdown>;
};
