import React, {FC, useCallback} from "react";
import Chip, {ChipProps} from "@material-ui/core/Chip";
import i18next from "i18next";
import {isPresent} from "ts-is-present";
import {format, parseISO} from "date-fns";

import {TaskViewFilter, TaskViewColumnNames} from "./tasks-view";

export type TasksViewFilterChipProps = {
    field: TaskViewColumnNames;
    value?: TaskViewFilter<any>;
    title: string;
    onClearFilter?: (field: TaskViewColumnNames) => void;
} & Omit<ChipProps, "onDelete" | "label">;

const getFilterValueDescription = (filter?: TaskViewFilter<any>): React.ReactNode => {
    switch (filter?.type) {
        case "boolean":
            return i18next.t(filter.value ? "Yes" : "No");
        case "textMatch":
            return filter.value;
        case "selection":
            return filter.value !== null ? filter.value : i18next.t("NullValueText");
        case "prefixTextMatch": {
            return `${filter.prefix}*`;
        }
        case "dateRange": {
            let result = "";
            if (isPresent(filter.from)) {
                result += filter.includeFrom ? "[" : "(";
                result += format(parseISO(filter.from), i18next.t("DATE_FORMAT"));
            } else {
                result += "(-∞";
            }
            result += ", ";
            if (isPresent(filter.to)) {
                result += format(parseISO(filter.to), i18next.t("DATE_FORMAT"));
                result += filter.includeTo ? "]" : ")";
            } else {
                result += "∞)";
            }
            return result;
        }
        case "numberRange": {
            let result = "";
            if (isPresent(filter.from)) {
                result += filter.includeFrom ? "[" : "(";
                result += filter.from;
            } else {
                result += "(-∞";
            }
            result += ", ";
            if (isPresent(filter.to)) {
                result += filter.to;
                result += filter.includeTo ? "]" : ")";
            } else {
                result += "∞)";
            }
            return result;
        }
        default:
            return "??";
    }
};

export const TasksViewFilterChip: FC<TasksViewFilterChipProps> = props => {
    const {field, title, value, onClearFilter, size, variant, ...rest} = props;

    const handleDelete = useCallback(() => {
        onClearFilter?.(field);
    }, [field, onClearFilter]);

    return (
        <Chip
            onDelete={handleDelete}
            label={`${title}: ${getFilterValueDescription(value)}`}
            size={size ?? "small"}
            variant={variant ?? "outlined"}
            {...rest}
        ></Chip>
    );
};
