import React, {FC, useCallback, useState} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import clsx from "clsx";
import Avatar from "@material-ui/core/Avatar";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import {usePopupState, bindTrigger, bindMenu} from "material-ui-popup-state/hooks";
import {useTranslation} from "react-i18next";

import {ReactComponentProps} from "../../utils/types";
import {useCurrentUnitInfo} from "../../api/nimflow-business-directory-provider";
import {UnitSwitchDialog} from "./unit-switch-dialog";
import {useOkCancelConfirm} from "../../components/confirm";
import {SystemInfoDialog} from "./system-info-dialog";
import {useAuthenticationService} from "../auth/authentication-service";

const useStyles = makeStyles(() =>
    createStyles({
        header: {
            display: "flex",
            alignItems: "center",
        },
        unitContainer: {
            flex: "1 1 auto",
            textAlign: "right",
            //paddingLeft: 20,
        },
        userName: {
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
            fontSize: 14,
            maxWidth: 160,
            textTransform: "none",
            fontWeight: "normal",
        },
        unit: {
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
            fontSize: 10,
            fontWeight: 600,
            maxWidth: 160,
            textTransform: "uppercase",
        },
        avatar: {
            flex: "0 0 auto",
            marginLeft: 6,
        },
    })
);
export type UserMenuButtonProps = Omit<ReactComponentProps<typeof Button>, "onClick">;

export const UserMenuButton: FC<UserMenuButtonProps> = props => {
    const {className, ...rest} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [switchUnit, setSwitchUnit] = useState(false);
    const [showInfo, setShowInfo] = useState(false);
    const {isAuthenticated, logout, supportsPasswordReset, startResetPasswordFlow, authenticatedUser} = useAuthenticationService();
    const okCancelConfirm = useOkCancelConfirm();
    const {unit} = useCurrentUnitInfo();
    const popupState = usePopupState({
        variant: "popover",
        popupId: "profileMenu",
    });

    const handleLogout = useCallback(() => {
        logout();
    }, [logout]);

    const handleSwitchUnit = useCallback(() => {
        popupState.close();
        setSwitchUnit(true);
    }, [popupState]);

    const handleChangePassword = useCallback(async () => {
        popupState.close();
        if (!authenticatedUser?.email) {
            await okCancelConfirm({
                title: t("Error"),
                description: t("UserEmailNotSet"),
                cancellationButtonProps: {
                    style: {
                        visibility: "hidden",
                    },
                },
            });
            return;
        }
        if (
            await okCancelConfirm({
                title: t("ChangePassword"),
                description: t("ChangePasswordDescription"),
                confirmationText: t("SendMail"),
            })
        ) {
            try {
                await startResetPasswordFlow(authenticatedUser?.email);
            } catch {
                await okCancelConfirm({
                    title: t("Error"),
                    description: t("ErrorChangingPassword"),
                    cancellationButtonProps: {
                        style: {
                            visibility: "hidden",
                        },
                    },
                });
                return;
            }
        }
    }, [okCancelConfirm, popupState, startResetPasswordFlow, t, authenticatedUser?.email]);

    const handleShowInfo = useCallback(() => {
        popupState.close();
        setShowInfo(true);
    }, [popupState]);

    if (!isAuthenticated) {
        return null;
    }

    return (
        <>
            <Button className={clsx(classes.header, className)} {...rest} {...bindTrigger(popupState)}>
                <div className={classes.unitContainer}>
                    <div className={classes.userName}>{authenticatedUser?.name}</div>
                    <div className={classes.unit}>{unit?.name}</div>
                </div>
                <Avatar src={authenticatedUser?.picture} alt={authenticatedUser?.name} className={classes.avatar} />
                <ExpandMoreIcon />
            </Button>
            <Menu {...bindMenu(popupState)}>
                <MenuItem>{authenticatedUser?.name}</MenuItem>
                <Divider />
                <MenuItem onClick={handleSwitchUnit}>{t("SwitchBusinessUnit")}</MenuItem>
                {supportsPasswordReset && <MenuItem onClick={handleChangePassword}>{t("ChangePassword")}</MenuItem>}
                <MenuItem onClick={handleShowInfo}>{t("SystemInfo")}</MenuItem>
                <Divider />
                <MenuItem onClick={handleLogout}>{t("Logout")}</MenuItem>
            </Menu>
            {switchUnit && <UnitSwitchDialog open={switchUnit} onClose={() => setSwitchUnit(false)} />}
            {showInfo && <SystemInfoDialog open={showInfo} onClose={() => setShowInfo(false)} />}
        </>
    );
};
