import React, {FC, useState, useCallback, useRef, useEffect} from "react";
import TextField from "@material-ui/core/TextField";
import {useDebounce} from "react-use";

import {TasksViewFilterEntryEditorProps} from "./tasks-view-filter";
import {TextMatchTaskViewFilter} from "./tasks-view";

export const TextMatchFilter: FC<TasksViewFilterEntryEditorProps<TextMatchTaskViewFilter>> = props => {
    const {value, onChange} = props;
    const [val, setVal] = useState(value?.type === "textMatch" ? value.value : undefined);
    const hasPendingChangesRef = useRef(false);
    const inputRef = useRef<any>();

    useEffect(() => {
        if (inputRef.current && !hasPendingChangesRef.current && value?.value !== val) {
            setVal(value?.value);
            inputRef.current.value = value?.value ?? "";
        }
    }, [val, value]);

    useDebounce(
        () => {
            hasPendingChangesRef.current = false;
            onChange?.(val ? {type: "textMatch", value: val} : undefined);
        },
        2000,
        [val, onChange]
    );

    const handleChange = useCallback((ev: React.ChangeEvent<any>) => {
        hasPendingChangesRef.current = true;
        setVal(ev.target.value);
    }, []);

    return <TextField value={val} onChange={handleChange} inputRef={inputRef} />;
};
