import React, {createContext, FC, useContext, useCallback, useState, useMemo} from "react";
import {clone, omit, mapObjIndexed} from "ramda";
import {subMinutes, subDays, subHours, format} from "date-fns";

import {
    DashboardDefinition,
    DashboardParameterState,
    getVariableNames,
    DashboardParameter,
    DynamicDashboardParameterTimeRangeValue,
    DashboardParameterTimeRangeValue,
} from "../../api/nimflow-dashboard-api";
import {
    DashboardWidgetDefinition,
    DashboardWidgetParameterType,
    isListParameter,
} from "../../api/nimflow-dashboard-widgets-api";
import {useQueryDataSourceEvaluator} from "./query-data-source-evaluator";
import {useFunctionDataSourceEvaluator} from "./function-data-source-evaluator";

export interface DashboardVariableDefinition {
    name: string;
    type: DashboardWidgetParameterType;
    declaringParameterId: string;
}

export interface DashboardService {
    getParameterState: (parameterId: string) => DashboardParameterState;
    setParameterState: (parameterId: string, state: DashboardParameterState) => void;
    getWidgetDefinition: (widgetDefinitionName: string) => DashboardWidgetDefinition | undefined;
    getVariableValue: (name: string) => any;
    getWidgetData: (widgetInstanceId: string) => Promise<Record<string, any>>;
    getWidgetVariables: (widgetInstanceId: string) => Promise<Record<string, any>>;
    updateDynamicVariableValues: () => Promise<void>;
    registerWidgetRefreshTrigger: (instanceId: string, trigger: () => Promise<any>) => void;
    unregisterWidgetRefreshTrigger: (instanceId: string, trigger: () => Promise<any>) => void;
    refreshWidgetData: (instanceId: string) => Promise<any>;
    refreshAllWidgetData: () => Promise<any>;
    dashboard: DashboardDefinition;
    variableDefinitions: DashboardVariableDefinition[];
}

const stub = (): never => {
    throw new Error("You forgot to wrap your component in <DashboardContextProvider>.");
};

const initialDashboardContext: DashboardService = {
    getParameterState: stub,
    setParameterState: stub,
    getWidgetDefinition: stub,
    getVariableValue: stub,
    updateDynamicVariableValues: stub,
    getWidgetData: stub,
    getWidgetVariables: stub,
    registerWidgetRefreshTrigger: stub,
    unregisterWidgetRefreshTrigger: stub,
    refreshWidgetData: stub,
    refreshAllWidgetData: stub,
    dashboard: {
        name: "",
        title: "",
        widgets: [],
    },
    variableDefinitions: [],
};

const DashboardContext = createContext<DashboardService>(initialDashboardContext);

export interface DashboardContextProviderProps {
    dashboard: DashboardDefinition;
    widgets: DashboardWidgetDefinition[];
}

const defaultState: DashboardParameterState = {
    selectAll: false,
    values: [],
};

const getVariableDefinitions = (parameters: DashboardParameter[]) => {
    const result: DashboardVariableDefinition[] = [];
    for (const param of parameters) {
        for (const name of getVariableNames(param)) {
            result.push({
                name,
                declaringParameterId: param.id,
                type:
                    param.selectionType === "timeRange" ? "datetime" : (param.dataType as DashboardWidgetParameterType),
            });
        }
    }
    return result;
};

export const resolveDynamicTimeRange = (value: DynamicDashboardParameterTimeRangeValue): [Date, Date] => {
    const end = new Date();
    let start = new Date();
    switch (value.range.unit) {
        case "minutes": {
            start = subMinutes(start, value.range.count);
            break;
        }
        case "hours": {
            start = subHours(start, value.range.count);
            break;
        }
        case "days": {
            start = subDays(start, value.range.count);
            break;
        }
    }
    return [start, end];
};

const calculateVariableValue = (name: string, parameter: DashboardParameter, state: DashboardParameterState) => {
    switch (parameter.selectionType) {
        case "freeText": {
            return state.values[0]?.value;
        }
        case "multiple": {
            if (state.selectAll) {
                return undefined;
            }
            return state.values.map(s => s.value);
        }
        case "single": {
            if (state.selectAll) {
                return undefined;
            }
            return state.values[0]?.value;
        }
        case "timeRange": {
            const val = state.values[0]?.value as DashboardParameterTimeRangeValue | undefined;
            if (!val) {
                return undefined;
            }
            if (val.type === "static") {
                return parameter.beginVariableName === name ? val.range.start : val.range.end;
            }
            const [from, to] = resolveDynamicTimeRange(val);
            return parameter.beginVariableName === name ? from.toISOString() : to.toISOString();
        }
    }
};

const getParameterVariableValues = (
    parameter: DashboardParameter,
    state: DashboardParameterState
): Record<string, any> => {
    const values: Record<string, any> = {};
    switch (parameter.selectionType) {
        case "timeRange":
            values[parameter.beginVariableName] = calculateVariableValue(parameter.beginVariableName, parameter, state);
            values[parameter.endVariableName] = calculateVariableValue(parameter.endVariableName, parameter, state);
            break;
        default:
            values[parameter.variableName] = calculateVariableValue(parameter.variableName, parameter, state);
    }
    return values;
};

export const DashboardContextProvider: FC<DashboardContextProviderProps> = props => {
    const {dashboard, widgets, children} = props;
    const [paramaterValues, setParamaterValues] = useState<Record<string, DashboardParameterState>>(() => {
        const values: Record<string, DashboardParameterState> = {};
        if (dashboard.parameters) {
            for (const param of dashboard.parameters) {
                values[param.id] = clone(param.defaultValue) ?? defaultState;
            }
        }
        return values;
    });
    const variableDefinitions = useMemo(() => {
        return dashboard.parameters ? getVariableDefinitions(dashboard.parameters) : [];
    }, [dashboard.parameters]);
    const [variableValues, setVariableValues] = useState<Record<string, any>>(() => {
        let values: Record<string, any> = {};
        if (dashboard.parameters) {
            for (const param of dashboard.parameters) {
                values = {
                    ...values,
                    ...getParameterVariableValues(param, paramaterValues[param.id]),
                };
            }
        }
        return values;
    });
    const [dataFetchTriggers, setDataFetchTriggers] = useState<Record<string, () => Promise<boolean>>>({});
    const {evaluateQueryDataSource} = useQueryDataSourceEvaluator();
    const {evaluateFunctionDataSource} = useFunctionDataSourceEvaluator();

    const getParameterState = useCallback(
        (parameterId: string) => {
            return paramaterValues[parameterId];
        },
        [paramaterValues]
    );

    const setParameterState = useCallback(
        (parameterId: string, state: DashboardParameterState) => {
            setParamaterValues(s => ({
                ...s,
                [parameterId]: state,
            }));
            const param = dashboard.parameters!.find(s => s.id === parameterId)!;
            setVariableValues(s => ({
                ...s,
                ...getParameterVariableValues(param, state),
            }));
        },
        [dashboard.parameters]
    );

    const getWidgetDefinition = useCallback(
        (widgetDefinitionName: string) => {
            return widgets.find(s => s.name === widgetDefinitionName);
        },
        [widgets]
    );

    const getVariableValue = useCallback(
        (name: string): any => {
            return variableValues[name];
        },
        [variableValues]
    );

    const updateDynamicVariableValues = useCallback(() => {
        if (dashboard.parameters) {
            let newValues: Record<string, any> = {};
            for (const param of dashboard.parameters.filter(s => s.selectionType === "timeRange")) {
                newValues = {
                    ...newValues,
                    ...getParameterVariableValues(param, paramaterValues[param.id]),
                };
            }
            if (Object.keys(newValues).length > 0) {
                setVariableValues(s => ({
                    ...s,
                    ...newValues,
                }));
            }
        }
        return Promise.resolve();
    }, [dashboard.parameters, paramaterValues]);

    const getWidgetData = useCallback(
        async (widgetInstanceId: string): Promise<Record<string, any>> => {
            const inst = dashboard.widgets.find(s => s.instantiationId === widgetInstanceId);
            const widget = widgets.find(s => s.name === inst?.widgetName);
            if (!inst || !widget?.dataSources) {
                return {};
            }
            const args = mapObjIndexed(varName => {
                return getVariableValue(varName);
            }, inst.parametersBindings);
            const result: Record<string, any> = {};
            const promises: Promise<any>[] = [];
            for (const ds of widget.dataSources) {
                let promise = Promise.resolve<any>([]);
                switch (ds.type) {
                    case "query": {
                        const timezone = format(new Date(), "XXX");
                        promise = evaluateQueryDataSource(ds, args, timezone);
                        break;
                    }
                    case "function": {
                        promise = evaluateFunctionDataSource(ds, args);
                        break;
                    }
                }
                promise = promise.then(s => (result[ds.name] = s));
                promises.push(promise);
            }
            await Promise.all(promises);
            return result;
        },
        [dashboard.widgets, evaluateFunctionDataSource, evaluateQueryDataSource, getVariableValue, widgets]
    );

    const getWidgetVariables = useCallback(
        (widgetInstanceId: string) => {
            const inst = dashboard.widgets.find(s => s.instantiationId === widgetInstanceId);
            const widget = widgets.find(s => s.name === inst?.widgetName);
            const result: Record<string, any> = {};
            if (inst && widget?.dataSources) {
                for (const [widgetParamName, dashboardVariableName] of Object.entries(inst.parametersBindings)) {
                    const wparam = widget.parameters?.find(s => s.name === widgetParamName);
                    if (wparam) {
                        let value = getVariableValue(dashboardVariableName);
                        if (isListParameter(wparam) && value && !Array.isArray(value)) {
                            value = [value];
                        }
                        result[widgetParamName] = value;
                    }
                }
            }
            return Promise.resolve(result);
        },
        [dashboard.widgets, getVariableValue, widgets]
    );

    const registerWidgetRefreshTrigger = useCallback((instanceId: string, trigger: () => Promise<boolean>) => {
        setDataFetchTriggers(s => ({
            ...s,
            [instanceId]: trigger,
        }));
    }, []);

    const unregisterWidgetRefreshTrigger = useCallback((instanceId: string, trigger: () => Promise<boolean>) => {
        setDataFetchTriggers(s => {
            return s[instanceId] === trigger ? omit([instanceId], s) : s;
        });
    }, []);

    const refreshWidgetData = useCallback(
        async (instanceId: string) => {
            await updateDynamicVariableValues();
            const trigger = dataFetchTriggers[instanceId];
            if (trigger) {
                return await trigger();
            }
        },
        [dataFetchTriggers, updateDynamicVariableValues]
    );

    const refreshAllWidgetData = useCallback(async () => {
        await updateDynamicVariableValues();
        const promises: Promise<boolean>[] = [];
        for (const fn of Object.values(dataFetchTriggers)) {
            promises.push(fn());
        }
        return await Promise.allSettled(promises);
    }, [dataFetchTriggers, updateDynamicVariableValues]);

    const dashboardContext: DashboardService = {
        getParameterState,
        setParameterState,
        getWidgetDefinition,
        updateDynamicVariableValues,
        getVariableValue,
        getWidgetData,
        getWidgetVariables,
        registerWidgetRefreshTrigger,
        unregisterWidgetRefreshTrigger,
        refreshWidgetData,
        refreshAllWidgetData,
        dashboard,
        variableDefinitions,
    };

    return <DashboardContext.Provider value={dashboardContext}>{children}</DashboardContext.Provider>;
};

export const useDashboardContext = () => useContext(DashboardContext);
