import {useCallback, useMemo, useRef} from "react";
import {omit} from "ramda";
import {CancelToken} from "axios";

import {useNimflow} from "./nimflow-api-provider";
import {JsConsoleEntry, FunctionInfo, UserDefinedFunction, SearchUserDefinedFunctions} from "./nimflow-api";
import {evaluateScript} from "../utils/scripting";

export interface JavascriptEvaluationResult {
    consoleEntries: JsConsoleEntry[];
    result: any;
}

export interface ModuleFunctionInfo extends FunctionInfo {
    moduleName: string;
}

export const useNimflowFunctions = () => {
    const {functions_Call, functions_GetModules, functions_Search, functions_Add, functions_Delete, functions_Update} =
        useNimflow();
    const functionInfosPromise = useRef<Promise<Record<string, ModuleFunctionInfo>>>();

    const call = useCallback(
        (moduleName: string, name: string, parameters: Record<string, any> = {}, cancelToken?: CancelToken) => {
            return functions_Call(
                {
                    moduleName,
                    name,
                    parameters,
                },
                cancelToken
            );
        },
        [functions_Call]
    );

    const evaluateArgsAndCall = useCallback(
        async (
            moduleName: string,
            functionName: string,
            jsArguments?: string,
            jsArgumentsContext: Record<string, any> = {},
            cancelToken?: CancelToken
        ) => {
            let params: Record<string, any> | undefined = undefined;
            if (jsArguments) {
                const outputValues = {
                    args: undefined as any,
                };
                evaluateScript(jsArguments, {
                    inputValues: jsArgumentsContext,
                    outputValues,
                });
                params = outputValues.args;
            }
            const result = await call(moduleName, functionName, params, cancelToken);
            return result;
        },
        [call]
    );

    const getModules = useCallback(
        (retrieveFunctionInfos: boolean, cancelToken?: CancelToken) => {
            return functions_GetModules({retrieveFunctionInfos}, cancelToken);
        },
        [functions_GetModules]
    );

    const getFunctions = useCallback(
        (cancelToken?: CancelToken) => {
            let promise = functionInfosPromise.current;
            if (!promise) {
                functionInfosPromise.current = promise = functions_GetModules(
                    {retrieveFunctionInfos: true},
                    cancelToken
                ).then(s => {
                    const result: Record<string, ModuleFunctionInfo> = {};
                    for (const module of s) {
                        if (module.functions) {
                            for (const fn of module.functions) {
                                if (module.name && fn.name) {
                                    result[`${module.name}/${fn.name}`] = {
                                        ...fn,
                                        moduleName: module.name,
                                    };
                                }
                            }
                        }
                    }
                    return result;
                });
            }
            return promise;
        },
        [functions_GetModules]
    );

    const javascript_EvaluateScript = useCallback(
        async (script: string, context?: Record<string, any>, cancelToken?: CancelToken) => {
            const result = await call(
                "javascript",
                "EvaluateScript",
                {
                    script,
                    context,
                },
                cancelToken
            );
            return result;
        },
        [call]
    );

    const javascript_EvaluateScriptWithConsole = useCallback(
        async (script: string, context?: Record<string, any>, cancelToken?: CancelToken) => {
            const result: JavascriptEvaluationResult = await call(
                "javascript",
                "EvaluateScriptWithConsole",
                {
                    script,
                    context,
                },
                cancelToken
            );
            return result;
        },
        [call]
    );

    const javascript = useMemo(
        () => ({
            evaluateScript: javascript_EvaluateScript,
            evaluateScriptWithConsole: javascript_EvaluateScriptWithConsole,
        }),
        [javascript_EvaluateScript, javascript_EvaluateScriptWithConsole]
    );

    const getAllUdFunctions = useCallback(
        async (cancelToken?: CancelToken) => {
            return await functions_Search({}, cancelToken);
        },
        [functions_Search]
    );

    const getUdFunction = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const fns = await functions_Search({id}, cancelToken);
            return fns.length > 0 ? fns[0] : undefined;
        },
        [functions_Search]
    );

    const searchUdFunctions = useCallback(
        async (query: SearchUserDefinedFunctions, cancelToken?: CancelToken) => {
            return await functions_Search(query, cancelToken);
        },
        [functions_Search]
    );

    const addUdFunction = useCallback(
        async (udfn: UserDefinedFunction, cancelToken?: CancelToken) => {
            return await functions_Add(omit(["id", "version"], udfn), cancelToken);
        },
        [functions_Add]
    );

    const updateUdFunction = useCallback(
        async (udfn: UserDefinedFunction, cancelToken?: CancelToken) => {
            return await functions_Update(omit(["version"], udfn), cancelToken);
        },
        [functions_Update]
    );

    const deleteUdFunction = useCallback(
        async (udfnId: string, cancelToken?: CancelToken) => {
            return await functions_Delete({id: udfnId}, cancelToken);
        },
        [functions_Delete]
    );

    return {
        javascript,
        call,
        evaluateArgsAndCall,
        getModules,
        getFunctions,
        getUdFunction,
        getAllUdFunctions,
        searchUdFunctions,
        addUdFunction,
        updateUdFunction,
        deleteUdFunction,
    };
};
