import React, {FC, useMemo, useCallback} from "react";
import {alpha, makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Popover from "@material-ui/core/Popover";
import {usePopupState, bindTrigger, bindPopover} from "material-ui-popup-state/hooks";
import InputBase from "@material-ui/core/InputBase";
import Typography from "@material-ui/core/Typography";
import Autocomplete, {
    AutocompleteChangeDetails,
    AutocompleteRenderOptionState,
} from "@material-ui/lab/Autocomplete/Autocomplete";
import Checkbox from "@material-ui/core/Checkbox";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxOutlinedIcon from "@material-ui/icons/CheckBoxOutlined";
import {useTranslation} from "react-i18next";
import {sum} from "ramda";
import i18next from "i18next";
import {isPresent} from "ts-is-present";

import {
    SelectionDashboardParameter,
    DashboardParameterState,
    DashboardParameter,
    getParameterValueDescription,
} from "../../api/nimflow-dashboard-api";
import {DashboardParameterPill} from "./dashboard-parameter-pill";
import {ParameterEditorProps} from "./dashboard-parameter-viewer";
import {
    SelectionDashboardParameterDataSourceOption,
    useSelectionDashboardParameterDataSourceOptions,
    getOptionsAndSelection,
} from "./selection-parameter-common";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        popper: {
            border: "1px solid rgba(27,31,35,.15)",
            boxShadow: "0 3px 12px rgba(27,31,35,.15)",
            borderRadius: theme.shape.borderRadius,
            //width: 300,
            //zIndex: 1,
            //fontSize: 13,
            //color: '#586069',
            backgroundColor: theme.palette.background.paper, // '#f6f8fa',
        },
        header: {
            borderBottom: "1px solid #e1e4e8",
            padding: "8px 10px",
            fontWeight: 600,
        },
        inputBase: {
            padding: 10,
            width: "100%",
            borderBottom: "1px solid #dfe2e5",
            "& input": {
                borderRadius: 4,
                padding: 8,
                transition: theme.transitions.create(["border-color", "box-shadow"]),
                border: "1px solid #ced4da",
                fontSize: 14,
                "&:focus": {
                    boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
                    borderColor: theme.palette.primary.main,
                },
            },
        },
        paper: {
            boxShadow: "none",
            margin: 0,
            //color: "#586069",
            fontSize: 13,
        },
        option: {
            minHeight: "auto",
            alignItems: "flex-start",
            padding: 8,
            '&[aria-selected="true"]': {
                backgroundColor: "transparent",
            },
            '&[data-focus="true"]': {
                backgroundColor: theme.palette.action.hover,
            },
        },
        popperDisablePortal: {
            position: "relative",
        },
        iconSelected: {
            width: 17,
            height: 17,
            marginRight: 5,
            marginLeft: -2,
        },
        color: {
            width: 14,
            height: 14,
            flexShrink: 0,
            borderRadius: 3,
            marginRight: 8,
            marginTop: 2,
        },
        text: {
            flexGrow: 1,
        },
        close: {
            opacity: 0.6,
            width: 18,
            height: 18,
        },
        checkbox: {
            marginRight: 8,
            padding: 0,
        },
    })
);

const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxOutlinedIcon fontSize="small" />;

export interface MultipleSelectionParameterEditorProps extends ParameterEditorProps<SelectionDashboardParameter> {}

const getSelectionDescription = (
    value: DashboardParameterState | undefined,
    parameter: DashboardParameter,
    totalCount: number
): string => {
    if (!value) {
        return "";
    }
    if (value.selectAll || value.values.length === 0) {
        return i18next.t("All");
    }
    const texts = value.values.map(s => getParameterValueDescription(s, parameter));
    const size = sum(texts.map(s => s?.length ?? 0)) + 2 * (texts.length - 1);
    if (size > 15) {
        return i18next.t("CountOfTotalSelected", {count: texts.length, total: totalCount});
    }
    return texts.join(", ");
};

const calculateNewState = (
    oldState: DashboardParameterState,
    selected: SelectionDashboardParameterDataSourceOption[],
    options: SelectionDashboardParameterDataSourceOption[]
): DashboardParameterState => {
    if (selected.some(s => s.selectAll)) {
        return {selectAll: !oldState.selectAll, values: []};
    }
    if (selected.length === options.length - 1) {
        return {selectAll: true, values: []};
    }
    return {selectAll: false, values: selected.map(s => s.value).filter(isPresent)};
};

export const MultipleSelectionParameterEditor: FC<MultipleSelectionParameterEditorProps> = props => {
    const {parameter, value, onChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const popupState = usePopupState({
        variant: "popover",
        popupId: `paramPopup_${parameter.id}`,
    });

    const {options: baseOptions} = useSelectionDashboardParameterDataSourceOptions(parameter);
    const [options, selection] = useMemo(() => {
        return getOptionsAndSelection(value, parameter, baseOptions);
    }, [value, parameter, baseOptions]);

    const handleChange = useCallback(
        (
            event: React.ChangeEvent<{}>,
            selected: SelectionDashboardParameterDataSourceOption[],
            reason: string,
            details?: AutocompleteChangeDetails<SelectionDashboardParameterDataSourceOption>
        ) => {
            if (reason === "remove-option") {
                if (details?.option.selectAll) {
                    selected = [];
                } else {
                    selected = selected.filter(s => !s.selectAll);
                }
            } else if (reason === "select-option") {
                if (details?.option.selectAll || selected.length === options.length - 1) {
                    selected = options;
                } else if (
                    !!details &&
                    !details.option.selectAll &&
                    selected.length === 2 &&
                    selected.some(s => s.selectAll)
                ) {
                    selected = [details.option];
                }
            }
            const newState = calculateNewState(value, selected, options);
            onChange?.(newState, parameter);
        },
        [onChange, options, parameter, value]
    );

    const renderOptions = useCallback(
        (option: SelectionDashboardParameterDataSourceOption, state: AutocompleteRenderOptionState) => (
            <>
                <Checkbox
                    icon={icon}
                    checkedIcon={checkedIcon}
                    className={classes.checkbox}
                    checked={state.selected}
                    color="default"
                />
                <Typography variant="body2">{option.description}</Typography>
            </>
        ),
        [classes]
    );

    return (
        <>
            <DashboardParameterPill
                parameter={parameter}
                valueDescription={getSelectionDescription(value, parameter, options.length - 1)}
                {...bindTrigger(popupState)}
            />
            <Popover
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                }}
                {...bindPopover(popupState)}
            >
                <Autocomplete
                    open
                    multiple
                    classes={{
                        paper: classes.paper,
                        option: classes.option,
                        popperDisablePortal: classes.popperDisablePortal,
                    }}
                    value={selection}
                    onChange={handleChange}
                    disableCloseOnSelect
                    disablePortal
                    renderTags={() => null}
                    noOptionsText={t("NoLabels")}
                    renderOption={renderOptions}
                    options={options}
                    getOptionLabel={option => option.description}
                    renderInput={params => {
                        return (
                            <InputBase
                                ref={params.InputProps.ref}
                                inputProps={params.inputProps}
                                autoFocus
                                className={classes.inputBase}
                            />
                        );
                    }}
                />
            </Popover>
        </>
    );
};
