import * as React from "react";
import Hidden from "@material-ui/core/Hidden";
import ReactMarkdown from "react-markdown";
import {rankWith, uiTypeIs, RankedTester, LayoutProps} from "@jsonforms/core";
import {withJsonFormsLayoutProps} from "@jsonforms/react";
import {FC, useMemo} from "react";

import {MarkdownElement} from "./markdown-element";
import {useJsonForms} from "./jsonforms-context";
import {evaluateHandlebars} from "../handlebars";

const MarkdownRenderer: FC<LayoutProps> = props => {
    const {uischema, visible, data, path} = props;
    const {bindContext, context} = useJsonForms();
    const text = useMemo(() => {
        const markdownElement = uischema as MarkdownElement;
        if (!markdownElement && !("text" in markdownElement)) {
            return undefined;
        }
        let ctx: Record<string, any> = {
            data,
            path,
        };
        if (bindContext) {
            Object.assign(ctx, context);
        }
        return evaluateHandlebars(markdownElement.text, ctx);
    }, [uischema, data, path, bindContext, context]);
    if (!text) {
        return null;
    }
    return (
        <Hidden xsUp={!visible}>
            <div>
                <ReactMarkdown>{text}</ReactMarkdown>
            </div>
        </Hidden>
    );
};

export default withJsonFormsLayoutProps(MarkdownRenderer);

export const markdownRendererTester: RankedTester = rankWith(
    3, //increase rank as needed
    uiTypeIs("Markdown")
);
