import {FC, useCallback, useMemo} from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import {useTranslation} from "react-i18next";

import {useSystemInfo} from "../../services/system-info-service";
import {MarkdownViewer} from "../../components/markdown-viewer";

export interface SystemInfoDialogProps {
    open: boolean;
    onClose: () => void;
}


export const SystemInfoDialog: FC<SystemInfoDialogProps> = props => {
    const {open, onClose} = props;
    const si = useSystemInfo();
    const {t} = useTranslation();

    const markdown = useMemo(() => {
        const supportedFeatures = si.supportedFeatures.join(", ");
        const result = `|${t("Item")}|${t("Value")}|
|------------|-------------|
|${t("unitId")}|${si.currentUnit?.id ?? ""}|
|${t("unitName")}|${si.currentUnit?.name ?? ""}|
|${t("unitDescription")}|${si.currentUnit?.description ?? ""}|
|${t("unitOrganizationId")}|${si.currentUnit?.organizationId ?? ""}|
|${t("unitProps")}|${JSON.stringify(si.currentUnit?.props)}|
|${t("hubName")}|${si.hubName}|
|${t("hubBaseUrl")}|${si.hubBaseUrl}|
|${t("hubVersion")}|${si.hubVersion}|
|${t("businessDirectoryBaseUrl")}|${si.businessDirectoryBaseUrl}|
|${t("businessDirectoryName")}|${si.businessDirectoryName}|
|${t("businessDirectoryVersion")}|${si.businessDirectoryVersion}|
|${t("supportedFeatures")}|${supportedFeatures}|
`;
        return result;
    }, [si, t]);

    const handleInteractiveClose = useCallback((event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog open={open} onClose={handleInteractiveClose} maxWidth="md" >
            <DialogTitle>{t("SystemInfo")}</DialogTitle>
            <DialogContent>
                <MarkdownViewer>{markdown}</MarkdownViewer>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    {t("Close")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
