import {useCallback, useMemo, createContext, FC, useContext} from "react";
import useSWR from "swr";
import {atomFamily, useRecoilState} from "recoil";

import {ContextTask, ContextType, ResponseType} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";
import {getSettings} from "../../api/nimflow-api-extensions";
import {evaluateHandlebars} from "../../libs/handlebars";
import {useTasksViewService} from "./tasks-view-service";
import {useKey} from "../../utils/use-key";
import {useAuthenticationService} from "../../features/auth/authentication-service";

export interface TasksViewTaskService {
    task: ContextTask;
    contextType?: ContextType;
    responses?: ResponseType[];
    taskDetails?: string;
    addResponse: (responseType: ResponseType, payload: any, startedOn: Date | undefined) => Promise<any>;
    refreshTasks: () => Promise<any>;
    taskBlocked: boolean;
}

const stub = () => {
    throw new Error("You forgot to wrap your component in <TasksViewTaskServiceProvider>.");
};

const initialTasksViewTaskServiceContext: TasksViewTaskService = {
    task: {} as ContextTask,
    addResponse: stub,
    refreshTasks: stub,
    taskBlocked: false,
};

const TasksViewTaskServiceContext = createContext<TasksViewTaskService>(initialTasksViewTaskServiceContext);

export interface TasksViewTaskServiceProviderProps {
    task: ContextTask;
}

export const taskBlockedState = atomFamily({
    key: "taskBlockedState",
    default: false,
});

const useContextType = (contextTypeName: string) => {
    const {contextTypes_FindOne} = useNimflow();
    const key = useKey(contextTypes_FindOne, contextTypeName);
    const {data, isValidating, mutate, error} = useSWR(
        key,
        async () => {
            const result = await contextTypes_FindOne({name: contextTypeName});
            return result;
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            contextType: data,
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const TasksViewTaskServiceProvider: FC<TasksViewTaskServiceProviderProps> = props => {
    const {task, children} = props;
    const {tasks_AddResponse} = useNimflow();
    const {authenticatedUser} = useAuthenticationService();
    const [taskBlocked, setTaskBlocked] = useRecoilState(taskBlockedState(task.id));
    const {refreshTasks} = useTasksViewService();
    const {contextType} = useContextType(task.contextTypeName);

    const [responses, taskDetails] = useMemo(() => {
        const taskType = contextType?.tasks.find(s => s.name === task.typeName);
        let responses: ResponseType[] = [];
        let taskDetails: string | undefined;
        if (taskType) {
            const taskDetailsTemplate = getSettings(taskType)?.taskDetailsTemplate;
            if (taskDetailsTemplate) {
                taskDetails = evaluateHandlebars(taskDetailsTemplate, {task});
            }
            if (taskType.validResponses) {
                responses = taskType.validResponses;
            }
        }
        return [responses, taskDetails];
    }, [contextType, task]);

    const addResponse = useCallback(
        async (responseType: ResponseType, payload: any, startedOn: Date | undefined) => {
            setTaskBlocked(true);
            try {
                await tasks_AddResponse({
                    contextId: task.contextId,
                    contextTypeName: task.contextTypeName,
                    taskTypeName: task.typeName,
                    responseTypeName: responseType.name,
                    payload,
                    startedOn: startedOn?.toISOString(),
                    sentOn: new Date().toISOString(),
                    sentBy: authenticatedUser?.name
                });
            } catch (e) {
                setTaskBlocked(false);
                throw e;
            }
        },
        [setTaskBlocked, tasks_AddResponse, task.contextId, task.contextTypeName, task.typeName, authenticatedUser?.name]
    );

    const service: TasksViewTaskService = {
        task,
        contextType,
        responses,
        taskDetails,
        addResponse,
        refreshTasks,
        taskBlocked,
    };
    return <TasksViewTaskServiceContext.Provider value={service}>{children}</TasksViewTaskServiceContext.Provider>;
};

export const TasksViewTaskServiceConsumer = TasksViewTaskServiceContext.Consumer;

export const useTasksViewTaskService = () => useContext(TasksViewTaskServiceContext);
