import { useNimflow } from './../../../api/nimflow-api-provider';
import {useEffect, useState} from "react";
import axios, {AxiosError, AxiosResponse} from "axios";
import Tiff from "tiff.js";
import {pdfjs} from "react-pdf/dist/esm/entry.webpack";
import produce from "immer";
import URI from "urijs";

import {useCurrentUnitInfo} from "./../../../api/nimflow-business-directory-provider";
import {RecognizedDocument, RecognizedImageBatch} from "./recognized-document";

export interface ImageInfoPage {
    getImageData: () => ImageData | Promise<ImageData>;
    width: number;
    height: number;
    fileIndex: number;
    filePageNumber: number;
    pageIndex: number;
    documentIndex?: number;
    documentOrder?: number;
}

export interface LoadingFileInfo {
    url: string;
    status: "loading";
    name: string;
}
export interface LoadedFileInfo {
    url: string;
    status: "loaded";
    name: string;
    mimeType?: string;
    content: ArrayBuffer;
    pages: ImageInfoPage[];
    pageStartIndex: number;
}

export interface FailedFileInfo {
    url: string;
    status: "failed";
    name: string;
    error: string;
}

export type FileInfo = LoadingFileInfo | LoadedFileInfo | FailedFileInfo;

const nativeMimeTypes = ["image/png", "image/jpeg", "image/webp"];

const loadNativeImage = (url: string) => {
    return new Promise<HTMLImageElement>((resolve, reject) => {
        const img = new Image();
        img.crossOrigin = "anonymous";
        img.addEventListener("load", () => resolve(img));
        img.addEventListener("error", err => reject(err));
        img.src = url;
    });
};

const processBrowserSupportedImage = async (result: LoadedFileInfo, fileIndex: number) => {
    const data = new Blob([result.content], {
        type: result.mimeType!,
    });
    const url = URL.createObjectURL(data);
    try {
        const img = await loadNativeImage(url);
        result.pages.push({
            getImageData: () => {
                const canvas = document.createElement("canvas");
                const context = canvas.getContext("2d")!;
                canvas.width = img.naturalWidth;
                canvas.height = img.naturalHeight;
                context.drawImage(img, 0, 0);
                return context.getImageData(0, 0, img.naturalWidth, img.naturalHeight);
            },
            width: img.naturalWidth,
            height: img.naturalHeight,
            fileIndex,
            filePageNumber: 0,
            pageIndex: 0,
        });
    } finally {
        URL.revokeObjectURL(url);
    }
}

const processTiffImages = async (result: LoadedFileInfo, fileIndex: number) => {
    const tiff = new Tiff({buffer: result.content});
    for (let i = 0, len = tiff.countDirectory(); i < len; ++i) {
        tiff.setDirectory(i);
        const page: ImageInfoPage = {
            getImageData: () => {
                tiff.setDirectory(i);
                const rgba = tiff.readRGBAImage();
                return new ImageData(new Uint8ClampedArray(rgba), tiff.width(), tiff.height());
            },
            width: tiff.width(),
            height: tiff.height(),
            fileIndex,
            filePageNumber: i,
            pageIndex: 0,
        };
        result.pages.push(page);
    }
}

const processPdfImages = async (result: LoadedFileInfo, fileIndex: number) => {
    const buffer = new Uint8Array(result.content);
    const pdf = await pdfjs.getDocument(buffer).promise;
    const promises: Promise<any>[] = [];
    const pages: ImageInfoPage[] = new Array(pdf.numPages);
    for (let i = 0; i < pdf.numPages; i++) {
        promises.push(
            // eslint-disable-next-line no-loop-func
            pdf.getPage(i + 1).then(async page => {
                const dpRatio = 2 * window.devicePixelRatio;
                const viewport = page.getViewport({scale: dpRatio});
                pages[i] = {
                    getImageData: async () => {
                        const canvas = document.createElement("canvas");
                        canvas.style.width = `${viewport.width / dpRatio}px`;
                        canvas.style.height = `${viewport.height / dpRatio}px`;
                        canvas.height = viewport.height;
                        canvas.width = viewport.width;
                        const ctx = canvas.getContext("2d")!;
                        await page.render({canvasContext: ctx, viewport: viewport}).promise;
                        return ctx.getImageData(0, 0, canvas.width, canvas.height);
                    },
                    width: viewport.width,
                    height: viewport.height,
                    fileIndex,
                    filePageNumber: i,
                    pageIndex: 0,
                };
            })
        );
    }
    await Promise.all(promises);
    result.pages = pages;
}

const processLoadedFileToImageData = async (
    url: string,
    fileIndex: number,
    response: AxiosResponse<any>
): Promise<FileInfo> => {
    const idx = url.lastIndexOf("/");
    const name = idx >= 0 ? url.substring(idx + 1) : url;
    const result: LoadedFileInfo = {
        url,
        status: "loaded",
        name,
        mimeType: response.headers["content-type"],
        content: response.data as ArrayBuffer,
        pages: [],
        pageStartIndex: 0,
    };
    const contentDisposition = response.headers?.["content-disposition"];
    const fileNameMatch = contentDisposition ? /filename="?([^"]*?)"?(;|$)/g.exec(contentDisposition) : undefined;
    if (fileNameMatch && fileNameMatch.length > 1) {
        result.name = fileNameMatch[1];
    }
    if (!result.mimeType || nativeMimeTypes.includes(result.mimeType)) {
        await processBrowserSupportedImage(result, fileIndex);
    } else if (result.mimeType === "image/tiff") {
        await processTiffImages(result, fileIndex);
    } else if (result.mimeType === "application/pdf") {
        await processPdfImages(result, fileIndex);
    }
    return result;
};

const processError = (url: string, e: AxiosError): FileInfo => {
    const idx = url.lastIndexOf("/");
    const name = idx >= 0 ? url.substring(idx + 1) : url;
    const result: FailedFileInfo = {
        url,
        status: "failed",
        name,
        error: e.message,
    };
    if (e.response) {
        result.error = `Code: ${e.response.status}\nData: ${e.response.data}`;
    }
    return result;
};

const assignDocumentInfo = (fileInfos: FileInfo[], docs: RecognizedDocument[]) => {
    return produce(fileInfos, draft => {
        docs?.forEach((doc, docIdx) => {
            doc.pages.forEach((pageImage, docOrder) => {
                const finfo = draft[pageImage.fileIndex];
                if (finfo?.status === "loaded") {
                    const page = finfo.pages[pageImage.filePageNumber];
                    page.documentIndex = docIdx;
                    page.documentOrder = docOrder;
                }
            });
        });
        let startIndex = 0;
        for (const fi of draft) {
            if (fi.status !== "loaded") {
                continue;
            }
            fi.pageStartIndex = startIndex;
            for (const page of fi.pages) {
                page.pageIndex = startIndex++;
            }
        }
    });
};

export const useFileInfos = (initialBatch: RecognizedImageBatch) => {
    const {hubBaseUrl} = useCurrentUnitInfo();
    const {getConfiguredAxiosInstance} = useNimflow();
    const [fileInfos, setFileInfos] = useState<FileInfo[]>(() => {
        return initialBatch.fileUrls.map(url => {
            let uri = URI(url);
            if (uri.is("relative") && hubBaseUrl) {
                uri = URI.joinPaths("BlobStorage", uri).absoluteTo(hubBaseUrl);
            }
            const result: LoadingFileInfo = {
                url: uri.valueOf(),
                status: "loading",
                name: uri.filename(),
            };
            return result;
        });
    });

    const [loadingFileInfos, setLoadingFileInfos] = useState(initialBatch.fileUrls.length > 0);

    useEffect(() => {
        const source = axios.CancelToken.source();
        const anonymousAxios = axios.create({
            responseType: "arraybuffer",
            cancelToken: source.token,
        });
        const nimflowApiAxios = getConfiguredAxiosInstance({
            responseType: "arraybuffer",
            cancelToken: source.token,
        });
        initialBatch.fileUrls.map((url, idx) => {
            let uri = URI(url);
            let axiosInstance = anonymousAxios;
            if (uri.is("relative") && hubBaseUrl) {
                uri = URI.joinPaths("BlobStorage", uri);
                axiosInstance = nimflowApiAxios;
            }
            url = uri.valueOf();
            return axiosInstance
                .get(url)
                .then(response => {
                    return processLoadedFileToImageData(url, idx, response).then(newInfo => {
                        setFileInfos(infos => {
                            let result = produce(infos, draft => {
                                draft[idx] = newInfo;
                            });
                            if (result.every(s => s.status !== "loading")) {
                                result = assignDocumentInfo(result, initialBatch.documents);
                                setLoadingFileInfos(false);
                            }
                            return result;
                        });
                    });
                })
                .catch((e: AxiosError) => {
                    setFileInfos(infos => {
                        let result = produce(infos, draft => {
                            draft[idx] = processError(url, e);
                        });
                        if (result.every(s => s.status !== "loading")) {
                            result = assignDocumentInfo(result, initialBatch.documents);
                            setLoadingFileInfos(false);
                        }
                        return result;
                    });
                });
        });
        return () => {
            source.cancel();
            setLoadingFileInfos(false);
        };
    }, [getConfiguredAxiosInstance, hubBaseUrl, initialBatch.documents, initialBatch.fileUrls]);

    return {
        loadingFileInfos,
        fileInfos,
        setFileInfos,
    };
};
