import {FC, useMemo} from "react";
import {and, ControlProps, EnumOption, isControl, OwnPropsOfEnum, RankedTester, rankWith} from "@jsonforms/core";
import {withJsonFormsControlProps} from "@jsonforms/react";
import {MaterialInputControl, MuiSelect} from "@jsonforms/material-renderers";
import useSWR from "swr";
import {isPresent} from "ts-is-present";

import {useNimflowFunctions} from "../../api/nimflow-functions-api";
import {isObjectLike} from "../../utils/types";

interface OptionsSource {
    moduleName: string;
    functionName: string;
}

const DynamicSelectionRenderer: FC<ControlProps> = props => {
    const {uischema} = props;
    const {moduleName, functionName} = uischema.options!.optionsSource as OptionsSource;
    const {call} = useNimflowFunctions();

    const {data: options} = useSWR(
        `function/${moduleName}/${functionName}`,
        async () => {
            const options: EnumOption[] = [];
            const result = await call(moduleName, functionName);
            if (Array.isArray(result)) {
                for (const item of result) {
                    let value: any = undefined;
                    let label: string | undefined = undefined;
                    if (isObjectLike(item)) {
                        value = item.value;
                        if (typeof item.label === "string") {
                            label = item.label;
                        }
                    } else {
                        value = item;
                    }
                    if (isPresent(value)) {
                        const option: EnumOption = {
                            label: label ?? (typeof value === "string" ? value : JSON.stringify(value)),
                            value,
                        };
                        options.push(option);
                    }
                }
            }
            return options;
        },
        {
            fallbackData: [],
            revalidateOnMount: true,
            revalidateOnFocus: false,
        }
    );

    const innerProps = useMemo(() => {
        const result: ControlProps & OwnPropsOfEnum = {
            ...props,
            options,
        };
        return result;
    }, [props, options]);

    return <MaterialInputControl {...innerProps} input={MuiSelect} />;
};

export default withJsonFormsControlProps(DynamicSelectionRenderer);

export const dynamicSelectionRendererTester: RankedTester = rankWith(
    3,
    and(isControl, uiSchema => {
        const source = uiSchema.options?.optionsSource;
        return !!source && !!source.moduleName && !!source.functionName;
    })
);
