import React, {FC, useMemo, useCallback} from "react";
import {alpha, makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Popover from "@material-ui/core/Popover";
import {usePopupState, bindTrigger, bindPopover} from "material-ui-popup-state/hooks";
import InputBase from "@material-ui/core/InputBase";
import DoneIcon from "@material-ui/icons/Done";
import Typography from "@material-ui/core/Typography";
import Autocomplete, {AutocompleteCloseReason} from "@material-ui/lab/Autocomplete/Autocomplete";
import {useTranslation} from "react-i18next";
import i18next from "i18next";

import {
    SelectionDashboardParameter,
    DashboardParameterState,
    getParameterValueDescription,
    DashboardParameter,
} from "../../api/nimflow-dashboard-api";
import {DashboardParameterPill} from "./dashboard-parameter-pill";
import {ParameterEditorProps} from "./dashboard-parameter-viewer";
import {
    SelectionDashboardParameterDataSourceOption,
    useSelectionDashboardParameterDataSourceOptions,
    getOptionsAndSelection,
} from "./selection-parameter-common";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        header: {
            borderBottom: "1px solid #e1e4e8",
            padding: "8px 10px",
            fontWeight: 600,
        },
        inputBase: {
            padding: 10,
            width: "100%",
            borderBottom: "1px solid #dfe2e5",
            "& input": {
                borderRadius: 4,
                padding: 8,
                transition: theme.transitions.create(["border-color", "box-shadow"]),
                border: "1px solid #ced4da",
                fontSize: 14,
                "&:focus": {
                    boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
                    borderColor: theme.palette.primary.main,
                },
            },
        },
        paper: {
            boxShadow: "none",
            margin: 0,
            //color: "#586069",
            fontSize: 13,
        },
        option: {
            minHeight: "auto",
            alignItems: "flex-start",
            padding: 8,
            '&[aria-selected="true"]': {
                backgroundColor: "transparent",
            },
            '&[data-focus="true"]': {
                backgroundColor: theme.palette.action.hover,
            },
        },
        popperDisablePortal: {
            position: "relative",
        },
        iconSelected: {
            width: 17,
            height: 17,
            marginRight: 5,
            marginLeft: -2,
        },
        color: {
            width: 14,
            height: 14,
            flexShrink: 0,
            borderRadius: 3,
            marginRight: 8,
            marginTop: 2,
        },
        text: {
            flexGrow: 1,
        },
        close: {
            opacity: 0.6,
            width: 18,
            height: 18,
        },
    })
);

export interface SingleSelectionParameterEditorProps extends ParameterEditorProps<SelectionDashboardParameter> {}

export const getSelectedValueDescription = (value: DashboardParameterState, parameter: DashboardParameter): string => {
    if (value.selectAll) {
        return i18next.t("SelectAll");
    }
    const paramval = value.values[0];
    return getParameterValueDescription(paramval, parameter);
};

export const SingleSelectionParameterEditor: FC<SingleSelectionParameterEditorProps> = props => {
    const {parameter, value: initialValue, onChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {options: baseOptions} = useSelectionDashboardParameterDataSourceOptions(parameter);
    const [options, value] = useMemo(() => {
        const [options, selection] = getOptionsAndSelection(initialValue, parameter, baseOptions);
        return [options, selection[0] ?? options[0]];
    }, [baseOptions, initialValue, parameter]);

    const popupState = usePopupState({
        variant: "popover",
        popupId: `paramPopup_${parameter.id}`,
    });

    const handleChange = useCallback(
        (
            event: React.ChangeEvent<{}>,
            newValue: SelectionDashboardParameterDataSourceOption | null,
            reason: string
        ) => {
            if (newValue !== null && reason === "select-option") {
                popupState.close();
                onChange?.(
                    {
                        selectAll: newValue.selectAll,
                        values: newValue.value ? [newValue.value] : [],
                    },
                    parameter
                );
            }
        },
        [onChange, parameter, popupState]
    );

    const handleClose = useCallback(
        (event: React.ChangeEvent<{}>, reason: AutocompleteCloseReason) => {
            if (reason === "toggleInput") {
                return;
            }
            popupState.close();
        },
        [popupState]
    );

    const renderOptions = useCallback(
        (option: SelectionDashboardParameterDataSourceOption) => (
            <>
                <DoneIcon
                    className={classes.iconSelected}
                    style={{
                        visibility: option === value ? "visible" : "hidden",
                    }}
                />
                <div className={classes.text}>
                    <Typography variant="body2" color="textPrimary">
                        {option.description}
                    </Typography>
                </div>
            </>
        ),
        [classes, value]
    );

    return (
        <>
            <DashboardParameterPill
                parameter={parameter}
                valueDescription={getSelectedValueDescription(initialValue, parameter)}
                {...bindTrigger(popupState)}
            />
            <Popover
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                }}
                {...bindPopover(popupState)}
            >
                <Autocomplete
                    open
                    onClose={handleClose}
                    classes={{
                        paper: classes.paper,
                        option: classes.option,
                        popperDisablePortal: classes.popperDisablePortal,
                    }}
                    value={undefined}
                    onChange={handleChange}
                    disableCloseOnSelect
                    disablePortal
                    renderTags={() => null}
                    noOptionsText={t("NoLabels")}
                    renderOption={renderOptions}
                    options={options}
                    getOptionLabel={option => option.description}
                    renderInput={params => {
                        return (
                            <InputBase
                                ref={params.InputProps.ref}
                                inputProps={params.inputProps}
                                autoFocus
                                className={classes.inputBase}
                            />
                        );
                    }}
                />
            </Popover>
        </>
    );
};
