import {FC, useMemo} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import AssignmentIcon from "@material-ui/icons/Assignment";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import SecurityIcon from "@material-ui/icons/Security";
import {useTranslation} from "react-i18next";
import useSWR from "swr";

import {TeamTasksIcon} from "../../components/icons/team-tasks-icon";
import {permissionRequests} from "../../utils/authorization";
import {useAuthorization} from "../../components/authorization-context";
import {SideMenuItemProps, SideMenuItem} from "./side-menu-item";
import {useNimflowDashboards} from "../../api/nimflow-dashboard-api";
import {DashboardsIcon} from "../../components/icons/dashboards-icon";
import {useKey} from "../../utils/use-key";
import {SystemFeature, useSystemInfo} from "../../services/system-info-service";

const {workMyTasks, manageTeamTasks, dispatchActions, manage} = permissionRequests;

const useStyles = makeStyles(theme =>
    createStyles({
        appMenu: {
            width: "100%",
            height: "100%",
        },
    })
);

const useDashboards = () => {
    const {dashboards_GetInfos} = useNimflowDashboards();
    const key = useKey(dashboards_GetInfos);
    const {data, isValidating, mutate, error} = useSWR(
        key,
        async () => {
            return await dashboards_GetInfos({});
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            dashboards: data,
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const SideMenu: FC<{}> = () => {
    const classes = useStyles();
    const {t} = useTranslation();
    const {can} = useAuthorization();
    const {dashboards} = useDashboards();
    const {isFeatureSupported} = useSystemInfo();

    const menuDef = useMemo(() => {
        const result: SideMenuItemProps[] = [];
        if (can(workMyTasks)) {
            result.push({
                name: t("MyTasks"),
                link: "/tasks",
                icon: <AssignmentIcon />,
            });
        }
        if (can(manageTeamTasks)) {
            result.push({
                name: t("UnitTasks"),
                link: "/team-tasks",
                icon: <TeamTasksIcon />,
            });
        }
        if (dashboards && dashboards.length > 0) {
            result.push({
                name: t("Dashboards"),
                icon: <DashboardsIcon />,
                items: dashboards?.map(s => ({
                    name: s.title ?? s.name,
                    link: `/dashboard/${s.id}`,
                })),
            });
        }
        if (can(dispatchActions)) {
            result.push({
                name: t("DispatchActions"),
                link: "/dispatch-action",
                icon: <MailOutlineIcon />,
            });
        }
        //TODO: API Versión must be >= 3.6.0

        if (can(manage) && isFeatureSupported(SystemFeature.Security)) {
            result.push({
                name: t("Security"),
                link: "/security",
                icon: <SecurityIcon />,
            });
        }
        return result;
    }, [can, dashboards, isFeatureSupported, t]);

    return (
        <List component="nav" className={classes.appMenu} disablePadding>
            {menuDef.map((item, index) => (
                <SideMenuItem {...item} key={index} />
            ))}
        </List>
    );
};
