import {FC, RefObject, useCallback, useEffect, useMemo, useRef, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import TextField from "@material-ui/core/TextField";
import Tooltip from "@material-ui/core/Tooltip";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import DeleteIcon from "@material-ui/icons/Delete";
import useSWR from "swr";
import merger from "json-schema-merge-allof";
import {isPresent} from "ts-is-present";
import produce from "immer";
import {JSONSchema7} from "json-schema";
import Cropper from "cropperjs";

import {useBatchProcessorService} from "./image-batch-recognition-service";
import {dereferenceSchema} from "../../json-schema-ref-parser";
import {isPlainObject} from "../../../utils/types";
import {SelectAreaIcon} from "../../../components/icons/select-area-icon";
import {RecognizedDocument} from "./recognized-document";
import {ensurePromise} from "../../../utils/promise-helpers";
import {getBasePath} from "../../../utils/get-base-url";

const imageListWidth = 400;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "90vh",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: `auto ${imageListWidth}px`,
            gridTemplateRows: "auto",
            rowGap: "5px",
            height: "100%",
            overflowY: "hidden",
        },
        imagePane: {
            gridRow: "1 / 2",
            gridColumn: "1 / 2",
            overflow: "auto",
        },
        listPane: {
            gridRow: "1 / 2",
            gridColumn: "2 / 3",
            overflow: "auto",
            borderLeft: `solid 2px ${theme.palette.divider}`,
            paddingLeft: 10,
        },
        cropper: {
            height: "100%",
            width: "100%",
        },
        cropperDisabled: {
            "& .cropper-crop-box": {
                display: "none",
            },
            "& .cropper-drag-box": {
                opacity: 0,
            },
        },
        imageItem: {
            width: "100%",
            //textAlign: "center",
            position: "relative",
        },
        imageTitle: {
            textDecoration: "underline",
        },
        image: {
            maxWidth: "100%",
            //minHeight: 20,
            marginTop: 10,
            marginBottom: 10,
        },
        imagePlaceholder: {
            marginTop: 10,
            //marginBottom: 10,
            height: 50,
        },
        imageActions: {
            marginLeft: -10,
        },
        deleteButton: {},
        grow: {
            flexGrow: 1,
        },
        degreesEdit: {
            marginLeft: 20,
            width: "5em",
        },
    })
);

export type ImageExtractorModalProps = {
    open: boolean;
    pageIndex: number;
    onClose?: () => void;
};

const getCropper = (cropperRef: RefObject<HTMLCanvasElement>) => {
    const canvasElement: any = cropperRef?.current;
    const cropper: Cropper | undefined = canvasElement?.cropper;
    return cropper;
};

interface ImageField {
    id: string;
    path: Array<string | number>;
    title: string;
    value: string | undefined;
    originalValue: string | undefined;
}

const recursiveExtractImageFields = (
    document: any,
    schema: JSONSchema7,
    imageFields: ImageField[],
    currentPath: Array<string | number>,
    currentTitle: string[],
    visited: any[]
) => {
    if (schema.type === "string" && schema.format === "image") {
        const id = currentPath.map(s => s.toString()).join(".");
        if (imageFields.findIndex(s => s.id === id) === -1) {
            const title = currentTitle.join(" -> ");
            imageFields.push({
                id,
                path: [...currentPath],
                title,
                value: document,
                originalValue: document,
            });
        }
        return;
    }
    // Ignore anyOf and oneOf combinators.
    if (visited.includes(document) || (schema.type !== "array" && schema.type !== "object")) {
        return;
    }
    if (schema.type === "array" && Array.isArray(document) && typeof schema.items === "object") {
        let itemSchema: JSONSchema7;
        if (Array.isArray(schema.items)) {
            const objectSchemas = schema.items.filter(isPlainObject);
            if (objectSchemas.length !== 1) {
                return;
            }
            itemSchema = objectSchemas[0];
        } else {
            itemSchema = schema.items;
        }
        visited.push(document);
        for (let i = 0; i < document.length; i++) {
            try {
                currentPath.push(i);
                currentTitle.push(i.toString());
                recursiveExtractImageFields(document[i], itemSchema, imageFields, currentPath, currentTitle, visited);
            } finally {
                currentPath.pop();
                currentTitle.pop();
            }
        }
    } else if (schema.type === "object" && (isPlainObject(document) || !isPresent(document))) {
        if (document) {
            visited.push(document);
            for (const [key, value] of Object.entries(document)) {
                const subschema = schema.properties?.[key] ?? schema.additionalProperties;
                if (typeof subschema !== "object") {
                    continue;
                }
                try {
                    currentPath.push(key);
                    currentTitle.push(subschema.title ?? key);
                    recursiveExtractImageFields(value, subschema, imageFields, currentPath, currentTitle, visited);
                } finally {
                    currentPath.pop();
                    currentTitle.pop();
                }
            }
        }
        if (schema.properties) {
            for (const [key, subschema] of Object.entries(schema.properties)) {
                if (typeof subschema === "object" && (!document || !(key in document))) {
                    try {
                        currentPath.push(key);
                        currentTitle.push(subschema.title ?? key);
                        recursiveExtractImageFields(
                            undefined,
                            subschema,
                            imageFields,
                            currentPath,
                            currentTitle,
                            visited
                        );
                    } finally {
                        currentPath.pop();
                        currentTitle.pop();
                    }
                }
            }
        }
    }
};

const extractImageFields = (document: any, schema: JSONSchema7): ImageField[] => {
    const result: ImageField[] = [];
    recursiveExtractImageFields(document, schema, result, [], [], []);
    return result;
};

const assignValue = (target: any, fld: ImageField) => {
    if (isPresent(fld.value) && !target) {
        target = {};
    }
    let currentValue = target;
    for (let i = 0; i < fld.path.length - 1; i++) {
        const key = fld.path[i];
        let nextCurrentValue = currentValue[key];
        if (!currentValue) {
            if (fld.value) {
                currentValue[key] = nextCurrentValue = {};
            } else {
                return target;
            }
        }
        currentValue = nextCurrentValue;
    }
    if (isPresent(currentValue)) {
        const lastKey = fld.path[fld.path.length - 1];
        if (isPresent(fld.value)) {
            currentValue[lastKey] = fld.value;
        } else {
            delete currentValue[lastKey];
        }
    }
    return target;
};

export const ImageExtractorModal: FC<ImageExtractorModalProps> = props => {
    const {open, pageIndex, onClose} = props;
    const {documentSchemas, currentDocumentEditionState, setCurrentDocumentEditionState, findByPageIndex} =
        useBatchProcessorService();
    const {t} = useTranslation();
    const classes = useStyles();
    const [rotationDegree, setRotationDegree] = useState(180);
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const currentCaptureRef = useRef<string>();
    const [captureAvailable, setCaptureAvailable] = useState(false);
    const [imageFields, setImageFields] = useState<ImageField[]>([]);
    useSWR(
        [`documentSchema/${currentDocumentEditionState?.document.schemaId}`, document],
        async () => {
            const schemaId = currentDocumentEditionState?.document.schemaId;
            const schema = documentSchemas?.find(s => s.$id === schemaId);
            if (!schema) {
                return undefined;
            }
            const dereferencedSchema = await dereferenceSchema(schema);
            const mergedSchema = merger(dereferencedSchema, {
                ignoreAdditionalProperties: true,
            });
            const fields = extractImageFields(currentDocumentEditionState?.document.data, mergedSchema);
            setImageFields(fields);
            return mergedSchema;
        },
        {
            revalidateOnFocus: false,
        }
    );

    const page = useMemo(() => {
        return findByPageIndex(pageIndex);
    }, [findByPageIndex, pageIndex]);

    const clearCapture = useCallback(() => {
        const cropper = getCropper(canvasRef);
        if (cropper) {
            cropper.clear();
        }
        currentCaptureRef.current = undefined;
        setCaptureAvailable(false);
    }, []);

    const handleOnAccept = useCallback(async () => {
        const updatedFields = imageFields.filter(s => s.originalValue !== s.value);
        setCurrentDocumentEditionState(s => {
            if (updatedFields.length > 0 && s!.document) {
                const data = produce(s!.document.data, (draft: any) => {
                    for (const fld of updatedFields) {
                        if (fld.path.length === 0) {
                            return fld.value;
                        }
                        draft = assignValue(draft, fld);
                    }
                    return draft;
                });
                const newDoc: RecognizedDocument = {
                    ...s!.document,
                    data,
                };
                return {
                    ...s,
                    document: newDoc,
                };
            }
        });
        onClose?.();
    }, [imageFields, onClose, setCurrentDocumentEditionState]);

    const handleOnClose = useCallback(async () => {
        onClose?.();
    }, [onClose]);

    const handleDialogClose = useCallback(
        async (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [onClose]
    );

    const handleKeyPress = useCallback(
        (ev: React.KeyboardEvent) => {
            if (currentCaptureRef.current) {
                if (ev.key === "Escape") {
                    ev.preventDefault();
                    ev.stopPropagation();
                    clearCapture();
                }
            }
        },
        [clearCapture]
    );

    const handleAssignCapture = useCallback(
        (index: number) => {
            const image = currentCaptureRef.current;
            if (!image) {
                return;
            }
            clearCapture();
            setImageFields(s => {
                const newImages = [...s];
                newImages[index] = {
                    ...s[index],
                    value: image,
                };
                return newImages;
            });
        },
        [clearCapture]
    );

    const handleDeleteImage = useCallback((index: number) => {
        setImageFields(s => {
            const newImages = [...s];
            delete newImages[index]?.value;
            return newImages;
        });
    }, []);

    useEffect(() => {
        let cropper: Cropper | undefined = undefined;
        if (!!page) {
            setTimeout(() => {
                const canvas = canvasRef.current;
                if (canvas) {
                    const context = canvas.getContext("2d");
                    if (context) {
                        ensurePromise(page.getImageData()).then(imageData => {
                            context.putImageData(imageData, 0, 0);
                            cropper = new Cropper(canvas, {
                                dragMode: "crop",
                                initialAspectRatio: Number.NaN,
                                guides: false,
                                crop: () => {
                                    setCaptureAvailable(true);
                                    currentCaptureRef.current = cropper!.getCroppedCanvas().toDataURL();
                                },
                                viewMode: 1,
                                autoCrop: false,
                            });
                            //setTimeout(clearCapture, 100);
                            cropper.clear();
                        });
                    }
                }
            }, 0);
        }

        return () => {
            cropper?.destroy();
        };
    }, [page]);

    const handleRotationDegreesChange = useCallback((ev: React.ChangeEvent<HTMLInputElement>) => {
        const num = parseInt(ev.target.value);
        if (!isNaN(num)) {
            setRotationDegree(num);
        }
    }, []);

    const handleRotate = useCallback(() => {
        const cropper = getCropper(canvasRef);
        if (cropper && !isNaN(rotationDegree)) {
            cropper.rotate(rotationDegree);
        }
    }, [rotationDegree]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="lg"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
            onKeyDown={handleKeyPress}
        >
            <DialogTitle>{t("ExtractingImages")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <div className={classes.imagePane}>
                    {!!page && (
                        <canvas
                            ref={canvasRef}
                            width={page.width}
                            height={page.height}
                            style={{visibility: "hidden"}}
                        />
                    )}
                </div>
                <div className={classes.listPane}>
                    <List>
                        {imageFields.map((fld, i) => {
                            return (
                                <ListItem key={i}>
                                    <div className={classes.imageItem}>
                                        <Typography variant="subtitle1" className={classes.imageTitle}>
                                            {fld.title}
                                        </Typography>
                                        {fld.value && <img src={fld.value} alt="" className={classes.image} />}
                                        {!fld.value && (
                                            <img
                                                src={`${getBasePath()}/images/image-placeholder.png`}
                                                alt="placeholder"
                                                className={classes.imagePlaceholder}
                                            />
                                        )}
                                        <div className={classes.imageActions}>
                                            <Tooltip title={t("CaptureCurrentSelection") as string}>
                                                <span>
                                                    <IconButton
                                                        onClick={() => handleAssignCapture(i)}
                                                        disabled={!captureAvailable}
                                                    >
                                                        <SelectAreaIcon />
                                                    </IconButton>
                                                </span>
                                            </Tooltip>
                                            <Tooltip title={t("Delete") as string}>
                                                <IconButton onClick={() => handleDeleteImage(i)}>
                                                    <DeleteIcon />
                                                </IconButton>
                                            </Tooltip>
                                        </div>
                                    </div>
                                </ListItem>
                            );
                        })}
                    </List>
                </div>
            </DialogContent>
            <DialogActions>
                <TextField
                    type="number"
                    value={rotationDegree}
                    onChange={handleRotationDegreesChange}
                    className={classes.degreesEdit}
                />
                <Button onClick={handleRotate} color="primary">
                    {t("Rotate")}
                </Button>
                <div className={classes.grow} />
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
