import React, {FC} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        button: {
            marginRight: theme.spacing(1),
        },
        content: {
            maxHeight: "60vh",
            overflowY: "auto",
        },
    })
);

export interface StepContainerProps {
    onNext?: () => void;
    onBack?: () => void;
    onSkip?: () => void;
    nextActionCaption?: string;
}

export const StepContainer: FC<StepContainerProps> = props => {
    const {onNext, onBack, onSkip, nextActionCaption, children} = props;
    const classes = useStyles();
    const {t} = useTranslation();

    return (
        <>
            <Grid container>
                <Grid item xs={12} className={classes.content}>
                    {children}
                </Grid>
            </Grid>
            <Grid container>
                <Grid item xs={12}>
                    <Button disabled={!onBack} onClick={onBack} className={classes.button}>
                        {t("Back")}
                    </Button>
                    {!!onSkip && (
                        <Button onClick={onSkip} className={classes.button}>
                            {t("Skip")}
                        </Button>
                    )}
                    {!!onNext && (
                        <Button onClick={onNext} className={classes.button}>
                            {nextActionCaption ?? t("Next")}
                        </Button>
                    )}
                </Grid>
            </Grid>
        </>
    );
};
