import {FC, useCallback, useEffect, useMemo} from "react";
import Alert from "@material-ui/lab/Alert";
import AlertTitle from "@material-ui/lab/AlertTitle";
import {useTranslation} from "react-i18next";
import {atom, useRecoilValue, useSetRecoilState} from "recoil";
import axios, {AxiosInstance, AxiosRequestConfig} from "axios";
import {
    Auth0ContextInterface,
    Auth0Provider,
    Auth0ProviderOptions,
    LogoutOptions,
    useAuth0,
} from "@auth0/auth0-react";
import {useNavigate} from "react-router-dom";
import {last} from "ramda";
import useSWRImmutable from "swr/immutable";

import {WaitPage} from "../../components/wait-page";
import {AuthenticationService, AuthenticationServiceContext} from "./authentication-service";
import {getNimflowBusinessDirectory} from "../../api/nimflow-business-directory-provider";
import {auth0Audience, auth0ClientId, auth0Domain, businessDirectoryServerUrl, publicUrl} from "../../env-vars";
import {BearerSchemePublicSettings} from "../../api/nimflow-business-directory-api";
import {getBasePath} from "../../utils/get-base-url";

export interface Auth0UserData {
    nickname: string;
    name: string;
    picture?: string;
    updated_at?: string;
    email?: string;
    email_verified?: boolean;
    sub?: string;
    [key: string]: any;
}

const authenticatedUser = atom<Auth0UserData | undefined>({
    key: "authenticatedUser",
    default: undefined,
});

export interface Auth0AuthenticationState {
    isLoading: boolean;
    isAuthenticated: boolean;
    error?: Error;
}

export const auth0AuthenticationState = atom<Auth0AuthenticationState>({
    key: "auth0AuthenticationState",
    default: {
        isAuthenticated: false,
        isLoading: true,
    },
});

let cachedGetAccessToken: Auth0ContextInterface["getAccessTokenSilently"] | undefined = undefined;
let cachedGetIdTokenClaims: Auth0ContextInterface["getIdTokenClaims"] | undefined = undefined;
let cachedLogout: Auth0ContextInterface["logout"] | undefined = undefined;
let cachedLoginWithRedirect: Auth0ContextInterface["loginWithRedirect"] | undefined = undefined;

export const getAccessToken: Auth0ContextInterface["getAccessTokenSilently"] = async options => {
    if (cachedGetAccessToken) {
        return await cachedGetAccessToken(options);
    }
    throw new Error("Auth0 not set up");
};

export const getIdTokenClaims: Auth0ContextInterface["getIdTokenClaims"] = async options => {
    if (cachedGetIdTokenClaims) {
        return await cachedGetIdTokenClaims(options);
    }
    throw new Error("Auth0 not set up");
};

export const Auth0StateMonitor: FC<{}> = () => {
    const {
        isLoading,
        isAuthenticated,
        user,
        error,
        getAccessTokenSilently,
        getIdTokenClaims,
        logout,
        loginWithRedirect,
    } = useAuth0();
    const setAuthenticatedUser = useSetRecoilState(authenticatedUser);
    const setAuth0State = useSetRecoilState(auth0AuthenticationState);

    cachedGetAccessToken = getAccessTokenSilently;
    cachedGetIdTokenClaims = getIdTokenClaims;
    cachedLogout = logout;
    cachedLoginWithRedirect = loginWithRedirect;

    useEffect(() => {
        setAuthenticatedUser(user as Auth0UserData | undefined);
    }, [setAuthenticatedUser, user]);

    useEffect(() => {
        const state: Auth0AuthenticationState = {
            isLoading,
            isAuthenticated,
            error,
        };
        setAuth0State(state);
    }, [setAuth0State, error, isAuthenticated, isLoading]);

    return null;
};

const InnerAuth0AuthenticationServiceProvider: FC<{}> = props => {
    const {children} = props;
    const {t} = useTranslation();
    const {isAuthenticated, isLoading, error} = useRecoilValue(auth0AuthenticationState);
    const user = useRecoilValue(authenticatedUser);

    const login = useCallback(async () => {
        if (cachedLoginWithRedirect) {
            return await cachedLoginWithRedirect({
                appState: {
                    returnTo: `${window.location.pathname}${window.location.search}`,
                },
            });
        }
        throw new Error("Auth0 not set up");
    }, []);

    const logout = useCallback(async () => {
        if (cachedLogout) {
            const options: LogoutOptions = {
                returnTo: window.location.origin + getBasePath(),
            };
            return await cachedLogout(options);
        }
        throw new Error("Auth0 not set up");
    }, []);

    const getAuthorizedAxios = useCallback((options?: AxiosRequestConfig): AxiosInstance => {
        const result = axios.create(options);
        result.interceptors.request.use(
            async config => {
                const token = await getAccessToken();
                if (token) {
                    config.headers["Authorization"] = "Bearer " + token;
                }
                return config;
            },
            error => {
                Promise.reject(error);
            }
        );
        return result;
    }, []);

    const startResetPasswordFlow = useCallback((email: string) => {
        const axiosInstance = getAuthorizedAxios({baseURL: `https://${auth0Domain}/`});
        let options: AxiosRequestConfig = {
            method: "POST",
            url: "/dbconnections/change_password",
            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
            },
            data: {
                client_id: auth0ClientId,
                email,
                connection: "Username-Password-Authentication",
            },
        };

        return axiosInstance.request(options);
    }, [getAuthorizedAxios]);

    const authenticationService: AuthenticationService = {
        isAuthenticated,
        supportsPasswordReset: true,
        login,
        logout,
        startResetPasswordFlow,
        authenticatedUser: user,
        getAuthorizedAxios,
    };

    if (isLoading) {
        return <WaitPage></WaitPage>;
    } else if (error) {
        return (
            <Alert severity="error">
                <AlertTitle>{t("Error")}</AlertTitle>
                {error.message}
            </Alert>
        );
    }

    return (
        <AuthenticationServiceContext.Provider value={authenticationService}>
            {children}
        </AuthenticationServiceContext.Provider>
    );
};

const getAuth0SchemeSettings = async () => {
    const instance = axios.create({
        baseURL: businessDirectoryServerUrl,
        transformResponse: [], // Disable default automatic json parsing.
    });
    const bdapi = getNimflowBusinessDirectory(instance);
    try {
        const settings = await bdapi.auth_BearerSchemeSettings();
        return settings;
    } catch {
        const settings: BearerSchemePublicSettings = {
            supported: true,
            audience: auth0Audience,
            authority: `https://${auth0Domain}/`,
        };
        return settings;
    }
};

const authorityToDomain = (authority: string) => {
    let domain = last(authority.split("//"))!;
    const idx = domain.indexOf("/");
    if (idx >= 0) {
        domain = domain.substring(0, idx);
    }
    return domain;
};

export const Auth0AuthenticationServiceProvider: FC<{}> = props => {
    const {children} = props;
    const {t} = useTranslation();
    const navigate = useNavigate();
    const {
        data: schemeSettings,
        isValidating,
        error,
    } = useSWRImmutable("auth0AuthenticationSchemeSettings", async () => {
        return await getAuth0SchemeSettings();
    });

    const options = useMemo(() => {
        let domain = "";
        if (schemeSettings?.authority) {
            domain = authorityToDomain(schemeSettings.authority);
        }
        const options: Auth0ProviderOptions = {
            domain,
            clientId: auth0ClientId ?? "",
            audience: schemeSettings?.audience,
            redirectUri: window.location.origin + publicUrl,
            onRedirectCallback: appState => {
                navigate(appState?.returnTo || window.location.pathname);
            },
        };
        return options;
    }, [navigate, schemeSettings?.audience, schemeSettings?.authority]);

    if (isValidating) {
        return <WaitPage></WaitPage>;
    } else if (error) {
        return (
            <Alert severity="error">
                <AlertTitle>{t("Error")}</AlertTitle>
                {error.message}
            </Alert>
        );
    }

    return (
        <Auth0Provider {...options}>
            <Auth0StateMonitor />
            <InnerAuth0AuthenticationServiceProvider>{children}</InnerAuth0AuthenticationServiceProvider>
        </Auth0Provider>
    );
};
