import {FC, useState, useEffect, useCallback} from "react";
import {makeStyles, useTheme} from "@material-ui/core";
import {VegaLite, VisualizationSpec} from "react-vega";
import {VegaLiteProps} from "react-vega/lib/VegaLite";
import {MuiVegaLite} from "@material-vega/material-ui";
import {useResizeDetector} from "react-resize-detector";
import {isPresent} from "ts-is-present";
import {ThemeTypes} from "@material-vega/core";

import {DashboardWidgetRendererProps} from "./dashboard-widget-viewer";
import {DashboardWidgetVegaLiteVisualization} from "../../api/nimflow-dashboard-widgets-api";
import {evaluateScript} from "../../utils/scripting";

export interface VegaLiteRendererProps extends DashboardWidgetRendererProps<DashboardWidgetVegaLiteVisualization> {}

const useStyles = makeStyles({
    root: {
        height: "100%",
        width: "100%",
        overflow: "hidden",
    },
});

const defaultSpec = {autosize: "fit" /*, width: "container", height: "container"*/};

const isSingleViewSpec = (spec: VisualizationSpec) => {
    const isLayered = "layer" in spec;
    let isFaceted = "facet" in spec;
    if ("encoding" in spec) {
        const enc = spec.encoding;
        if (enc) {
            isFaceted = isFaceted || "facet" in enc || "row" in enc || "column" in enc;
        }
    }
    const isMultiview = "concat" in spec || "hconcat" in spec || "vconcat" in spec || "repeat" in spec;
    return !isLayered && !isFaceted && !isMultiview;
};

const useVegaLiteSpecEvaluator = () => {
    const evaluator = useCallback(async (jsSpec: string, params: Record<string, any>) => {
        const outputValues = {
            vegaLiteSpec: undefined as any,
        };
        evaluateScript(jsSpec, {
            inputValues: {params},
            outputValues,
        });
        if (isSingleViewSpec(outputValues.vegaLiteSpec)) {
            return {...defaultSpec, ...outputValues.vegaLiteSpec};
        }
        return outputValues.vegaLiteSpec;
    }, []);

    return evaluator;
};

interface ViewerProps {
    variant?: ThemeTypes;
    data: Record<string, any>;
    spec?: VisualizationSpec;
    width?: number;
    height?: number;
}

const Viewer: FC<ViewerProps> = props => {
    const {data, spec, variant, width, height} = props;
    const theme = useTheme();
    const classes = useStyles();

    const vegaTheme = theme.palette.type === "dark" ? "dark" : undefined;

    if (!spec || !isPresent(width) || !isPresent(height)) {
        return null;
    }

    const commonProps: VegaLiteProps = {
        className: classes.root,
        renderer: "svg",
        spec,
        data,
        theme: vegaTheme,
        actions: false,
        width: width - 30,
        height: height - 30,
    };

    if (variant) {
        return <MuiVegaLite tooltip={true} variant={variant} {...commonProps} />;
    }
    return <VegaLite {...commonProps} />;
};

export const VegaLiteRenderer: FC<VegaLiteRendererProps> = props => {
    const {visualization, params, data} = props;
    const classes = useStyles();
    const [spec, setSpec] = useState<VisualizationSpec>();
    const specEvaluator = useVegaLiteSpecEvaluator();
    const {width, height, ref} = useResizeDetector();

    useEffect(() => {
        specEvaluator(visualization.jsSpecification, params).then(newSpec => {
            setSpec(newSpec);
        });
    }, [params, specEvaluator, visualization.jsSpecification]);

    return (
        <div ref={ref} className={classes.root}>
            <Viewer
                variant={visualization.variant}
                spec={spec}
                data={data}
                width={width}
                height={height}
            />
        </div>
    );
};
