import {FC, useMemo} from "react";
import {
    StatePropsOfRenderer,
    RankedTester,
    rankWith,
    uiTypeIs,
    Resolve,
    JsonFormsRendererRegistryEntry,
    UISchemaElement,
    composeWithUi,
} from "@jsonforms/core";
import {UnknownRenderer, useJsonForms as useOriginalJsonForms, withJsonFormsLayoutProps} from "@jsonforms/react";
import {isPresent} from "ts-is-present";

import {useJsonForms} from "./jsonforms-context";
import {UseElement} from "./use-element";

const isUseElement = (element: UISchemaElement): element is UseElement => {
    return element.type === "Use";
};

const selectBestRenderer = (
    renderers: JsonFormsRendererRegistryEntry[],
    rankRenderer: (renderer: JsonFormsRendererRegistryEntry) => number
) => {
    let bestRanking = -1;
    let selectedRenderer: JsonFormsRendererRegistryEntry | undefined = undefined;
    for (const renderer of renderers) {
        const ranking = rankRenderer(renderer);
        if (ranking > bestRanking) {
            bestRanking = ranking;
            selectedRenderer = renderer;
        }
    }
    return selectedRenderer;
};

const UseRenderer: FC<StatePropsOfRenderer> = props => {
    const {uischema, schema, path, renderers, ...rest} = props;
    const {core} = useOriginalJsonForms();
    const {uiSchemaLibrary} = useJsonForms();
    const useElement = isUseElement(uischema) ? uischema : undefined;
    const targetUiSchema = useElement ? uiSchemaLibrary[useElement.name] : undefined;
    const resolvedSchema = useElement ? Resolve.schema(schema, useElement.scope) : undefined;
    const renderer = useMemo(() => {
        if (!renderers || !targetUiSchema || !resolvedSchema) {
            return undefined;
        }
        const selectedRenderer = selectBestRenderer(renderers, s => s.tester(targetUiSchema, resolvedSchema));
        return selectedRenderer;
    }, [targetUiSchema, renderers, resolvedSchema]);

    if (!targetUiSchema || !useElement || !resolvedSchema) {
        return null;
    }

    const newPath = composeWithUi(useElement, path);
    const resolvedData = Resolve.data(core?.data, newPath);

    if (!isPresent(renderer) || renderer.tester(targetUiSchema, resolvedSchema) === -1) {
        return <UnknownRenderer type={"renderer"} />;
    } else {
        const Render = renderer.renderer;
        return (
            <Render
                uischema={targetUiSchema}
                schema={resolvedSchema}
                path={newPath}
                data={resolvedData}
                renderers={renderers}
                {...rest}
            />
        );
    }
};

export default withJsonFormsLayoutProps(UseRenderer);

export const useRendererTester: RankedTester = rankWith(
    3, //increase rank as needed
    uiTypeIs("Use")
);
