import React, {FC, ReactElement} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import {useAsync} from "react-use";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import ErrorIcon from "@material-ui/icons/Error";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import {green} from "@material-ui/core/colors";

import {DispatchContextAction} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";
import {WaitSpinner} from "../../components/wait-spinner";
import {ErrorViewer} from "../../components/error-viewer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        button: {
            marginRight: theme.spacing(1),
        },
        icon: {
            textAlign: "center",
        },
    })
);

export interface ActionExecutorProps {
    action: DispatchContextAction;
    onDispatchCompleted?: () => void;
    onBack?: () => void;
}

interface FeedbackProps {
    icon?: ReactElement;
    title: string;
    details?: string | ReactElement;
}

const Feedback: FC<FeedbackProps> = props => {
    const {icon, title, details} = props;
    const classes = useStyles();
    return (
        <Box pt="50px" pb="50px" maxHeight="60vh">
            <Grid container justifyContent="center" alignItems="center" spacing={4}>
                {!!icon && (
                    <Grid item xs={12} className={classes.icon}>
                        {icon}
                    </Grid>
                )}
                <Grid item xs={12}>
                    <Typography variant="h4" align="center">
                        {title}
                    </Typography>
                </Grid>
                {!!details && (
                    <Grid item xs={12}>
                        {typeof details === "string" ? (
                            <Typography variant="h6" align="center">
                                {details}
                            </Typography>
                        ) : (
                            <div>
                                {details}
                            </div>
                        )}
                    </Grid>
                )}
            </Grid>
        </Box>
    );
};

export const ActionExecutor: FC<ActionExecutorProps> = props => {
    const {action, onDispatchCompleted, onBack} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {contexts_DispatchAction} = useNimflow();
    const {loading, value: response, error} = useAsync(async () => {
        return await contexts_DispatchAction(action);
    }, [contexts_DispatchAction, action]);

    if (loading) {
        return <WaitSpinner>{t("ExecutingAction")}</WaitSpinner>;
    }

    return (
        <>
            {!error && (
                <Feedback
                    icon={<CheckCircleIcon style={{color: green[500], fontSize: 60}} />}
                    title={t("ActionCompleted")}
                    details={t("DispatchActionResult", {id: response?.contextId})}
                />
            )}
            {!!error && (
                <Feedback
                    icon={<ErrorIcon color="error" style={{fontSize: 60}} />}
                    title={t("ActionFailed")}
                    details={<ErrorViewer error={error} />}
                />
            )}
            <Grid container>
                <Grid item xs={12}>
                    {!!error && (
                        <Button onClick={onBack} className={classes.button}>
                            {t("Back")}
                        </Button>
                    )}
                    <Button onClick={onDispatchCompleted} className={classes.button}>
                        {t("Ok")}
                    </Button>
                </Grid>
            </Grid>
        </>
    );
};
