import {useCallback, useMemo} from "react";
import {isPresent} from "ts-is-present";
import i18next from "i18next";
import {format, parseISO} from "date-fns";
import useSWR from "swr";
import {CancelToken} from "axios";

import {useNimflow} from "./nimflow-api-provider";
import {Stages} from "./nimflow-api";
import {DashboardWidgetParameterType} from "./nimflow-dashboard-widgets-api";
import {useNimflowFunctions} from "./nimflow-functions-api";
import {useKey} from "../utils/use-key";

export interface DashboardDefinitionInfo {
    id?: string;
    name: string;
    title?: string;
}

export const dashboardParameterDataTypes = ["number", "string", "boolean", "datetime", "dateinterval"] as const;
Object.freeze(dashboardParameterDataTypes);

export type DashboardParameterDataType = typeof dashboardParameterDataTypes[number];

export const dashboardParameterSelectionTypes = ["single", "multiple", "freeText", "timeRange"] as const;
Object.freeze(dashboardParameterSelectionTypes);

export type DashboardParameterSelectionType = typeof dashboardParameterSelectionTypes[number];

export interface StaticDashboardParameterTimeRangeValue {
    type: "static";
    range: {
        start: string;
        end: string;
    };
}

export type DynamicTimeRangeUnit = "minutes" | "hours" | "days";

export interface DynamicDashboardParameterTimeRangeValue {
    type: "dynamic";
    range: {
        count: number;
        unit: DynamicTimeRangeUnit;
    };
}

export type DashboardParameterTimeRangeValue =
    | StaticDashboardParameterTimeRangeValue
    | DynamicDashboardParameterTimeRangeValue;

export interface DashboardParameterValue {
    value: number | string | boolean | DashboardParameterTimeRangeValue;
    displayText?: string;
}

export interface DashboardParameterState {
    values: DashboardParameterValue[];
    selectAll: boolean;
}

export interface BaseDashboardParameter {
    id: string;
    displayName: string;
    dataType: DashboardParameterDataType;
    selectionType: DashboardParameterSelectionType;
    defaultValue: DashboardParameterState;
}

export interface StaticSelectionDashboardParameterDataSource {
    type: "static";
    options: DashboardParameterValue[];
}

export interface FunctionSelectionDashboardParameterDataSource {
    type: "function";
    moduleName: string;
    functionName: string;
    jsArguments?: string;
    columns: {
        value: string;
        label?: string;
    };
}

export type SelectionDashboardParameterDataSource =
    | StaticSelectionDashboardParameterDataSource
    | FunctionSelectionDashboardParameterDataSource;

export type SelectionDashboardParameterDataSourceType = SelectionDashboardParameterDataSource["type"];

export const selectionDashboardParameterDataSourceTypes: SelectionDashboardParameterDataSourceType[] = [
    "static",
    "function",
];
Object.freeze(selectionDashboardParameterDataSourceTypes);

export interface SelectionDashboardParameter extends BaseDashboardParameter {
    selectionType: "single" | "multiple";
    variableName: string;
    allowEmptySelection: boolean;
    dataSource: SelectionDashboardParameterDataSource;
}

export const isSelectionDashboardParameter = (param: DashboardParameter): param is SelectionDashboardParameter => {
    return param.selectionType === "single" || param.selectionType === "multiple";
};
export interface TimeRangeDashboardParameter extends BaseDashboardParameter {
    selectionType: "timeRange";
    beginVariableName: string;
    endVariableName: string;
}

export interface FreeTextDashboardParameter extends BaseDashboardParameter {
    selectionType: "freeText";
    variableName: string;
}

export type DashboardParameter = SelectionDashboardParameter | FreeTextDashboardParameter | TimeRangeDashboardParameter;

export const getParameterType = (param: DashboardParameter) => {
    return param.dataType;
};

export const getParameterVariablesType = (param: DashboardParameter): DashboardWidgetParameterType => {
    switch (param.selectionType) {
        case "timeRange":
            return "datetime";
        case "multiple":
            return "stringList";
        default:
            const dt = param.dataType;
            return dt === "dateinterval" ? "datetime" : dt;
    }
};

export const getVariableNames = (param: DashboardParameter): string[] => {
    switch (param.selectionType) {
        case "timeRange":
            return [param.beginVariableName, param.endVariableName];
        default:
            return [param.variableName];
    }
};

export const getParameterValueDescription = (value: DashboardParameterValue, parameter: DashboardParameter) => {
    if (isPresent(value?.displayText)) {
        return value?.displayText;
    }
    const val = value?.value;
    if (typeof val === "string" && parameter.dataType === "datetime") {
        const dateFormat = i18next.t("DATE_FORMAT");
        const timeFormat = i18next.t("TIME_FORMAT");
        return format(parseISO(val), dateFormat) + " " + format(parseISO(val), timeFormat) + " (UTC)";
    }
    return val?.toString();
};

export const useSelectionDashboardParameterDataSource = (ds: SelectionDashboardParameterDataSource) => {
    const {evaluateArgsAndCall} = useNimflowFunctions();
    const key = useKey(ds);
    const {data, error, isValidating, mutate} = useSWR(
        key,
        async () => {
            const result: DashboardParameterValue[] = [];
            if (ds.type === "static") {
                result.push(...ds.options);
            } else if (ds.type === "function") {
                if (ds.moduleName && ds.functionName) {
                    const vals = await evaluateArgsAndCall(ds.moduleName, ds.functionName, ds.jsArguments);
                    if (Array.isArray(vals)) {
                        for (const val of vals) {
                            result.push({
                                value: val[ds.columns.value],
                                displayText: val[ds.columns.label ?? ds.columns.value],
                            });
                        }
                    }
                }
            }
            return result;
        },
        {
            revalidateOnFocus: false,
        }
    );

    const result = useMemo(() => {
        return {data: data ?? [], error, isValidating, mutate};
    }, [data, error, isValidating, mutate]);

    return result;
};

export interface DashboardWidgetInstantiation {
    instantiationId: string;
    widgetName: string;
    parametersBindings: Record<string, string>;
}

export interface DashboardResponsiveLayout {
    name: string;
    breakpoint: number;
    cols: number;
    margin?: [number, number];
    containerPadding?: [number, number];
    rowHeight?: number;
    widgetsLayouts?: any[];
}

export interface DashboardResponsiveLayoutSet {
    margin?: [number, number];
    containerPadding?: [number, number];
    rowHeight?: number;
    layouts: DashboardResponsiveLayout[];
}

export const autoRefreshIntervals = ["30s", "1m", "5m", "15m", "30m", "1h", "2h", "1d"] as const;
Object.freeze(autoRefreshIntervals);
export type AutoRefreshInterval = typeof autoRefreshIntervals[number];

export interface DashboardAutoRefresh {
    enabled: boolean;
    defaultInterval?: AutoRefreshInterval;
    minInterval?: AutoRefreshInterval;
}

export interface DashboardDefinition {
    id?: string;
    name: string;
    title: string;
    description?: string;
    parameters?: DashboardParameter[];
    widgets: DashboardWidgetInstantiation[];
    layoutSet?: DashboardResponsiveLayoutSet;
    autoRefresh?: DashboardAutoRefresh;
}

export interface DashboardsGetInfos {
    name?: string;
}

const oneMinute = 60 * 1000;
const oneHour = 60 * oneMinute;

export const autoRefreshIntervalToMillisecons = (interval: AutoRefreshInterval): number | undefined => {
    switch (interval) {
        case "30s":
            return oneMinute / 2;
        case "1m":
            return oneMinute;
        case "5m":
            return 5 * oneMinute;
        case "15m":
            return 15 * oneMinute;
        case "30m":
            return 30 * oneMinute;
        case "1h":
            return oneHour;
        case "2h":
            return 2 * oneHour;
        case "1d":
            return 24 * oneHour;
        default: 
            return undefined;
    }
}

export const dashboardsCollectionName = "NimflowDashboards";

export const useNimflowDashboards = () => {
    const {
        collections_Aggregate,
        collections_FindById,
        collections_InsertOne,
        collections_DeleteOne,
        collections_Replace,
    } = useNimflow();

    const dashboards_GetInfos = useCallback(
        async (query?: DashboardsGetInfos, cancelToken?: CancelToken) => {
            let stages: Stages[] = [
                {
                    $project: {
                        _id: false,
                        id: "$_id",
                        name: 1,
                        title: 1,
                    },
                },
                {
                    $sort: {
                        name: 1,
                    },
                },
            ];
            const name = query?.name;
            if (name) {
                stages = [
                    {
                        $match: {
                            name,
                        },
                    },
                    ...stages,
                ];
            }
            const result: DashboardDefinitionInfo[] = await collections_Aggregate(
                {
                    collectionName: dashboardsCollectionName,
                    stages,
                },
                cancelToken
            );
            return result;
        },
        [collections_Aggregate]
    );

    const dashboards_FindOne = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result: DashboardDefinition = await collections_FindById(
                {
                    collectionName: dashboardsCollectionName,
                    id,
                },
                cancelToken
            );
            if (!result.id) {
                result.id = (result as any)._id;
            }
            return result;
        },
        [collections_FindById]
    );

    const dashboards_InsertOne = useCallback(
        async (dahsboardDefinition: DashboardDefinition, cancelToken?: CancelToken) => {
            const result = await collections_InsertOne(
                {
                    collectionName: dashboardsCollectionName,
                    document: dahsboardDefinition,
                },
                cancelToken
            );
            return result.id;
        },
        [collections_InsertOne]
    );

    const dashboards_Replace = useCallback(
        async (dashboardDefinition: DashboardDefinition, cancelToken?: CancelToken) => {
            const result = await collections_Replace(
                {
                    id: dashboardDefinition.id ?? (dashboardDefinition as any)._id,
                    collectionName: dashboardsCollectionName,
                    replacement: dashboardDefinition,
                },
                cancelToken
            );
            return result;
        },
        [collections_Replace]
    );

    const dashboards_Delete = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result = await collections_DeleteOne(
                {
                    collectionName: dashboardsCollectionName,
                    id,
                },
                cancelToken
            );
            return result;
        },
        [collections_DeleteOne]
    );

    return {
        dashboards_GetInfos,
        dashboards_FindOne,
        dashboards_InsertOne,
        dashboards_Replace,
        dashboards_Delete,
    };
};
