export const permissions = {
    dispatchContextActions: "dispatch:context-actions",
    manageTasks: "manage:tasks",
    readContexts: "read:contexts",
    readContextTypes: "read:context-types",
    readTasks: "read:tasks",
    readUsers: "read:users",
    sendTaskResponses: "send:task-responses",
    writeContextTypes: "write:context-types",
    manage: "manage",
};

export interface UserRole {
    name: string,
    permissions: string[]
}

export const standardUserRoles: Record<string, UserRole> = {
    member: {
        name: "member",
        permissions: [permissions.readTasks, permissions.sendTaskResponses],
    },
    coordinator: {
        name: "coordinator",
        permissions: [
            permissions.manageTasks,
            permissions.readContexts,
            permissions.readUsers
        ],
    },
    developer: {
        name: "developer",
        permissions: [
            permissions.readContextTypes,
            permissions.writeContextTypes,
            permissions.dispatchContextActions,
        ],
    },
    administrator: {
        name: "administrator",
        permissions: [permissions.manage],
    },
};

export const permissionRequests = {
    workMyTasks: [permissions.readTasks, permissions.readContextTypes, permissions.sendTaskResponses],
    manageTeamTasks: [permissions.manageTasks, permissions.readUsers],
    dispatchActions: [permissions.dispatchContextActions, permissions.readContextTypes],
    manage: [permissions.manage],
};
