import {FC, useCallback, useRef, useState} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormHelperText from "@material-ui/core/FormHelperText";
import Checkbox from "@material-ui/core/Checkbox";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import {useSnackbar} from "notistack";

import {ErrorBoundary} from "../../components/error-boundary";
import {useReportError} from "../../utils/use-report-error";
import {RoleName, roleNames, useSecurityService} from "./security-context";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        formControl: {},
        formGroup: {
            marginTop: theme.spacing(1),
        },
    })
);

const re =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

const validateEmail = (email: string) => {
    return re.test(String(email).toLowerCase());
};

const validateEmailField = (email: string | undefined) => {
    let error: string | undefined = undefined;
    if (!email) {
        error = i18next.t("EmailRequired");
    } else if (!validateEmail(email)) {
        error = i18next.t("InvalidEmail");
    }
    return error;
};

const validateRolesField = (roles: {[role in RoleName]?: boolean}) => {
    let error: string | undefined = undefined;
    if (!Object.values(roles).some(s => !!s)) {
        error = i18next.t("AtLeastOneRoleRequired");
    }
    return error;
};

export interface InviteUserDialogProps {
    open: boolean;
    onClose: (invitationSent: boolean) => void;
}

export const InviteUserDialog: FC<InviteUserDialogProps> = props => {
    const {open, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [email, setEmail] = useState<string>();
    const [roles, setRoles] = useState<{[role in RoleName]?: boolean}>({});
    const emailTouchedRef = useRef(false);
    const rolesTouchedRef = useRef(false);
    const [emailError, setEmailError] = useState<string>();
    const [rolesError, setRolesError] = useState<string>();
    const reportError = useReportError();
    const {enqueueSnackbar} = useSnackbar();
    const {inviteUser} = useSecurityService();

    const handleEmailChange = useCallback((ev: React.ChangeEvent<HTMLInputElement>) => {
        const newValue = ev.target.value;
        setEmail(newValue);
        emailTouchedRef.current = true;
        setEmailError(validateEmailField(newValue));
    }, []);

    const handleEmailBlur = useCallback(() => {
        emailTouchedRef.current = true;
        setEmailError(validateEmailField(email));
    }, [email]);

    const handleChecked = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
        let error: string | undefined = undefined;
        setRoles(s => {
            const newValue = {
                ...s,
                [event.target.name]: event.target.checked,
            };
            error = validateRolesField(newValue);
            return newValue;
        });
        rolesTouchedRef.current = true;
        setRolesError(error);
    }, []);

    const handleRolesBlur = useCallback(() => {
        rolesTouchedRef.current = true;
    }, []);

    const handleInvite = useCallback(async () => {
        emailTouchedRef.current = true;
        rolesTouchedRef.current = true;
        const emailError = validateEmailField(email);
        const rolesError = validateRolesField(roles);
        if (!!emailError || !!rolesError) {
            setEmailError(emailError);
            setRolesError(rolesError);
            return;
        }
        try {
            await inviteUser(email!, roles);
        } catch (e) {
            reportError(t("CouldNotInviteUser"), e);
            return false;
        }
        enqueueSnackbar(t("InvitationSucessful"), {
            variant: "success",
        });
        setEmail(undefined);
        setRoles({});
        onClose?.(true);
    }, [email, enqueueSnackbar, inviteUser, onClose, reportError, roles, t]);

    const handleCancel = useCallback(() => {
        onClose?.(false);
    }, [onClose]);

    const handleInteractiveClose = useCallback((event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.(false);
        }
    }, [onClose]);

    const showEmailError = !!emailError && emailTouchedRef.current;
    const showRolesError = !!rolesError && rolesTouchedRef.current;

    return (
        <ErrorBoundary>
            <Dialog open={open} onClose={handleInteractiveClose} maxWidth="sm" fullWidth>
                <DialogTitle>{t("InvitingUser")}</DialogTitle>
                <DialogContent>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <TextField
                                label={t("Email")}
                                value={email}
                                onChange={handleEmailChange}
                                onBlur={handleEmailBlur}
                                required
                                fullWidth
                                InputProps={{required: true}}
                                error={showEmailError}
                                helperText={emailError}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <FormControl
                                required
                                error={showRolesError}
                                component="fieldset"
                                className={classes.formControl}
                                margin="none"
                            >
                                <FormLabel component="legend">{t("Roles")}</FormLabel>
                                <FormGroup className={classes.formGroup}>
                                    {roleNames.map(s => (
                                        <FormControlLabel
                                            key={s}
                                            control={
                                                <Checkbox
                                                    checked={roles[s]}
                                                    onBlur={handleRolesBlur}
                                                    onChange={handleChecked}
                                                    name={s}
                                                    color="default"
                                                />
                                            }
                                            label={s}
                                        />
                                    ))}
                                </FormGroup>
                                <FormHelperText>{rolesError}</FormHelperText>
                            </FormControl>
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleInvite} color="primary">
                        {t("Invite")}
                    </Button>
                    <Button onClick={handleCancel} color="primary">
                        {t("Close")}
                    </Button>
                </DialogActions>
            </Dialog>
        </ErrorBoundary>
    );
};
