import {useCallback} from "react";

import {DashboardWidgetFunctionDataSource} from "../../api/nimflow-dashboard-widgets-api";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";
import {evaluateScript} from "../../utils/scripting";

export const useFunctionDataSourceEvaluator = () => {
    const {call} = useNimflowFunctions();

    const evaluateArgs = useCallback(
        async (dataSource: DashboardWidgetFunctionDataSource, args: Record<string, any>) => {
            const argsScript = dataSource.jsArguments;
            if (!argsScript) {
                return undefined;
            }
            const outputValues = {
                args: undefined,
            };
            evaluateScript(argsScript, {
                inputValues: args,
                outputValues,
            });
            return outputValues.args;
        },
        []
    );

    const evaluateFunctionDataSource = useCallback(
        async (dataSource: DashboardWidgetFunctionDataSource, args: Record<string, any>): Promise<any> => {
            const fnargs = await evaluateArgs(dataSource, {params: args});
            const result = await call(dataSource.moduleName, dataSource.functionName, fnargs);
            return result;
        },
        [call, evaluateArgs]
    );

    return {
        evaluateFunctionDataSource,
    };
};
