import React, {FC, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import {useTranslation} from "react-i18next";

import {TasksViewFilterEntryEditorProps} from "./tasks-view-filter";
import {BooleanTaskViewFilter} from "./tasks-view";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        size: {
            width: "7em",
        },
    })
);

export const BooleanFilter: FC<TasksViewFilterEntryEditorProps<BooleanTaskViewFilter>> = props => {
    const {value, onChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const val = value?.type === "boolean" ? (value.value === true ? 1 : 0) : undefined;

    const handleChange = useCallback(
        (ev: React.ChangeEvent<any>) => {
            const v = ev.target.value;

            onChange?.(
                v === 1 ? {type: "boolean", value: true} : v === 0 ? {type: "boolean", value: false} : undefined
            );
        },
        [onChange]
    );

    return (
        <TextField select value={val} onChange={handleChange} className={classes.size}>
            <MenuItem value={undefined}>&nbsp;</MenuItem>
            <MenuItem value={1}>{t("Yes")}</MenuItem>
            <MenuItem value={0}>{t("No")}</MenuItem>
        </TextField>
    );
};
