import React, {FC} from "react";
import {
    DateRangePicker as OriginalDateRangePicker,
    DateRangePickerProps as OriginalDateRangePickerProps,
} from "react-date-range/";
import {defaultStaticRanges, defaultInputRanges} from "react-date-range/dist/defaultRanges";
import enLocale from "date-fns/locale/en-US";
import esLocale from "date-fns/locale/es";
import i18next from "i18next";
import "react-date-range/dist/styles.css"; // main style file
import "react-date-range/dist/theme/default.css"; // theme css file
import { useTranslation } from "react-i18next";

const localeMap: Record<string, any> = {
    en: enLocale,
    es: esLocale,
};

let currentLocale = enLocale;
let currentStaticRanges = defaultStaticRanges;
let currentInputRanges = defaultInputRanges;

function setLanguage(lang: string) {
    let locale = localeMap[lang];
    if (!locale) {
        const idx = lang.indexOf("-");
        if (idx > 0) {
            locale = localeMap[lang.substring(0, idx)];
        }
    }
    currentLocale = locale ?? enLocale;
    if (lang.startsWith("en")) {
        currentStaticRanges = defaultStaticRanges;
        currentInputRanges = defaultInputRanges;
    } else {
        currentStaticRanges = defaultStaticRanges.map(s => ({
            ...s,
            label: i18next.t(s.label)
        }));
        currentInputRanges = defaultInputRanges.map(s => ({
            ...s,
            label: i18next.t(s.label)
        }));
    }
}

if (i18next.language) {
    setLanguage(i18next.language);
}

i18next.on("languageChanged", lang => {
    setLanguage(lang);
});

export type DateRangePickerProps = Omit<OriginalDateRangePickerProps, "locale" | "staticRanges" | "inputRanges">;

export const DateRangePicker: FC<DateRangePickerProps> = props => {
    const {t} = useTranslation();
    return (
        <OriginalDateRangePicker
            locale={currentLocale}
            staticRanges={currentStaticRanges}
            inputRanges={currentInputRanges}
            startDatePlaceholder={t("From")}
            endDatePlaceholder={t("To")}
            {...props}
        />
    );
};
