import {format as timeagoFormat, render as timeagoRender, register, TDate, Opts} from "timeago.js";
import de from "timeago.js/esm/lang/de";
import es from "timeago.js/esm/lang/es";
import fr from "timeago.js/esm/lang/fr";
import it from "timeago.js/esm/lang/it";
import pt_BR from "timeago.js/esm/lang/pt_BR";
import ru from "timeago.js/esm/lang/ru";
import i18next from "i18next";

let language = "en_US";

const registeredLocales: string[] = ["en_US", "zh_CN"];

const registerLocale = (lang: string, locale: any) => {
    register(lang, locale);    
    registeredLocales.push(lang);
}
registerLocale("de", de);
registerLocale("es", es);
registerLocale("fr", fr);
registerLocale("it", it);
registerLocale("pt_BR", pt_BR);
registerLocale("ru", ru);

function loadLang(lang: string) {
    if (registeredLocales.includes(lang)) {
        language = lang;
        return;
    }
    const idx = lang.indexOf("_");
    if (idx > 0) {
        loadLang(lang.substring(0, idx));
    } else {
        language = "en_US";
    }
}

function setLanguage(lang: string) {
    if (lang === "en") {
        lang = "en_US";
    }
    lang = lang.replace("-", "_");
    loadLang(lang);
}

if (i18next.language) {
    setLanguage(i18next.language);
}

i18next.on("languageChanged", lang => {
    setLanguage(lang);
});

export const format = (date: TDate, opts?: Opts) => timeagoFormat(date, language, opts);
export const render = (node: HTMLElement | HTMLElement[] | NodeList, opts?: Opts) => timeagoRender(node, language, opts);
