import {FC, useMemo, useState, useCallback, useEffect} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import RefreshIcon from "@material-ui/icons/Refresh";
import {useTranslation} from "react-i18next";

import {ContextTask, ContextType, ResponseType} from "../../api/nimflow-api";
import {useTasksViewService, TasksViewServiceProvider} from "./tasks-view-service";
import {TasksViewTable} from "./tasks-view-table";
import {TasksViewResponseEditorModal} from "./tasks-view-response-editor-modal";
import {TasksViewFilter} from "./tasks-view-filter";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            position: "relative",
        },
        refreshButton: {
            position: "absolute",
            right: 10,
            top: 10,
        },
    })
);
export type TaskViewColumnNames = keyof ContextTask;

export interface PrefixTextMatchTaskViewFilter {
    type: "prefixTextMatch";
    prefix: string;
}

export interface TextMatchTaskViewFilter {
    type: "textMatch";
    value: string;
}
export interface DateRangeTaskViewFilter {
    type: "dateRange";
    from: string | undefined;
    to: string | undefined;
    includeFrom: boolean | undefined;
    includeTo: boolean | undefined;
}

export interface SelectionTaskViewFilter {
    type: "selection";
    value: string | null;
}

export interface OrTaskViewFilter<T> {
    type: "or";
    filters: Array<TaskViewFilter<T>>;
}
export interface AndTaskViewFilter<T> {
    type: "and";
    filters: Array<TaskViewFilter<T>>;
}

export type StringTaskViewFilter =
    | PrefixTextMatchTaskViewFilter
    | TextMatchTaskViewFilter
    | DateRangeTaskViewFilter
    | SelectionTaskViewFilter;

export interface BooleanTaskViewFilter {
    type: "boolean";
    value: boolean;
}

export interface NumberRangeTaskViewFilter {
    type: "numberRange";
    from: number | undefined;
    to: number | undefined;
    includeFrom: boolean | undefined;
    includeTo: boolean | undefined;
}

export type NumberTaskViewFilter = NumberRangeTaskViewFilter;

export type TaskViewFilter<T> =
    | OrTaskViewFilter<T>
    | AndTaskViewFilter<T>
    | (T extends string
          ? StringTaskViewFilter
          : T extends boolean
          ? BooleanTaskViewFilter
          : T extends number
          ? NumberTaskViewFilter
          : never);

export interface TasksViewFilterCandidate {
    name: string;
    value: any;
    [key: string]: any;
}

export interface TaskViewColumn {
    field: TaskViewColumnNames;
    title?: string;
    allowSorting?: boolean;
    allowFiltering?: boolean;
    editable?: boolean;
    filterCandidates?:
        | TasksViewFilterCandidate[]
        | (() => TasksViewFilterCandidate[] | Promise<TasksViewFilterCandidate[]>);
    hidden?: boolean;
}

export interface TasksViewStateSortInfo {
    column: TaskViewColumnNames;
    direction: "asc" | "desc";
}

export type FilterOf<T extends object> = {
    [P in keyof T]?: TaskViewFilter<T[P]>;
};

export interface TasksViewState {
    filters?: FilterOf<ContextTask>;
    sortInfo?: TasksViewStateSortInfo;
    currentPage: number;
    pageSize: number;
}

export interface TasksViewData {
    data: ContextTask[];
    page: number;
    totalCount: number;
}

interface ResponseEditionData {
    contextType: ContextType;
    task: ContextTask;
    responseType: ResponseType;
}

export interface InternalTasksViewProps {
    columns: TaskViewColumn[];
    pageSizeOptions: number[];
    initialState: TasksViewState;
    allowResponseCreation: boolean;
    allowTaskReassignment: boolean;
    allowFocusing: boolean;
    onStateChanged?: (state: TasksViewState) => void;
}

const InternalTasksView: FC<InternalTasksViewProps> = props => {
    const {
        onStateChanged,
        allowResponseCreation: userDefAllowResponseCreation,
        allowTaskReassignment: userDefAllowTaskReassignment,
        ...rest
    } = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {addResponse, refreshTasks, currentState, canCreateReponses, canReassignUsers} = useTasksViewService();
    const [responseEditionData, setResponseEditionData] = useState<ResponseEditionData>();

    useEffect(() => {
        onStateChanged?.(currentState);
    }, [currentState, onStateChanged]);

    const handleResponseRequested = useCallback(
        (contextType: ContextType, task: ContextTask, responseType: ResponseType) => {
            setResponseEditionData({contextType, task, responseType});
        },
        []
    );

    const handleAddResponse = useCallback(
        async (payload: any, startedOn: Date | undefined) => {
            if (!responseEditionData) {
                return;
            }
            await addResponse(responseEditionData.task, responseEditionData.responseType, payload, startedOn);
        },
        [addResponse, responseEditionData]
    );

    const handleOnClose = useCallback(
        async (refreshRequested: boolean) => {
            setResponseEditionData(undefined);
            if (refreshRequested) {
                await refreshTasks();
            }
        },
        [refreshTasks]
    );

    const allowResponseCreation = useMemo(() => {
        return userDefAllowResponseCreation && canCreateReponses();
    }, [canCreateReponses, userDefAllowResponseCreation]);

    const allowTaskReassignment = useMemo(() => {
        return userDefAllowTaskReassignment && canReassignUsers();
    }, [canReassignUsers, userDefAllowTaskReassignment]);

    return (
        <>
            <div className={classes.root}>
                <Tooltip title={t("RefreshData") as string}>
                    <IconButton className={classes.refreshButton} onClick={refreshTasks}>
                        <RefreshIcon />
                    </IconButton>
                </Tooltip>
            </div>
            <TasksViewFilter {...rest} />
            <div style={{height: 10}} />
            <TasksViewTable
                allowResponseCreation={allowResponseCreation}
                allowTaskReassignment={allowTaskReassignment}
                onResponseRequested={handleResponseRequested}
                {...rest}
            />
            {!!responseEditionData && (
                <TasksViewResponseEditorModal
                    task={responseEditionData.task}
                    contextType={responseEditionData.contextType}
                    responseType={responseEditionData.responseType}
                    open={!!responseEditionData}
                    onAddResponse={handleAddResponse}
                    onClose={handleOnClose}
                />
            )}
        </>
    );
};

export interface TasksViewProps {
    columns: TaskViewColumn[];
    pageSizeOptions?: number[];
    initialState?: TasksViewState;
    allowResponseCreation?: boolean;
    allowTaskReassignment?: boolean;
    allowFocusing?: boolean;
    enforcedFilter?: FilterOf<ContextTask>;
    onStateChanged?: (state: TasksViewState) => void;
}

export const TasksView: FC<TasksViewProps> = props => {
    const {
        initialState,
        pageSizeOptions: userPageSizeOptions,
        allowResponseCreation = false,
        allowTaskReassignment = false,
        allowFocusing = false,
        enforcedFilter,
        ...rest
    } = props;

    const pageSizeOptions = useMemo(() => {
        let opts = userPageSizeOptions;
        if (!opts || opts.length === 0) {
            opts = [5, 10, 20, 100];
        }
        return opts;
    }, [userPageSizeOptions]);

    const resolvedInitialState = useMemo(() => {
        const state: TasksViewState = {
            currentPage: 0,
            pageSize: pageSizeOptions[0],
            ...initialState,
        };
        return state;
    }, [initialState, pageSizeOptions]);

    return (
        <TasksViewServiceProvider initialState={resolvedInitialState} enforcedFilter={enforcedFilter}>
            <InternalTasksView
                pageSizeOptions={pageSizeOptions}
                initialState={resolvedInitialState}
                allowResponseCreation={allowResponseCreation}
                allowTaskReassignment={allowTaskReassignment}
                allowFocusing={allowFocusing}
                {...rest}
            />
        </TasksViewServiceProvider>
    );
};
