import React, {FC} from "react";

import {AuthenticatedRouteProps, AuthenticatedRoute} from "./authenticated-route";
import {Unauthorized} from "./unauthorized";
import {useCurrentUnitInfo} from "../api/nimflow-business-directory-provider";
import {useAuthorization} from "./authorization-context";

export interface PermissionRestrictedRouteProps {
    element?: AuthenticatedRouteProps["element"];
    permissions?: string | Array<string | string[]>;
    unauthorized?: React.ReactElement;
}

const PermissionGuardedComponent: FC<PermissionRestrictedRouteProps> = props => {
    const {permissions, unauthorized, element} = props;
    const {unit} = useCurrentUnitInfo();
    const {checkPermissions, permissions: grantedPermissions} = useAuthorization();

    if (!unit || !grantedPermissions) {
        return <></>;
    }
    if (permissions) {
        const permissionList = Array.isArray(permissions) ? permissions : [permissions];
        if (permissionList.length > 0 && checkPermissions(permissionList).length > 0) {
            return unauthorized ?? <Unauthorized />;
        }
    }
    return element ?? null;
};

export interface PermissionRestrictedRouteProps extends AuthenticatedRouteProps {
    permissions?: string | Array<string | string[]>;
    unauthorized?: React.ReactElement;
}

export const PermissionRestrictedRoute: FC<PermissionRestrictedRouteProps> = props => {
    const {permissions, unauthorized, element, ...rest} = props;
    return (
        <AuthenticatedRoute
            element={
                <PermissionGuardedComponent element={element} permissions={permissions} unauthorized={unauthorized} />
            }
            {...rest}
        />
    );
};
