import React, {FC, useCallback, useState} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";

import {ContextTask, ResponseType} from "../../api/nimflow-api";
import {TasksViewGroupServiceProvider, TaskGroupService, TasksViewGroupServiceConsumer} from "./tasks-view-group-service";
import {TasksViewResponseButton} from "./tasks-view-response-button";
import {TasksViewResponseEditorModal} from "./tasks-view-response-editor-modal";
import {useTasksViewTaskService} from "./tasks-view-task-service";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        responses: {
            textAlign: "center",
            marginBottom: 10,
            marginLeft: 10,
        },
        response: {
            marginRight: 5,
        },
    })
);

interface InternalTaskGroupResponsesProps {
    taskGroupService: TaskGroupService;
    onResponsesAdded?: () => void;
}

const InternalTaskGroupResponses: FC<InternalTaskGroupResponsesProps> = props => {
    const {taskGroupService, onResponsesAdded} = props;
    const classes = useStyles();
    const {representativeTask, tasks, responses, contextType, addResponse} = taskGroupService;
    const {refreshTasks} = useTasksViewTaskService();
    const [responseType, setResponseType] = useState<ResponseType>();

    const handleResponseClick = useCallback((responseType: ResponseType) => {
        setResponseType(responseType);
    }, []);

    const handleOnClose = useCallback(
        async (refreshRequested: boolean) => {
            setResponseType(undefined);
            if (refreshRequested) {
                await refreshTasks();
            }
        },
        [refreshTasks]
    );

    const handleAddResponse = useCallback(
        async (payload: any) => {
            await addResponse(responseType!, payload);
            onResponsesAdded?.();
        },
        [addResponse, onResponsesAdded, responseType]
    );

    if (!responses || !contextType) {
        return null;
    }

    return (
        <div className={classes.responses}>
            {responses?.map((response, i) => (
                <TasksViewResponseButton
                    key={i}
                    response={response}
                    onClick={() => handleResponseClick(response)}
                    className={classes.response}
                    variant="outlined"
                />
            ))}
            {!!responseType && (
                <TasksViewResponseEditorModal
                    task={representativeTask}
                    contextType={contextType}
                    responseType={responseType}
                    open={!!responseType}
                    taskGroupSize={tasks.length}
                    onAddResponse={handleAddResponse}
                    onClose={handleOnClose}
                />
            )}
        </div>
    );
};

export interface TasksViewGroupResponsesProps {
    data: ContextTask[];
    onResponsesAdded?: () => void;
}

export const TasksViewGroupResponses: FC<TasksViewGroupResponsesProps> = props => {
    const {data, onResponsesAdded} = props;

    return (
        <TasksViewGroupServiceProvider tasks={data}>
            <TasksViewGroupServiceConsumer>
                {service => (
                    <InternalTaskGroupResponses taskGroupService={service} onResponsesAdded={onResponsesAdded} />
                )}
            </TasksViewGroupServiceConsumer>
        </TasksViewGroupServiceProvider>
    );
};
