import React, {FC, useCallback} from "react";

import {useDashboardContext} from "./dashboard-context";
import {DashboardParameterViewer} from "./dashboard-parameter-viewer";
import {DashboardParameterState, DashboardParameter} from "../../api/nimflow-dashboard-api";

export const DashboardParameters: FC<{}> = props => {
    const {dashboard, getParameterState, setParameterState} = useDashboardContext();

    const handleParameterChange = useCallback(
        (value: DashboardParameterState, parameter: DashboardParameter) => {
            setParameterState(parameter.id, value);
        },
        [setParameterState]
    );

    return (
        <div>
            {dashboard.parameters?.map(s => (
                <DashboardParameterViewer
                    key={s.id}
                    parameter={s}
                    value={getParameterState(s.id)}
                    onChange={handleParameterChange}
                />
            ))}
        </div>
    );
};
