import {FC, useCallback, useMemo, useState} from "react";
import {useNavigate} from "react-router";
import {atom, useRecoilState} from "recoil";
import axios, {AxiosInstance, AxiosRequestConfig} from "axios";

import {AuthenticationService, AuthenticationServiceContext, UserData} from "./authentication-service";
import {BasicAuthenticationLoginModal} from "./basic-authentication-login-modal";
import {isPlainObject} from "../../utils/types";
import {getBasePath} from "../../utils/get-base-url";

interface AuthenticationData {
    user: UserData;
    credentials: string;
    tokenData: any;
}

const basicAuthenticationDataState = atom<AuthenticationData | undefined>({
    key: "basicAuthenticationData",
    default: undefined,
});

export const BasicAuthenticationServiceProvider: FC<{}> = props => {
    const {children} = props;
    const navigate = useNavigate();
    const [logingIn, setLogingIn] = useState(false);
    const [basicAuthenticationData, setBasicAuthenticationData] = useRecoilState(basicAuthenticationDataState);

    const user = useMemo(() => {
        if (!basicAuthenticationData) {
            return undefined;
        }
        return basicAuthenticationData.user;
    }, [basicAuthenticationData]);

    const login = useCallback(async () => {
        setLogingIn(true);
    }, []);

    const logout = useCallback(async () => {
        setBasicAuthenticationData(undefined);
        navigate(getBasePath());
    }, [navigate, setBasicAuthenticationData]);

    const startResetPasswordFlow = useCallback((email: string) => {
        throw new Error("Functionality not supported");
    }, []);

    const getAuthorizedAxios = useCallback(
        (options?: AxiosRequestConfig): AxiosInstance => {
            const result = axios.create(options);
            result.interceptors.request.use(
                async config => {
                    if (!basicAuthenticationData) {
                        await login();
                    }
                    if (basicAuthenticationData) {
                        config.headers["Authorization"] = "Basic " + basicAuthenticationData.credentials;
                    }
                    return config;
                },
                error => {
                    Promise.reject(error);
                }
            );
            return result;
        },
        [basicAuthenticationData, login]
    );

    const handleLogin = useCallback(
        (userName: string, password: string, token: any) => {
            const user: UserData = {
                name: userName,
            };
            //Try read standard IDToken claims
            if (isPlainObject(token)) {
                for (const key of Object.keys(token)) {
                    switch (key.toLowerCase()) {
                        case "name":
                            user.name = token[key];
                            break;
                        case "picture":
                            user.picture = token[key];
                            break;
                        case "email":
                            user.email = token[key];
                            break;
                    }
                }
            }
            setBasicAuthenticationData({
                user,
                credentials: btoa(`${userName}:${password}`),
                tokenData: token,
            });
        },
        [setBasicAuthenticationData]
    );

    const authenticationService: AuthenticationService = {
        isAuthenticated: !!basicAuthenticationData,
        supportsPasswordReset: false,
        login,
        logout,
        startResetPasswordFlow,
        authenticatedUser: user,
        getAuthorizedAxios,
    };

    return (
        <AuthenticationServiceContext.Provider value={authenticationService}>
            {children}
            {logingIn && (
                <BasicAuthenticationLoginModal
                    open={logingIn}
                    onChange={handleLogin}
                    onClose={() => setLogingIn(false)}
                />
            )}
        </AuthenticationServiceContext.Provider>
    );
};
