import {createContext, FC, useCallback, useContext, useEffect, useMemo, useState} from "react";
import compareVersions from "compare-versions";

import {Info as HubInfo} from "../api/nimflow-api";
import {useNimflow} from "../api/nimflow-api-provider";
import {Info as BdInfo, Unit} from "../api/nimflow-business-directory-api";
import {useCurrentUnitInfo, useNimflowBusinessDirectory} from "../api/nimflow-business-directory-provider";

export enum SystemFeature {
    Security = "security",
}

const featureVersionSupport: Record<string, string> = {
    [SystemFeature.Security]: "3.6.0",
};

export interface SystemInfoService {
    hubBaseUrl: string;
    hubName: string;
    hubVersion: string;
    businessDirectoryBaseUrl: string;
    businessDirectoryName: string;
    businessDirectoryVersion: string;
    currentUnit?: Unit;
    supportedFeatures: SystemFeature[];
    isFeatureSupported(feature: SystemFeature): boolean;
}

const stub = (): never => {
    throw new Error("You forgot to wrap your component in <SystemInfoContextProvider>.");
};

const initialSystemInfoContext: SystemInfoService = {
    hubBaseUrl: "",
    hubName: "",
    hubVersion: "",
    businessDirectoryBaseUrl: "",
    businessDirectoryName: "",
    businessDirectoryVersion: "",
    supportedFeatures: [],
    isFeatureSupported: stub,
};

const SystemInfoContext = createContext<SystemInfoService>(initialSystemInfoContext);

export const SystemInfoContextProvider: FC<{}> = props => {
    const {children} = props;
    const {baseUrl: businessDirectoryBaseUrl, info_Get: getBdInfo} = useNimflowBusinessDirectory();
    const {unit: currentUnit, hubBaseUrl} = useCurrentUnitInfo();
    const {info_Get: getHubInfo, isHubSelected} = useNimflow();
    const [bdInfo, setBdInfo] = useState<BdInfo>();
    const [hubInfo, setHubInfo] = useState<HubInfo>();

    useEffect(() => {
        if (isHubSelected) {
            try {
                getBdInfo().then(setBdInfo);
            } catch {}
        }
    }, [getBdInfo, isHubSelected]);
    useEffect(() => {
        if (isHubSelected) {
            try {
                getHubInfo().then(setHubInfo);
            } catch{}
        }
    }, [getHubInfo, isHubSelected]);

    const supportedFeatures = useMemo(() => {
        const result: SystemFeature[] = [];
        const version = hubInfo?.version;
        if (version) {
            const featureNames = Object.keys(SystemFeature).filter(s =>
                Number.isNaN(+s)
            ) as (keyof typeof SystemFeature)[];
            for (const fn of featureNames) {
                const feature = SystemFeature[fn];
                const minVer = featureVersionSupport[feature];
                if (minVer && compareVersions.compare(version, minVer, ">=")) {
                    result.push(feature);
                }
            }
        }
        return result;
    }, [hubInfo?.version]);

    const isFeatureSupported = useCallback(
        (feature: SystemFeature) => {
            return supportedFeatures.includes(feature);
        },
        [supportedFeatures]
    );

    const systemInfoService = useMemo(() => {
        const result: SystemInfoService = {
            hubBaseUrl: hubBaseUrl ?? "",
            hubName: hubInfo?.name ?? "",
            hubVersion: hubInfo?.version ?? "",
            businessDirectoryBaseUrl,
            businessDirectoryName: bdInfo?.name ?? "",
            businessDirectoryVersion: bdInfo?.version ?? "",
            currentUnit,
            supportedFeatures,
            isFeatureSupported,
        };
        return result;
    }, [bdInfo, businessDirectoryBaseUrl, currentUnit, hubBaseUrl, hubInfo, isFeatureSupported, supportedFeatures]);

    return <SystemInfoContext.Provider value={systemInfoService}>{children}</SystemInfoContext.Provider>;
};

export const useSystemInfo = () => useContext(SystemInfoContext);
