import {FC} from "react";
import clsx from "clsx";
import Icon, {IconProps} from "@material-ui/core/Icon";
import {IconFamily} from "../api/nimflow-api-extensions";

export type MaterialFontIconProps = {
    family?: IconFamily;
} & IconProps;

const getClassName = (family?: IconFamily) => {
    switch (family) {
        case "Outlined":
            return "material-icons-outlined";
        case "Rounded":
            return "material-icons-round";
        case "Sharp":
            return "material-icons-sharp";
        case "Two tone":
            return "material-icons-two-tone";
        default:
            return undefined;
    }
};

export const MaterialFontIcon: FC<MaterialFontIconProps> = props => {
    const {family, className, ...rest} = props;

    return <Icon className={clsx(getClassName(family), className)} {...rest} />;
};