import {VisualizationSpec, SignalListeners} from "react-vega";
import i18next from "i18next";
import {MuiVegaLite} from "@material-vega/material-ui";
import {VariableParameter} from "vega-lite/build/src/parameter";
import {TopLevelSelectionParameter} from "vega-lite/build/src/selection";
import {useEffect} from "react";
import {equals} from "ramda";

import {EventStream, Spec as VgSpec} from "vega";
import {TasksViewFilterEntryEditorProps} from "./tasks-view-filter";
import {WaitSpinner} from "../wait-spinner";
import {SelectionTaskViewFilter, TaskViewColumnNames, TasksViewFilterCandidate} from "./tasks-view";

