const nullValueText = i18next.t("NullValueText");

const specBase: VisualizationSpec = {
    $schema: "https://vega.github.io/schema/vega-lite/v5.json",
    data: {name: "data"}, // note: vega-lite data attribute is a plain object instead of an array
    width: {step: barWidth},
    height: 100,
    mark: {
        type: "bar",
        //fill: "#4C78A8",
        stroke: "#0099cc",
        cursor: "pointer",
    },
    params: [
        {
            name: "select",
            select: {type: "point", resolve: "global"},
        },
        {
            name: "highlight",
            select: {type: "point", on: "mouseover"},
        },
    ],
    encoding: {
        x: {
            field: "name",
            type: "ordinal",
            axis: {
                title: null,
                labelAngle: -45,
                labelFontSize: 14,
                labelLimit: 100,
            },
        },
        y: {field: "value", type: "quantitative", axis: {title: null}},
        tooltip: {field: "value", type: "quantitative"},
        fillOpacity: {
            condition: {param: "select", value: 1},
            value: 0.4,
        },
        strokeWidth: {
            condition: [
                // {
                //     test: {
                //         and: [{param: "select"}, 'length(data("select_store"))'],
                //     },
                //     empty: false,
                //     value: 2,
                // },
                {
                    param: "highlight",
                    empty: false,
                    value: 1,
                },
            ],
            value: 0,
        },
    },
    config: {
        scale: {
            bandPaddingInner: 0.2,
        },
    },
};

const labelName = "mark_x_axis_labels";

const patchVega = (spec: VgSpec) => {
    let signals = spec.signals;
    if (!signals) {
        spec.signals = signals = [];
    }
    const hltupleSignal = signals.find(s => s.name === "highlight_tuple");
    if (hltupleSignal) {
        let handlers = hltupleSignal.on;
        if (!handlers) {
            hltupleSignal.on = handlers = [];
        }
        const eventsList = handlers.map(s => s.events) ?? [];
        for (const events of eventsList) {
            if (Array.isArray(events)) {
                const event = events.find(s => "type" in s && s.type === "mouseover") as EventStream;
                if (event) {
                    event.filter = `!item().mark || item().mark.name !== '${labelName}'`;
                }
            }
        }
        handlers.push({
            events: `@${labelName}:mouseover`,
            update: '{unit: "", fields: select_tuple_fields, values: [datum.value]}',
            //force: true,
        });
    }

    const seltupleSignal = signals.find(s => s.name === "select_tuple");
    if (seltupleSignal) {
        let handlers = seltupleSignal.on;
        if (!handlers) {
            seltupleSignal.on = handlers = [];
        }
        const eventsList = handlers.map(s => s.events) ?? [];
        for (const events of eventsList) {
            if (Array.isArray(events)) {
                const event = events.find(s => "type" in s && s.type === "click") as EventStream;
                if (event) {
                    event.filter = `!item().mark || item().mark.name !== '${labelName}'`;
                }
            }
        }
        handlers.push({
            events: `@${labelName}:click`,
            //update: "debug(data('data_0'), data('data_0')[indexof(pluck(data('data_0'), 'name'), datum.value)], datum.value)",
            update: '{unit: "", fields: select_tuple_fields, values: [datum.value]}',
            force: true,
        });
    }

    const dataSourceName = spec.marks?.[0].from?.data ?? "data_0";

    signals.push({
        name: "select_extended",
        on: [
            {
                events: {
                    signal: "select",
                },
                update: `{select: select, data: data('data_0')}`,
            },
        ],
    });

    let xaxis = spec.axes?.find(s => s.scale === "x");
    if (xaxis) {
        xaxis.encode = Object.assign(xaxis.encode ?? {}, {
            labels: {
                name: labelName,
                interactive: true,
                update: {
                    tooltip: {
                        signal: `datum.value + ": " + format(data('${dataSourceName}')[indexof(pluck(data('${dataSourceName}'), 'name'), datum.value)].value, "")`,
                    },
                },
            },
        });
    }
    return spec;
};

const useCandidates = (field: TaskViewColumnNames, getCandidates: () => Promise<TasksViewFilterCandidate[]>) => {
    const lastCandidatesRef = useRef<{data: TasksViewFilterCandidate[]}>();
    const {data, isValidating, mutate} = useSWR(
        `/ColumnCandidates/${field}`,
        async () => {
            const result = await getCandidates();
            result.forEach(s => {
                if (s.name === null) {
                    s.name = nullValueText;
                }
            });
            if (!equals(lastCandidatesRef.current?.data, result)) {
                lastCandidatesRef.current = {data: result};
            }
            return lastCandidatesRef.current;
        },
        {
            revalidateOnFocus: false,
        }
    );

    //Force a revalidation when candidate retrieval fn changes.
    const validationRef = useRef({isValidating, revalidate: mutate});
    validationRef.current = {isValidating, revalidate: mutate};
    useEffect(() => {
        if (!validationRef.current.isValidating) {
            validationRef.current.revalidate();
        }
    }, [getCandidates]);

    return {
        data,
        isValidating,
    };
};

export const SelectionFilter: FC<TasksViewFilterEntryEditorProps<SelectionTaskViewFilter>> = props => {
    const {column, value, onChange, getCandidates} = props;
    const theme = useTheme();
    const {data, isValidating} = useCandidates(column.field, getCandidates);

    const spec = useMemo(() => {
        if (!data || data.data.length === 0 || value?.type !== "selection") {
            return specBase;
        }
        const result: VisualizationSpec = {
            ...specBase,
            params: specBase.params?.map(s => {
                if (s.name !== "select") {
                    return s;
                }
                return {
                    ...s,
                    value: {
                        x: value.value ?? nullValueText,
                    },
                } as VariableParameter | TopLevelSelectionParameter;
            }),
        };
        return result;
    }, [data, value]);

    const signalListeners: SignalListeners = useMemo(() => {
        return {
            /*select: (_name: string, value: any) => {
                const newValue = extractValueFromSingleSelection(value, "name", data!.data);
                onChange?.(
                    newValue !== undefined
                        ? {type: "selection", value: newValue === nullValueText ? null : newValue}
                        : undefined
                );
            },*/
            select_extended: (_name: string, value: any) => {
                const sel = value.select;
                let newValue: string | undefined = undefined;
                if ("name" in sel && Array.isArray(sel.name) && typeof sel.name[0] === "string") {
                    newValue = sel.name[0];
                } else if ("_vgsid_" in sel && Array.isArray(sel._vgsid_) && sel._vgsid_.length > 0) {
                    const id = sel._vgsid_[0];
                    if (typeof id === "string") {
                        newValue = id;
                    } else {
                        const data = value.data;
                        if (Array.isArray(data)) {
                            newValue = data.find(s => s._vgsid_ === id)?.name;
                        }
                    }
                }
                onChange?.(
                    newValue !== undefined
                        ? {type: "selection", value: newValue === nullValueText ? null : newValue}
                        : undefined
                );
            },
        };
    }, [onChange]);

    if ((!data && isValidating) || !spec) {
        return <WaitSpinner />;
    }

    const vegaTheme = theme.palette.type === "dark" ? "dark" : undefined;

    return (
        <MuiVegaLite
            variant="verticalBarChart"
            spec={spec}
            data={data}
            theme={vegaTheme}
            signalListeners={signalListeners}
            actions={false}
            patch={patchVega}
            //logLevel={4}
        />
    );
};
