import React, {FC, useCallback} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import Collapse from "@material-ui/core/Collapse";
import IconExpandLess from "@material-ui/icons/ExpandLess";
import IconExpandMore from "@material-ui/icons/ExpandMore";
import {useNavigate} from "react-router-dom";

const useStyles = makeStyles(theme =>
    createStyles({
        menuItem: {
        },
        menuItemIcon: {
        },
    })
);

export interface SideMenuItemProps {
    name: string;
    link?: string;
    icon?: React.ReactNode;
    items?: SideMenuItemProps[];
}

export const SideMenuItem: FC<SideMenuItemProps> = props => {
    const {name, link, icon, items = []} = props;
    const classes = useStyles();
    const navigate = useNavigate();
    const isExpandable = items && items.length > 0;
    const [open, setOpen] = React.useState(false);

    const handleClick = useCallback(() => {
        if (isExpandable) {
            setOpen(s => !s);
        } else if (typeof link === "string" && link) {
            navigate(link);
        }
    }, [isExpandable, link, navigate]);

    const MenuItemRoot = (
        <ListItem button className={classes.menuItem} onClick={handleClick}>
            {!!icon && <ListItemIcon className={classes.menuItemIcon}>{icon}</ListItemIcon>}
            <ListItemText primary={name} inset={!icon} />
            {isExpandable && !open && <IconExpandMore />}
            {isExpandable && open && <IconExpandLess />}
        </ListItem>
    );

    const MenuItemChildren = isExpandable ? (
        <Collapse in={open} timeout="auto" unmountOnExit>
            <Divider />
            <List component="div" disablePadding>
                {items.map((item, index) => (
                    <SideMenuItem {...item} key={index} />
                ))}
            </List>
        </Collapse>
    ) : null;

    return (
        <>
            {MenuItemRoot}
            {MenuItemChildren}
        </>
    );
};
