import React, {FC, useCallback, useState, useEffect} from "react";
import TextField, {TextFieldProps} from "@material-ui/core/TextField";
import {useFormikContext, useField} from "formik";
import {isPresent} from "ts-is-present";

export type FormikTextFieldProps = {
    name: string;
    controlValueAdapter?: (value: string) => any;
    debounce?: number;
} & Omit<TextFieldProps, "name" | "value" | "defaultValue" | "checked">;

export const FormikTextField: FC<FormikTextFieldProps> = props => {
    const {name, debounce, onChange, onBlur, helperText, error, disabled, controlValueAdapter, ...rest} = props;
    const {isSubmitting} = useFormikContext();
    const [field, meta, helpers] = useField({name});
    const fieldError = meta.error;
    const showError = meta.touched && !!fieldError;
    const [text, setText] = useState(field.value);
    const [t, setT] = useState<ReturnType<typeof setTimeout>>();

    useEffect(() => {
        setText(field.value);
    }, [field.value]);

    const applyChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            const val = ev.target.value;
            helpers.setValue(controlValueAdapter ? controlValueAdapter(val) : val);
            onChange?.(ev);
        },
        [controlValueAdapter, helpers, onChange]
    );

    const handleChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            setText((s: any) => {
                const newValue = ev.target.value;
                if (!newValue) {
                    return s;
                }
                return newValue;
            });
            if (isPresent(debounce)) {
                ev.persist();
                if (t) {
                    clearTimeout(t);
                }
                setT(setTimeout(() => applyChange(ev), debounce));
            } else {
                applyChange(ev);
            }
        },
        [applyChange, debounce, t]
    );

    const handleBlur = useCallback(
        (ev: React.FocusEvent<HTMLInputElement>) => {
            field.onBlur(ev);
            onBlur?.(ev);
        },
        [field, onBlur]
    );

    return (
        <>
            <TextField
                {...field}
                value={text ?? ""}
                onChange={handleChange}
                onBlur={handleBlur}
                error={showError}
                helperText={showError ? fieldError : helperText}
                disabled={disabled ?? isSubmitting}
                {...rest}
            />
            {/* {JSON.stringify({showError, fieldError, touched: meta.touched})} */}
        </>
    );
};
