import {FC, useCallback, useState, useEffect} from "react";
import {useTranslation} from "react-i18next";
import {useSetRecoilState} from "recoil";
import { useCurrentUnitInfo } from "../api/nimflow-business-directory-provider";

import {useNimflowLocalization} from "../api/nimflow-localization-api";
import {currenti18nLanguage} from "../libs/i18next";

export const userDefinedLocalizationsNs = "ud";

export const LocalizationProvider: FC<{}> = () => {
    const {i18n} = useTranslation();
    const [lastLanguage, setLastLanguage] = useState<string>();
    const setCurrenti18nLanguage = useSetRecoilState(currenti18nLanguage);
    const {localizations_FindByLanguage} = useNimflowLocalization();
    const {unit} = useCurrentUnitInfo();

    const loadLocalization = useCallback(
        async (language: string) => {
            if (lastLanguage === language || !unit) {
                return;
            }
            if (lastLanguage) {
                i18n.removeResourceBundle(lastLanguage, userDefinedLocalizationsNs);
            }
            try {
                const locs = await localizations_FindByLanguage(language);
                if (locs.length > 0) {
                    i18n.addResourceBundle(language, userDefinedLocalizationsNs, locs[0].translations);
                    setLastLanguage(language);
                } else {
                    setLastLanguage(undefined);
                }
            }
            catch { /**/ }
        },
        [i18n, lastLanguage, localizations_FindByLanguage, unit]
    );

    useEffect(() => {
        setCurrenti18nLanguage(i18n.language);
        loadLocalization(i18n.language);
    }, [i18n.language, loadLocalization, setCurrenti18nLanguage]);

    return null;
};
