import React, {FC, useCallback, useState} from "react";
import {alpha, makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Popover from "@material-ui/core/Popover";
import Paper from "@material-ui/core/Paper";
import {usePopupState, bindTrigger, bindPopover} from "material-ui-popup-state/hooks";

import {FreeTextDashboardParameter} from "../../api/nimflow-dashboard-api";
import {DashboardParameterPill} from "./dashboard-parameter-pill";
import {ParameterEditorProps} from "./dashboard-parameter-viewer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        inputBase: {
            //padding: 10,
            width: "100%",
            borderBottom: "1px solid #dfe2e5",
            borderWidth: 0,
            "& input": {
                borderRadius: 4,
                padding: 8,
                transition: theme.transitions.create(["border-color", "box-shadow"]),
                border: "1px solid #ced4da",
                fontSize: 14,
                "&:focus": {
                    boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
                    borderColor: theme.palette.primary.main,
                },
            },
        },
        paper: {
            padding: "8px 10px",
            boxShadow: "none",
            margin: 0,
            color: "#586069",
            fontSize: 13,
        },
    })
);

export interface FreeTextParameterEditorProps extends ParameterEditorProps<FreeTextDashboardParameter> {}

export const FreeTextParameterEditor: FC<FreeTextParameterEditorProps> = props => {
    const {parameter, value: initialValue, onChange} = props;
    const classes = useStyles();
    const [value, setValue] = useState(() => {
        const val = initialValue.values[0]?.value;
        return val ?? (parameter.dataType === "string" ? "" : 0);
    });
    const [pendingValue, setPendingValue] = useState(value);
    const popupState = usePopupState({
        variant: "popover",
        popupId: `paramPopup_${parameter.id}`,
    });

    // TODO: validate value
    const handleChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            let value: number | string = ev.target.value;
            if (parameter.dataType === "number") {
                value = parseFloat(value);
            }
            setPendingValue(value);
        },
        [parameter]
    );

    const bindings = bindPopover(popupState);
    const oldOnClose = bindings.onClose;
    bindings.onClose = () => {
        setValue(pendingValue);
        onChange?.({selectAll: false, values: [{value: pendingValue}]}, parameter);
        oldOnClose();
    };

    return (
        <>
            <DashboardParameterPill
                parameter={parameter}
                valueDescription={value.toString()}
                {...bindTrigger(popupState)}
            />
            <Popover
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                }}
                {...bindings}
            >
                <Paper className={classes.paper}>
                    <TextField
                        type={parameter.dataType === "number" ? "number" : "text"}
                        value={pendingValue}
                        onChange={handleChange}
                        variant="outlined"
                        size="small"
                        autoFocus
                        InputProps={{className: classes.inputBase}}
                    />
                </Paper>
            </Popover>
        </>
    );
};
