export interface FileData {
    index: number;
    pageCount: number;
    processingError?: string;
}

export interface RecognizedImageBatch {
    fileUrls: string[];
    documents: RecognizedDocument[];
}

export interface PageImageLocation {
    fileIndex: number;
    filePageNumber: number;
}

export const recognizedDocumentActions = ["saveAsDraft", "send", "dontSend"] as const;
Object.freeze(recognizedDocumentActions);

export type RecognizedDocumentAction = typeof recognizedDocumentActions[number];

export interface RecognizedDocument {
    schemaId: string;
    pages: PageImageLocation[];
    data: any;
    action: RecognizedDocumentAction;
}