import { AxiosError } from "axios";

export const isAxiosError = (obj: any | undefined): obj is AxiosError => {
    return obj && obj.isAxiosError === true;
};

export const isAxiosServerError = (obj: any): obj is AxiosError => {
    return isAxiosError(obj) && !!obj.response && !!obj.request;
}

export type AxiosNetworkError = Omit<AxiosError, "response" | "request"> & {
    request: any;
};

export const isAxiosNetworkError = (obj: any): obj is AxiosNetworkError => {
    return isAxiosError(obj) && !obj.response && !!obj.request;
}