import {isPresent} from "ts-is-present";
import {FormikProps} from "formik";

export const getTouchedObj = (errors: any) => {
    const touched: any = {};
    if (errors) {
        for (const key of Object.keys(errors)) {
            if (Array.isArray(errors[key])) {
                errors[key].forEach((val: any, index: any) => {
                    let array = touched[key];
                    if (!isPresent(array)) {
                        touched[key] = array = [];
                    }
                    array[index] = getTouchedObj(val);
                });
            } else if (typeof errors[key] === "object" && errors[key] !== null) {
                touched[key] = getTouchedObj(errors[key]);
            } else {
                touched[key] = true;
            }
        }
    }
    return touched;
};

export const validateForm = <T extends any>(form: FormikProps<T>) => {
    return form.validateForm().then(errors => {
        if (Object.keys(errors).length > 0) {
            form.setTouched(getTouchedObj(errors));
        }
        return errors;
    });
};

export const validateAndSubmit = <T extends any>(form: FormikProps<T>) => {
    return form.validateForm().then(errors => {
        if (Object.keys(errors).length > 0) {
            form.setTouched(getTouchedObj(errors));
        } else {
            form.handleSubmit();
        }
    });
};
