import {FC, useMemo, useCallback, useState} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import RefreshIcon from "@material-ui/icons/Refresh";
import {Responsive} from "react-grid-layout";
import {isPresent} from "ts-is-present";
import {useTranslation} from "react-i18next";
import {useResizeDetector} from "react-resize-detector";
import {useInterval} from "react-use";

import {
    AutoRefreshInterval,
    autoRefreshIntervals,
    autoRefreshIntervalToMillisecons,
    DashboardAutoRefresh,
    DashboardDefinition,
    DashboardResponsiveLayoutSet,
} from "../../api/nimflow-dashboard-api";
import {DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetViewer} from "./dashboard-widget-viewer";
import {DashboardContextProvider, useDashboardContext} from "./dashboard-context";
import {DashboardBreadcrumbs} from "./dashboard-breadcrumbs";
import {DashboardParameters} from "./dashboard-parameters";

const useStyles = makeStyles(theme =>
    createStyles({
        title: {
            display: "flex",
            overflow: "hidden",
            flexDirection: "row",
        },
        layout: {
            backgroundColor: theme.palette.background.default,
        },
        spaceFiller: {
            flex: "1 1",
        },
    })
);

export interface DashboardViewerProps {
    dashboard: DashboardDefinition;
    widgets: DashboardWidgetDefinition[];
}

const getGridLayoutProps = (layoutSet: DashboardResponsiveLayoutSet) => {
    const los = layoutSet?.layouts;
    if (!los) {
        return {};
    }
    const layouts: ReactGridLayout.Layouts = {};
    const breakpoints: Record<string, number> = {};
    const cols: Record<string, number> = {};
    const margins: Record<string, [number, number]> = {};
    for (const lo of los) {
        if (lo.widgetsLayouts) {
            layouts[lo.name] = lo.widgetsLayouts;
        }
        breakpoints[lo.name] = lo.breakpoint;
        cols[lo.name] = lo.cols;
        if (lo.margin) {
            margins[lo.name] = lo.margin;
        }
    }
    const margin = isPresent(layoutSet.margin)
        ? layoutSet.margin
        : Object.keys(margins).length > 0
        ? margins
        : undefined;
    return {layouts, breakpoints, cols, margin};
};

const DashboardRefreshButton: FC<{}> = props => {
    const {t} = useTranslation();
    const {refreshAllWidgetData} = useDashboardContext();

    const handleRefresh = useCallback(() => {
        refreshAllWidgetData();
    }, [refreshAllWidgetData]);

    return (
        <Button startIcon={<RefreshIcon />} onClick={handleRefresh}>
            {t("Refresh")}
        </Button>
    );
};

interface DashboardAutoRefreshSelectorProps {
    autoRefresh: DashboardAutoRefresh;
}

const DashboardAutoRefreshSelector: FC<DashboardAutoRefreshSelectorProps> = props => {
    const {autoRefresh} = props;
    const {t} = useTranslation();
    const {refreshAllWidgetData} = useDashboardContext();
    const [refreshInterval, setRefreshInterval] = useState<AutoRefreshInterval | "">(autoRefresh.defaultInterval ?? "");
    const options = useMemo(() => {
        if (!autoRefresh.minInterval) {
            return autoRefreshIntervals;
        }
        const idx = autoRefreshIntervals.indexOf(autoRefresh.minInterval);
        if (idx === -1) {
            return autoRefreshIntervals;
        }
        return autoRefreshIntervals.slice(idx);
    }, [autoRefresh.minInterval]);

    const milliseconds = useMemo(() => {
        if (!refreshInterval) {
            return null;
        }
        return autoRefreshIntervalToMillisecons(refreshInterval) ?? null;
    }, [refreshInterval]);

    useInterval(() => {
        refreshAllWidgetData();
    }, milliseconds);

    const handleChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value as AutoRefreshInterval | "";
        setRefreshInterval(value);
    }, []);

    return (
        <>
            <Divider orientation="vertical" style={{height: 40}} variant="middle" />
            <TextField select value={refreshInterval} onChange={handleChange}>
                <MenuItem value={""}>{t("DisableAutoRefresh")}</MenuItem>
                {options.map(s => (
                    <MenuItem key={s} value={s}>
                        {t(s)}
                    </MenuItem>
                ))}
            </TextField>
        </>
    );
};

export const DashboardViewer: FC<DashboardViewerProps> = props => {
    const {dashboard, widgets} = props;
    const classes = useStyles();

    const gridProps = useMemo(() => {
        const layoutSet = dashboard.layoutSet;
        return layoutSet ? getGridLayoutProps(layoutSet) : {};
    }, [dashboard.layoutSet]);

    const {width, ref} = useResizeDetector({
        handleHeight: false,
    });

    return (
        <DashboardContextProvider dashboard={dashboard} widgets={widgets}>
            <div className={classes.title}>
                <DashboardBreadcrumbs />
                <div className={classes.spaceFiller} />
                <DashboardRefreshButton />
                {!!dashboard.autoRefresh?.enabled && (
                    <DashboardAutoRefreshSelector autoRefresh={dashboard.autoRefresh} />
                )}
            </div>
            <DashboardParameters />
            <Divider ref={ref} />
            {isPresent(width) && (
                <Responsive
                    className={classes.layout}
                    {...gridProps}
                    isDraggable={false}
                    isResizable={false}
                    width={width}
                >
                    {dashboard.widgets.map(s => (
                        <div key={s.instantiationId}>
                            <DashboardWidgetViewer instantiation={s} />
                        </div>
                    ))}
                </Responsive>
            )}
        </DashboardContextProvider>
    );
};
