import {FC, useCallback, useMemo, useRef} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Grid from "@material-ui/core/Grid";
import {difference, sortBy} from "ramda";
import {useTranslation} from "react-i18next";
import {Form, Formik, FormikHelpers, FormikProps} from "formik";
import * as yup from "yup";
import type {SchemaOf} from "yup";
import {JSONSchema7} from "json-schema";
import i18next from "i18next";

import {RecognizedDocument, RecognizedDocumentAction, recognizedDocumentActions} from "./recognized-document";
import {
    pageImageLocationsToPageIndices,
    pageIndicesToPageImageLocations,
    pageIndicesToPageSpec,
    pagesSpecToPageIndices,
    useBatchProcessorService,
} from "./image-batch-recognition-service";
import {ErrorBoundary} from "../../../components/error-boundary";
import {validateAndSubmit} from "../../../utils/formikHelpers";
import {FormikTextField} from "../../../components/formik-components/formik-text-field";
import {FormikAutocomplete} from "../../../components/formik-components/formik-autocomplete";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            //height: "90vh",
        },
        dialogContent: {},
    })
);

export interface DocumentAdditionInfo {
    schemaId: string;
    pages?: string;
    action: RecognizedDocumentAction;
}

interface DocumentAdditionInfoEditorProps {
    availablePageIndices: number[];
}

const DocumentAdditionInfoEditor: FC<DocumentAdditionInfoEditorProps> = props => {
    const {t} = useTranslation();
    const {documentSchemas} = useBatchProcessorService();

    const labels = useMemo(() => {
        const result: Record<string, {label: string; description?: string}> = {};
        if (documentSchemas) {
            for (const s of documentSchemas) {
                if (!s.$id) {
                    continue;
                }
                result[s.$id] = {
                    label: s.title ?? s.$id,
                    description: s.description,
                };
            }
        }
        return result;
    }, [documentSchemas]);

    const options = useMemo(() => {
        const options = Object.keys(labels);
        return sortBy(s => labels[s].label, options);
    }, [labels]);

    return (
        <Grid container spacing={3}>
            <Grid item xs={12}>
                <FormikAutocomplete
                    name="schemaId"
                    options={options}
                    getOptionLabel={s => labels[s]?.label}
                    renderInput={params => (
                        <TextField
                            {...params}
                            label={t("Schema")}
                            InputLabelProps={{required: true}}
                            required
                            autoFocus
                        />
                    )}
                    fullWidth
                />
            </Grid>
            <Grid item xs={12}>
                <FormikTextField name="pages" label={t("Pages")} fullWidth />
            </Grid>
            <Grid item xs={12}>
                <FormikTextField select name="action" label={t("Action")} fullWidth>
                    {recognizedDocumentActions.map(s => (
                        <MenuItem key={s} value={s}>{t(`RecognizedDocumentAction.${s}`)}</MenuItem>
                    ))}
                </FormikTextField>
            </Grid>
            {/* {JSON.stringify(values)} */}
        </Grid>
    );
};

const numberOrRange = `(\\d+(\\s*-\\s*\\d+)?)`;
const pagesRangesPattern = `^\\s*${numberOrRange}(\\s*,\\s*${numberOrRange})*\\s*$`;
const pagesRangesRegex = new RegExp(pagesRangesPattern);

const getSchema = (availablePageIndices: number[], documentSchemas: JSONSchema7[]): SchemaOf<DocumentAdditionInfo> => {
    const schema = yup.object().shape({
        schemaId: yup
            .string()
            .required()
            .test({
                test: function (val: any) {
                    if (!val) {
                        return true;
                    }
                    if (typeof val !== "string" || !documentSchemas.find(s => s.$id === val)) {
                        return this.createError({
                            path: this.path,
                            message: i18next.t("UnknownDocumentSchema", {schemaId: val}),
                        });
                    }
                    return true;
                },
            }),
        pages: yup.string().test({
            test: function (val: any) {
                if (!val) {
                    return true;
                }
                if (typeof val !== "string" || !pagesRangesRegex.test(val)) {
                    return this.createError({
                        path: this.path,
                        message: i18next.t("InvalidPageRange", {range: val}),
                    });
                }
                const pageIndices = pagesSpecToPageIndices(val);
                const alreadyUsedPages = difference(pageIndices, availablePageIndices);
                if (alreadyUsedPages.length > 0) {
                    alreadyUsedPages.sort();
                    return this.createError({
                        path: this.path,
                        message: i18next.t("PagesAlreadyUsedInOtherDocuments", {
                            pages: alreadyUsedPages.map(s => s + 1).join(", "),
                        }),
                    });
                }
                return true;
            },
        }),
        action: yup.string().required().oneOf([...recognizedDocumentActions]),
    }) as SchemaOf<DocumentAdditionInfo>;
    return schema;
};

export interface EditDocumentModalProps {
    open: boolean;
    document?: RecognizedDocument;
    availablePageIndices: number[];
    onChange?: (document: RecognizedDocument) => void;
    onClose?: () => void;
}

export const EditDocumentModal: FC<EditDocumentModalProps> = props => {
    const {open, document, availablePageIndices, onChange, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {documentSchemas, fileInfos} = useBatchProcessorService();
    const initialValues = useMemo(() => {
        const pageIndices = document
            ? pageImageLocationsToPageIndices(document.pages, fileInfos)
            : availablePageIndices;
        const result: Partial<DocumentAdditionInfo> = {
            schemaId: document?.schemaId,
            pages: pageIndicesToPageSpec(pageIndices),
            action: document?.action ?? "saveAsDraft",
        };
        return result;
    }, [availablePageIndices, document, fileInfos]);
    const formRef = useRef<FormikProps<Partial<DocumentAdditionInfo>> | null>();

    const schema = useMemo(() => {
        return getSchema(availablePageIndices, documentSchemas ?? []);
    }, [availablePageIndices, documentSchemas]);

    const handleAccept = useCallback(() => {
        const form = formRef.current;
        if (form) {
            validateAndSubmit(form);
        }
    }, []);

    const handleSubmit = useCallback(
        async (values: Partial<DocumentAdditionInfo>, actions: FormikHelpers<Partial<DocumentAdditionInfo>>) => {
            try {
                const value: RecognizedDocument = {
                    schemaId: values.schemaId!,
                    pages: [],
                    data: document?.data,
                    action: values.action!,
                };
                if (values.pages) {
                    const pageIndices = pagesSpecToPageIndices(values.pages);
                    value.pages = pageIndicesToPageImageLocations(pageIndices, fileInfos);
                }
                onChange?.(value);
                onClose?.();
            } finally {
                actions.setSubmitting(false);
            }
        },
        [document?.data, fileInfos, onChange, onClose]
    );

    const handleClose = useCallback(() => {
        onClose?.();
    }, [onClose]);

    const handleDialogClose = useCallback(
        async (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [onClose]
    );

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="sm"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{t(document ? "EditingDocument" : "AddingDocument")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <ErrorBoundary>
                    <Formik
                        innerRef={s => (formRef.current = s)}
                        initialValues={initialValues}
                        onSubmit={handleSubmit}
                        validationSchema={schema}
                        validateOnChange={true}
                        validateOnBlur={true}
                    >
                        <Form autoComplete="off">
                            <DocumentAdditionInfoEditor availablePageIndices={availablePageIndices} />
                        </Form>
                    </Formik>
                </ErrorBoundary>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
