import {FC, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import ReactMarkdown from "react-markdown";

import {ContextTask, ContextType} from "../../api/nimflow-api";
import {getSettings} from "../../api/nimflow-api-extensions";
import {evaluateHandlebars} from "../../libs/handlebars";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "100%",
            width: "100%",
            maxHeight: "50vh",
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

export interface TasksViewTaskDetailDialogProps {
    open: boolean;
    task: ContextTask;
    contextType: ContextType;
    onClose: () => void;
}

export const TasksViewTaskDetailDialog: FC<TasksViewTaskDetailDialogProps> = props => {
    const {open, task, contextType, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();

    const taskDetails = useMemo(() => {
        const taskType = contextType?.tasks.find(s => s.name === task.typeName);
        const taskDetailsTemplate = taskType ? getSettings(taskType)?.taskDetailsTemplate : undefined;
        let taskDetails: string | undefined = undefined;
        if (taskDetailsTemplate) {
            taskDetails = evaluateHandlebars(taskDetailsTemplate, {task});
        }
        return taskDetails;
    }, [contextType?.tasks, task]);

    return (
        <Dialog open={open} onClose={onClose} fullWidth maxWidth="md" classes={{paper: classes.paper}}>
            <DialogTitle className={classes.dialogTitle}>{t("TaskDetails")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <ReactMarkdown>{taskDetails ?? ""}</ReactMarkdown>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogFooter}>
                <Button onClick={onClose} color="primary">
                    {t("Close")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
