import React, {FC, useCallback} from "react";
import {MutableSnapshot, RecoilRoot, RecoilRootProps, RecoilState, useRecoilTransactionObserver_UNSTABLE} from "recoil";
import {isPresent} from "ts-is-present";

const loadPersistentState = (storageKey: string): Record<string, any> | undefined => {
    const json = localStorage.getItem(storageKey);
    if (!isPresent(json) || json.length === 0) {
        return undefined;
    }
    var values = JSON.parse(json);
    if (typeof values !== "object" || Array.isArray(values) || values === null) {
        return undefined;
    }
    return values;
};

interface RecoilRootPersistenceObserverProps {
    storageKey: string;
    persistedAtoms: RecoilState<any>[];
}

const RecoilRootPersistenceObserver: FC<RecoilRootPersistenceObserverProps> = props => {
    const {storageKey, persistedAtoms, children} = props;

    useRecoilTransactionObserver_UNSTABLE(({snapshot}) => {
        let values: any = undefined;
        const modifiedAtoms = Array.from(snapshot.getNodes_UNSTABLE({isModified: true}));
        const persistedAtomsKeys = persistedAtoms.map(s => s.key);
        for (const modifiedAtom of modifiedAtoms) {
            if (!persistedAtomsKeys.includes(modifiedAtom.key)) {
                continue;
            }
            const atomLoadable = snapshot.getLoadable(modifiedAtom);
            if (atomLoadable.state === "hasValue") {
                if (!isPresent(values)) {
                    values = loadPersistentState(storageKey) ?? {};
                }
                values[modifiedAtom.key] = atomLoadable.contents;
            }
        }
        if (isPresent(values)) {
            const json = JSON.stringify(values);
            localStorage.setItem(storageKey, json);
        }
    });

    return <>{children}</>;
};

export type PersistentRecoilRootProps = RecoilRootProps & {
    storageKey: string;
    persistedAtoms: RecoilState<any>[];
}

export const PersistentRecoilRoot: FC<PersistentRecoilRootProps> = props => {
    const {storageKey, persistedAtoms, children} = props;

    const initializeState = useCallback(
        (mutableSnapshot: MutableSnapshot) => {
            var values = loadPersistentState(props.storageKey);
            if (values) {
                for (const [key, value] of Object.entries(values)) {
                    const atom = props.persistedAtoms.find(s => s.key === key);
                    if (atom) {
                        mutableSnapshot.set(atom, value);
                    }
                }
            }
            if (props.override) {
                props.initializeState?.(mutableSnapshot);
            }
        },
        [props]
    );

    return (
        <RecoilRoot initializeState={initializeState}>
            <RecoilRootPersistenceObserver storageKey={storageKey} persistedAtoms={persistedAtoms} />
            {children}
        </RecoilRoot>
    );
};
