import React, {FC, useCallback, useMemo, useState, useRef} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Alert from "@material-ui/lab/Alert";
import Grid from "@material-ui/core/Grid";
import {useTranslation} from "react-i18next";
import useSWR from "swr";
import {clone} from "ramda";
import {JsonFormsCore, JsonSchema7, UISchemaElement} from "@jsonforms/core";

import {DispatchActionStep} from "./dispatch-action-stepper";
import {StepContainer} from "./step-container";
import {useNimflow} from "../../api/nimflow-api-provider";
import {getActionJsonSchema} from "../../utils/json-schema";
import {translateValidationErrors} from "../../libs/ajv";
import {ContextType, ActionType} from "../../api/nimflow-api";
import {getSettings} from "../../api/nimflow-api-extensions";
import {JsonForms} from "../../libs/jsonforms";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            width: "100%",
            //marginTop: "20px",
            marginBottom: "20px",
        },
        textField: {
            width: "100%",
        },
        content: {
            padding: "8px 24px",
            "&:first-child": {
                // dialog without title
                paddingTop: 20,
            },
        },
    })
);

const getActionDataSchema = (actionType?: ActionType, contextType?: ContextType) => {
    if (!actionType || !contextType) {
        return undefined;
    }
    const payloadDefinitionName = actionType.payloadSchemaDefinitionName;
    if (!payloadDefinitionName) {
        return undefined;
    }
    const schema = getActionJsonSchema(contextType, actionType.name);
    return schema;
};

const useContextType = (contextTypeName?: string) => {
    const {contextTypes_FindOne} = useNimflow();
    const {data, error, isValidating, mutate} = useSWR(
        `/ContextType/${contextTypeName}`,
        async () => {
            return await contextTypes_FindOne({name: contextTypeName});
        },
        {revalidateOnFocus: false}
    );
    return useMemo(() => {
        return {
            contextType: data,
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export interface ActionDataEditorStepProps extends DispatchActionStep {}

export const ActionDataEditorStep: FC<ActionDataEditorStepProps> = props => {
    const {value, onBack, onNext} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {contextType, error, isValidating} = useContextType(value.contextTypeName);

    const [actionData, setActionData] = useState<any>({
        reference: value.reference,
        contextId: value.contextId,
    });
    const [payload, setPayload] = useState<any>(value.payload ?? {});
    const actionDataErrorsRef = useRef<JsonFormsCore["errors"]>();
    const payloadErrorsRef = useRef<JsonFormsCore["errors"]>();

    const actionType = useMemo(() => {
        return contextType?.actions.find(s => s.name === value.action);
    }, [contextType, value]);

    const handleNext = useCallback(() => {
        if (!actionType || !contextType) {
            return;
        }
        //const isInitial = contextType.initialActions.includes(actionType.name);
        //if (isInitial)
        if (
            (actionDataErrorsRef.current && actionDataErrorsRef.current.length > 0) ||
            (payloadErrorsRef.current && payloadErrorsRef.current.length > 0)
        ) {
            return;
        }
        onNext({
            ...value,
            ...actionData,
            payload,
        });
    }, [onNext, actionType, contextType, actionData, payload, value]);

    const actionDataSchema = useMemo(() => {
        const schema: JsonSchema7 = {
            type: "object",
            properties: {
                reference: {
                    type: "string",
                },
                /*contextId: {
                    type: "string"
                }*/
            },
            required: ["reference"],
        };
        return schema;
    }, []);

    const payloadSchema = useMemo(() => {
        return getActionDataSchema(actionType, contextType);
    }, [actionType, contextType]);

    const payloadUischema = useMemo(() => {
        const schema = actionType ? getSettings(actionType)?.uiSchema : undefined;
        if (!schema) {
            return undefined;
        }
        return JSON.parse(schema) as UISchemaElement;
    }, [actionType]);

    const handleActionDataChange = useCallback(
        (state: Pick<JsonFormsCore, "data" | "errors">) => {
            if (state.errors && state.errors.length > 0) {
                translateValidationErrors(
                    state.errors.map(s => ({
                        ...s,
                        instancePath: s.dataPath,
                    }))
                );
                actionDataErrorsRef.current = clone(state.errors);
            } else {
                setActionData(state.data);
                actionDataErrorsRef.current = undefined;
            }
        },
        [setActionData]
    );

    const handlePayloadChange = useCallback(
        (state: Pick<JsonFormsCore, "data" | "errors">) => {
            if (state.errors && state.errors.length > 0) {
                translateValidationErrors(
                    state.errors.map(s => ({
                        ...s,
                        instancePath: s.dataPath,
                    }))
                );
                payloadErrorsRef.current = clone(state.errors);
            } else {
                setPayload(state.data);
                payloadErrorsRef.current = undefined;
            }
        },
        [setPayload]
    );

    const uiSchemaLibrary = useMemo(() => {
        if (!contextType) {
            return undefined;
        }
        const uiSchemaStr = getSettings(contextType)?.uiSchemaCommon;
        if (!uiSchemaStr) {
            return undefined;
        }
        return JSON.parse(uiSchemaStr) as Record<string, UISchemaElement>;
    }, [contextType]);

    if (!isValidating && !!error) {
        return <Alert severity="error">{error.toString()}</Alert>;
    }
    if (!contextType || !actionType) {
        return null;
    }

    //const isInitialAction = contextType?.initialActions.includes(actionType.name) ?? false;

    return (
        <StepContainer onBack={onBack} onNext={handleNext} nextActionCaption={t("ExecuteAction")}>
            <Grid container spacing={2} className={classes.root}>
                <Grid item xs={12} className={classes.content}>
                    <JsonForms
                        schema={actionDataSchema}
                        data={actionData}
                        onChange={handleActionDataChange}
                        uiSchemaLibrary={uiSchemaLibrary}
                    />
                </Grid>
                <Grid item xs={12} className={classes.content}>
                    {!!payloadSchema && (
                        <JsonForms
                            schema={payloadSchema as any}
                            uischema={payloadUischema}
                            data={payload}
                            onChange={handlePayloadChange}
                            uiSchemaLibrary={uiSchemaLibrary}
                        />
                    )}
                </Grid>
            </Grid>
        </StepContainer>
    );
};
