import React, {FC, useCallback, createContext, useState, useContext} from "react";
import Button, {ButtonProps} from "@material-ui/core/Button";
import Dialog, {DialogProps} from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Typography from "@material-ui/core/Typography";
import {mergeDeepRight, pick} from "ramda";

export interface ConfirmAction<T extends string> {
    type: T;
    text?: React.ReactNode;
    buttonProps?: ButtonProps;
}

export interface ConfirmOptions<TActions extends string> {
    title?: React.ReactNode;
    description?: React.ReactNode;
    dialogProps?: Omit<DialogProps, "open" | "onClose">;
    actions?: ConfirmAction<TActions>[];
    mapCloseTo?: TActions;
}

export interface OkCancelConfirmOptions {
    title?: React.ReactNode;
    description?: React.ReactNode;
    dialogProps?: Omit<DialogProps, "open" | "onClose">;
    confirmationText?: React.ReactNode;
    cancellationText?: React.ReactNode;
    confirmationButtonProps?: ButtonProps;
    cancellationButtonProps?: ButtonProps;
}

type ConfirmationDialogProps<TActions extends string> = {
    options: ConfirmOptions<TActions>;
    onClick: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>, type: TActions) => void;
} & Pick<DialogProps, "open" | "onClose">;

const ConfirmationDialog = <TActions extends string>(props: ConfirmationDialogProps<TActions>) => {
    const {open, options, onClose, onClick} = props;
    const {title, description, actions, dialogProps} = options;

    return (
        <Dialog fullWidth {...dialogProps} open={open} onClose={onClose}>
            {title && <DialogTitle>{title}</DialogTitle>}
            {description && (
                <DialogContent>
                    <Typography component="div" variant="body1" color="textSecondary">
                        {description}
                    </Typography>
                </DialogContent>
            )}
            <DialogActions>
                {actions?.map(action => (
                    <Button key={action.type} {...action.buttonProps} onClick={ev => onClick(ev, action.type)}>
                        {action.text}
                    </Button>
                ))}
            </DialogActions>
        </Dialog>
    );
};

const DEFAULT_OPTIONS: ConfirmOptions<string> & OkCancelConfirmOptions = {
    title: "Are you sure?",
    description: "",
    dialogProps: {},
    actions: [],
    confirmationText: "Ok",
    cancellationText: "Cancel",
    confirmationButtonProps: {},
    cancellationButtonProps: {},
};

const stub = () => {
    throw new Error("You forgot to wrap your component in <ConfirmProvider>.");
};

export interface ConfirmService {
    confirm: <TActions extends string>(opts: ConfirmOptions<TActions>) => Promise<TActions>;
    getDefaultOptions(): ConfirmOptions<any> & OkCancelConfirmOptions;
}

const initialConfirmContext: ConfirmService = {
    confirm: stub,
    getDefaultOptions: stub,
};

export const ConfirmContext = createContext<ConfirmService>(initialConfirmContext);

export interface ConfirmProviderProps {
    defaultOptions?: ConfirmOptions<any> & OkCancelConfirmOptions;
}

export const ConfirmProvider: FC<ConfirmProviderProps> = props => {
    const {children, defaultOptions = {}} = props;
    const [options, setOptions] = useState({...DEFAULT_OPTIONS, ...defaultOptions});
    const [resolveReject, setResolveReject] = useState<Array<(value?: any) => void>>([]);
    const [resolve, reject] = resolveReject;

    const confirm = useCallback(<TActions extends string>(options: ConfirmOptions<TActions>): Promise<TActions> => {
        return new Promise((resolve, reject) => {
            setOptions(options);
            setResolveReject([resolve, reject]);
        });
    }, []);

    const handleClose = useCallback(
        (ev, reason: string) => {
            if (options.mapCloseTo) {
                resolve(options.mapCloseTo);
            } else {
                reject(reason);
            }
            setResolveReject([]);
        },
        [reject, resolve, options.mapCloseTo]
    );

    const handleClick = useCallback(
        (ev, type: string) => {
            resolve(type);
            setResolveReject([]);
        },
        [resolve]
    );

    const getDefaultOptions = useCallback(() => {
        return defaultOptions;
    }, [defaultOptions]);

    return (
        <>
            <ConfirmContext.Provider value={{confirm, getDefaultOptions}}>{children}</ConfirmContext.Provider>
            <ConfirmationDialog
                open={resolveReject.length === 2}
                options={options}
                onClose={handleClose}
                onClick={handleClick}
            />
        </>
    );
};

export const buildConfirmOptions = <TActions extends string>(
    defaultOptions: ConfirmOptions<any>,
    options: ConfirmOptions<TActions>
): ConfirmOptions<TActions> & OkCancelConfirmOptions => {
    return mergeDeepRight(DEFAULT_OPTIONS, mergeDeepRight(defaultOptions, options)) as any;
};

export const useConfirm = (): (<TActions extends string>(opts: ConfirmOptions<TActions>) => Promise<TActions>) => {
    const {confirm, getDefaultOptions} = useContext(ConfirmContext);
    return useCallback(opts => {
        const options = buildConfirmOptions(getDefaultOptions(), opts);
        return confirm(pick(["title", "description", "dialogProps", "actions", "mapCloseTo"], options));
    }, [confirm, getDefaultOptions]);
};

export const useOkCancelConfirm = (): ((opts?: OkCancelConfirmOptions) => Promise<boolean>) => {
    const {confirm, getDefaultOptions} = useContext(ConfirmContext);
    return useCallback((opts = {}) => {
        const options = buildConfirmOptions(getDefaultOptions(), opts);
        return confirm({
            title: options.title,
            description: options.description,
            dialogProps: options.dialogProps,
            mapCloseTo: "cancel",
            actions: [
                {
                    type: "ok",
                    text: opts.confirmationText ?? options.confirmationText,
                    buttonProps: mergeDeepRight(
                        options.confirmationButtonProps ?? {},
                        opts.confirmationButtonProps ?? {}
                    ) as any,
                },
                {
                    type: "cancel",
                    text: opts.cancellationText ?? options.cancellationText,
                    buttonProps: mergeDeepRight(
                        options.cancellationButtonProps ?? {},
                        opts.cancellationButtonProps ?? {}
                    ) as any,
                },
            ],
        })
            .then(s => s === "ok")
            .catch(() => false);
    }, [confirm, getDefaultOptions]);
};
