import {FC, useMemo} from "react";
import ReactMarkdown from "react-markdown";

import {ApiException} from "../api/nimflow-api";
import i18next from "i18next";
import {isAxiosNetworkError, AxiosNetworkError} from "../libs/axios";

export interface ErrorViewerProps {
    error: any;
}

const apiExceptionToMarkdown = (apiError: ApiException) => {
    function tryGetErrors(response: any) {
        if ("errors" in response) {
            return response.errors;
        }
        if ("extensions" in response) {
            return response.extensions.errors;
        }
        return undefined;
    }

    try {
        const response = JSON.parse(apiError.response);
        if ("title" in response) {
            let errors = tryGetErrors(response);
            if (errors) {
                let md = `### ${response.title}\n\n`;
                if (!Array.isArray(errors)) {
                    errors = [errors];
                }
                for (const error of errors) {
                    if (typeof error === "object") {
                        const entries = Object.entries(error);
                        if (entries.length === 1 && entries[0][0] === "") {
                            const value = entries[0][1];
                            if (Array.isArray(value)) {
                                // eslint-disable-next-line no-loop-func
                                value.forEach((s, j) => {
                                    md += `${j}. ${s}\n`;
                                });
                            } else if (value) {
                                md += `${value}\n`;
                            }
                        } else {
                            // eslint-disable-next-line no-loop-func
                            entries.forEach(([key, value], i) => {
                                md += `${i}. ${key}`;
                                if (Array.isArray(value)) {
                                    md += "\n";
                                    value.forEach((s, j) => {
                                        md += `\t${j}. ${s}`;
                                    });
                                } else if (value) {
                                    md += `: ${value}`;
                                }
                                md += "\n";
                            });
                        }
                    }
                }
                return md;
            } else {
                return `${response.title} (${response.status})`;
            }
        }
    } catch {
        /**/
    }
    return apiError.toString();
};

const axiosNetworkErrorToMarkdown = (_err: AxiosNetworkError) => {
    if (window.navigator?.onLine === false) {
        return i18next.t("BrowserIsOffline");
    }
    // client never received a response, or request never left
    return i18next.t("AxiosNetworkErrorMarkdown");
};

const errorToMarkdown = (error: any): string => {
    if (error instanceof ApiException) {
        return apiExceptionToMarkdown(error);
    }
    if (isAxiosNetworkError(error)) {
        return axiosNetworkErrorToMarkdown(error);
    }
    if (Array.isArray(error)) {
        const msgs = error.map(s => errorToMarkdown(s));
        return msgs.join("\n\n");
    }
    if (error instanceof Error) {
        return error.message;
    }
    return error?.toString() ?? "";
};

export const ErrorViewer: FC<ErrorViewerProps> = props => {
    const {error} = props;

    const errorMarkdown = useMemo(() => {
        return errorToMarkdown(error);
    }, [error]);

    return <ReactMarkdown>{errorMarkdown}</ReactMarkdown>;
};
