import {FC, useMemo, useState, useCallback} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import useSWR from "swr";
import {sortBy} from "ramda";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Alert from "@material-ui/lab/Alert";
import CheckIcon from "@material-ui/icons/Check";

import {useNimflow} from "../../api/nimflow-api-provider";
import {ContextTypeInfo} from "../../api/nimflow-api";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {ContextTypeIcon} from "../../components/icons/context-type-icon";
import {SearchField} from "../../components/search-field";
import {DispatchActionStep} from "./dispatch-action-stepper";
import {StepContainer} from "./step-container";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        },
    })
);

const filterData = (data?: ContextTypeInfo[], search?: string): ContextTypeInfo[] => {
    if (!data) {
        return [];
    }
    if (!search) {
        return data;
    }
    const lcsearch = search.toLowerCase();
    return data.filter(
        s => s.name.toLowerCase().includes(lcsearch) || (s.description && s.description.includes(lcsearch))
    );
};

const useContextTypes = (filter: string) => {
    const {contextTypes_Search} = useNimflow();
    const {data, error, isValidating, mutate} = useSWR("/ContextTypes/Search", async () => {
        const result = await contextTypes_Search({limit: 100});
        return sortBy(s => s.name, result);
    });
    return useMemo(() => {
        return {
            data: filterData(data, filter),
            isValidating,
            error,
            mutate,
        };
    }, [data, filter, isValidating, error, mutate]);
};

export interface ContextTypeSelectorStepProps extends DispatchActionStep {}

export const ContextTypeSelectorStep: FC<ContextTypeSelectorStepProps> = props => {
    const {value, onNext} = props;
    const classes = useStyles();
    const [filter, setFilter] = useState("");
    const {data, error, isValidating} = useContextTypes(filter);

    const handleContextTypeSelected = useCallback(
        (name: string) => {
            if (value.contextTypeName === name) {
                onNext(value);
            } else {
                onNext({contextTypeName: name});
            }
        },
        [value, onNext]
    );

    return (
        <StepContainer>
            <StandardSectionHeader
                //title={t("SelectContextType")}
                actions={<SearchField value={filter} onChange={setFilter} />}
            />
            <List className={classes.root} style={{maxHeight: "400", overflowY: "auto"}}>
                {data.map((s, i) => (
                    <ListItem key={s.name}>
                        <ListItemIcon>
                            <ContextTypeIcon />
                        </ListItemIcon>
                        <ListItemText primary={s.name} secondary={s.description} />
                        <ListItemSecondaryAction>
                            <IconButton edge="end" onClick={() => handleContextTypeSelected(s.name)}>
                                <CheckIcon />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                ))}
            </List>
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
        </StepContainer>
    );
};
