import { UISchemaElement } from "@jsonforms/core";
import {createContext, FC, useContext} from "react";

export interface JsonFormsService {
    context: Record<string, any>;
    bindContext: boolean;
    uiSchemaLibrary: Record<string, UISchemaElement>;
}

export const JsonFormsContext = createContext<JsonFormsService>({
    context: {},
    bindContext: true,
    uiSchemaLibrary: {},
});

export interface JsonFormsContextProviderProps {
    context?: Record<string, any>;
    bindContext?: boolean;
    uiSchemaLibrary?: Record<string, UISchemaElement>;
}

export const JsonFormsContextProvider: FC<JsonFormsContextProviderProps> = props => {
    const {context, bindContext, uiSchemaLibrary, children} = props;
    return (
        <JsonFormsContext.Provider
            value={{
                context: context ?? {},
                bindContext: bindContext ?? true,
                uiSchemaLibrary: uiSchemaLibrary ?? {},
            }}
        >
            {children}
        </JsonFormsContext.Provider>
    );
};

export const useJsonForms = (): JsonFormsService => useContext(JsonFormsContext);
