import {FC, useCallback, useState} from "react";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Alert from "@material-ui/lab/Alert";
import axios from "axios";

import {businessDirectoryServerUrl} from "../../env-vars";
import {isAxiosError} from "../../libs/axios";

export interface BasicAuthenticationLoginModalProps {
    open: boolean;
    onChange?: (userName: string, password: string, token: any) => void;
    onClose: () => void;
}

export const BasicAuthenticationLoginModal: FC<BasicAuthenticationLoginModalProps> = props => {
    const {open, onChange, onClose} = props;
    const {t} = useTranslation();
    const [userName, setUserName] = useState("");
    const [password, setPassword] = useState("");
    const [lastError, setLastError] = useState("");

    const handleLogin = useCallback(async () => {
        if (!userName) {
            return;
        }
        const data = {
            username: userName,
            password,
        };
        try {
            const response = await axios.post("user/authenticate", data, {
                baseURL: businessDirectoryServerUrl,
            });
            onChange?.(userName, password, response.data);
            onClose();
        } catch (e: any) {
            if (isAxiosError(e)) {
                setLastError(e.response ? e.response.statusText : e.message);
            } else {
                setLastError(e.toString());
            }
        }
    }, [onChange, onClose, password, userName]);

    const handleClose = useCallback(async () => {
        onClose();
    }, [onClose]);

    const handleDialogClose = useCallback(
        async (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [onClose]
    );

    return (
        <Dialog open={open} onClose={handleDialogClose} maxWidth="sm" fullWidth>
            <DialogTitle>{t("Login")}</DialogTitle>
            <DialogContent>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            label={t("UserName")}
                            value={userName}
                            onChange={ev => setUserName(ev.target.value)}
                            fullWidth
                            required
                            InputProps={{
                                autoFocus: true,
                                required: true,
                            }}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            type="password"
                            label={t("Password")}
                            value={password}
                            onChange={ev => setPassword(ev.target.value)}
                            fullWidth
                        />
                    </Grid>
                    {!!lastError && <Alert severity="error">{lastError}</Alert>}
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleLogin} color="primary">
                    {t("Login")}
                </Button>
                <Button onClick={handleClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
