import React, {FC, useMemo, useState, useRef, useCallback, useEffect} from "react";
import {Column, Components, Options, MTableActions, Action, Query, MTableToolbar} from "@material-table/core";
import {CheckboxProps} from "@material-ui/core/Checkbox";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import RefreshIcon from "@material-ui/icons/Refresh";
import VisibilityIcon from "@material-ui/icons/Visibility";
import VisibilityOffIcon from "@material-ui/icons/VisibilityOff";
import KeyboardReturnIcon from "@material-ui/icons/KeyboardReturn";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import InfoIcon from "@material-ui/icons/Info";
import i18next from "i18next";
import {useTranslation} from "react-i18next";
import {equals, times} from "ramda";
import useSWR from "swr";
import {usePopupState, bindMenu} from "material-ui-popup-state/hooks";

import {ContextTask, ContextType, ResponseType} from "../../api/nimflow-api";
import {formatDate} from "../../utils/date-utils";
import MaterialTable, {MaterialTableProps} from "../../libs/material-table";
import {useTasksViewService, defaultTasksViewData} from "./tasks-view-service";
import {useOkCancelConfirm} from "../confirm";
import {TaskViewColumn, TasksViewState, TasksViewData} from "./tasks-view";
import {TasksViewGroupResponses} from "./tasks-view-group-responses";
import {useNimflow} from "../../api/nimflow-api-provider";
import {permissions} from "../../utils/authorization";
import {useReportError} from "../../utils/use-report-error";
import {TasksViewUserAssignmentButton} from "./tasks-view-user-assignment-button";
import {TasksViewColumnUserAssignmentEditor} from "./tasks-view-user-column-assignment-editor";
import {useKey} from "../../utils/use-key";
import {getCommandText, getSettings} from "../../api/nimflow-api-extensions";
import {evaluateHandlebars} from "../../libs/handlebars";
import {TasksViewTaskDetailDialog} from "./tasks-view-task-detail-dialog";
import {MaterialFontIcon} from "../material-font-icon";
import {userDefinedLocalizationsNs} from "../localization-provider";

const defaultTableOptions: Options<any> = {
    padding: "dense",
    showSelectAllCheckbox: false,
    search: false,
};

export interface TasksViewTableProps {
    columns: TaskViewColumn[];
    pageSizeOptions?: number[];
    initialState: TasksViewState;
    allowResponseCreation: boolean;
    allowTaskReassignment: boolean;
    allowFocusing: boolean;
    onResponseRequested: (contextType: ContextType, task: ContextTask, responseType: ResponseType) => void;
}

interface TaskSelection {
    contextTypeName: string;
    taskTypeName: string;
    selectedTasks: ContextTask[];
}

const queryToTasksViewState = (query: Query<ContextTask>): TasksViewState => {
    const result: TasksViewState = {
        currentPage: query.page,
        pageSize: query.pageSize,
    };
    if (query.filters && query.filters.length > 0) {
        result.filters = {};
        for (const filter of query.filters) {
            (result.filters as any)[filter.column.field!] = filter?.value;
        }
    }
    if (query.orderBy?.field) {
        result.sortInfo = {
            column: query.orderBy.field as any,
            direction: query.orderDirection,
        };
    }
    return result;
};

const useUsersWithPermission = (allowTaskReassignment: boolean) => {
    const {users_GetUsersWithPermission} = useNimflow();
    const key = useKey(users_GetUsersWithPermission, permissions.readTasks, allowTaskReassignment);
    const {data, isValidating, mutate, error} = useSWR(key, async () => {
        if (!allowTaskReassignment) {
            return [];
        }
        const result = await users_GetUsersWithPermission({
            action: permissions.readTasks,
            pageSize: 100,
            pageNumber: 0,
        });
        return result;
    });
    return useMemo(() => {
        return {
            users: data ?? [],
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

const useDigitalWorkers = (allowTaskReassignment: boolean) => {
    const {digitalWorkers_SearchData} = useNimflow();
    const key = useKey(digitalWorkers_SearchData, allowTaskReassignment);
    const {data, isValidating, mutate, error} = useSWR(key, async () => {
        if (!allowTaskReassignment) {
            return [];
        }
        const result = await digitalWorkers_SearchData({
            active: true,
        });
        return result;
    });
    return useMemo(() => {
        return {
            digitalWorkers: data ?? [],
            isValidating,
            error,
             mutate,
        };
    }, [data, isValidating, mutate, error]);
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const getFocusIcon = () => <VisibilityIcon />;
const getUnfocusIcon = () => <VisibilityOffIcon />;

const buildFocusAction = (rowData: ContextTask, handler: (rowData: ContextTask) => void) => {
    const action: Action<any> = {
        icon: rowData.focused ? getUnfocusIcon : getFocusIcon,
        tooltip: i18next.t(rowData.focused ? "Unfocus" : "Focus"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDetailsAction = (
    rowData: ContextTask,
    contextType: ContextType | undefined,
    handler: (rowData: ContextTask) => void
) => {
    const taskType = contextType?.tasks.find(s => s.name === rowData.typeName);
    const taskDetailsTemplate = taskType ? getSettings(taskType)?.taskDetailsTemplate : undefined;
    let taskDetails: string | undefined = undefined;
    if (taskDetailsTemplate) {
        taskDetails = evaluateHandlebars(taskDetailsTemplate.trimStart().split("\n")[0], {task: rowData});
    }
    const action: Action<any> = {
        icon: InfoIcon,
        tooltip: taskDetails,
        onClick: adaptHandler(handler),
        disabled: !taskDetails,
    };
    return action;
};

const buildInlineSendResponseAction = (
    rowData: ContextTask,
    contextType: ContextType | undefined,
    index: number,
    handler: (rowData: ContextTask, responseType: ResponseType) => void
) => {
    const taskType = contextType?.tasks.find(s => s.name === rowData.typeName);
    const responseType = taskType?.validResponses.filter(s => !!getSettings(s)?.icon)[index];
    const icon = responseType ? getSettings(responseType)?.icon : undefined;
    const action: Action<any> = {
        icon: () => <MaterialFontIcon family={icon?.family}>{icon?.name}</MaterialFontIcon>,
        tooltip: responseType ? getCommandText(responseType) : "",
        onClick: adaptHandler(rowData => handler(rowData, responseType!)),
        position: "row",
        hidden: !responseType,
    };
    return action;
};

const buildSendResponseAction = (
    rowData: ContextTask,
    contextType: ContextType,
    handler: (event: React.MouseEvent, rowData: ContextTask, contextType: ContextType) => void
) => {
    const taskType = contextType?.tasks.find(s => s.name === rowData.typeName);
    let anyWithIcon = false;
    let anyWithoutIcon = false;
    if (taskType) {
        for (const response of taskType.validResponses) {
            if (!!getSettings(response)?.icon) {
                anyWithIcon = true;
            } else {
                anyWithoutIcon = true;
            }
        }
    }
    const action: Action<any> = {
        icon: anyWithIcon ? MoreHorizIcon : KeyboardReturnIcon,
        tooltip: i18next.t(anyWithIcon ? "MoreResponses" : "SendResponse"),
        onClick: (event, rowData) => handler(event, Array.isArray(rowData) ? rowData[0] : rowData, contextType),
        position: "row",
        hidden: !anyWithoutIcon,
    };
    return action;
};

const useActionMenu = () => {
    const popupState = usePopupState({
        variant: "popover",
        popupId: "actionMenu",
    });
    const [currentTask, setCurrentTask] = useState<ContextTask>();
    const [responseTypes, setResponseTypes] = useState<ResponseType[]>();

    const openMenu = useCallback(
        (event: React.MouseEvent, task: ContextTask, contextType: ContextType) => {
            let target = event.target as Node | null;
            while (target && !(target instanceof HTMLElement)) {
                target = target.parentNode;
            }
            if (!target) {
                return;
            }
            const taskType = contextType.tasks.find(s => s.name === task.typeName);
            if (taskType?.validResponses) {
                setResponseTypes(taskType.validResponses);
            }
            setCurrentTask(task);
            setTimeout(() => {
                popupState.open(target as HTMLElement);
            }, 0);
        },
        [popupState]
    );

    const closeMenu = useCallback(() => {
        popupState.close();
        setCurrentTask(undefined);
        setResponseTypes(undefined);
    }, [popupState]);

    return {
        popupState,
        openMenu,
        closeMenu,
        currentTask,
        responseTypes,
    };
};

export const TasksViewTable: FC<TasksViewTableProps> = React.memo(props => {
    const {
        columns,
        pageSizeOptions,
        initialState,
        allowResponseCreation,
        allowTaskReassignment,
        allowFocusing,
        onResponseRequested,
    } = props;
    const {data, contextTypes, canCreateResponsesTo, currentState, setCurrentState, refreshTasks, updateTask} =
        useTasksViewService();
    const {t} = useTranslation();
    const reportError = useReportError();
    const tableRef = useRef<any>();
    const [selection, internalSetSelection] = useState<TaskSelection>();
    const selectionRef = useRef<TaskSelection>(); //Needed to avoid table options recalculation
    const dataRef = useRef<TasksViewData>();
    const pendingDataLoadRef = useRef<(data: TasksViewData) => void>();
    const prevFiltersRef = useRef<any>();
    const setSelection = useCallback((value?: TaskSelection) => {
        internalSetSelection(value);
        selectionRef.current = value;
    }, []);
    const confirm = useOkCancelConfirm();
    const {users} = useUsersWithPermission(allowTaskReassignment);
    const {digitalWorkers} = useDigitalWorkers(allowTaskReassignment);
    const [taskDetails, setTaskDetails] = useState<ContextTask>();
    const {popupState: actionMenuPopupState, openMenu, closeMenu, currentTask, responseTypes} = useActionMenu();

    const handleAssignedToChanged = useCallback(
        async (rowData: ContextTask, assignedTo?: string) => {
            try {
                await updateTask(rowData, {assignedTo});
            } catch (e) {
                reportError(t("TaskAssignmentFailed"), e);
            }
        },
        [reportError, t, updateTask]
    );

    const handleFocusedChanged = useCallback(
        async (rowData: ContextTask) => {
            try {
                await updateTask(rowData, {focused: !rowData.focused});
            } catch (e) {
                reportError(t("TaskAssignmentFailed"), e);
            }
        },
        [reportError, t, updateTask]
    );

    const mtcols = useMemo(() => {
        const cols = columns.map(s => {
            const col: Column<ContextTask> = {
                field: s.field,
                title: s.title ?? t(s.field.toUpperCase()[0] + s.field.substring(1)),
                sorting: s.field !== "focused" && (s.allowSorting ?? true),
                hidden: s.hidden,
            };
            if (s.field === "dueDate") {
                col.render = rowData => (rowData.dueDate ? formatDate(rowData.dueDate) : "");
            }
            if (s.field === "dateCreated") {
                col.render = rowData => (rowData.dateCreated ? formatDate(rowData.dateCreated) : "");
            }
            if (s.field === "assignedTo" && allowTaskReassignment) {
                col.render = rowData => (
                    <TasksViewColumnUserAssignmentEditor
                        value={rowData.assignedTo}
                        users={users}
                        digitalWorkers={digitalWorkers}
                        onChange={assignedTo => handleAssignedToChanged(rowData, assignedTo)}
                    />
                );
            }
            if (s.field === "typeName") {
                col.render = rowData => {
                    return t(rowData.typeName, {ns: userDefinedLocalizationsNs});
                };
            }
            if (s.field === "contextTypeName") {
                col.render = rowData => {
                    return t(rowData.contextTypeName, {ns: userDefinedLocalizationsNs});
                };
            }
            // if (s.field === "focused") {
            //     col.type = "boolean";
            //     if (allowFocusing) {
            //         col.render = rowData => (
            //             <IconButton onClick={() => handleFocusedChanged(rowData)}>
            //                 {rowData.focused ? <VisibilityIcon /> : <VisibilityOffIcon />}
            //             </IconButton>
            //         );
            //     }
            // }
            if (initialState?.sortInfo?.column === s.field) {
                col.defaultSort = initialState.sortInfo.direction;
            }
            return col;
        });
        return cols;
    }, [allowTaskReassignment, columns, digitalWorkers, handleAssignedToChanged, initialState.sortInfo, t, users]);

    useEffect(() => {
        if (!equals(prevFiltersRef.current, currentState.filters)) {
            prevFiltersRef.current = currentState.filters;
            columns.forEach((col, i) => {
                tableRef.current?.onFilterChange(i, currentState.filters?.[col.field]);
            });
        }
    }, [columns, currentState.filters]);

    useEffect(() => {
        dataRef.current = data;
        if (pendingDataLoadRef.current) {
            pendingDataLoadRef.current(data);
        } else {
            tableRef.current?.onQueryChange();
        }
    }, [data]);

    const handleSelectionChange = useCallback(
        (data: ContextTask[], rowData?: ContextTask) => {
            if (data.length === 0) {
                setSelection(undefined);
                selectionRef.current = undefined;
            } else {
                const newSelection = {
                    taskTypeName: data[0].typeName!,
                    contextTypeName: data[0].contextTypeName!,
                    selectedTasks: data,
                };
                setSelection(newSelection);
            }
        },
        [setSelection]
    );

    const clearSelection = useCallback(() => {
        setSelection(undefined);
    }, [setSelection]);

    const handleChangePage = useCallback(
        (pageNumber: number, pageSize: number) => {
            setSelection(undefined);
        },
        [setSelection]
    );

    const handleChangeRowsPerPage = useCallback((pageSize: number) => {}, []);

    const handleViewTaskDetails = useCallback((task: ContextTask) => {
        setTaskDetails(task);
    }, []);

    const handleResponseSelect = useCallback(
        (value: ResponseType) => {
            closeMenu();
            if (currentTask) {
                const contextType = contextTypes[currentTask.contextTypeName];
                onResponseRequested?.(contextType, currentTask, value);
            }
        },
        [closeMenu, contextTypes, currentTask, onResponseRequested]
    );

    // Important: you sould avoid the recalculation of this value as much as possible, otherwise the table performance degrades very quickly.
    const opts = useMemo(() => {
        const selectionProps = (rowData: ContextTask) => {
            if (!allowResponseCreation) {
                return {};
            }
            const ts = selectionRef.current;
            const disabled =
                (!!ts && (ts.taskTypeName !== rowData.typeName || ts.contextTypeName !== rowData.contextTypeName)) ||
                !canCreateResponsesTo(rowData);
            (rowData as any).tableData.disabled = disabled;
            return {
                disabled,
            };
        };

        const headerSelectionProps: CheckboxProps = {
            onChange: (_event: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
                const tbl = tableRef.current;
                const dm = tbl?.dataManager;
                if (!dm) {
                    return;
                }
                if (dm.selectedCount === 0 && allowResponseCreation) {
                    confirm({
                        title: null,
                        description: i18next.t("SelectAtLeastOneRowToDetermineType"),
                        cancellationButtonProps: {
                            style: {display: "none"},
                        },
                    });
                    return;
                }
                dm.changeAllSelected(checked);
                tbl.setState(dm.getRenderState(), () => tbl.onSelectionChange());
            },
        };

        const opts: Options<ContextTask> = {
            ...defaultTableOptions,
            selectionProps,
            headerSelectionProps,
            selection: allowResponseCreation || allowTaskReassignment,
            showSelectAllCheckbox: allowResponseCreation || allowTaskReassignment,
            pageSizeOptions,
            pageSize: currentState.pageSize,
            initialPage: initialState.currentPage,
        };
        return opts;
    }, [
        allowResponseCreation,
        allowTaskReassignment,
        pageSizeOptions,
        currentState.pageSize,
        initialState.currentPage,
        canCreateResponsesTo,
        confirm,
    ]);

    const actions = useMemo(() => {
        const actions: MaterialTableProps<ContextTask>["actions"] = [buildRefreshAction(() => refreshTasks())];
        if (allowFocusing) {
            actions.push({
                action: rowData => buildFocusAction(rowData, () => handleFocusedChanged(rowData)),
                position: "row",
            });
        }
        if (allowResponseCreation) {
            actions.push({
                action: rowData =>
                    buildDetailsAction(rowData, contextTypes[rowData.contextTypeName], () => {
                        handleViewTaskDetails(rowData);
                    }),
                position: "row",
            });
            actions.push(
                ...times(
                    i => ({
                        action: (rowData: ContextTask) => {
                            const ct = contextTypes[rowData.contextTypeName];
                            return buildInlineSendResponseAction(rowData, ct, i, (task, responseType) => {
                                onResponseRequested?.(ct, task, responseType);
                            });
                        },
                        position: "row",
                    }),
                    3
                )
            );
            actions.push({
                action: rowData => {
                    const ct = contextTypes[rowData.contextTypeName];
                    return buildSendResponseAction(rowData, ct, (event, task, ct) => {
                        openMenu(event, task, ct);
                    });
                },
                position: "row",
            });
        }
        return actions;
    }, [
        allowFocusing,
        allowResponseCreation,
        contextTypes,
        handleFocusedChanged,
        handleViewTaskDetails,
        onResponseRequested,
        openMenu,
        refreshTasks,
    ]);

    const components: Components = useMemo(
        () => ({
            Container: props => <div {...props} />,
            Actions: props =>
                !!selection && Array.isArray(props.data) ? (
                    <>
                        {allowTaskReassignment && (
                            <TasksViewUserAssignmentButton
                                onAssignmentChange={clearSelection}
                                users={users}
                                digitalWorkers={digitalWorkers}
                                {...props}
                            />
                        )}
                        {allowResponseCreation && (
                            <TasksViewGroupResponses onResponsesAdded={clearSelection} {...props} />
                        )}
                    </>
                ) : (
                    <MTableActions {...props} />
                ),
            Toolbar: props => (!selection || !Array.isArray(props.data) ? <div /> : <MTableToolbar {...props} />),
        }),
        [allowResponseCreation, allowTaskReassignment, clearSelection, digitalWorkers, selection, users]
    );

    const dataFetcher = useCallback(
        async (query: Query<ContextTask>) => {
            let changed = false;
            setCurrentState(s => {
                const state = queryToTasksViewState(query);
                state.filters = s.filters; //Don´t override filters, they are modified elsewhere
                changed = !equals(s, state);
                return changed ? state : s;
            });
            if (changed) {
                return new Promise<TasksViewData>((resolve, reject) => {
                    pendingDataLoadRef.current = data => {
                        resolve(data);
                        pendingDataLoadRef.current = undefined;
                    };
                });
            }
            return Promise.resolve(dataRef.current ?? defaultTasksViewData);
        },
        [setCurrentState]
    );

    const shouldDisplayMenuIcons = useMemo(() => {
        return !!responseTypes && responseTypes.some(s => !!getSettings(s)?.icon);
    }, [responseTypes]);

    return (
        <>
            <MaterialTable
                tableRef={tableRef}
                columns={mtcols}
                data={dataFetcher}
                options={opts}
                title=" "
                components={components}
                actions={actions}
                onSelectionChange={handleSelectionChange}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
            />
            <Menu {...bindMenu(actionMenuPopupState)}>
                {responseTypes?.map(s => {
                    const iconSpec = getSettings(s)?.icon;
                    return (
                        <MenuItem key={s.name} onClick={() => handleResponseSelect(s)}>
                            {shouldDisplayMenuIcons && (
                                <ListItemIcon>
                                    <MaterialFontIcon fontSize="small" family={iconSpec?.family}>
                                        {iconSpec?.name}
                                    </MaterialFontIcon>
                                </ListItemIcon>
                            )}
                            <ListItemText primary={getCommandText(s) ?? ""} />
                        </MenuItem>
                    );
                })}
            </Menu>
            {!!taskDetails && (
                <TasksViewTaskDetailDialog
                    open={!!taskDetails}
                    task={taskDetails}
                    contextType={contextTypes[taskDetails.contextTypeName]}
                    onClose={() => setTaskDetails(undefined)}
                />
            )}
        </>
    );
});
