import React, {FC, useState, useCallback, useMemo, useRef} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField, {TextFieldProps} from "@material-ui/core/TextField";
import {isPresent} from "ts-is-present";
import i18next from "i18next";
import {equals} from "ramda";

import {
    TasksViewState,
    TaskViewColumn,
    TaskViewColumnNames,
    TasksViewFilterCandidate,
    TaskViewFilter,
    FilterOf,
} from "./tasks-view";
import {useTasksViewService} from "./tasks-view-service";
import {ErrorBoundary} from "../error-boundary";
import {DateRangeFilter} from "./tasks-view-filter-date-range";
import {SelectionFilter} from "./tasks-view-filter-selection";
import {BooleanFilter} from "./tasks-view-filter-boolean";
import {FilterIcon} from "../icons/filter-icon";
import {PrefixTextMatchFilter} from "./tasks-view-filter-prefix-text-match";
import {TasksViewFilterChip} from "./tasks-view-filter-chip";

const markerSize = {
    width: 20,
    height: 20,
};

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        filter: {
            marginRight: 20,
        },
        filterIconContainer: {
            position: "relative",
        },
        filteringActiveIcon: {
            position: "absolute",
            fontSize: 12,
            right: -5,
            top: -20,
        },
        filterAppliedIcon: {
            fontSize: "1rem",
            verticalAlign: "text-bottom",
            ...markerSize,
        },
        clearIconContainer: {
            position: "relative",
        },
        clearButton: {
            position: "absolute",
            right: -35,
        },
        appliedFilterMarker: {
            display: "inline-block",
            ...markerSize,
        },
        appliedFilterMarkerClearButton: {
            padding: 0,
            ...markerSize,
        },
        chip: {
            marginRight: 5,
        },
    })
);

export interface TasksViewFilterEntryEditorProps<T extends TaskViewFilter<any>> {
    column: TaskViewColumn;
    value: T | undefined;
    onChange?: (value: T | undefined) => void;
    getCandidates: () => Promise<TasksViewFilterCandidate[]>;
}

type FilterEditorMap = {[key in TaskViewColumnNames]?: React.ComponentType<TasksViewFilterEntryEditorProps<any>>};

const filterEditorMap: FilterEditorMap = {
    contextReference: PrefixTextMatchFilter,
    title: PrefixTextMatchFilter,
    assignedTo: SelectionFilter,
    dueDate: DateRangeFilter,
    contextTypeName: SelectionFilter,
    typeName: SelectionFilter,
    focused: BooleanFilter,
    status: SelectionFilter,
    dateCreated: DateRangeFilter,
};

const getColumnTitle = (col: TaskViewColumn) => {
    return col.title ?? i18next.t(col.field.toUpperCase()[0] + col.field.substring(1)) ?? "";
};

export interface TasksViewFilterProps {
    columns: TaskViewColumn[];
    initialState: TasksViewState;
}

export const TasksViewFilter: FC<TasksViewFilterProps> = props => {
    const {columns} = props;
    const classes = useStyles();
    const {currentState, setCurrentState, getColumnCandidates} = useTasksViewService();
    const [selectedColumn, setSelectedColumn] = useState<TaskViewColumn>(columns[0]);

    const handleSelectionChange = useCallback(
        (ev: React.ChangeEvent<any>) => {
            setSelectedColumn(columns.find(s => s.field === ev.target.value) ?? columns[0]);
        },
        [columns]
    );

    const changeFilterValue = useCallback(
        (field: TaskViewColumnNames, value: any) => {
            setCurrentState(s => {
                let filters = s.filters;
                if (isPresent(value)) {
                    filters = {
                        ...filters,
                        [field]: value,
                    };
                } else if (isPresent(filters)) {
                    delete filters[field];
                }
                const state = {
                    ...s,
                    filters,
                };
                if (isPresent(filters) && Object.keys(filters).length === 0) {
                    delete state.filters;
                }
                return state;
            });
        },
        [setCurrentState]
    );

    const handleFilterValueChanged = useCallback(
        (value: any) => {
            changeFilterValue(selectedColumn.field, value);
        },
        [changeFilterValue, selectedColumn.field]
    );

    const handleFilterCleared = useCallback(
        (field: TaskViewColumnNames) => {
            changeFilterValue(field, undefined);
            if (field === selectedColumn.field) {
            }
        },
        [changeFilterValue, selectedColumn]
    );

    const selectedColumnFilterCache = useRef<FilterOf<any>>({});
    const selectedColumnFilter = useMemo(() => {
        const filter = {...(currentState.filters ?? {})};
        delete filter[selectedColumn.field];
        if (!equals(selectedColumnFilterCache.current, filter)) {
            selectedColumnFilterCache.current = filter;
        }
        return selectedColumnFilterCache.current;
    }, [currentState.filters, selectedColumn.field]);

    const getCandidates = useCallback(async () => {
        if (!selectedColumn) {
            return [];
        }
        return await getColumnCandidates(selectedColumn, selectedColumnFilter);
    }, [getColumnCandidates, selectedColumn, selectedColumnFilter]);


    const EditorComponent = filterEditorMap[selectedColumn.field];
    const filters = currentState.filters ?? {};
    const selectedFilterValue = filters[selectedColumn.field];

    const inputProps: TextFieldProps["InputProps"] = useMemo(
        () => ({
            startAdornment: (
                <InputAdornment position="start" className={classes.filterIconContainer}>
                    <FilterIcon fontSize="small" />
                    {/* {currentState.filters && Object.keys(currentState.filters).length > 0 && (
                        <CheckIcon className={classes.filteringActiveIcon} />
                    )} */}
                </InputAdornment>
            ),
        }),
        [classes]
    );

    return (
        <Grid container spacing={3} alignItems="flex-start">
            <Grid item xs={12}>
                {columns
                    .filter(s => s.field in filters)
                    .map(col => (
                        <TasksViewFilterChip
                            key={col.field}
                            title={getColumnTitle(col)}
                            field={col.field}
                            value={filters[col.field]}
                            onClearFilter={handleFilterCleared}
                            className={classes.chip}
                        />
                    ))}
            </Grid>
            <Grid item xs={2}>
                <TextField
                    select
                    value={selectedColumn.field}
                    onChange={handleSelectionChange}
                    fullWidth
                    InputProps={inputProps}
                    className={classes.filter}
                >
                    {columns.map(s => (
                        <MenuItem key={s.field} value={s.field}>
                            {getColumnTitle(s)}
                        </MenuItem>
                    ))}
                </TextField>
            </Grid>
            <Grid item xs={10}>
                <ErrorBoundary>
                    {!!EditorComponent && (
                        <EditorComponent
                            column={selectedColumn}
                            value={selectedFilterValue}
                            onChange={handleFilterValueChanged}
                            getCandidates={getCandidates}
                        />
                    )}
                </ErrorBoundary>
            </Grid>
        </Grid>
    );
};
