import React, {FC, useCallback} from "react";

import {DashboardParameter, DashboardParameterState} from "../../api/nimflow-dashboard-api";
import {MultipleSelectionParameterEditor} from "./multiple-selection-paramater-editor";
import {FreeTextParameterEditor} from "./free-text-parameter-editor";
import {TimeRangeParameterEditor} from "./time-range-parameter-editor";
import {SingleSelectionParameterEditor} from "./single-selection-parameter-editor";

export interface ParameterEditorProps<T extends DashboardParameter> {
    parameter: T;
    value: DashboardParameterState;
    onChange?: (value: DashboardParameterState, parameter: T) => void;
}

export interface DashboardParameterViewerProps {
    parameter: DashboardParameter;
    value: DashboardParameterState;
    onChange?: (value: DashboardParameterState, parameter: DashboardParameter) => void;
}

const componentMap: Record<string, React.ComponentType<any>> = {    
    "freeText": FreeTextParameterEditor,
    "timeRange": TimeRangeParameterEditor,
    "single": SingleSelectionParameterEditor,
    "multiple": MultipleSelectionParameterEditor
};

export const DashboardParameterViewer: FC<DashboardParameterViewerProps> = props => {
    const {parameter, value, onChange} = props;

    const handleChange = useCallback((value: DashboardParameterState, parameter: DashboardParameter) => {
        onChange?.(value, parameter);
    }, [onChange]);

    const Component = componentMap[parameter.selectionType];
    if (!Component) {
        return null;
    }

    return <Component parameter={parameter} value={value} onChange={handleChange} />
};
