import React, {FC, useRef, useEffect} from "react";
import {Opts, TDate, cancel} from "timeago.js";
import {render, format} from "../libs/timeago";

export type TimeAgoProps = {
    readonly datetime: TDate; // date to be formatted
    readonly live?: boolean; // real time render.
    readonly opts?: Opts;
} & Omit<React.TimeHTMLAttributes<HTMLTimeElement>, "dateTime">;

/**
 * Convert input to a valid datetime string of <time> tag
 * https://developer.mozilla.org/en-US/docs/Web/HTML/Element/time
 * @param input
 * @returns datetime string
 */
const toDateTime = (input: TDate): string => {
    return "" + (input instanceof Date ? input.getTime() : input);
};

export const TimeAgo: FC<TimeAgoProps> = props => {
    const {datetime, live, opts} = props;
    const timeRef = useRef<HTMLTimeElement>(null);

    useEffect(() => {
        const dom = timeRef.current;
        if (!dom) {
            return;
        }
        if (live !== false) {
            dom.setAttribute("datetime", toDateTime(datetime));
            render(dom, opts);
        }
        return () => cancel(dom);
    }, [datetime, live, opts]);

    return <time ref={timeRef}>{format(datetime, opts)}</time>;
};
