import React, {FC, useCallback, useState, useMemo} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import DoneIcon from "@material-ui/icons/Done";
import i18next from "i18next";
import {subMinutes, format, parseISO, isSameDay} from "date-fns";
import Popover from "@material-ui/core/Popover";
import {usePopupState, bindTrigger, bindPopover} from "material-ui-popup-state/hooks";
import {clone, equals} from "ramda";

import {
    TimeRangeDashboardParameter,
    DynamicTimeRangeUnit,
    DashboardParameterTimeRangeValue,
    DynamicDashboardParameterTimeRangeValue,
    StaticDashboardParameterTimeRangeValue,
} from "../../api/nimflow-dashboard-api";
import {StaticTimeRangeParameterEditorDialog} from "./static-time-range-parameter-editor-dialog";
import {DashboardParameterPill} from "./dashboard-parameter-pill";
import {ParameterEditorProps} from "./dashboard-parameter-viewer";
import {resolveDynamicTimeRange} from "./dashboard-context";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        listItemIcon: {
            minWidth: 30,
        },
    })
);
export interface TimeRangeParameterEditorProps extends ParameterEditorProps<TimeRangeDashboardParameter> {}

interface TimeRangeOption {
    id: string;
    label: string;
    value: DashboardParameterTimeRangeValue;
}

const makeDynamicTimeRangeUnit = (count: number, unit: DynamicTimeRangeUnit): TimeRangeOption => {
    const id = `Last${count}${unit}`;
    return {
        id,
        label: i18next.t(`Last${unit}`, {count}),
        value: {
            type: "dynamic",
            range: {
                count,
                unit,
            },
        },
    };
};

const options: TimeRangeOption[] = [
    {
        id: "CustomTimeRange",
        label: i18next.t("CustomTimeRange") as string,
        value: {type: "static", range: {start: "", end: ""}},
    },
    makeDynamicTimeRangeUnit(15, "minutes"),
    makeDynamicTimeRangeUnit(30, "minutes"),
    makeDynamicTimeRangeUnit(1, "hours"),
    makeDynamicTimeRangeUnit(3, "hours"),
    makeDynamicTimeRangeUnit(6, "hours"),
    makeDynamicTimeRangeUnit(12, "hours"),
    makeDynamicTimeRangeUnit(24, "hours"),
    makeDynamicTimeRangeUnit(2, "days"),
    makeDynamicTimeRangeUnit(7, "days"),
    makeDynamicTimeRangeUnit(14, "days"),
    makeDynamicTimeRangeUnit(30, "days"),
    makeDynamicTimeRangeUnit(60, "days"),
];

const getValueFromOptions = (option: TimeRangeOption) => {
    let value = clone(option.value);
    if (value.type === "static") {
        const now = new Date();
        const prev = subMinutes(now, 15);
        value.range = {start: prev.toISOString(), end: now.toISOString()};
    }
    return value;
};

const makeStaticValueFromState = (value: DashboardParameterTimeRangeValue): StaticDashboardParameterTimeRangeValue => {
    if (value.type === "static") {
        return clone(value);
    }
    const [from, to] = resolveDynamicTimeRange(value);
    return {type: "static", range: {start: from.toISOString(), end: to.toISOString()}};
};

const getValueDescription = (value: DashboardParameterTimeRangeValue): string => {
    if (value.type === "dynamic") {
        return makeDynamicTimeRangeUnit(value.range.count, value.range.unit).label;
    }
    const [from, to] = [parseISO(value.range.start), parseISO(value.range.end)];
    const dateFormat = i18next.t("DATE_FORMAT");
    const timeFormat = i18next.t("TIME_FORMAT");
    if (isSameDay(from, to)) {
        return i18next.t("SameDateTimeRangeDescription", {
            date: format(from, dateFormat),
            startTime: format(from, timeFormat),
            endTime: format(to, timeFormat),
        });
    }
    return i18next.t("TimeRangeDescription", {
        startDate: format(from, dateFormat),
        startTime: format(from, timeFormat),
        endDate: format(to, dateFormat),
        endTime: format(to, timeFormat),
    });
};

export const TimeRangeParameterEditor: FC<TimeRangeParameterEditorProps> = props => {
    const {parameter, value: initialValue, onChange} = props;
    const classes = useStyles();
    const [value, setValue] = useState(() => {
        const val = initialValue.values[0]?.value as DashboardParameterTimeRangeValue;
        return val ?? {type: "dynamic", range: {count: 15, unit: "minutes"}};
    });
    const [selectedOption, setSelectedOption] = useState<TimeRangeOption>(() => {
        const defvalue = value;
        if (defvalue.type === "static") {
            return options[0];
        }
        const range = defvalue.range;
        for (let i = 1; i < options.length; i++) {
            const opt = options[i];
            const optrange = opt.value.range as DynamicDashboardParameterTimeRangeValue["range"];
            if (equals(range, optrange)) {
                return opt;
            }
        }
        return options[1];
    });
    const [customTimeRange, setCustomTimeRange] = useState<StaticDashboardParameterTimeRangeValue>();
    const popupState = usePopupState({
        variant: "popover",
        popupId: `paramPopup_${parameter.id}`,
    });

    const handleListItemClick = useCallback(
        (event: React.MouseEvent<HTMLDivElement, MouseEvent>, option: TimeRangeOption) => {
            popupState.close();
            if (option === options[0]) {
                let range = makeStaticValueFromState(value);
                setCustomTimeRange(range);
                return;
            }
            if (selectedOption === option) {
                return;
            }
            setSelectedOption(option);
            const newValue = getValueFromOptions(option);
            setValue(newValue);
            onChange?.({selectAll: false, values: [{value: newValue}]}, parameter);
        },
        [onChange, parameter, popupState, selectedOption, value]
    );

    const handleChange = useCallback(
        (value: StaticDashboardParameterTimeRangeValue) => {
            setValue(value);
            setSelectedOption(options[0]);
            onChange?.({selectAll: false, values: [{value}]}, parameter);
        },
        [onChange, parameter]
    );

    const handleClose = useCallback(() => {
        setCustomTimeRange(undefined);
    }, []);

    const description = useMemo(() => {
        return getValueDescription(value);
    }, [value]);

    return (
        <>
            <DashboardParameterPill parameter={parameter} valueDescription={description} {...bindTrigger(popupState)} />
            <Popover
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                }}
                {...bindPopover(popupState)}
            >
                <List component="nav">
                    {options.map(s => (
                        <ListItem
                            key={s.id}
                            button
                            selected={s === selectedOption}
                            onClick={event => handleListItemClick(event, s)}
                            dense={true}
                        >
                            <ListItemIcon className={classes.listItemIcon}>
                                {s === selectedOption ? <DoneIcon /> : null}
                            </ListItemIcon>
                            <ListItemText primary={s.label} primaryTypographyProps={{variant: "body2"}} />
                        </ListItem>
                    ))}
                </List>
            </Popover>

            {!!customTimeRange && (
                <StaticTimeRangeParameterEditorDialog
                    open={!!customTimeRange}
                    value={customTimeRange}
                    onChange={handleChange}
                    onClose={handleClose}
                />
            )}
        </>
    );
};
