import React, {FC, useMemo, useState, useCallback, KeyboardEvent} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import IconButton from "@material-ui/core/IconButton";
import ClearIcon from "@material-ui/icons/Clear";
import {useTranslation} from "react-i18next";

import {
    TaskAssignmentView,
    TasksViewUserAssignmentEditor,
    TasksViewUserAssignmentEditorProps,
    getTaskAssignment,
    getTaskAssignmentDescription,
    TaskAssignment,
    getTaskAssignmentId,
} from "./tasks-view-user-assignment-editor";
import {DigitalWorkerData, User} from "../../api/nimflow-api";
import {useOkCancelConfirm} from "../../components/confirm";

const assignedToWidths = "13rem";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        view: {
            width: assignedToWidths,
            borderRadius: theme.shape.borderRadius,
            padding: theme.spacing(1),
            overflowX: "hidden",
            borderWidth: 1,
            borderStyle: "solid",
            borderColor: "transparent",
            "&:hover": {
                borderColor: theme.palette.divider,
            },
        },
        container: {
            position: "relative",
            "&:hover .clear-button": {
                visibility: "visible",
            },
        },
        clearButton: {
            position: "absolute",
            left: -40,
            top: 0,
            visibility: "hidden",
        },
    })
);
export type TasksViewColumnUserAssignmentEditorProps = {
    value?: string;
    onChange?: (value?: string) => void;
    users?: User[];
    digitalWorkers?: DigitalWorkerData[];
} & Omit<TasksViewUserAssignmentEditorProps<true, true>, "value" | "onChange">;

export const TasksViewColumnUserAssignmentEditor: FC<TasksViewColumnUserAssignmentEditorProps> = props => {
    const {value, onChange, users, digitalWorkers, ...rest} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useOkCancelConfirm();
    const [editing, setEditing] = useState(false);
    const taskAssignment = useMemo(() => {
        return value ? getTaskAssignment(value, users, digitalWorkers) : undefined;
    }, [value, users, digitalWorkers]);

    const handleOnClick = useCallback(() => {
        setEditing(true);
    }, [setEditing]);

    const handleOnClickAway = useCallback(() => {
        setEditing(false);
    }, [setEditing]);

    const handleKeyUp = useCallback(
        (event: KeyboardEvent<HTMLDivElement>) => {
            if (event.key === "Escape") {
                setEditing(false);
            }
        },
        [setEditing]
    );

    const handleOnChange = useCallback(
        (taskAssignment?: TaskAssignment) => {
            setEditing(false);
            onChange?.(taskAssignment ? getTaskAssignmentId(taskAssignment) : undefined);
        },
        [onChange]
    );

    const handleClear = useCallback(async () => {
        setEditing(false);
        const name = getTaskAssignmentDescription(taskAssignment);
        let result = await confirm({description: t("ConfirmClearAssignment", {name})});
        if (result) {
            onChange?.(undefined);
        }
    }, [onChange, taskAssignment, confirm, t]);

    if (!editing) {
        return (
            <div className={classes.container}>
                <div className={classes.view}>
                    <TaskAssignmentView
                        taskAssignment={taskAssignment ?? {type: "unknown", id: value ?? "\u00A0"}}
                        onClick={handleOnClick}
                    />
                </div>
                {!!value && (
                    <IconButton onClick={handleClear} className={`${classes.clearButton} clear-button`}>
                        <ClearIcon />
                    </IconButton>
                )}
            </div>
        );
    }

    return (
        <ClickAwayListener onClickAway={handleOnClickAway}>
            <TasksViewUserAssignmentEditor
                value={taskAssignment}
                onChange={handleOnChange}
                users={users}
                digitalWorkers={digitalWorkers}
                onKeyUp={handleKeyUp}
                disableClearable
                {...rest}
            />
        </ClickAwayListener>
    );
};
