import React, {FC, useMemo, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import Autocomplete, {AutocompleteProps, AutocompleteRenderInputParams} from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import i18next from "i18next";

import {removeDiacritics} from "../../utils/string-utils";
import {DigitalWorkerData, User} from "../../api/nimflow-api";

const assignedToWidths = "13rem";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        small: {
            width: theme.spacing(3),
            height: theme.spacing(3),
        },
        view: {
            width: assignedToWidths,
            borderRadius: theme.shape.borderRadius,
            padding: theme.spacing(1),
            overflowX: "hidden",
            "&:hover": {
                borderWidth: 1,
                borderStyle: "solid",
                borderColor: theme.palette.divider,
            },
        },
        textFieldRoot: {
            width: assignedToWidths,
            "& .MuiInput-root": {
                fontFamily: "inherit",
                fontSize: "inherit",
                fontWeight: "inherit",
            },
        },
        container: {
            position: "relative",
            "&:hover .clear-button": {
                visibility: "visible",
            },
        },
        clearButton: {
            position: "absolute",
            left: -40,
            top: 0,
            visibility: "hidden",
        },
    })
);

export type TaskAssignment =
    | {
        type: "user";
        user: User;
      }
    | {
        type: "digital-worker";
        digitalWorker: DigitalWorkerData;
      }
    | {
        type: "any-user";
      }
    | {
        type: "unknown";
        id: string;
    };

export const getTaskAssignment = (
    assignedTo: string,
    users?: User[],
    digitalWorkers?: DigitalWorkerData[]
): TaskAssignment | undefined => {
    if (assignedTo === "*") {
        return {
            type: "any-user",
        };
    }
    if (assignedTo.startsWith("dw:")) {
        const id = assignedTo.substring(3);
        const digitalWorker = digitalWorkers?.find(s => s.id === id);
        if (digitalWorker) {
            return {
                type: "digital-worker",
                digitalWorker,
            };
        }
    } else {
        const user = users?.find(s => s.id === assignedTo || s.email === assignedTo);
        if (user) {
            return {
                type: "user",
                user,
            };
        }
    }
    if (assignedTo) {
        return {
            type: "unknown",
            id: assignedTo,
        };
    }
    return undefined;
};

export function getTaskAssignmentId(taskAssignment: TaskAssignment | undefined): string | undefined;
export function getTaskAssignmentId(taskAssignment: TaskAssignment): string;
export function getTaskAssignmentId(taskAssignment: TaskAssignment| undefined): string | undefined {
    if(!taskAssignment) {
        return undefined;
    }
    switch (taskAssignment.type) {
        case "any-user":
            return "*";
        case "digital-worker":
            return `dw:${taskAssignment.digitalWorker.id}`;
        case "user":
            return taskAssignment.user.email!;
        case "unknown":
            return taskAssignment.id;
    }
};

export const getTaskAssignmentDescription = (taskAssignment: TaskAssignment | undefined) => {
    switch (taskAssignment?.type) {
        case "any-user":
            return i18next.t("AnyUser");
        case "digital-worker":
            return taskAssignment.digitalWorker.name;
        case "user":
            return taskAssignment.user.fullName ?? taskAssignment.user.email;
        case "unknown":
            return taskAssignment.id;
    }
    return "";
};

const taskAssignmentMatches = (assignment: TaskAssignment, filter: string) => {
    switch (assignment.type) {
        case "any-user":
            return filter === "*";
        case "digital-worker":
            const name = assignment.digitalWorker.name;
            return name && name.includes(filter);
        case "user":
            const fullName = assignment.user.fullName;
            const email = assignment.user.email;
            return (fullName && fullName.includes(filter)) || (email && email.includes(filter));
    }
    return false;
};

export interface TaskAssignmentViewProps {
    taskAssignment: TaskAssignment;
    onClick?: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

export const TaskAssignmentView: FC<TaskAssignmentViewProps> = props => {
    const {taskAssignment, onClick} = props;
    const classes = useStyles();

    return (
        <Grid container spacing={1} alignItems="center" onClick={onClick} wrap="nowrap">
            {taskAssignment.type === "user" && (
                <Grid item>
                    <Tooltip title={taskAssignment.user.email ?? ""}>
                        <Avatar alt={taskAssignment.user.fullName} src={taskAssignment.user.props?.Picture} className={classes.small} />
                    </Tooltip>
                </Grid>
            )}
            <Grid item xs>
                {getTaskAssignmentDescription(taskAssignment)}
            </Grid>
        </Grid>
    );
};

export type TasksViewUserAssignmentEditorProps<
    DisableClearable extends boolean | undefined,
    FreeSolo extends boolean | undefined
> = {
    value?: TaskAssignment;
    onChange?: (value?: TaskAssignment) => void;
    users?: User[];
    digitalWorkers?: DigitalWorkerData[];
    renderInput?: (params: AutocompleteRenderInputParams) => React.ReactNode;
} & Omit<
    AutocompleteProps<TaskAssignment, false, DisableClearable, FreeSolo>,
    "defaultValue" | "onChange" | "children" | "options" | "renderInput"
>;

export const TasksViewUserAssignmentEditor = React.forwardRef(<
    DisableClearable extends boolean | undefined = undefined,
    FreeSolo extends boolean | undefined = undefined
>(
    props: TasksViewUserAssignmentEditorProps<DisableClearable, FreeSolo>, ref: React.ForwardedRef<unknown>
) => {
    const {value, onChange, onBlur, users, digitalWorkers, renderInput, freeSolo, ...rest} = props;
    const classes = useStyles();
    const candidates = useMemo(() => {
        const result: TaskAssignment[] = [];
        if (users) {
            const userCandidates = users
                .filter(s => s.id && s.email)
                .map(s => {
                    const result: TaskAssignment = {
                        type: "user",
                        user: s,
                    };
                    return result;
                });
            result.push(...userCandidates);
        }
        if (digitalWorkers) {
            const dwCandidates = digitalWorkers
                .filter(s => s.id)
                .map(s => {
                    const result: TaskAssignment = {
                        type: "digital-worker",
                        digitalWorker: s,
                    };
                    return result;
                });
            result.push(...dwCandidates);
        }
        result.unshift({
            type: "any-user",
        });
        return result;
    }, [users, digitalWorkers]);

    const selectedCandidate = useMemo(() => {
        return candidates.find(s => {
            switch (s.type) {
                case "any-user":
                    return value?.type === "any-user";
                case "digital-worker":
                    return value?.type === "digital-worker" && s.digitalWorker.id === value.digitalWorker.id;
                case "user":
                    return value?.type === "user" && s.user.id === value.user.id;
            }
            return false;
        });
    }, [candidates, value]);

    const handleFocus = useCallback((event: React.FocusEvent<HTMLInputElement>) => {
        event.preventDefault();
        const {target} = event;
        setTimeout(() => target.setSelectionRange(0, target.value.length), 0);
    }, []);

    return (
        <Autocomplete
            ref={ref}
            freeSolo={freeSolo ?? true}
            blurOnSelect
            options={candidates ?? []}
            value={selectedCandidate as any}
            onChange={(_event, newValue, reason) => {
                if (typeof newValue === "object" && reason === "select-option") {
                    onChange?.((newValue as any) ?? undefined);
                }
            }}
            getOptionLabel={s => getTaskAssignmentDescription(s) ?? ""}
            filterOptions={(options, state) => {
                const filterStr = removeDiacritics(state.inputValue).toLowerCase();
                if (!filterStr) {
                    return options;
                }
                return options.filter(s => taskAssignmentMatches(s, filterStr));
            }}
            renderInput={
                renderInput ??
                (params => (
                    <TextField
                        {...params}
                        autoFocus
                        margin="dense"
                        onFocus={handleFocus}
                        classes={{root: classes.textFieldRoot}}
                    />
                ))
            }
            renderOption={user => <TaskAssignmentView taskAssignment={user} />}
            {...rest}
        />
    );
});
