import React, {FC, useCallback, useMemo, useState} from "react";
import {useTranslation} from "react-i18next";
import useSWR from "swr";
import {uniq, groupBy, mapObjIndexed} from "ramda";
import MaterialTable, {Column} from "@material-table/core";
import Avatar from "@material-ui/core/Avatar";
import Checkbox from "@material-ui/core/Checkbox";
import {useRecoilValue} from "recoil";

import {PermissionData, User} from "../../api/nimflow-business-directory-api";
import {UserRole, standardUserRoles} from "../../utils/authorization";
import {useNimflow} from "../../api/nimflow-api-provider";
import {useNimflowBusinessDirectory, currentUnitIdState} from "../../api/nimflow-business-directory-provider";
import {useKey} from "../../utils/use-key";

export interface UserRoles {
    user: User;
    roles: UserRole[];
}

const useUsers = () => {
    const {users_GetUsersWithPermission} = useNimflow();
    const {data, isValidating, mutate, error} = useSWR(`/users_GetUsersWithPermission/`, async () => {
        const result = await users_GetUsersWithPermission({
            pageSize: 100,
            pageNumber: 0,
        });
        return result;
    });
    return useMemo(() => {
        return {
            users: data ?? [],
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

const usePermissions = (currentUnitId?: string) => {
    const {permissions_Search} = useNimflowBusinessDirectory();
    const key = useKey(permissions_Search, currentUnitId);
    const {data, mutate, isValidating, error} = useSWR(key, async () => {
        if (!currentUnitId) {
            return undefined;
        }
        const permissions = await permissions_Search({
            entityType: "Unit",
            entityId: currentUnitId,
        });
        const result: Record<string, PermissionData> = mapObjIndexed(
            (s: typeof permissions) => s[0],
            groupBy(s => s.userId ?? "", permissions)
        );
        return result;
    });
    return useMemo(() => {
        return {
            permissions: data ?? {},
            isValidating,
            error,
            revalidate: mutate,
            mutatePermissionsSearch: mutate,
        };
    }, [data, error, isValidating, mutate]);
};

export const UnitSecurity: FC<{}> = () => {
    const {t} = useTranslation();
    const currentUnitId = useRecoilValue(currentUnitIdState);
    const {permissions_Set} = useNimflowBusinessDirectory();
    const {users} = useUsers();
    const {permissions, mutatePermissionsSearch} = usePermissions(currentUnitId);

    const userRoles = useCallback(
        (user: User): UserRole[] => {
            if (user?.id == null) {
                return [];
            }
            const userPermissions = permissions[user.id];
            if (!userPermissions) {
                return [];
            }
            const knownRoles = Object.values(standardUserRoles);
            return knownRoles.filter(knownRole =>
                knownRole.permissions.find(
                    permission => userPermissions.actions && userPermissions.actions.indexOf(permission) >= 0
                )
            );
        },
        [permissions]
    );

    const userRolesData = useMemo(() => {
        return users.map(user => {
            return {
                user,
                roles: userRoles(user),
            };
        });
    }, [users, userRoles]);

    const userDataRender = (user: User) => (
        <>
            <Avatar alt={user.fullName} src={user.props?.Picture} />
            <p>{user.fullName}</p>
            {user.email !== user.fullName && <p>{user.email}</p>}
        </>
    );

    const standardUserRoleValues = Object.values(standardUserRoles);

    const [updating, setUpdating] = useState(false);

    const handleRoleChange = useCallback(
        async (userRoles: UserRoles, role: UserRole, checked: boolean) => {
            let newRoles = userRoles.roles.filter(s => s.name !== role.name);
            if (checked) {
                newRoles = [...newRoles, role];
            }
            let actions = uniq(newRoles.flatMap(roles => roles.permissions));
            setUpdating(true);
            try {
                await permissions_Set({
                    userIds: [userRoles.user.id as string],
                    entityType: "Unit",
                    entityId: currentUnitId as string,
                    actions: actions,
                });
            } finally {
                mutatePermissionsSearch();
                setUpdating(false);
            }
        },
        [currentUnitId, permissions_Set, mutatePermissionsSearch]
    );

    const userRoleRender = (rowData: UserRoles, role: UserRole) => {
        return (
            <Checkbox
                disabled={updating}
                checked={rowData.roles.find(assignedRole => assignedRole.name === role.name) != null}
                onChange={e => handleRoleChange(rowData, role, e.target.checked)}
                color="primary"
            />
        );
    };

    const columns: Column<UserRoles>[] = [
        {field: "user.fullName", render: rowData => userDataRender(rowData.user), title: t("User")},
        ...standardUserRoleValues.map(role => ({
            render: (rowData: UserRoles) => userRoleRender(rowData, role),
            title: t(role.name),
        })),
    ];

    const actions = useMemo(() => {
        return [];
    }, []);

    return (
        <>
            <MaterialTable columns={columns} data={userRolesData} title={t("UnitMembers")} actions={actions} />
        </>
    );
};
