import React, {FC} from "react";
import Typography from "@material-ui/core/Typography/Typography";
import Breadcrumbs, {BreadcrumbsProps} from "@material-ui/core/Breadcrumbs";
import Link from "@material-ui/core/Link";
import {useNavigate} from "react-router-dom";
import {useTranslation} from "react-i18next";

import {useDashboardContext} from "./dashboard-context";

export interface DashboardBreadcrumbsProps extends BreadcrumbsProps {}

export const DashboardBreadcrumbs: FC<DashboardBreadcrumbsProps> = props => {
    const {t} = useTranslation();
    const navigate = useNavigate();
    const {dashboard} = useDashboardContext();

    return (
        <Breadcrumbs
            separator={
                <Typography variant="h5" color="textPrimary" gutterBottom>
                    &gt;
                </Typography>
            }
            aria-label="breadcrumb"
        >
            <Typography variant="h5" color="textPrimary" gutterBottom>
                {t("Dashboards")}
            </Typography>
            <Link color="inherit" href="#" onClick={() => navigate(`../${dashboard.id}`)}>
                <Typography variant="h5" color="textPrimary" gutterBottom>
                    {dashboard.name}
                </Typography>
            </Link>
        </Breadcrumbs>
    );
};
