import {FC, Suspense} from "react";
import {Routes, Route} from "react-router-dom";
import {useTranslation} from "react-i18next";

import {Shell} from "./features/shell/shell";
import {PermissionRestrictedRoute} from "./components/permission-restricted-route";
import {permissions, permissionRequests} from "./utils/authorization";
import {DispatchActionStepper} from "./features/dispatch-actions/dispatch-action-stepper";
import {WaitSpinner} from "./components/wait-spinner";
import {UnitSecurity} from "./features/unit-admin/unit-security";
import {TasksPage} from "./features/tasks-page";
import {TasksViewer} from "./features/tasks";
import {TeamTasksViewer} from "./features/team-tasks";
import {DashboardViewerPage} from "./features/dashboards/dashboard-viewer-page";
import {Security} from "./features/security/security";
import {SystemFeature, useSystemInfo} from "./services/system-info-service";

const publicUrl = process.env.PUBLIC_URL;

const {readTasks} = permissions;

const {workMyTasks, manageTeamTasks, dispatchActions, manage} = permissionRequests;

export const App: FC<{}> = () => {
    const {t} = useTranslation();
    const {isFeatureSupported} = useSystemInfo();

    const spinner = <WaitSpinner>{t("SuspenseLoading")}</WaitSpinner>;

    return (
        <Suspense fallback={spinner}>
            <Routes basename={publicUrl}>
                <Route path="/" element={<Shell />}>
                    <PermissionRestrictedRoute permissions={workMyTasks} path="tasks" element={<TasksViewer />} />
                    <PermissionRestrictedRoute
                        permissions={manageTeamTasks}
                        path="team-tasks"
                        element={<TeamTasksViewer />}
                    />
                    {/* Permission to assign tasks to self? */}
                    <PermissionRestrictedRoute permissions={readTasks} path="unassigned-tasks" element={<div></div>} />
                    <PermissionRestrictedRoute
                        permissions={dispatchActions}
                        path="dispatch-action"
                        element={<DispatchActionStepper />}
                    />
                    <PermissionRestrictedRoute permissions={manage} path="unit-security" element={<UnitSecurity />} />
                    <PermissionRestrictedRoute permissions={workMyTasks} path="tasks-page" element={<TasksPage />} />
                    <Route path="dashboard/:dashboardId" element={<DashboardViewerPage />} />
                    {isFeatureSupported(SystemFeature.Security) && (
                        <PermissionRestrictedRoute permissions={manage} path="security" element={<Security />} />
                    )}
                </Route>
            </Routes>
        </Suspense>
    );
};
