import {useCallback, useMemo, createContext, FC, useContext, useRef, useState, Dispatch, SetStateAction} from "react";
import useSWR from "swr";
import {useRecoilCallback} from "recoil";
import {equals, uniq} from "ramda";
import {isPresent} from "ts-is-present";

import {ContextTask, ContextType, ResponseType, Stages} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";
import {taskBlockedState} from "./tasks-view-task-service";
import {
    TasksViewState,
    TasksViewData,
    TaskViewColumn,
    TasksViewFilterCandidate,
    FilterOf,
    TaskViewFilter,
} from "./tasks-view";
import {useAuthorization} from "../authorization-context";
import {permissions} from "../../utils/authorization";
import {useAuthenticationService} from "../../features/auth/authentication-service";

export interface TasksViewService {
    data: TasksViewData;
    contextTypes: Record<string, ContextType>;
    currentState: TasksViewState;
    updateTask: (task: ContextTask, updates: Partial<ContextTask>) => Promise<void>;
    refreshTasks: () => Promise<any>;
    addResponse: (task: ContextTask, responseType: ResponseType, payload: any, startedOn: Date | undefined) => Promise<any>;
    setCurrentState: Dispatch<SetStateAction<TasksViewState>>;
    canCreateReponses: () => boolean;
    canReassignUsers: () => boolean;
    canCreateResponsesTo: (task: ContextTask) => boolean;
    getColumnCandidates: (
        column: TaskViewColumn,
        filters?: FilterOf<ContextTask>
    ) => Promise<TasksViewFilterCandidate[]>;
}

const stub = () => {
    throw new Error("You forgot to wrap your component in <TasksViewServiceProvider>.");
};

export const defaultTasksViewData: TasksViewData = Object.freeze({data: [], page: 0, totalCount: 0});
export const defaultContextTypes: Record<string, ContextType> = Object.freeze({});

const initialTasksViewServiceContext: TasksViewService = {
    data: defaultTasksViewData,
    contextTypes: defaultContextTypes,
    currentState: {
        currentPage: 0,
        pageSize: 0,
    },
    updateTask: stub,
    refreshTasks: stub,
    addResponse: stub,
    setCurrentState: stub,
    canCreateReponses: stub,
    canReassignUsers: stub,
    canCreateResponsesTo: stub,
    getColumnCandidates: stub,
};

const TasksViewServiceContext = createContext<TasksViewService>(initialTasksViewServiceContext);

export interface TasksViewServiceProviderProps {
    initialState: TasksViewState;
    enforcedFilter?: FilterOf<ContextTask>;
}

const getNextDate = (date: Date) => {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate(), 24);
};

const translateFilter = (filter: TaskViewFilter<any>, key: string): any => {
    switch (filter.type) {
        case "or":
        case "and":
            return {
                [`$${filter.type}`]: filter.filters.map(s => translateFilter(s, key)),
            };
        case "boolean":
        case "textMatch":
        case "selection": {
            if (!isPresent(filter.value)) {
                return {
                    $in: [
                        {
                            $type: `$${key}`,
                        },
                        ["missing", "null"],
                    ],
                };
            }
            return {
                $eq: [`$${key}`, filter.value],
            };
        }
        case "prefixTextMatch": {
            return {
                $regexMatch: {input: `$${key}`, regex: `^${filter.prefix}.*`},
            };
        }
        case "dateRange": {
            const result: any[] = [];
            if (isPresent(filter.from)) {
                result.push({
                    [filter.includeFrom ? "$gte" : "$gt"]: [
                        `$${key}`,
                        {
                            $dateFromString: {
                                dateString: filter.from,
                            },
                        },
                    ],
                });
            }
            if (isPresent(filter.to)) {
                result.push({
                    $lt: [
                        `$${key}`,
                        {
                            $dateFromString: {
                                dateString: filter.includeTo
                                    ? getNextDate(new Date(filter.to)).toISOString()
                                    : filter.to,
                            },
                        },
                    ],
                });
            }
            return result;
        }
        case "numberRange": {
            const result: any[] = [];
            if (isPresent(filter.from)) {
                result.push({
                    [filter.includeFrom ? "$gte" : "$gt"]: [`$${key}`, filter.from],
                });
            }
            if (isPresent(filter.to)) {
                result.push({
                    [filter.includeTo ? "$lte" : "$lt"]: [`$${key}`, filter.to],
                });
            }
            return result;
        }
        default:
            return undefined;
    }
};

const generateMatchStage = (filter: FilterOf<ContextTask>) => {
    const predicates: any[] = [];
    for (const [key, value] of Object.entries(filter)) {
        if (value) {
            const preds = translateFilter(value, key);
            if (Array.isArray(preds)) {
                predicates.push(...preds);
            } else {
                predicates.push(preds);
            }
        }
    }
    const expr =
        predicates.length === 1
            ? predicates[0]
            : {
                  $and: predicates,
              };
    return {
        $match: {
            $expr: expr,
        },
    };
};

const getTasksViewDataStages = (state: TasksViewState, enforcedFilter?: FilterOf<ContextTask>): Stages[] => {
    const stages: Stages[] = [
        {
            $set: {
                id: "$_id",
            },
        },
        {
            $unset: "_id",
        },
    ];
    if (enforcedFilter) {
        stages.push(generateMatchStage(enforcedFilter));
    }
    if (state.filters && Object.keys(state.filters).length > 0) {
        stages.push(generateMatchStage(state.filters));
    }
    if (state.sortInfo) {
        stages.push({
            $sort: {
                [state.sortInfo.column]: state.sortInfo.direction === "desc" ? -1 : 1,
            },
        });
    }
    const dataFacet: Stages[] = [];
    if (state.currentPage > 0) {
        dataFacet.push({
            $skip: state.currentPage * state.pageSize,
        });
    }
    dataFacet.push({
        $limit: state.pageSize,
    });
    stages.push({
        $facet: {
            data: dataFacet,
            totalCount: [
                {
                    $count: "count",
                },
            ],
        },
    });
    stages.push({
        $project: {
            data: 1,
            totalCount: {
                $arrayElemAt: [
                    {
                        $map: {
                            input: "$totalCount",
                            as: "cnt",
                            in: "$$cnt.count",
                        },
                    },
                    0,
                ],
            },
        },
    });
    return stages;
};

export const TasksViewServiceProvider: FC<TasksViewServiceProviderProps> = props => {
    const {initialState, enforcedFilter} = props;
    const {tasks_Aggregate, tasks_Update, tasks_AddResponse, contextTypes_FindOne} = useNimflow();
    const {authenticatedUser} = useAuthenticationService();
    const pendingUpdateTasksRef = useRef<Promise<any>[]>([]);
    const [currentState, setCurrentState] = useState<TasksViewState>(initialState);
    const lastData = useRef<TasksViewData>(defaultTasksViewData);
    const lastContextTypes = useRef<Record<string, ContextType>>(defaultContextTypes);
    const {can} = useAuthorization();
    const [serverDataUpdated, setServerDataUpdated] = useState(0);

    const {
        data: {data, contextTypes} = {},
        mutate,
    } = useSWR(
        ["/Tasks/Search", currentState],
        async () => {
            const stages: Stages[] = getTasksViewDataStages(currentState, enforcedFilter);
            const aggres = await tasks_Aggregate({stages});
            const {data, totalCount} = aggres[0] as {data: ContextTask[]; totalCount: number};
            const result: TasksViewData = {
                data,
                page: currentState.currentPage,
                totalCount,
            };
            const contextTypeNames = uniq(data.map(s => s.contextTypeName));
            const promiseResults = await Promise.allSettled(contextTypeNames.map(s => contextTypes_FindOne({name: s})));
            const contextTypes: Record<string, ContextType> = {};
            for (const promiseResult of promiseResults) {
                if (promiseResult.status === "fulfilled") {
                    const ct = promiseResult.value;
                    if (ct) {
                        contextTypes[ct.name] = ct;
                    }
                }
            }
            lastData.current = result;
            lastContextTypes.current = contextTypes;
            return {
                data: result,
                contextTypes,
            };
        },
        {
            revalidateOnFocus: false,
        }
    );

    const refreshTasks = useCallback(async () => {
        const result = await mutate();
        // Workaround to force task-view-table refresh
        setCurrentState(s => ({
            ...s,
        }));
        return result;
    }, [mutate]);

    const updateTask = useCallback(
        async (task: ContextTask, updates: Partial<ContextTask>) => {
            function hasUpdates(task: ContextTask) {
                for (const [key, value] of Object.entries(updates)) {
                    if (!equals((task as any)[key], value)) {
                        return true;
                    }
                }
                return false;
            }

            let changed = false;
            mutate(data => {
                if (!data) {
                    return undefined;
                }
                const newData = data.data.data.map(s => {
                    if (s.id !== task.id) {
                        return s;
                    }
                    changed = hasUpdates(s);
                    return {
                        ...s,
                        ...updates,
                    };
                });
                return changed
                    ? {
                          ...data,
                          data: {
                              ...data.data,
                              data: newData,
                          },
                      }
                    : data;
            }, false);

            if (!changed) {
                return;
            }

            const updatesQuerySet: any = {};
            const updatesQueryUnset: any = {};
            for (const [key, value] of Object.entries(updates)) {
                (isPresent(value) ? updatesQuerySet : updatesQueryUnset)[key] = value ?? "";
            }
            const updateOps: any[] = [];
            if (Object.keys(updatesQuerySet).length > 0) {
                updateOps.push({
                    $set: updatesQuerySet,
                });
            }
            if (Object.keys(updatesQueryUnset).length > 0) {
                updateOps.push({
                    $unset: updatesQueryUnset,
                });
            }
            const promise = tasks_Update({
                id: task.id,
                update: updateOps,
                returnTask: true,
            });
            pendingUpdateTasksRef.current.push(promise);
            promise
                .finally(() => {
                    const index = pendingUpdateTasksRef.current.indexOf(promise);
                    if (index >= -1) {
                        pendingUpdateTasksRef.current.splice(index, 1);
                    }
                })
                .then(result => {
                    if (!result.task || hasUpdates(result.task)) {
                        mutate();
                    }
                    setServerDataUpdated(s => s + 1);
                })
                .catch(() => mutate());
        },
        [mutate, tasks_Update]
    );

    const addResponse = useRecoilCallback(
        ({set}) =>
            async (task: ContextTask, responseType: ResponseType, payload: any, startedOn: Date | undefined) => {
                set(taskBlockedState(task.id), true);
                try {
                    await tasks_AddResponse({
                        contextId: task.contextId,
                        contextTypeName: task.contextTypeName,
                        taskTypeName: task.typeName,
                        responseTypeName: responseType.name,
                        payload,
                        startedOn: startedOn?.toISOString(),
                        sentOn: new Date().toISOString(),
                        sentBy: authenticatedUser?.name,
                    });
                    await refreshTasks();
                } finally {
                    set(taskBlockedState(task.id), false);
                }
            },
        [refreshTasks, tasks_AddResponse, authenticatedUser?.name]
    );

    const canCreateReponses = useCallback(() => {
        return can([permissions.sendTaskResponses]);
    }, [can]);

    const canReassignUsers = useCallback(() => {
        return can([permissions.manageTasks, permissions.readUsers]);
    }, [can]);

    const canCreateResponsesTo = useCallback(
        (task: ContextTask) => {
            return (
                canCreateReponses() &&
                (task.assignedTo === authenticatedUser?.name || task.assignedTo === authenticatedUser?.email || task.assignedTo === "*")
            );
        },
        [canCreateReponses, authenticatedUser]
    );

    const getColumnCandidates = useCallback(
        async (column: TaskViewColumn, filters?: FilterOf<ContextTask>) => {
            const cands = column.filterCandidates;
            if (Array.isArray(cands)) {
                return cands;
            }
            const stages: Stages[] = [];
            const hasFilters = !!filters && Object.keys(filters).length > 0;
            if (enforcedFilter || hasFilters) {
                stages.push(
                    {
                        $set: {
                            name: "$_id",
                        },
                    },
                    {
                        $unset: "_id",
                    }
                );
                if (enforcedFilter) {
                    stages.push(generateMatchStage(enforcedFilter));
                }
                if (hasFilters) {
                    stages.push(generateMatchStage(filters!));
                }
            }
            stages.push(
                {
                    $group: {
                        _id: "$" + column.field,
                        value: {$sum: 1},
                    },
                },
                {
                    $set: {
                        name: "$_id",
                    },
                },
                {
                    $unset: "_id",
                },
                {
                    $sort: {
                        name: 1,
                    },
                }
            );
            const result = await tasks_Aggregate({stages});
            //reference serverDataUpdated to detect local mutations
            serverDataUpdated.toString();
            return result;
        },
        [enforcedFilter, serverDataUpdated, tasks_Aggregate]
    );

    const service: TasksViewService = useMemo(() => {
        const service: TasksViewService = {
            data: data ?? lastData.current,
            contextTypes: contextTypes ?? lastContextTypes.current,
            updateTask,
            refreshTasks,
            addResponse,
            currentState,
            setCurrentState,
            canCreateReponses,
            canReassignUsers,
            canCreateResponsesTo,
            getColumnCandidates,
        };
        return service;
    }, [
        addResponse,
        canCreateReponses,
        canCreateResponsesTo,
        canReassignUsers,
        contextTypes,
        currentState,
        data,
        getColumnCandidates,
        refreshTasks,
        updateTask,
    ]);

    return <TasksViewServiceContext.Provider value={service}>{props.children}</TasksViewServiceContext.Provider>;
};

export const TasksViewServiceConsumer = TasksViewServiceContext.Consumer;

export const useTasksViewService = () => useContext(TasksViewServiceContext);
