import i18next from "i18next";
import Handlebars from "handlebars";
import {userDefinedLocalizationsNs} from "../components/localization-provider";

type CompileOptions = NonNullable<Parameters<typeof Handlebars.compile>[1]>;
type I18nextOptions = Exclude<Parameters<typeof i18next.t>[2], string | undefined>;

Handlebars.registerHelper("translate", function (key: string, options?: Handlebars.HelperOptions) {
    let opts: I18nextOptions = {
        ns: userDefinedLocalizationsNs,
    };
    if (typeof options === "object" && "hash" in options) {
        opts = {
            ...opts,
            ...options.hash,
        };
    }
    return i18next.t(key, opts);
});

export const compileHandlebars = <T = any>(input: any, options?: CompileOptions): Handlebars.TemplateDelegate<T> => {
    return Handlebars.compile(input, options);
};

export const evaluateHandlebars = <T = any>(
    input: any,
    context: T,
    options?: CompileOptions,
    rtOptions?: RuntimeOptions
) => {
    const template = compileHandlebars<T>(input, options);
    return template(context, rtOptions);
};
