import React, {FC, useMemo} from "react";
import {useParams} from "react-router";
import useSWR from "swr";
import {uniq} from "ramda";
import {useTranslation} from "react-i18next";

import {useNimflowDashboards} from "../../api/nimflow-dashboard-api";
import {useNimflowDashboardWidgets, DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";
import {WaitSpinner} from "../../components/wait-spinner";
import {ErrorViewer} from "../../components/error-viewer";
import {DashboardViewer} from "./dashboard-viewer";
import {useKey} from "../../utils/use-key";

const useDashboardDefinitions = (dashboardId: string) => {
    const {dashboards_FindOne} = useNimflowDashboards();
    const {dashboardWidgets_FindAllByName, getWidgetDefinition} = useNimflowDashboardWidgets();
    const key = useKey(dashboards_FindOne, dashboardWidgets_FindAllByName, dashboardId);
    const {data, isValidating, error, mutate} = useSWR(
        key,
        async () => {
            const dashboard = await dashboards_FindOne(dashboardId);
            const widgetNames = uniq(dashboard.widgets.map(s => s.widgetName));
            const widgetDefOrSpecs = await dashboardWidgets_FindAllByName(widgetNames);
            const promises: Promise<DashboardWidgetDefinition>[] = [];
            for(const defOrSpec of widgetDefOrSpecs) {
                promises.push(getWidgetDefinition(defOrSpec));
            }
            const widgets = await Promise.all(promises);
            return {dashboard, widgets};
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            definitions: data,
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const DashboardViewerPage: FC<{}> = () => {
    const {t} = useTranslation();
    const {dashboardId} = useParams();
    const {definitions, isValidating, error} = useDashboardDefinitions(dashboardId);

    if (!!error) {
        return <ErrorViewer error={error} />;
    }

    if (isValidating) {
        return <WaitSpinner>{t("LoadingDashboardDefinition")}</WaitSpinner>;
    }

    if (!definitions) {
        return null;
    }

    return <DashboardViewer dashboard={definitions.dashboard} widgets={definitions.widgets} />;
};
