import React, {useCallback} from "react";
import Autocomplete, {
    AutocompleteProps,
    AutocompleteChangeReason,
    AutocompleteChangeDetails,
    AutocompleteRenderInputParams,
} from "@material-ui/lab/Autocomplete";
import {useFormikContext, useField} from "formik";
import {Value} from "@material-ui/lab/useAutocomplete";

export interface FormikAutocompleteRenderInputParams extends AutocompleteRenderInputParams{
    error?: boolean;
    helperText?: React.ReactNode;
}

export type FormikAutocompleteProps<
    T,
    Multiple extends boolean | undefined,
    DisableClearable extends boolean | undefined,
    FreeSolo extends boolean | undefined
> = {
    name: string;
    controlValueAdapter?: (value: Value<T, Multiple, DisableClearable, FreeSolo>) => any;
    renderInput?: (params: FormikAutocompleteRenderInputParams) => React.ReactNode;
} & Omit<AutocompleteProps<T, Multiple, DisableClearable, FreeSolo>, "name" | "value" | "defaultValue" | "checked" | "renderInput">;

export const FormikAutocomplete = <
    T,
    Multiple extends boolean | undefined = undefined,
    DisableClearable extends boolean | undefined = undefined,
    FreeSolo extends boolean | undefined = undefined
>(
    props: FormikAutocompleteProps<T, Multiple, DisableClearable, FreeSolo> & {children?: React.ReactNode}
) => {
    const {name, controlValueAdapter, onChange, onBlur, renderInput, multiple, loading, disabled, ...rest} = props;
    const {isSubmitting, setFieldTouched} = useFormikContext();
    const [field, meta, helpers] = useField({name});
    const fieldError = meta.error;
    const showError = meta.touched && !!fieldError;

    const handleChange = useCallback(
        (
            event: React.ChangeEvent<{}>,
            value: Value<T, Multiple, DisableClearable, FreeSolo>,
            reason: AutocompleteChangeReason,
            details?: AutocompleteChangeDetails<T>
        ) => {
            const adaptedValue = controlValueAdapter ? controlValueAdapter(value) : value;
            helpers.setValue(adaptedValue ?? undefined);
            onChange?.(event, value, reason, details);
        },
        [controlValueAdapter, helpers, onChange]
    );
    const handleBlur = useCallback(
        (ev: React.FocusEvent<HTMLInputElement>) => {
            setFieldTouched(field.name);
            field.onBlur(ev);
            onBlur?.(ev);
        },
        [field, onBlur, setFieldTouched]
    );

    const renderInputHandler = useCallback((params: AutocompleteRenderInputParams) => {
        if (!renderInput) {
            return null;
        }

        return renderInput({
            ...params,
            error: showError,
            helperText: showError ? fieldError : undefined,
        });
    }, [fieldError, renderInput, showError]);

    return (
        <>
            <Autocomplete<T, Multiple, DisableClearable, FreeSolo>
                {...field}
                value={field.value ?? null}
                onChange={handleChange}
                onBlur={handleBlur}
                renderInput={renderInputHandler}
                disabled={disabled ?? isSubmitting}
                loading={loading || isSubmitting}
                multiple={multiple}
                {...rest}
            />
            {/* {JSON.stringify({showError, fieldError, touched: meta.touched})} */}
        </>
    );
};
