import {FC} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {
    and,
    computeLabel,
    ControlElement,
    ControlProps,
    isControl,
    isDescriptionHidden,
    isPlainLabel,
    RankedTester,
    rankWith,
    Resolve,
} from "@jsonforms/core";
import {withJsonFormsControlProps} from "@jsonforms/react";
import Hidden from "@material-ui/core/Hidden";
import {FormControl, FormHelperText, InputLabel} from "@material-ui/core";
import {merge} from "lodash";
import {getBasePath} from "../../../utils/get-base-url";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        image: {
            marginTop: theme.spacing(2),
            width: "max-content",
            maxWidth: "100%",
        },
        imagePlaceholder: {
            marginTop: theme.spacing(6),
            height: 50,
        },
    })
);

const ImageRenderer: FC<ControlProps> = props => {
    const {uischema, config, id, data, visible, errors, label, required, description} = props;
    const classes = useStyles();
    const isValid = errors.length === 0;
    const appliedUiSchemaOptions = merge({}, config, uischema.options);
    const showDescription = !isDescriptionHidden(
        visible,
        description ?? "",
        false,
        appliedUiSchemaOptions.showUnfocusedDescription
    );

    const firstFormHelperText = showDescription ? description : !isValid ? errors : null;
    const secondFormHelperText = showDescription && !isValid ? errors : null;

    return (
        <Hidden xsUp={!visible}>
            <FormControl fullWidth={!appliedUiSchemaOptions.trim} id={id}>
                <InputLabel htmlFor={id + "-input"} error={!isValid} shrink={typeof data === "string"}>
                    {computeLabel(
                        isPlainLabel(label) ? label : label.default,
                        !!required,
                        appliedUiSchemaOptions.hideRequiredAsterisk
                    )}
                </InputLabel>
                <div>
                    {typeof data === "string" && <img src={data} alt="" className={classes.image} />}
                    {typeof data !== "string" && (
                        <img
                            src={`${getBasePath()}/images/image-placeholder.png`}
                            alt="placeholder"
                            className={classes.imagePlaceholder}
                        />
                    )}
                </div>
                <FormHelperText error={!isValid && !showDescription}>{firstFormHelperText}</FormHelperText>
                <FormHelperText error={!isValid}>{secondFormHelperText}</FormHelperText>
            </FormControl>
        </Hidden>
    );
};

export default withJsonFormsControlProps(ImageRenderer);

export const imageRendererTester: RankedTester = rankWith(
    4,
    and(isControl, (uiSchema, schema) => {
        const scopeSchema = Resolve.schema(schema, (uiSchema as ControlElement).scope);
        return scopeSchema && scopeSchema.type === "string" && scopeSchema.format === "image";
    })
);
