import {useMemo} from "react";
import i18next from "i18next";
import {equals} from "ramda";

import {
    DashboardParameterState,
    DashboardParameterValue,
    getParameterValueDescription,
    SelectionDashboardParameter,
    useSelectionDashboardParameterDataSource,
} from "../../api/nimflow-dashboard-api";

export interface SelectionDashboardParameterDataSourceOption {
    description: string;
    selectAll: boolean;
    value?: DashboardParameterValue;
    isAdHoc?: boolean;
}

export const selectAllOption: SelectionDashboardParameterDataSourceOption = {
    description: i18next.t("SelectAll"),
    selectAll: true,
};

export const useSelectionDashboardParameterDataSourceOptions = (parameter: SelectionDashboardParameter) => {
    const {
        data: baseData,
        error,
        isValidating,
        mutate,
    } = useSelectionDashboardParameterDataSource(parameter.dataSource);

    const result = useMemo(() => {
        const options: SelectionDashboardParameterDataSourceOption[] = baseData.map(s => ({
            selectAll: false,
            description: getParameterValueDescription(s, parameter),
            value: s,
        }));
        if (parameter.allowEmptySelection) {
            options.unshift(selectAllOption);
        }
        return {options, error, isValidating, mutate};
    }, [baseData, error, isValidating, mutate, parameter]);

    return result;
};

export const getOptionsAndSelection = (
    value: DashboardParameterState | undefined,
    parameter: SelectionDashboardParameter,
    baseOptions: SelectionDashboardParameterDataSourceOption[]
): [SelectionDashboardParameterDataSourceOption[], SelectionDashboardParameterDataSourceOption[]] => {
    if (!value) {
        return [baseOptions, []];
    }
    if (value.selectAll) {
        return [baseOptions, [selectAllOption]];
    }
    let options: SelectionDashboardParameterDataSourceOption[] = baseOptions;
    const selection: SelectionDashboardParameterDataSourceOption[] = [];
    const pendingValues = [...value.values];
    for (const opt of options) {
        const index = pendingValues.findIndex(s => equals(s.value, opt.value?.value));
        if (index >= 0 || value.selectAll) {
            if (index >= 0) {
                pendingValues.splice(index, 1);
            }
            selection.push(opt);
        }
    }
    if (pendingValues.length > 0) {
        for (const val of pendingValues) {
            const opt: SelectionDashboardParameterDataSourceOption = {
                description: getParameterValueDescription(val, parameter),
                selectAll: false,
                value: val,
                isAdHoc: true,
            };
            options = [...options, opt];
            selection.push(opt);
        }
    }
    return [options, selection];
};
