import React, {FC, useCallback, useMemo, useState} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";
import {useTranslation} from "react-i18next";
import useSWR from "swr";
import {useNavigate} from "react-router";
import {useRecoilState} from "recoil";
import {ascend, descend, groupBy, sortWith, toLower} from "ramda";
import i18next from "i18next";

import {currentUnitIdState, useNimflowBusinessDirectory} from "../../api/nimflow-business-directory-provider";
import {Unit, UnitInfo} from "../../api/nimflow-business-directory-api";
import {useKey} from "../../utils/use-key";
import {WaitSpinner} from "../../components/wait-spinner";
import {ErrorViewer} from "../../components/error-viewer";
import {hubApiUrlProp, studioAllowedPop} from "../../api/nimflow-api-provider";
import {SearchField} from "../../components/search-field";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialog: {
            height: "50vh",
        },
        title: {
            display: "flex",
            flexDirection: "row",
        },
        grow: {
            flexGrow: 1,
        },
        nested: {
            paddingLeft: theme.spacing(5),
        },
    })
);

const unknownOrgName = i18next.t("UnknownOrganization");

const useUnitInfos = () => {
    const {userSession_GetUnits} = useNimflowBusinessDirectory();
    const queryKey = useKey(userSession_GetUnits);
    const {data, isValidating, error, mutate} = useSWR(queryKey, async () => {
        const units = await userSession_GetUnits({withProps: [hubApiUrlProp, studioAllowedPop]});
        return sortWith(
            [descend(s => toLower(s.organizationName ?? unknownOrgName)), ascend(s => toLower(s.name))],
            units
        );
    });

    const [filter, setFilter] = useState<string>();

    const filteredData = useMemo(() => {
        if (!data) {
            return [];
        }
        if (!filter) {
            return data;
        }
        return data.filter(
            s =>
                (s.name && toLower(s.name).includes(toLower(filter))) ||
                (s.organizationName && toLower(s.organizationName).includes(toLower(filter)))
        );
    }, [data, filter]);

    return useMemo(() => {
        return {
            units: filteredData,
            isValidating,
            error,
            revalidate: mutate,
            filter,
            setFilter,
        };
    }, [filteredData, isValidating, error, mutate, filter]);
};

export interface UnitSwitchDialogContentProps {
    units: UnitInfo[];
    onClose: () => void;
}

const UnitSwitchDialogContent: FC<UnitSwitchDialogContentProps> = props => {
    const {units, onClose} = props;
    const navigate = useNavigate();
    const classes = useStyles();
    const [currentUnitId, setCurrentUnitId] = useRecoilState(currentUnitIdState);
    const [groupedInfos, orgs] = useMemo(() => {
        const groups = groupBy(s => s.organizationName ?? unknownOrgName, units);
        const orgs = sortWith([ascend(s => toLower(s))], Object.keys(groups));
        return [groups, orgs];
    }, [units]);

    const handleListItemClick = useCallback(
        (_event: React.MouseEvent<HTMLDivElement, MouseEvent>, unit: Unit) => {
            setCurrentUnitId(unit.id);
            onClose();
            navigate("/", {replace: true});
        },
        [setCurrentUnitId, onClose, navigate]
    );

    const getUnits = (units: UnitInfo[], grouped: boolean) => {
        return units.map(unit => (
            <ListItem
                key={unit.id}
                button
                selected={unit.id === currentUnitId}
                onClick={ev => handleListItemClick(ev, unit)}
                className={grouped ? classes.nested : undefined}
            >
                <ListItemText primary={unit.name} secondary={unit.description} />
            </ListItem>
        ));
    };
    if (orgs.length === 1 && orgs[0] === unknownOrgName) {
        return <List>{getUnits(units, false)}</List>;
    }

    return (
        <List>
            {orgs.map(org => (
                <React.Fragment key={org}>
                    <ListItem>
                        <ListItemText primary={org} />
                    </ListItem>
                    <List component="div" disablePadding>
                        {getUnits(groupedInfos[org], true)}
                    </List>
                </React.Fragment>
            ))}
        </List>
    );
};

export interface UnitSwitchDialogProps {
    open: boolean;
    onClose: () => void;
}

export const UnitSwitchDialog: FC<UnitSwitchDialogProps> = props => {
    const {open, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {units, isValidating, error, filter, setFilter} = useUnitInfos();

    const handleInteractiveClose = useCallback(
        (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [onClose]
    );

    return (
        <Dialog open={open} onClose={handleInteractiveClose} fullWidth maxWidth="md">
            <DialogTitle disableTypography className={classes.title}>
                <Typography variant="h6" display="inline">
                    {t("SwitchBusinessUnit")}
                </Typography>
                <div className={classes.grow} />
                <SearchField value={filter} onChange={setFilter} />
            </DialogTitle>
            <DialogContent className={classes.dialog}>
                {isValidating && <WaitSpinner />}
                {!!error && <ErrorViewer error={error} />}
                {!isValidating && !error && <UnitSwitchDialogContent units={units} onClose={onClose} />}
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
