/* eslint-disable no-new-func */
export interface ScriptEvaluationOptions<T extends Record<string, any> = {}>{
    inputValues?: Record<string, any>;
    outputValues?: T,
}

export const evaluateScript = <T extends Record<string, any> = {}>(script: string, options?: ScriptEvaluationOptions<T>) => {
    const inputVals = options?.inputValues ?? {};
    const outputVals: T = options?.outputValues ?? {} as T;
    const inputKeys = Object.keys(inputVals);
    const outputKeys = Object.keys(outputVals) as Array<keyof T>;

    const fnbody = `
    (function() {
        ${script}
    })();
    return {${outputKeys.join(", ")}};
    `;
    const fn = new Function(...inputKeys.concat(outputKeys as string[]), fnbody);
    const inputArgs = inputKeys.map(s => inputVals[s]);
    const outputArgs = outputKeys.map(s => outputVals[s]);
    const result = fn(...inputArgs.concat(outputArgs));
    for(const key of outputKeys) {
        outputVals[key] = result[key];
    }
};