import React, {FC, useMemo, useCallback} from "react";

import {TasksViewFilterEntryEditorProps} from "./tasks-view-filter";
import {PrefixTextMatchTaskViewFilter, TextMatchTaskViewFilter} from "./tasks-view";
import {TextMatchFilter} from "./tasks-view-filter-text-match";

export const PrefixTextMatchFilter: FC<TasksViewFilterEntryEditorProps<PrefixTextMatchTaskViewFilter>> = props => {
    const {value, onChange, ...rest} = props;

    const matchValue = useMemo(() => {
        if (!value) {
            return undefined;
        }
        return {type: "textMatch", value: value.prefix} as const;
    }, [value]);

    const handleChange = useCallback((filter: TextMatchTaskViewFilter | undefined) => {
        let result: PrefixTextMatchTaskViewFilter | undefined = undefined;
        if (filter) {
            result = {
                type: "prefixTextMatch",
                prefix: filter.value,
            };
        }
        onChange?.(result);
    }, [onChange]);

    return <TextMatchFilter value={matchValue} onChange={handleChange} {...rest} />;
};
