import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import React, {FC} from "react";
import {WaitSpinner} from "./wait-spinner";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        waitContainer: {
            alignItems: "center",
            display: "flex",
            justifyContent: "center",
            height: "100vh",
            width: "100vw",
        },
    })
);

export interface WaitPageProps extends React.HTMLAttributes<HTMLDivElement> {}

export const WaitPage: FC<WaitPageProps> = ({children, ...rest}) => {
    const classes = useStyles();
    return (
        <div className={classes.waitContainer}>
            <WaitSpinner>{children}</WaitSpinner>
        </div>
    );
};
