import React, {FC, useCallback, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Grid from "@material-ui/core/Grid";
import {KeyboardTimePicker, KeyboardDatePicker} from "@material-ui/pickers";
import {StaticDashboardParameterTimeRangeValue} from "../../api/nimflow-dashboard-api";
import {ErrorBoundary} from "../../components/error-boundary";
import {useTranslation} from "react-i18next";
import {parseISO, differenceInDays, differenceInMinutes, addMinutes, addDays} from "date-fns";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            //height: "90vh",
            width: "100%",
            minHeight: "30em",
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

export interface StaticTimeRangeParameterEditorDialogProps {
    open: boolean;
    value: StaticDashboardParameterTimeRangeValue;
    onChange?: (value: StaticDashboardParameterTimeRangeValue) => void;
    onClose?: () => void;
}

const adjustDate = (refDate: Date, newDate: Date, targetDate: Date) => {
    let diff = differenceInDays(newDate, refDate);
    if (diff === 0) {
        diff = differenceInMinutes(newDate, refDate);
        return addMinutes(targetDate, diff);
    } else {
        return addDays(targetDate, diff);
    }
};

export const StaticTimeRangeParameterEditorDialog: FC<StaticTimeRangeParameterEditorDialogProps> = props => {
    const {open, value, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();

    const [fromDate, setFromDate] = useState<Date>(() => {
        return parseISO(value.range.start);
    });
    const [toDate, setToDate] = useState<Date>(() => {
        return parseISO(value.range.end);
    });

    const handleFromDateChange = useCallback((date: Date | null) => {
        if (date) {
            if (date > toDate) {
                setToDate(s => adjustDate(fromDate, date, s));
            }
            setFromDate(date);
        }
    }, [fromDate, toDate]);

    const handleToDateChange = useCallback((date: Date | null) => {
        if (date) {
            if (date < fromDate) {
                setFromDate(s => adjustDate(toDate, date, s));
            }
            setToDate(date);
        }
    }, [fromDate, toDate]);

    const handleAccept = useCallback(() => {
        onChange?.({
            type: "static",
            range: {
                start: fromDate.toISOString(),
                end: toDate.toISOString(),
            }
        });
        onClose?.();
    }, [fromDate, onChange, onClose, toDate]);

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="md"
            classes={{paper: classes.paper}}
        >
            <DialogTitle className={classes.dialogTitle}>{t("CustomTimeRange")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <ErrorBoundary>
                    <Grid item xs={12}>
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <KeyboardDatePicker
                                    margin="normal"
                                    label={t("StartTime")}
                                    format={t("DATE_FORMAT")}
                                    required
                                    fullWidth
                                    value={fromDate}
                                    onChange={handleFromDateChange}
                                    KeyboardButtonProps={{
                                        "aria-label": "change date",
                                    }}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <KeyboardTimePicker
                                    margin="normal"
                                    label=" "
                                    fullWidth
                                    value={fromDate}
                                    onChange={handleFromDateChange}
                                    KeyboardButtonProps={{
                                        "aria-label": "change time",
                                    }}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <KeyboardDatePicker
                                    margin="normal"
                                    label={t("EndTime")}
                                    format={t("DATE_FORMAT")}
                                    fullWidth
                                    required
                                    value={toDate}
                                    onChange={handleToDateChange}
                                    KeyboardButtonProps={{
                                        "aria-label": "change date",
                                    }}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <KeyboardTimePicker
                                    margin="normal"
                                    label=" "
                                    fullWidth
                                    value={toDate}
                                    onChange={handleToDateChange}
                                    KeyboardButtonProps={{
                                        "aria-label": "change time",
                                    }}
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                </ErrorBoundary>
            </DialogContent>
            <DialogActions className={classes.dialogFooter}>
                <Button onClick={handleAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
