import React, {createContext, FC, useEffect, useLayoutEffect, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {ThemeProvider as MuiThemeProvider} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import {PaletteType} from "@material-ui/core";

import {getTheme} from "./theme";

const ThemeDispatchContext = createContext<(paletteType: PaletteType) => void>(() => {
    throw new Error("Forgot to wrap component in `ThemeProvider`");
});

if (process.env.NODE_ENV !== "production") {
    ThemeDispatchContext.displayName = "ThemeDispatchContext";
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        globalStyles: {
            "& *::-webkit-scrollbar": {
                width: 10,
            },
            "& *::-webkit-scrollbar-track": {
                backgroundColor: "#9993 !important",
            },
            "& *::-webkit-scrollbar-thumb": {
                backgroundColor: "#9999 !important",
            },
            scrollbarWidth: "thin",          /* "auto" or "thin" */
            scrollbarColor: "#9999 #9993",   /* scroll thumb and track */
        },
    })
);

export const GlobalStyling: FC<{}> = props => {
    const {children} = props;
    const classes = useStyles();
    //const theme = useTheme();
    useLayoutEffect(() => {
        const body = document.getElementsByTagName("body")[0];
        const className = classes.globalStyles;
        if (!body.classList.contains(className)) {
            body.classList.add(className);
        }
        return () => body.classList.remove(className);
    }, [classes.globalStyles]);

    return <>{children}</>;
};

export interface ThemeProviderProps {
    persistKey: string;
}

export const ThemeProvider: FC<ThemeProviderProps> = props => {
    const {persistKey, children} = props;
    const typeKey = `${persistKey}:type`;

    const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");
    const [paletteType, setPaletteType] = useState<PaletteType>(() => {
        const paletteType = localStorage.getItem(typeKey) as PaletteType;
        if (paletteType === "light" || paletteType === "dark") {
            return paletteType;
        }
        return prefersDarkMode ? "dark" : "light";
    });

    useEffect(() => {
        try {
            localStorage.setItem(typeKey, paletteType);
        } catch (error) {
            console.warn(error);
        }
    }, [paletteType, typeKey]);

    const theme = React.useMemo(() => {
        const nextTheme = getTheme({
            paletteType,
        });
        return nextTheme;
    }, [paletteType]);

    return (
        <MuiThemeProvider theme={theme}>
            <ThemeDispatchContext.Provider value={setPaletteType}>
                <GlobalStyling>{children}</GlobalStyling>
            </ThemeDispatchContext.Provider>
        </MuiThemeProvider>
    );
};

export function useSetThemePaletteType() {
    const setPaletteType = React.useContext(ThemeDispatchContext);
    return React.useCallback((paletteType: PaletteType) => setPaletteType(paletteType), [setPaletteType]);
}
