import React, {FC, useState, useMemo, useCallback} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import {useTranslation} from "react-i18next";

import {StandardSection} from "../../components/standard-section";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {DispatchContextAction} from "../../api/nimflow-api";
import {ContextTypeSelectorStep} from "./context-type-selector-step";
import {ActionTypeSelectorStep} from "./action-type-selector-step";
import {ActionDataEditorStep} from "./action-data-editor-step";
import {ActionExecutor} from "./action-executor";

export type DispatchActionData = Partial<DispatchContextAction>;

export interface DispatchActionStep {
    value: DispatchActionData;
    onNext: (data: DispatchActionData) => void;
    onBack: () => void;
    onSkip: () => void;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "100%",
            //maxHeight: "70vh",
        },
        button: {
            marginRight: theme.spacing(1),
        },
        content: {
            maxHeight: "60vh",
            overflowY: "auto",
        },
    })
);

const stepComponents = [ContextTypeSelectorStep, ActionTypeSelectorStep, ActionDataEditorStep];

export const DispatchActionStepper: FC<{}> = () => {
    const {t} = useTranslation();
    const classes = useStyles();
    const [activeStep, setActiveStep] = useState(0);
    const [actionData, setActionData] = useState<DispatchActionData>({});
    const steps = useMemo(() => {
        return [
            activeStep > 0 ? t("SelectedContextType", {name: actionData.contextTypeName}) : t("SelectContextType"),
            activeStep > 1 ? t("SelectedActionType", {name: actionData.action}) : t("SelectActionType"),
            t("CompleteActionData"),
        ];
    }, [activeStep, actionData, t]);

    const handleNext = useCallback(
        (data: DispatchActionData) => {
            setActionData(data);
            setActiveStep(prevActiveStep => prevActiveStep + 1);
        },
        [setActiveStep]
    );

    const handleSkip = useCallback(() => {
        setActiveStep(prevActiveStep => prevActiveStep + 1);
    }, [setActiveStep]);

    const handleBack = useCallback(() => {
        setActiveStep(prevActiveStep => prevActiveStep - 1);
    }, [setActiveStep]);

    const handleDispatchCompleted = useCallback(() => {
        setActiveStep(0);
        setActionData({});
    }, []);

    const ActionBuilder: FC<{}> = () => {
        const Component = stepComponents[activeStep];
        return (
            <>
                <Stepper activeStep={activeStep} alternativeLabel>
                    {steps.map((label, i) => (
                        <Step key={i}>
                            <StepLabel>{label}</StepLabel>
                        </Step>
                    ))}
                </Stepper>
                {!!Component && (
                    <Component value={actionData} onNext={handleNext} onBack={handleBack} onSkip={handleSkip} />
                )}
            </>
        );
    };

    return (
        <Grid container spacing={3} justifyContent="center">
            <Grid item xs={9}>
                <StandardSection className={classes.root}>
                    <StandardSectionHeader title={t("DispatchingAction")} />
                    {activeStep < 3 ? (
                        <ActionBuilder />
                    ) : (
                        <ActionExecutor
                            action={actionData as DispatchContextAction}
                            onDispatchCompleted={handleDispatchCompleted}
                            onBack={handleBack}
                        />
                    )}
                </StandardSection>
            </Grid>
        </Grid>
    );
};
