import React, {FC} from "react";
import Chip, {ChipProps} from "@material-ui/core/Chip";
import AssignmentIcon from "@material-ui/icons/Assignment";

import {ResponseType} from "../../api/nimflow-api";
import {getCommandText} from "../../api/nimflow-api-extensions";

export interface TasksViewResponseButtonProps extends ChipProps {
    response: ResponseType;
}

export const TasksViewResponseButton: FC<TasksViewResponseButtonProps> = props => {
    const {response, title, icon, ...rest} = props;
    return <Chip icon={icon ?? <AssignmentIcon />} label={title ?? getCommandText(response)} {...rest} />;
};
