import React, {FC, useCallback, useMemo, useState} from "react";
import {
    and,
    CellProps,
    ControlElement,
    ControlProps,
    isControl,
    RankedTester,
    rankWith,
    Resolve,
    WithClassname,
} from "@jsonforms/core";
import {areEqual, withJsonFormsControlProps} from "@jsonforms/react";
import {JsonFormsTheme, MaterialInputControl} from "@jsonforms/material-renderers";
import {IMaskMixin} from "react-imask";
import merge from "lodash/merge";
import {useTheme} from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import Close from "@material-ui/icons/Close";

const withMask = <P extends object>(Component: React.ComponentType<P>) =>
    IMaskMixin<P>((props) => <Component {...props} />);

const MaskedInput = withMask(Input);

interface MuiTextInputProps {
    muiInputProps?: React.HTMLAttributes<HTMLInputElement>;
}

export const MuiInputText = React.memo((props: CellProps & WithClassname & MuiTextInputProps) => {
    const [showAdornment, setShowAdornment] = useState(false);
    const {
        data,
        config,
        className,
        id,
        enabled,
        uischema,
        isValid,
        path,
        handleChange,
        schema,
        muiInputProps,
    } = props;
    const maxLength = schema.maxLength;
    const appliedUiSchemaOptions = merge({}, config, uischema.options);
    let inputProps: any;
    if (appliedUiSchemaOptions.restrict) {
        inputProps = {maxLength: maxLength};
    } else {
        inputProps = {};
    }
    const mask = useMemo(() => {
        return (schema as any).patternMask as string;
    }, [schema]);

    inputProps = merge(inputProps, muiInputProps);

    if (appliedUiSchemaOptions.trim && maxLength !== undefined) {
        inputProps.size = maxLength;
    }
    const theme: JsonFormsTheme = useTheme();
    const inputDeleteBackgroundColor = theme.jsonforms?.input?.delete?.background || theme.palette.background.default;

    const handleAccept = useCallback(
        (value: any) => {
            handleChange(path, value);
        },
        [handleChange, path]
    );

    return (
        <MaskedInput
            mask={mask}
            onAccept={handleAccept}
            type="text"
            className={className}
            id={id}
            value={data}
            disabled={!enabled}
            autoFocus={appliedUiSchemaOptions.focus}
            multiline={appliedUiSchemaOptions.multi}
            fullWidth={!appliedUiSchemaOptions.trim || maxLength === undefined}
            inputProps={inputProps}
            error={!isValid}
            onPointerEnter={() => setShowAdornment(true)}
            onPointerLeave={() => setShowAdornment(false)}
            endAdornment={
                <InputAdornment
                    position="end"
                    style={{
                        display: !showAdornment || !enabled || data === undefined ? "none" : "flex",
                        position: "absolute",
                        right: 0,
                    }}
                >
                    <IconButton aria-label="Clear input field" onClick={() => handleChange(path, undefined)}>
                        <Close style={{background: inputDeleteBackgroundColor, borderRadius: "50%"}} />
                    </IconButton>
                </InputAdornment>
            }
        />
    );
}, areEqual);

const MaskedTextControlRenderer: FC<ControlProps> = props => {
    return <MaterialInputControl {...props} input={MuiInputText} />;
};

export default withJsonFormsControlProps(MaskedTextControlRenderer);

export const maskedTextControlRendererTester: RankedTester = rankWith(
    4,
    and(isControl, (uiSchema, schema) => {
        const scopeSchema = Resolve.schema(schema, (uiSchema as ControlElement).scope);
        return scopeSchema && scopeSchema.type === "string" && typeof (scopeSchema as any).patternMask === "string";
    })
);
