import {
    createContext,
    Dispatch,
    FC,
    SetStateAction,
    useCallback,
    useContext,
    useEffect,
    useMemo,
    useState,
} from "react";
import {useJsonForms} from "@jsonforms/react";
import {update as updatePath} from "@jsonforms/core";
import useSWR from "swr";
import {isPresent} from "ts-is-present";
import produce from "immer";
import {comparator, equals, remove, update} from "ramda";
import {compose} from "@jsonforms/core/lib/util/path";
import {JSONSchema7} from "json-schema";
import rangeParser from "parse-numeric-range";
import {ErrorObject} from "ajv";

import {PageImageLocation, RecognizedDocument, RecognizedImageBatch} from "./recognized-document";
import {DocumentSchemasSource} from "./image-batch-recognition-element";
import {useNimflowFunctions} from "../../../api/nimflow-functions-api";
import {FileInfo, ImageInfoPage, useFileInfos} from "./use-file-infos";
import {translateValidationErrors, validateJsonSchema} from "../../ajv";
import {useArrayContentsMemo} from "../../../utils/use-array-contents-memo";
import {jsonFormsAjv} from "./ajv";

export interface DocumentTreeLocation {
    documentIndex?: number;
    pageIndex?: number;
}

export interface PageLocation {
    documentIndex: number;
    documentOrder: number;
}

export interface BatchProcessorService {
    fileInfos: FileInfo[];
    documentSchemas?: JSONSchema7[];
    currentDocumentLocation?: DocumentTreeLocation;
    setCurrentDocumentLocation: (value: DocumentTreeLocation | undefined) => void;
    currentDocument?: RecognizedDocument;
    updateCurrentDocument: (value: RecognizedDocument) => void;
    schemaIdsByDocumentIndex: string[];
    loadingImages: boolean;
    updateBatch: <T extends any>(path: string, value: T | ((existingValue: T) => T)) => void;
    movePage: (pageIndex: number, location?: PageLocation) => void;
    findByPageIndex: (pageIndex: number) => ImageInfoPage | undefined;
    deleteDocument: (documentIndex: number) => void;
    addDocument: (document: RecognizedDocument) => number;
    updateDocument: (documentIndex: number, document: RecognizedDocument) => number;
    getDocument: (documentIndex: number) => RecognizedDocument;
    currentDocumentEditionState?: RecognizedDocumentEditionState;
    setCurrentDocumentEditionState: Dispatch<SetStateAction<RecognizedDocumentEditionState | undefined>>;
    getLastDocumentEditionErrors: (documentIndex: number) => ErrorObject[];
}

const stub = (): never => {
    throw new Error("You forgot to wrap your component in <BatchProcessorContextProvider>.");
};

const initialBatchProcessorContext: BatchProcessorService = {
    fileInfos: [],
    schemaIdsByDocumentIndex: [],
    loadingImages: false,
    setCurrentDocumentLocation: stub,
    updateCurrentDocument: stub,
    updateBatch: stub,
    movePage: stub,
    findByPageIndex: stub,
    deleteDocument: stub,
    addDocument: stub,
    updateDocument: stub,
    getDocument: stub,
    setCurrentDocumentEditionState: stub,
    getLastDocumentEditionErrors: stub,
};

const BatchProcessorContext = createContext<BatchProcessorService>(initialBatchProcessorContext);

export interface BatchProcessorContextProviderProps {
    basePath: string;
    batch: RecognizedImageBatch;
    documentSchemasSource: DocumentSchemasSource;
}

const useDocumentSchemasSource = (documentSchemaSource: DocumentSchemasSource) => {
    const {moduleName, functionName} = documentSchemaSource;
    const {call} = useNimflowFunctions();
    const {
        data: documentSchemas,
        isValidating: loading,
        error,
    } = useSWR(
        `function/${moduleName}/${functionName}`,
        async () => {
            const schemas: JSONSchema7[] = [];
            const result = await call(moduleName, functionName);
            if (Array.isArray(result)) {
                for (const item of result) {
                    const result = validateJsonSchema(item);
                    if (result.type === "success") {
                        schemas.push(item);
                    }
                }
            }
            return schemas;
        },
        {
            fallbackData: [],
            revalidateOnMount: true,
            revalidateOnFocus: false,
        }
    );
    return {documentSchemas, loading, error};
};

const findByPageIndex = (pageIndex: number, finfos: FileInfo[]) => {
    for (const finfo of finfos) {
        const pages = finfo.status === "loaded" ? finfo.pages : [];
        for (const pinfo of pages) {
            if (pinfo.pageIndex === pageIndex) {
                return pinfo;
            }
        }
    }
    return undefined;
};

export const pagesSpecToPageIndices = (spec: string) => {
    const result = rangeParser(spec).map(s => s - 1);
    return result;
};

const appendRange = (spec: string, start: number, end: number) => {
    let result = spec;
    if (result) {
        result += ",";
    }
    result += start.toString();
    if (start !== end) {
        result += `-${end}`;
    }
    return result;
};

export const pageIndicesToPageSpec = (pageIndices: number[]) => {
    if (pageIndices.length === 0) {
        return "";
    }
    const shiftedValues = pageIndices.map(s => s + 1);
    let start: number = shiftedValues[0];
    let current: number = start;
    let result = "";
    for (let i = 1; i < shiftedValues.length; i++) {
        const val = shiftedValues[i];
        if ((val === current + 1 && current >= start) || (val === current - 1 && current <= start)) {
            current = val;
        } else {
            result = appendRange(result, start, current);
            start = current = val;
        }
    }
    result = appendRange(result, start, current);
    return result;
};

export const pageImageLocationsToPageIndices = (locations: PageImageLocation[], fileInfos: FileInfo[]): number[] => {
    return locations.map(s => {
        const finfo = fileInfos[s.fileIndex];
        if (finfo.status === "loaded") {
            return finfo.pages[s.filePageNumber].pageIndex;
        }
        return -1;
    });
};

export const pageIndicesToPageImageLocations = (pageIndices: number[], fileInfos: FileInfo[]): PageImageLocation[] => {
    const result: PageImageLocation[] = new Array<PageImageLocation>(pageIndices.length);
    fileInfos.forEach((finfo, fidx) => {
        if (finfo.status === "loaded") {
            finfo.pages.forEach((pinfo, pidx) => {
                const idx = pageIndices.indexOf(pinfo.pageIndex);
                if (idx >= 0) {
                    result[idx] = {
                        fileIndex: fidx,
                        filePageNumber: pidx,
                    };
                }
            });
        }
    });
    return result;
};

export interface RecognizedDocumentEditionState {
    document: RecognizedDocument;
    errors?: ErrorObject[];
}

const validateDocument = (document: RecognizedDocument, schema?: JSONSchema7): ErrorObject[] => {
    if (schema && !jsonFormsAjv.validate(schema, document.data) && !!jsonFormsAjv.errors) {
        return translateValidationErrors(
            jsonFormsAjv.errors.map(s => ({
                ...s,
                instancePath: s.dataPath,
            }))
        );
    }
    return [];
};

export const BatchProcessorContextProvider: FC<BatchProcessorContextProviderProps> = props => {
    const {basePath, batch, documentSchemasSource, children} = props;
    const [initialBatch] = useState(batch);
    const {documentSchemas} = useDocumentSchemasSource(documentSchemasSource);
    const ctx = useJsonForms();
    const {fileInfos, loadingFileInfos, setFileInfos} = useFileInfos(initialBatch);
    const [currentDocumentLocation, setCurrentDocumentLocation] = useState<DocumentTreeLocation>({
        documentIndex: initialBatch.documents.length > 0 ? 0 : undefined,
    });
    const [lastDocumentEditionErrors, setLastDocumentEditionErrors] = useState<ErrorObject[][]>([]);
    const [currentDocument, setCurrentDocument] = useState<RecognizedDocument | undefined>(initialBatch.documents[0]);

    const schemaIdsByDocumentIndex = useArrayContentsMemo(batch.documents.map(s => s.schemaId));

    useEffect(() => {
        if (!documentSchemas || initialBatch.documents.length === 0) {
            return;
        }
        const errors = initialBatch.documents.map(doc => {
            const schema = documentSchemas.find(s => s.$id === doc.schemaId);
            return validateDocument(doc, schema);
        });
        setLastDocumentEditionErrors(prev => {
            return errors.map((s, i) => {
                if (prev[i]) {
                    return prev[i];
                }
                return s;
            });
        });
    }, [initialBatch, documentSchemas]);

    const updateBatch = useCallback(
        <T extends any>(path: string, value: T | ((existingValue: T) => T)) => {
            const val = typeof value === "function" ? value : ((() => value) as any);
            ctx.dispatch?.(updatePath(compose(basePath, path), val));
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [ctx.dispatch]
    );

    useEffect(() => {
        const idxSelected = isPresent(currentDocumentLocation.documentIndex);
        const docSelected = idxSelected ? batch.documents[currentDocumentLocation.documentIndex!] : undefined;
        setCurrentDocument(docSelected);
    }, [batch.documents, currentDocumentLocation]);

    const handleSetCurrentDocumentLocation = useCallback((value: DocumentTreeLocation | undefined) => {
        const location = value ?? {};
        setCurrentDocumentLocation(s => {
            if (s.documentIndex !== location.documentIndex || s.pageIndex !== location.pageIndex) {
                return location;
            }
            return s;
        });
    }, []);

    const updateCurrentDocument = useCallback(
        (value: RecognizedDocument) => {
            const docIdx = currentDocumentLocation?.documentIndex;
            if (!isPresent(docIdx)) {
                return undefined;
            }
            updateBatch(`documents[${docIdx}]`, value);
        },
        [currentDocumentLocation?.documentIndex, updateBatch]
    );

    const currentDocumentEditionState = useMemo(() => {
        if (!currentDocument || !isPresent(currentDocumentLocation.documentIndex)) {
            return undefined;
        }
        const state: RecognizedDocumentEditionState = {
            document: currentDocument,
            errors: lastDocumentEditionErrors[currentDocumentLocation.documentIndex],
        };
        return state;
    }, [currentDocument, currentDocumentLocation.documentIndex, lastDocumentEditionErrors]);

    const handleSetCurrentDocumentEditionState = useCallback(
        (value?: SetStateAction<RecognizedDocumentEditionState | undefined>) => {
            const docIdx = currentDocumentLocation.documentIndex!;
            setCurrentDocument(curdoc => {
                let newdoc = curdoc;
                setLastDocumentEditionErrors(lastErrors => {
                    const curState = !!curdoc ? {document: curdoc, errors: lastErrors[docIdx]} : undefined;
                    const state = typeof value === "function" ? value(curState) : value;
                    if (equals(state, curState)) {
                        return lastErrors;
                    }
                    newdoc = state?.document;
                    return update(docIdx, state?.errors ?? [], lastErrors);
                });
                return newdoc;
            });
        },
        [currentDocumentLocation.documentIndex]
    );

    const movePage = useCallback(
        (pageIndex: number, location?: PageLocation) => {
            const result = produce({fileInfos, docs: batch.documents}, draft => {
                const pageInfo = findByPageIndex(pageIndex, draft.fileInfos);
                if (!pageInfo) {
                    return;
                }
                const sourcePages: ImageInfoPage[] = [];
                const targetPages: ImageInfoPage[] = [];
                const isSameDoc = pageInfo.documentIndex === location?.documentIndex;
                for (const finfo of draft.fileInfos) {
                    if (finfo.status !== "loaded") {
                        continue;
                    }
                    for (const pinfo of finfo.pages) {
                        if (isPresent(pageInfo.documentIndex) && pinfo.documentIndex === pageInfo.documentIndex) {
                            sourcePages.push(pinfo);
                        }
                        if (isPresent(location) && pinfo.documentIndex === location.documentIndex && !isSameDoc) {
                            targetPages.push(pinfo);
                        }
                    }
                }
                sourcePages.sort(comparator((a, b) => a.documentOrder! < b.documentOrder!));
                targetPages.sort(comparator((a, b) => a.documentOrder! < b.documentOrder!));
                const sourceDocIdx = pageInfo.documentIndex;
                if (isPresent(sourceDocIdx)) {
                    const doc = draft.docs[sourceDocIdx];
                    sourcePages.splice(pageInfo.documentOrder!, 1);
                    if (isSameDoc) {
                        sourcePages.splice(location!.documentOrder, 0, pageInfo);
                    } else {
                        pageInfo.documentIndex = undefined;
                        pageInfo.documentOrder = undefined;
                    }
                    for (let i = 0; i < sourcePages.length; i++) {
                        sourcePages[i].documentOrder = i;
                    }
                    if (doc) {
                        doc.pages = sourcePages.map(s => ({fileIndex: s.fileIndex, filePageNumber: s.filePageNumber}));
                    }
                }
                if (isPresent(location) && !isSameDoc) {
                    const {documentIndex, documentOrder} = location;
                    const doc = draft.docs[documentIndex];
                    targetPages.splice(documentOrder!, 0, pageInfo);
                    pageInfo.documentIndex = documentIndex;
                    pageInfo.documentOrder = documentOrder;
                    for (let i = 0; i < targetPages.length; i++) {
                        targetPages[i].documentOrder = i;
                    }
                    if (doc) {
                        doc.pages = targetPages.map(s => ({fileIndex: s.fileIndex, filePageNumber: s.filePageNumber}));
                    }
                }
            });
            setFileInfos(result.fileInfos);
            updateBatch("documents", result.docs);
        },
        [batch.documents, fileInfos, setFileInfos, updateBatch]
    );

    const handleFindByPageIndex = useCallback(
        (pageIndex: number) => {
            return findByPageIndex(pageIndex, fileInfos);
        },
        [fileInfos]
    );

    const deleteDocument = useCallback(
        (documentIndex: number) => {
            if (!isPresent(batch.documents[documentIndex])) {
                return;
            }
            const docs = [...batch.documents];
            docs.splice(documentIndex, 1);
            updateBatch("documents", docs);
            setLastDocumentEditionErrors(s => remove(documentIndex, 1, s));
            setFileInfos(fileInfos => {
                return produce(fileInfos, draft => {
                    for (const finfo of draft) {
                        if (finfo.status === "loaded") {
                            for (const pinfo of finfo.pages) {
                                if (pinfo.documentIndex === documentIndex) {
                                    pinfo.documentIndex = undefined;
                                    pinfo.documentOrder = undefined;
                                }
                            }
                        }
                    }
                });
            });
            setCurrentDocumentLocation(s => {
                if (s.documentIndex === documentIndex) {
                    return {};
                }
                return s;
            });
        },
        [batch.documents, setFileInfos, updateBatch]
    );

    const addDocument = useCallback(
        (document: RecognizedDocument) => {
            const documentIndex = batch.documents.length;
            const docs = [...batch.documents, document];
            updateBatch("documents", docs);
            setLastDocumentEditionErrors(s => [...s, []]);
            if (document.pages.length > 0) {
                setFileInfos(fileInfos => {
                    return produce(fileInfos, draft => {
                        document.pages.forEach((loc, i) => {
                            const finfo = draft[loc.fileIndex];
                            if (finfo?.status === "loaded") {
                                const pinfo = finfo.pages[loc.filePageNumber];
                                if (pinfo) {
                                    pinfo.documentIndex = documentIndex;
                                    pinfo.documentOrder = i;
                                }
                            }
                        });
                    });
                });
            }
            return documentIndex;
        },
        [batch.documents, setFileInfos, updateBatch]
    );

    const updateDocument = useCallback(
        (documentIndex: number, document: RecognizedDocument) => {
            const docs = [...batch.documents];
            const oldSchemaId = docs[documentIndex].schemaId;
            const oldPages = docs[documentIndex].pages;
            docs[documentIndex] = document;
            updateBatch("documents", docs);
            const newPages = document.pages;
            if (oldPages !== newPages) {
                setFileInfos(fileInfos => {
                    return produce(fileInfos, draft => {
                        for (let i = 0; i < Math.max(oldPages.length, newPages.length); i++) {
                            const oldPage = oldPages[i];
                            const newPage = newPages[i];
                            if (
                                oldPage?.fileIndex !== newPage?.fileIndex ||
                                oldPage?.filePageNumber !== newPage?.filePageNumber
                            ) {
                                if (oldPage) {
                                    const finfo = draft[oldPage.fileIndex];
                                    if (finfo.status === "loaded") {
                                        const pinfo = finfo.pages[oldPage.filePageNumber];
                                        pinfo.documentIndex = undefined;
                                        pinfo.documentOrder = undefined;
                                    }
                                }
                                if (newPage) {
                                    const finfo = draft[newPage.fileIndex];
                                    if (finfo.status === "loaded") {
                                        const pinfo = finfo.pages[newPage!.filePageNumber];
                                        pinfo.documentIndex = documentIndex;
                                        pinfo.documentOrder = i;
                                    }
                                }
                            }
                        }
                    });
                });
            }
            if (oldSchemaId !== document.schemaId) {
                const schema = documentSchemas?.find(s => s.$id === document.schemaId);
                const errors = validateDocument(document, schema);
                setLastDocumentEditionErrors(s => update(documentIndex, errors, s));
            }
            return documentIndex;
        },
        [batch.documents, documentSchemas, setFileInfos, updateBatch]
    );

    const getDocument = useCallback(
        (documentIndex: number) => {
            return batch.documents[documentIndex];
        },
        [batch.documents]
    );

    const getLastDocumentEditionErrors = useCallback(
        (documentIndex: number): ErrorObject[] => {
            return lastDocumentEditionErrors[documentIndex];
        },
        [lastDocumentEditionErrors]
    );

    const service = useMemo(() => {
        const result: BatchProcessorService = {
            fileInfos,
            documentSchemas,
            loadingImages: loadingFileInfos,
            schemaIdsByDocumentIndex,
            currentDocumentLocation,
            setCurrentDocumentLocation: handleSetCurrentDocumentLocation,
            currentDocument,
            updateCurrentDocument,
            updateBatch,
            movePage,
            findByPageIndex: handleFindByPageIndex,
            deleteDocument,
            addDocument,
            updateDocument,
            getDocument,
            currentDocumentEditionState,
            setCurrentDocumentEditionState: handleSetCurrentDocumentEditionState,
            getLastDocumentEditionErrors,
        };
        return result;
    }, [
        addDocument,
        currentDocument,
        currentDocumentEditionState,
        currentDocumentLocation,
        deleteDocument,
        documentSchemas,
        fileInfos,
        getDocument,
        getLastDocumentEditionErrors,
        handleFindByPageIndex,
        handleSetCurrentDocumentEditionState,
        handleSetCurrentDocumentLocation,
        loadingFileInfos,
        movePage,
        schemaIdsByDocumentIndex,
        updateBatch,
        updateCurrentDocument,
        updateDocument,
    ]);

    return <BatchProcessorContext.Provider value={service}>{children}</BatchProcessorContext.Provider>;
};

export const useBatchProcessorService = () => useContext(BatchProcessorContext);
