import React from "react";
import Button from "@material-ui/core/Button";
import {useCallback} from "react";
import {useSnackbar} from "notistack";
import {useTranslation} from "react-i18next";

import {useConfirm} from "../components/confirm";
import {ErrorViewer} from "../components/error-viewer";
import {isAxiosNetworkError} from "../libs/axios";

export interface ReportErrorOptions {
    variant?: "error" | "warning";
    dialogTitle?: React.ReactNode;
}

export const useReportError = () => {
    const {t} = useTranslation();
    const {enqueueSnackbar} = useSnackbar();
    const confirm = useConfirm();

    const reportEvent = useCallback(
        (message: React.ReactNode, error: any, options?: ReportErrorOptions) => {
            const variant = options?.variant ?? "error";
            let title = options?.dialogTitle ?? t("Error");
            if (isAxiosNetworkError(error) && typeof message === "string") {
                const isOffline = window.navigator?.onLine === false;
                const prefixTemplate = isOffline ? "BrowserOfflineMessage" : "NetworkErrorMessage";
                message = t(prefixTemplate, {message});
            }
            enqueueSnackbar(message, {
                variant,
                action: (
                    <Button
                        onClick={() => {
                            confirm({
                                title,
                                description: <ErrorViewer error={error} />,
                            });
                        }}
                    >
                        {t("ShowDetails")}
                    </Button>
                ),
            });
        },
        [confirm, enqueueSnackbar, t]
    );

    return reportEvent;
};
