export const useNimflow = () => {
    const {unit, hubBaseUrl} = useCurrentUnitInfo();
    const {getAuthorizedAxios} = useAuthenticationService();

    const getConfiguredAxiosInstance = useCallback(
        (options?: NimflowApiAxiosRequestConfig) => {
            let baseUrl = hubBaseUrl;
            if (baseUrl) {
                for (let i = baseUrl.length - 1; i >= 0; i--) {
                    if (baseUrl[i] !== "/") {
                        baseUrl = baseUrl.substring(0, i + 1);
                        break;
                    }
                }
            }
            let config: AxiosRequestConfig = {
                baseURL: baseUrl,
                headers: {"unit-id": unit?.id},
            };
            if (options) {
                config = mergeDeepRight(options, config) as AxiosRequestConfig;
            }
            const axios = getAuthorizedAxios(config);
            return axios;
        },
        [getAuthorizedAxios, hubBaseUrl, unit]
    );

    const remoteApi = useMemo(() => {
        const axios = getConfiguredAxiosInstance({
            transformResponse: [], // Disable default automatic json parsing.
        });
        return getNimflowApi(axios);
    }, [getConfiguredAxiosInstance]);

    const api = useMemo(() => {
        const api: NimflowApi = {
            ...(remoteApi ??
                makeStubContext(() => {
                    throw new Error("Invalid hub api state.");
                }, false)),
            isHubSelected: !!hubBaseUrl,
            getConfiguredAxiosInstance,
        };
        return api;
    }, [remoteApi, hubBaseUrl, getConfiguredAxiosInstance]);

    return api;
};
