import React, {FC, useCallback, useState, useMemo} from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import DateRangeIcon from "@material-ui/icons/DateRange";
import {isPresent} from "ts-is-present";
import {format, parseISO} from "date-fns";
import {useTranslation} from "react-i18next";
import {Range} from "react-date-range";
import {IconButton} from "@material-ui/core";

import {TasksViewFilterEntryEditorProps} from "./tasks-view-filter";
import {DateRangePicker} from "../../libs/react-date-range";
import {DateRangeTaskViewFilter} from "./tasks-view";

interface DateRangeModalEditorProps {
    from: Date | undefined;
    to: Date | undefined;
    open: boolean;
    onClose: () => void;
    onChange?: (from: Date | undefined, to: Date | undefined) => void;
}

const DateRangeModalEditor: FC<DateRangeModalEditorProps> = props => {
    const {open, onClose, from, to, onChange} = props;
    const {t} = useTranslation();
    const [selection, setSelection] = useState<Range>(() => {
        return {
            startDate: from,
            endDate: to,
            key: "selection",
        };
    });

    const handleAccept = useCallback(() => {
        onChange?.(selection.startDate, selection.endDate);
        onClose();
    }, [onChange, onClose, selection]);

    const handleChange = useCallback((value: any) => {
        setSelection(value.selection);
    }, []);

    const handleInteractiveClose = useCallback((event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog open={open} onClose={handleInteractiveClose} maxWidth="md" >
            <DialogTitle>{t("EditingDateRange")}</DialogTitle>
            <DialogContent>
                <DateRangePicker
                    format={t("DATE_FORMAT")}
                    ranges={[selection]}
                    onChange={handleChange}
                    editableDateInputs
                    dateDisplayFormat={t("DATE_FORMAT")}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export const DateRangeFilter: FC<TasksViewFilterEntryEditorProps<DateRangeTaskViewFilter>> = props => {
    const {value, onChange} = props;
    const {t} = useTranslation();
    const [editorOpen, setEditorOpen] = useState(false);

    const handleOpenEditor = useCallback(() => {
        setEditorOpen(true);
    }, []);

    const handleEditorClosed = useCallback(() => {
        setEditorOpen(false);
    }, []);

    const handleChange = useCallback(
        (from: Date | undefined, to: Date | undefined) => {
            let filter: DateRangeTaskViewFilter | undefined = undefined;
            if (isPresent(from) || isPresent(to)) {
                filter = {
                    type: "dateRange",
                    from: undefined,
                    to: undefined,
                    includeFrom: true,
                    includeTo: true,
                };
                if (from) {
                    filter.from = from.toISOString();
                }
                if (to) {
                    filter.to = to.toISOString();
                }
            }
            onChange?.(filter);
        },
        [onChange]
    );

    const formatDate = useCallback(
        (date: Date | undefined) => {
            let result = format(date ?? new Date(), t("DATE_FORMAT"));
            if (!date) {
                result = result.replaceAll(/[0-9]/g, "_");
            }
            return result;
        },
        [t]
    );

    const [from, to] = useMemo(() => {
        let from: Date | undefined = undefined;
        let to: Date | undefined = undefined;
        if (value?.type === "dateRange") {
            from = value.from ? parseISO(value.from) : undefined;
            to = value.to ? parseISO(value.to) : undefined;
        }
        return [from, to];
    }, [value]);

    return (
        <>
            {formatDate(from)} -- {formatDate(to)}{" "}
            <IconButton onClick={handleOpenEditor}>
                <DateRangeIcon />
            </IconButton>
            {editorOpen && (
                <DateRangeModalEditor
                    from={from}
                    to={to}
                    onChange={handleChange}
                    open={editorOpen}
                    onClose={handleEditorClosed}
                />
            )}
        </>
    );
};
