import {createContext, FC, useCallback, useContext} from "react";
import useSWR from "swr";
import {sortBy} from "ramda";

import {AppInvitation} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";

const portalUrl = process.env.REACT_APP_PORTAL_URL;
const studioUrl = process.env.REACT_APP_STUDIO_URL;

if (!portalUrl) {
    throw new Error("Environment variable REACT_APP_PORTAL_URL nor defined");
}

if (!studioUrl) {
    throw new Error("Environment variable REACT_APP_STUDIO_URL nor defined");
}

export const roleNames = ["Worker", "Maker", "Owner"] as const;
Object.freeze(roleNames);
export type RoleName = typeof roleNames[number];

export interface UserSecurity {
    fullName: string;
    email: string;
    emailVerified: boolean;
    picture?: string;
    isWorker: boolean;
    isMaker: boolean;
    isOwner: boolean;
}

export interface SecurityService {
    users: UserSecurity[];
    userLoadError?: any;
    isRefreshingUsers: boolean;
    refreshUsers: () => Promise<any>;
    inviteUser: (email: string, roles: {[role in RoleName]?: boolean}) => Promise<void>;
    updateUserRoles: (user: UserSecurity) => Promise<void>;
}

const stub = () => {
    throw new Error("You forgot to wrap your component in <SecurityServiceProvider>.");
};

const initialSecurityServiceContext: SecurityService = {
    users: [],
    isRefreshingUsers: false,
    refreshUsers: stub,
    inviteUser: stub,
    updateUserRoles: stub,
};

const SecurityServiceContext = createContext<SecurityService>(initialSecurityServiceContext);

export const SecurityServiceProvider: FC<{}> = props => {
    const {children} = props;
    const {users_GetUnitUsers, users_SearchRoles, users_InviteUsersOrRequestToVerifyEmails, users_SetRoles} =
        useNimflow();

    const {data, error, isValidating, mutate} = useSWR(
        "users",
        async () => {
            const [users, allUserRoles] = await Promise.all([users_GetUnitUsers(), users_SearchRoles({})]);
            let secUsers = users.map(s => {
                const result: UserSecurity = {
                    fullName: s.fullName!,
                    email: s.email!,
                    emailVerified: s.emailVerified ?? false,
                    picture: s.props?.Picture as string | undefined,
                    isWorker: false,
                    isMaker: false,
                    isOwner: false,
                };
                return result;
            });
            secUsers = sortBy(s => s.fullName, secUsers);
            for (const userRoles of allUserRoles) {
                const roles = userRoles.roleNames;
                if (!roles || roles.length === 0) {
                    continue;
                }
                const secUser = secUsers.find(s => s.email === userRoles.emailAddress);
                if (secUser) {
                    secUser.isWorker = roles.includes("Worker");
                    secUser.isMaker = roles.includes("Maker");
                    secUser.isOwner = roles.includes("Owner");
                }
            }
            return secUsers;
        },
        {
            revalidateOnFocus: false,
        }
    );

    const inviteUser = useCallback(
        async (email: string, roles: {[role in RoleName]?: boolean}) => {
            await users_SetRoles({
                emailAddress: email!,
                roleNames: Object.keys(roles).filter(s => !!roles[s as RoleName]),
            });
            const apps: AppInvitation[] = [{description: "nimFlow Portal", url: portalUrl}];
            if (!!roles.Maker) {
                apps.push({description: "nimFlow Studio", url: studioUrl});
            }
            await users_InviteUsersOrRequestToVerifyEmails({
                invitations: [
                    {
                        apps,
                        email: email!,
                    },
                ],
            });
            await mutate();
        },
        [mutate, users_InviteUsersOrRequestToVerifyEmails, users_SetRoles]
    );

    const updateUserRoles = useCallback(async (user: UserSecurity): Promise<void> => {
        const roles: RoleName[] = [];
        if (user.isWorker) {
            roles.push("Worker");
        }
        if (user.isMaker) {
            roles.push("Maker");
        }
        if (user.isOwner) {
            roles.push("Owner");
        }
        await users_SetRoles({
            emailAddress: user.email,
            roleNames: roles,
        });
        mutate(data => {
            if (!data) {
                return data;
            }
            return data.map(s => {
                if (s.email === user.email) {
                    return {
                        ...s,
                        isWorker: user.isWorker,
                        isMaker: user.isMaker,
                        isOwner: user.isOwner,
                    }
                } else {
                    return s;
                }
            });
        });
    }, [mutate, users_SetRoles]);

    const service: SecurityService = {
        users: data ?? [],
        userLoadError: error,
        isRefreshingUsers: isValidating,
        refreshUsers: mutate,
        inviteUser,
        updateUserRoles,
    };

    return <SecurityServiceContext.Provider value={service}>{children}</SecurityServiceContext.Provider>;
};

export const useSecurityService = () => useContext(SecurityServiceContext);
