import {useContext, createContext, FC} from "react";
import Alert from "@material-ui/lab/Alert";
import AlertTitle from "@material-ui/lab/AlertTitle";
import {useTranslation} from "react-i18next";
import axios, {AxiosInstance, AxiosRequestConfig} from "axios";
import useSWRImmutable from "swr/immutable";

import {Auth0AuthenticationServiceProvider} from "./auth0-authentication-service";
import {getNimflowBusinessDirectory} from "../../api/nimflow-business-directory-provider";
import {WaitPage} from "../../components/wait-page";
import {BasicAuthenticationServiceProvider} from "./basic-authentication-service";
import {businessDirectoryServerUrl} from "../../env-vars";

export interface UserData {
    name: string;
    email?: string;
    picture?: string;
    [key: string]: any;
}

export interface AuthenticationService {
    isAuthenticated: boolean;
    authenticatedUser?: UserData;
    login: () => Promise<void>;
    logout: () => void;
    supportsPasswordReset: boolean;
    startResetPasswordFlow: (email: string) => Promise<any>;
    getAuthorizedAxios: (options?: AxiosRequestConfig) => AxiosInstance;
}

const stub = (): never => {
    throw new Error("You forgot to wrap your component in <AuthenticationProvider>.");
};

const initialAuthenticationServiceContext: AuthenticationService = {
    isAuthenticated: false,
    supportsPasswordReset: false,
    login: stub,
    logout: stub,
    startResetPasswordFlow: stub,
    getAuthorizedAxios: stub,
};

export const AuthenticationServiceContext = createContext<AuthenticationService>(initialAuthenticationServiceContext);

export const AuthenticationServiceProvider: FC<{}> = props => {
    const {children} = props;
    const {t} = useTranslation();
    const {
        data: authSchemes,
        isValidating,
        error,
    } = useSWRImmutable("supportedAuthenticationSchemes", async () => {
        const instance = axios.create({
            baseURL: businessDirectoryServerUrl,
            transformResponse: [], // Disable default automatic json parsing.
        });
        const bdapi = getNimflowBusinessDirectory(instance);
        try {
            const schemes = await bdapi.auth_SupportedSchemes();
            return !schemes || schemes.length === 0 ? ["Bearer"] : schemes;
        } catch {
            return ["Bearer"];
        }
    });

    if (isValidating) {
        return <WaitPage></WaitPage>;
    } else if (error) {
        return (
            <Alert severity="error">
                <AlertTitle>{t("Error")}</AlertTitle>
                {error.message}
            </Alert>
        );
    }
    if (!authSchemes) {
        return null;
    }

    for (const authScheme of authSchemes) {
        if (authScheme === "Bearer") {
            return <Auth0AuthenticationServiceProvider>{children}</Auth0AuthenticationServiceProvider>;
        }
        if (authScheme === "Basic") {
            return <BasicAuthenticationServiceProvider>{children}</BasicAuthenticationServiceProvider>;
        }
    }
    return (
        <Alert severity="error">
            <AlertTitle>{t("Error")}</AlertTitle>
            {t("CouldNotFindSupportedAuthenticationSchemes", {schemes: authSchemes.join(", ")})}
        </Alert>
    );
};

export const useAuthenticationService = () => useContext(AuthenticationServiceContext);
