import {useRef} from "react";

const marker = Symbol("marker");

export const useArrayContentsMemo = <T extends any>(array: T[], ignoreOrder: boolean = false) => {
    const valueRef = useRef(array);
    const value = valueRef.current;

    if (array !== value) {
        if (value.length !== array.length) {
            valueRef.current = array;
        } else {
            if (!ignoreOrder) {
                for(let i = 0; i < value.length; i++) {
                    if (value[i] !== array[i]) {
                        valueRef.current = array;
                        break;
                    }
                }
            } else {
                const workArray: Array<T | typeof marker> = [...array];
                for(let i = 0; i < value.length; i++) {
                    const idx = workArray.indexOf(value[i]);
                    if (idx === -1) {
                        valueRef.current = array;
                        break;
                    }
                    workArray[idx] = marker;
                }
            }
        }
    }

    return valueRef.current;
};
