import React, {FC, useCallback, useEffect, useMemo, useRef, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import clsx from "clsx";
import ListItem from "@material-ui/core/ListItem";
import Tooltip from "@material-ui/core/Tooltip";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import Fab from "@material-ui/core/Fab";
import ErrorIcon from "@material-ui/icons/Error";
import {isPresent} from "ts-is-present";
import {comparator} from "ramda";
import {useResizeDetector} from "react-resize-detector";
import {useTranslation} from "react-i18next";
import {VariableSizeList as List} from "react-window";

import {useBatchProcessorService} from "./image-batch-recognition-service";
import {WaitSpinner} from "../../../components/wait-spinner";
import {FileInfo, ImageInfoPage} from "./use-file-infos";
import {SelectAreaIcon} from "../../../components/icons/select-area-icon";
import {ensurePromise} from "../../../utils/promise-helpers";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        listContainer: {
            width: "100%",
            height: "100%",
        },
        imageItem: {
            width: "100%",
            display: "flex",
            justifyContent: "center",
            position: "relative",
        },
        imageContainer: {
            width: "100%",
        },
        image: {
            padding: 3,
            borderRadius: theme.shape.borderRadius,
            boxShadow: theme.shadows[4],
        },
        divider: {
            marginTop: 20,
            marginBottom: 10,
        },
        name: {
            textAlign: "center",
            marginTop: theme.spacing(2),
        },
        extractIcon: {
            position: "absolute",
            bottom: theme.spacing(4),
            right: theme.spacing(2),
        },
    })
);

interface IncompletePageInfo {
    status: "loading" | "failed";
    name: string;
}

interface CompletePageInfo {
    status: "loaded";
    getImageData: () => ImageData | Promise<ImageData>;
    width: number;
    height: number;
    name: string;
    fileInfoIndex: number;
    filePageNumber: number;
    pageIndex: number;
    documentIndex?: number;
    documentOrder?: number;
}

type PageInfo = CompletePageInfo | IncompletePageInfo;

interface MapPageInfo {
    finfo: FileInfo;
    pinfo?: ImageInfoPage;
    pinfoOrder?: number;
}

const mapPages = (pages: MapPageInfo[]): PageInfo[] => {
    const result: PageInfo[] = pages.map(({finfo, pinfo, pinfoOrder}) => {
        if (finfo.status !== "loaded") {
            const result: IncompletePageInfo = {
                status: finfo.status,
                name: finfo.name,
            };
            return result;
        } else {
            let name = `${pinfo!.pageIndex + 1} - ${finfo.name}`;
            if (finfo.pages.length > 1) {
                name += ` / ${pinfoOrder! + 1}`;
            }
            const result: CompletePageInfo = {
                status: finfo.status,
                name,
                getImageData: pinfo!.getImageData,
                width: pinfo!.width,
                height: pinfo!.height,
                fileInfoIndex: pinfo!.fileIndex,
                filePageNumber: pinfo!.filePageNumber,
                pageIndex: pinfo!.pageIndex,
                documentIndex: pinfo!.documentIndex,
                documentOrder: pinfo!.documentOrder,
            };
            return result as PageInfo;
        }
    });
    return result;
};

interface ImageEntryProps {
    page: CompletePageInfo;
    listWidth: number;
    onImageExtractionRequested?: (pageIndex: number) => void;
    readonly: boolean;
}

interface MousePosition {
    x: number;
    y: number;
}

const resizeImage = (originalImageData: ImageData, width: number, height: number) => {
    const originalImageCanvas = document.createElement("canvas");
    originalImageCanvas.width = originalImageData.width;
    originalImageCanvas.height = originalImageData.height;
    const oictx = originalImageCanvas.getContext("2d")!;
    oictx.imageSmoothingEnabled = false;
    oictx.putImageData(originalImageData, 0, 0);
    const reducedImageCanvas = document.createElement("canvas");
    reducedImageCanvas.width = width;
    reducedImageCanvas.height = height;
    const context = reducedImageCanvas.getContext("2d")!;
    context.imageSmoothingEnabled = false;
    context.drawImage(
        originalImageCanvas,
        0,
        0,
        originalImageData.width,
        originalImageData.height,
        0,
        0,
        reducedImageCanvas.width,
        reducedImageCanvas.height
    );
    return context.getImageData(0, 0, width, height);
};

const ImageEntry: FC<ImageEntryProps> = props => {
    const {page: p, onImageExtractionRequested, listWidth, readonly} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const [mousePosition, setMousePosition] = useState<MousePosition>();
    const width = listWidth - 40;
    const height = useMemo(() => {
        return (width * p.height) / p.width;
    }, [p.height, p.width, width]);
    const [imageData, setImageData] = useState<ImageData>(new ImageData(p.width, p.height));
    const [smallImageData, setSmallImageData] = useState<ImageData>(new ImageData(width, height));

    useEffect(() => {
        ensurePromise(p.getImageData()).then(imageData => {
            setImageData(imageData);
        });
    }, [p]);

    useEffect(() => {
        const id = resizeImage(imageData, width, height);
        setSmallImageData(id);
    }, [height, imageData, width]);

    useEffect(() => {
        const canvas = canvasRef.current;
        if (canvas) {
            const context = canvas.getContext("2d")!;
            if (!mousePosition) {
                context.putImageData(smallImageData, 0, 0);
            } else {
                const {x, y} = mousePosition;
                const originalImageX = (x * p.width) / canvas.width;
                const originalImageY = (y * p.height) / canvas.height;
                context.putImageData(imageData, x - originalImageX, y - originalImageY);
            }
        }
    }, [imageData, mousePosition, p.height, p.width, smallImageData]);

    const handleMouseOut = useCallback(() => {
        setMousePosition(undefined);
    }, []);

    const handleMouseMovement = useCallback((e: React.MouseEvent) => {
        const canvas = canvasRef.current;
        if (!canvas) {
            return;
        }
        // const {left: offsetLeft, top: offsetTop} = canvas.getBoundingClientRect();
        // setMousePosition({
        //     x: e.pageX - offsetLeft,
        //     y: e.pageY - offsetTop,
        // });
    }, []);

    return (
        <ListItem className={clsx(classes.imageItem, `page-index-${p.pageIndex}`)}>
            <div className={classes.imageContainer}>
                <canvas
                    ref={canvasRef}
                    width={width}
                    height={height}
                    className={classes.image}
                    onMouseOut={handleMouseOut}
                    onMouseMove={handleMouseMovement}
                />
                <Typography className={classes.name}>{p.name}</Typography>
                <Divider variant="fullWidth" className={classes.divider} />
                {(isPresent(p.documentIndex) && !readonly) && (
                    <Tooltip title={t("ExtractImages") as string}>
                        <Fab
                            className={classes.extractIcon}
                            color="secondary"
                            size="small"
                            onClick={() => onImageExtractionRequested?.(p.pageIndex)}
                        >
                            <SelectAreaIcon />
                        </Fab>
                    </Tooltip>
                )}
            </div>
        </ListItem>
    );
};

export interface InternalImageListProps {
    filteredPages: PageInfo[];
    width: number;
    height: number;
    readonly: boolean;
    onImageExtractionRequested?: (pageIndex: number) => void;
}

const InternalImageList: FC<InternalImageListProps> = React.memo(props => {
    const {filteredPages, width, height, onImageExtractionRequested, readonly} = props;
    const classes = useStyles();

    const getItemSize = useCallback(
        (index: number) => {
            if (!isPresent(width)) {
                return 0;
            }
            const p = filteredPages[index];
            if (p.status !== "loaded") {
                return 100;
            }
            return ((width - 40) * p.height) / p.width + 100;
        },
        [filteredPages, width]
    );

    return (
        <List height={height} itemCount={filteredPages.length} itemSize={getItemSize} width={width}>
            {({index, style}) => {
                const p = filteredPages[index];
                if (p.status !== "loaded") {
                    return (
                        <ListItem key={index} className={classes.imageItem} style={style}>
                            {p.status === "loading" && <WaitSpinner />}
                            {p.status === "failed" && <ErrorIcon color="error" />}
                            <Typography style={{textAlign: "center"}}>{p.name}</Typography>
                        </ListItem>
                    );
                }
                return (
                    <div key={p.pageIndex} style={style}>
                        <ImageEntry
                            page={p}
                            onImageExtractionRequested={onImageExtractionRequested}
                            listWidth={width}
                            readonly={readonly}
                        />
                    </div>
                );
            }}
        </List>
    );
});

export interface ImageListProps {
    onImageExtractionRequested?: (pageIndex: number) => void;
}

export const ImageList: FC<ImageListProps> = props => {
    const {onImageExtractionRequested} = props;
    const {fileInfos, currentDocumentLocation, currentDocument} = useBatchProcessorService();
    const classes = useStyles();
    const {width, height, ref} = useResizeDetector({});

    const allPages = useMemo(() => {
        const pinfos: MapPageInfo[] = fileInfos.flatMap(finfo => {
            if (finfo.status !== "loaded") {
                const pinfo: MapPageInfo = {
                    finfo,
                };
                return [pinfo];
            }
            return finfo.pages.map((pinfo, i) => {
                const result: MapPageInfo = {
                    finfo,
                    pinfo,
                    pinfoOrder: i,
                };
                return result;
            });
        });
        return mapPages(pinfos);
    }, [fileInfos]);

    const filteredPages = useMemo(() => {
        if (!currentDocumentLocation) {
            return allPages;
        }
        const pindex = currentDocumentLocation.pageIndex;
        if (isPresent(pindex)) {
            return allPages.filter(s => s.status === "loaded" && s.pageIndex === pindex);
        }

        const dindex = currentDocumentLocation.documentIndex;
        const pages = allPages.filter(s => s.status === "loaded" && s.documentIndex === dindex) as CompletePageInfo[];
        if (isPresent(dindex)) {
            pages.sort(comparator((a, b) => a.documentOrder! < b.documentOrder!));
        } else {
            pages.sort(comparator((a, b) => a.pageIndex < b.pageIndex));
        }
        return pages;
    }, [allPages, currentDocumentLocation]);

    return (
        <div ref={ref} className={classes.listContainer}>
            {isPresent(width) && isPresent(height) && (
                <InternalImageList
                    filteredPages={filteredPages}
                    width={width}
                    height={height}
                    onImageExtractionRequested={onImageExtractionRequested}
                    readonly={currentDocument?.action === "dontSend"}
                />
            )}
        </div>
    );
};
