import {useMemo, createContext, FC, useContext, useCallback} from "react";
import useSWR from "swr";
import {clone, init, last} from "ramda";
import {useRecoilCallback} from "recoil";

import {ContextTask, ContextType, ResponseType, AddTaskResponseResult} from "../../api/nimflow-api";
import {useNimflow, NimflowApi} from "../../api/nimflow-api-provider";
import {getSettings} from "../../api/nimflow-api-extensions";
import {evaluateHandlebars} from "../../libs/handlebars";
import {useTasksViewService} from "./tasks-view-service";
import {taskBlockedState} from "./tasks-view-task-service";
import {useAuthenticationService} from "../../features/auth/authentication-service";

export interface TaskGroupService {
    representativeTask: ContextTask;
    tasks: ContextTask[];
    contextType?: ContextType;
    responses?: ResponseType[];
    taskDetails?: string;
    addResponse: (responseType: ResponseType, payload: any) => Promise<any>;
    updateAssignedTo: (assignedTo: string | undefined) => Promise<any>;
}

const stub = () => {
    throw new Error("You forgot to wrap your component in <TasksViewGroupServiceProvider>.");
};

const initialTaskGroupServiceContext: TaskGroupService = {
    representativeTask: {} as ContextTask,
    tasks: [],
    addResponse: stub,
    updateAssignedTo: stub,
};

const TasksViewGroupServiceContext = createContext<TaskGroupService>(initialTaskGroupServiceContext);

export interface TasksViewGroupServiceProviderProps {
    tasks: ContextTask[];
}

const getInitialPayload = async (
    representativeTask: ContextTask,
    responseType: ResponseType,
    tasks_BuildInitialResponsePayload: NimflowApi["tasks_BuildInitialResponsePayload"]
) => {
    if (!responseType.jsInitialPayloadBuilder) {
        return undefined;
    }
    const result = await tasks_BuildInitialResponsePayload({
        contextId: representativeTask.contextId,
        contextTypeName: representativeTask.contextTypeName,
        responseTypeName: responseType.name,
        taskTypeName: representativeTask.typeName,
    });
    if (result.defined) {
        return result.payload;
    }
    return undefined;
};

const detectUnchangedPaths = (
    payload: any,
    initialPayload: any,
    currentPath: string[],
    unchangedPaths: string[][],
    visited: any[] = []
) => {
    if (visited.includes(initialPayload)) {
        return;
    }
    visited.push(initialPayload);
    if (payload === initialPayload) {
        unchangedPaths.push(currentPath);
    } else if (
        typeof initialPayload === "object" &&
        initialPayload !== null &&
        typeof payload === "object" &&
        payload !== null
    ) {
        for (const key of Object.keys(initialPayload)) {
            detectUnchangedPaths(payload[key], initialPayload[key], [...currentPath, key], unchangedPaths, visited);
        }
    }
};

const calculatePayload = (payload: any, initialPayload: any, unchangedPaths: string[][]): any => {
    const result = clone(payload);
    for (const path of unchangedPaths) {
        let src = initialPayload;
        let tgt = result;
        for (const key of init(path)) {
            tgt = result[key];
            src = src?.[key];
        }
        tgt[last(path)!] = src?.[last(path)!];
    }
    return result;
};

const useContextType = (contextTypeName: string) => {
    const {contextTypes_FindOne} = useNimflow();
    const {data, isValidating, mutate, error} = useSWR(
        `/ContextType/${contextTypeName}`,
        async () => {
            const result = await contextTypes_FindOne({name: contextTypeName});
            return result;
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            contextType: data,
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const TasksViewGroupServiceProvider: FC<TasksViewGroupServiceProviderProps> = props => {
    const {tasks, children} = props;
    const {tasks_AddResponse, tasks_BuildInitialResponsePayload, tasks_UpdateMany} = useNimflow();
    const {authenticatedUser} = useAuthenticationService();
    const {refreshTasks} = useTasksViewService();
    const representativeTask = useMemo(() => {
        if (tasks.length === 0) {
            throw new Error("tasks.length === 0");
        }
        return tasks[0];
    }, [tasks]);
    const {contextType} = useContextType(representativeTask.contextTypeName);

    const [responses, taskDetails] = useMemo(() => {
        const taskType = contextType?.tasks.find(s => s.name === representativeTask.typeName);
        let responses: ResponseType[] = [];
        let taskDetails: string | undefined;
        if (taskType) {
            const taskDetailsTemplate = getSettings(taskType)?.taskDetailsTemplate;
            if (taskDetailsTemplate) {
                taskDetails = evaluateHandlebars(taskDetailsTemplate, {task: representativeTask});
            }
            if (taskType.validResponses) {
                responses = taskType.validResponses;
            }
        }
        return [responses, taskDetails];
    }, [contextType, representativeTask]);

    const addResponse = useRecoilCallback(
        ({set}) =>
            async (responseType: ResponseType, referencePayload: any) => {
                async function getInitPayload(task: ContextTask) {
                    return await getInitialPayload(task, responseType, tasks_BuildInitialResponsePayload);
                }
                let referenceInitialPayload = await getInitPayload(representativeTask);
                const unchangedPaths: string[][] = [];
                detectUnchangedPaths(referencePayload, referenceInitialPayload, [], unchangedPaths);
                const promises: Promise<AddTaskResponseResult>[] = [];
                for (const task of tasks) {
                    const payload =
                        task === representativeTask || unchangedPaths.length === 0
                            ? referencePayload
                            : calculatePayload(referencePayload, await getInitPayload(task), unchangedPaths);
                    set(taskBlockedState(task.id), true);
                    const promise = tasks_AddResponse({
                        contextId: task.contextId,
                        contextTypeName: task.contextTypeName,
                        taskTypeName: task.typeName,
                        responseTypeName: responseType.name,
                        payload,
                        sentBy: authenticatedUser?.name,
                    }).catch(e => {
                        set(taskBlockedState(task.id), false);
                        throw e;
                    });
                    promises.push(promise);
                }
                await Promise.allSettled(promises);
                refreshTasks();
            },
        [representativeTask, tasks_BuildInitialResponsePayload, refreshTasks, tasks, tasks_AddResponse, authenticatedUser]
    );

    const updateAssignedTo = useCallback(
        async (assignedTo: string | undefined) => {
            const update: any[] = [];
            if (assignedTo) {
                update.push({
                    $set: {
                        assignedTo,
                    },
                });
            } else {
                update.push({
                    $unset: {
                        assignedTo: "",
                    },
                });
            }
            await tasks_UpdateMany({
                filter: {
                    _id: {
                        $in: tasks.map(s => s.id),
                    },
                },
                update,
                returnTasks: false,
            });
            refreshTasks();
        },
        [refreshTasks, tasks, tasks_UpdateMany]
    );

    const service: TaskGroupService = {
        representativeTask,
        tasks,
        contextType,
        responses,
        taskDetails,
        addResponse,
        updateAssignedTo,
    };
    return <TasksViewGroupServiceContext.Provider value={service}>{children}</TasksViewGroupServiceContext.Provider>;
};

export const TasksViewGroupServiceConsumer = TasksViewGroupServiceContext.Consumer;

export const useTasksViewGroupService = () => useContext(TasksViewGroupServiceContext);
