export interface DocumentTreeProps {}

interface BatchTreeItem extends TreeItem {
    documentIndex?: number;
    pageIndex?: number;
}

const isDocumentTreeItem = (item: TreeItem): item is BatchTreeItem => {
    return isPresent(item.documentIndex) && !isPresent(item.pageIndex);
};

const isOrphanPagesTreeItem = (item: TreeItem): item is BatchTreeItem => {
    return !isPresent(item.documentIndex) && !isPresent(item.pageIndex);
};

const isPageTreeItem = (item: TreeItem): item is BatchTreeItem => {
    return isPresent(item.pageIndex);
};

const updateTreeData = (
    treeData: TreeItem[],
    fileInfos: FileInfo[],
    schemaIdsByDocumentIndex: string[],
    documentSchemas: JSONSchema7[] | undefined
): TreeItem[] => {
    const result = buildTreeData(fileInfos, schemaIdsByDocumentIndex, documentSchemas);
    if (treeData.length > 0) {
        for (let i = 0; i < Math.min(result.length, treeData.length - 1); i++) {
            result[i].expanded = treeData[i].expanded;
        }
        result[result.length - 1].expanded = treeData[treeData.length - 1].expanded;
    }
    return result;
};

const buildTreeData = (
    fileInfos: FileInfo[],
    schemaIdsByDocumentIndex: string[],
    documentSchemas: JSONSchema7[] | undefined
): TreeItem[] => {
    const orphans: BatchTreeItem[] = [];
    const docLists: Array<BatchTreeItem[] | undefined> = [];
    for (const finfo of fileInfos) {
        if (finfo.status !== "loaded") {
            continue;
        }
        finfo.pages.forEach((pinfo, i) => {
            let title = `${pinfo.pageIndex + 1} - ${finfo.name}`;
            if (finfo.pages.length > 1) {
                title += ` / ${i + 1}`;
            }
            const documentIndex = pinfo.documentIndex;
            const page: BatchTreeItem = {
                title,
                pageIndex: pinfo.pageIndex,
                documentIndex,
            };
            if (isPresent(documentIndex) && isPresent(pinfo.documentOrder)) {
                let docList = docLists[documentIndex];
                if (!docList) {
                    docLists[documentIndex] = docList = [];
                }
                docList[pinfo.documentOrder] = page;
            } else {
                orphans.push(page);
            }
        });
    }

    return [
        ...docLists.map((s, i) => {
            let title = i18next.t("DocumentN", {index: i + 1});
            const schemaId = schemaIdsByDocumentIndex[i];
            if (schemaId) {
                const schema = documentSchemas?.find(s => s.$id === schemaId);
                if (schema) {
                    let str = schema.title;
                    if (!str) {
                        const idx = schemaId.lastIndexOf("/");
                        str = schemaId.substring(idx + 1);
                        if (str.toLocaleLowerCase().endsWith(".json")) {
                            str = str.substring(0, str.length - 5);
                        }
                    }
                    title = `${i + 1} - ${str}`;
                }
            }
            const doc: BatchTreeItem = {
                documentIndex: i,
                title,
                children: s ?? [],
            };
            return doc;
        }),
        {
            title: i18next.t("OrphanPages"),
            children: orphans,
        },
    ];
};

const getNodeKey = (node: TreeNode & TreeIndex) => {
    const batchNode = node.node;
    if (isPageTreeItem(batchNode)) {
        return `p-${batchNode.pageIndex}`;
    }
    if (isDocumentTreeItem(batchNode)) {
        return `d-${batchNode.documentIndex}`;
    }
    return "o";
};

const canNodeHaveChildren = (item: TreeItem) => {
    return !isPageTreeItem(item);
};

const canDragNode = (item: ExtendedNodeData) => {
    return isPageTreeItem(item.node);
};

const canDrop = (data: OnDragPreviousAndNextLocation & NodeData): boolean => {
    const {node, nextParent} = data;
    const pageNode = node as BatchTreeItem;
    if (!nextParent) {
        return false;
    }
    if (isPresent(pageNode.documentIndex)) {
        return true;
    }
    //Disallow reordering orphan nodes
    return !isOrphanPagesTreeItem(nextParent as BatchTreeItem);
};

const ensureOrphanNodesOrdering = (items: TreeItem[]) => {
    const lastNode = items[items.length - 1];
    const children = lastNode.children;
    if (isOrphanPagesTreeItem(lastNode) && Array.isArray(children)) {
        (children as BatchTreeItem[]).sort(comparator((a, b) => a.pageIndex! < b.pageIndex!));
    }
};

export const DocumentTree: FC<DocumentTreeProps> = props => {
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useOkCancelConfirm();
    const [addingDocument, setAddingDocument] = useState(false);
    const [editingDocumentIndex, setEditingDocumentIndex] = useState<number>();
    const {
        loadingImages,
        documentSchemas,
        fileInfos,
        movePage,
        currentDocumentLocation,
        setCurrentDocumentLocation,
        setCurrentDocumentEditionState,
        deleteDocument,
        schemaIdsByDocumentIndex,
        addDocument,
        getDocument,
        updateDocument,
        getLastDocumentEditionErrors,
    } = useBatchProcessorService();

    const [treeData, setTreeData] = useState<TreeItem[]>([]);

    const unusedPageIndices = useMemo(() => {
        if (treeData.length === 0) {
            return [];
        }
        const children = treeData[treeData.length - 1].children;
        if (!Array.isArray(children)) {
            return [];
        }
        return children.map((s: BatchTreeItem) => s.pageIndex).filter(isPresent);
    }, [treeData]);

    const availablePageIndices = useMemo(() => {
        let result = unusedPageIndices;
        if (isPresent(editingDocumentIndex)) {
            const pages = getDocument(editingDocumentIndex).pages;
            const pageIndices = pages
                .map(s => {
                    const finfo = fileInfos[s.fileIndex];
                    if (finfo.status !== "loaded") {
                        return undefined;
                    }
                    return finfo.pages[s.filePageNumber].pageIndex;
                })
                .filter(isPresent);
            pageIndices.push(...result);
            result = pageIndices.sort();
        }
        return result;
    }, [editingDocumentIndex, fileInfos, getDocument, unusedPageIndices]);

    useEffect(() => {
        setTreeData(s => {
            return updateTreeData(s, fileInfos, schemaIdsByDocumentIndex, documentSchemas);
        });
    }, [schemaIdsByDocumentIndex, documentSchemas, fileInfos]);

    const handleDataChange = useCallback((items: TreeItem[]) => {
        ensureOrphanNodesOrdering(items);
        setTreeData(items);
    }, []);

    const handleNodeMove = useCallback(
        (data: NodeData & FullTree & OnMovePreviousAndNextLocation) => {
            const {node, nextParentNode, prevTreeIndex, nextTreeIndex} = data;
            if (!nextParentNode || prevTreeIndex === nextTreeIndex) {
                return;
            }
            const pageNode = node as BatchTreeItem;
            const nextParent = nextParentNode as BatchTreeItem;
            // Ignore orphan nodes reordering
            if (isOrphanPagesTreeItem(nextParent) && !isPresent(pageNode.documentIndex)) {
                return;
            }
            if (isDocumentTreeItem(nextParent)) {
                const docIdx = nextParent.documentIndex!;
                const order = (nextParent.children as BatchTreeItem[]).findIndex(
                    s => s.pageIndex === pageNode.pageIndex
                );
                //console.log(`move to document ${docIdx} order ${order}`);
                movePage(pageNode.pageIndex!, {documentIndex: docIdx, documentOrder: order});
            } else if (isOrphanPagesTreeItem(nextParent)) {
                //console.log(`move to orphans`);
                movePage(pageNode.pageIndex!);
            }
        },
        [movePage]
    );

    const handleNodeClicked = useCallback(
        (ev: React.MouseEvent, node: TreeItem) => {
            const tag = ev.target as HTMLElement;
            // Don't select when expanding/collapsing tree nodes.
            if (
                tag.classList?.contains("rst__collapseButton") === true ||
                tag.classList?.contains("rst__expandButton") === true
            ) {
                return;
            }
            const batchNode = node as BatchTreeItem;
            setCurrentDocumentLocation({
                documentIndex: batchNode.documentIndex,
                pageIndex: batchNode.pageIndex,
            });
        },
        [setCurrentDocumentLocation]
    );

    const handleAddDocument = useCallback(() => {
        setAddingDocument(true);
    }, []);

    const handleEditDocument = useCallback((ev: React.MouseEvent, documentIndex: number) => {
        setEditingDocumentIndex(documentIndex);
    }, []);

    const handleDeleteDocument = useCallback(
        async (ev: React.MouseEvent, documentIndex: number, documentTitle: string) => {
            ev.stopPropagation();
            let result = await confirm({description: t("ConfirmDeleteBatchDocument", {name: documentTitle})});
            if (!result) {
                return;
            }
            deleteDocument(documentIndex);
            setCurrentDocumentEditionState(undefined);
        },
        [confirm, deleteDocument, setCurrentDocumentEditionState, t]
    );

    const generateNodeProps = useCallback(
        (data: ExtendedNodeData): Partial<NodeRendererProps & {[key: string]: any}> => {
            const node = data.node as BatchTreeItem;
            const isSelected =
                currentDocumentLocation &&
                node.documentIndex === currentDocumentLocation.documentIndex &&
                node.pageIndex === currentDocumentLocation.pageIndex;
            const result: Partial<NodeRendererProps & {[key: string]: any}> = {
                onClick: (event: any) => handleNodeClicked(event, data.node),
                className: clsx(classes.node, {[classes.selectedNode]: isSelected}),
            };
            if (isDocumentTreeItem(node)) {
                const editButton = (
                    <Tooltip title={t("Edit") as string}>
                        <IconButton size="small" onClick={ev => handleEditDocument(ev, node.documentIndex!)}>
                            <EditIcon />
                        </IconButton>
                    </Tooltip>
                );
                const deleteButton = (
                    <Tooltip title={t("Delete") as string}>
                        <IconButton
                            size="small"
                            onClick={ev => handleDeleteDocument(ev, node.documentIndex!, node.title! as string)}
                        >
                            <DeleteIcon />
                        </IconButton>
                    </Tooltip>
                );
                result.buttons = [editButton, deleteButton];
                const doc = getDocument(node.documentIndex!);
                const errors = getLastDocumentEditionErrors(node.documentIndex!);
                const errorClassName =
                    doc.action === "send" && errors.length > 0 ? classes.documentWithErrors : undefined;
                const actionIcon =
                    doc.action === "send" ? (
                        <CheckCircleOutlineIcon className={classes.sendActionIcon} />
                    ) : doc.action === "dontSend" ? (
                        <HighlightOffIcon className={classes.actionIcon} color="error" />
                    ) : (
                        <DraftDocumentIcon className={classes.actionIcon} />
                    );
                result.title = data => {
                    return (
                        <>
                            <span className={errorClassName}>{node.title}</span>
                            <Tooltip title={t(`RecognizedDocumentAction.${doc.action}`) as string}>
                                <span>{actionIcon}</span>
                            </Tooltip>
                        </>
                    );
                };
            }

            return result;
        },
        [
            classes.actionIcon,
            classes.documentWithErrors,
            classes.node,
            classes.selectedNode,
            classes.sendActionIcon,
            currentDocumentLocation,
            getDocument,
            getLastDocumentEditionErrors,
            handleDeleteDocument,
            handleEditDocument,
            handleNodeClicked,
            t,
        ]
    );

    const handleDocumentAdded = useCallback(
        (doc: RecognizedDocument) => {
            doc.data = {};
            const documentIndex = addDocument(doc);
            setCurrentDocumentLocation({
                documentIndex,
            });
        },
        [addDocument, setCurrentDocumentLocation]
    );

    const handleDocumentUpdated = useCallback(
        (doc: RecognizedDocument) => {
            const oldDoc = getDocument(editingDocumentIndex!);
            if (oldDoc.schemaId !== doc.schemaId) {
                doc.data = undefined;
            }
            updateDocument(editingDocumentIndex!, doc);
        },
        [editingDocumentIndex, getDocument, updateDocument]
    );

    if (loadingImages) {
        return null;
    }

    return (
        <>
            <Toolbar className={classes.toolbar}>
                <Tooltip title={t("AddDocument") as string}>
                    <IconButton onClick={handleAddDocument}>
                        <AddIcon />
                    </IconButton>
                </Tooltip>
            </Toolbar>
            <SortableTree
                treeData={treeData}
                onChange={handleDataChange}
                onMoveNode={handleNodeMove}
                getNodeKey={getNodeKey}
                canDrag={canDragNode}
                canDrop={canDrop}
                canNodeHaveChildren={canNodeHaveChildren}
                isVirtualized={false}
                generateNodeProps={generateNodeProps}
                className={classes.tree}
            />
            {addingDocument && (
                <EditDocumentModal
                    open={addingDocument}
                    availablePageIndices={availablePageIndices}
                    onChange={handleDocumentAdded}
                    onClose={() => setAddingDocument(false)}
                />
            )}
            {isPresent(editingDocumentIndex) && (
                <EditDocumentModal
                    open={isPresent(editingDocumentIndex)}
                    document={getDocument(editingDocumentIndex)}
                    availablePageIndices={availablePageIndices}
                    onChange={handleDocumentUpdated}
                    onClose={() => setEditingDocumentIndex(undefined)}
                />
            )}
        </>
    );
};
