import React, {FC, useCallback, useState, useMemo, useRef, useEffect} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog, {DialogProps} from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import {JsonFormsCore, UISchemaElement} from "@jsonforms/core";
import {useTranslation} from "react-i18next";
import {clone} from "ramda";
import {useSnackbar} from "notistack";
import {useAsync} from "react-use";

import {useNimflow} from "../../api/nimflow-api-provider";
import {translateValidationErrors} from "../../libs/ajv";
import {getResponseJsonSchema} from "../../utils/json-schema";
import {getActionInProgressText, getSettings, getCommandText} from "../../api/nimflow-api-extensions";
import {ContextTask, ResponseType, ContextType} from "../../api/nimflow-api";
import {WaitSpinner} from "../../components/wait-spinner";
import {JsonForms} from "../../libs/jsonforms";
import {useReportError} from "../../utils/use-report-error";
import {getDialogMaxWidthForResponseType} from "../../utils/dialog-max-width";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            maxHeight: "90vh",
            minHeight: "60vh",
        },
        dialogContent: {},
        grow: {
            flexGrow: 1,
        },
        content: {
            padding: "8px 24px",
            "&:first-child": {
                // dialog without title
                paddingTop: 20,
            },
        },
        loading: {
            position: "absolute",
            top: "50%",
            left: "50%",
        },
    })
);

export interface TasksViewResponseEditorModalProps {
    task: ContextTask;
    contextType: ContextType;
    responseType: ResponseType;
    open: boolean;
    taskGroupSize?: number;
    onAddResponse: (payload: any, startedOn: Date | undefined) => Promise<void>;
    onClose: (refreshTasksRequested: boolean) => void;
}

function isTaskNoLongerActiveError(error: any) {
    if (error?.response == null) {
        return false;
    }
    const detail = JSON.parse(error?.response)?.detail;
    return typeof detail === "string" && detail.startsWith("Active");
}

export const TasksViewResponseEditorModal: FC<TasksViewResponseEditorModalProps> = props => {
    const {task, contextType, responseType, open, taskGroupSize = 1, onAddResponse, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {enqueueSnackbar} = useSnackbar();
    const reportError2 = useReportError();
    const {tasks_BuildInitialResponsePayload} = useNimflow();
    const [payload, setPayload] = useState<any>();
    const [startedOn, setStartedOn] = useState<Date>();
    const errorsRef = useRef<JsonFormsCore["errors"]>();
    const {loading, error} = useAsync(async () => {
        let initialPayload = {};
        if (responseType?.jsInitialPayloadBuilder) {
            try {
                const result = await tasks_BuildInitialResponsePayload({
                    contextId: task.contextId,
                    contextTypeName: task.contextTypeName,
                    responseTypeName: responseType.name,
                    taskTypeName: task.typeName,
                });
                if (result.defined) {
                    initialPayload = result.payload;
                }
            } catch (e) {
                reportError(t("ServerErrorInitializingResponsePayload"), e);
                onClose(true);
                return;
            }
        }
        setPayload(initialPayload);
    }, []);

    const reportError = useCallback(
        (message: string, error: any) => {
            if (isTaskNoLongerActiveError(error)) {
                enqueueSnackbar(t("TaskIsNoLongerActive"), {
                    variant: "warning",
                    action: (
                        <Button
                            onClick={() => {
                                onClose(true);
                            }}
                        >
                            {t("Refresh")}
                        </Button>
                    ),
                });
            } else {
                reportError2(message, error);
            }
        },
        [enqueueSnackbar, t, onClose, reportError2]
    );

    const handleAcceptResponse = useCallback(async () => {
        if (!task || (errorsRef.current && errorsRef.current.length > 0)) {
            return;
        }
        const description = `${task.title}: ${getCommandText(responseType)}`;
        onAddResponse(payload, startedOn).catch(e => {
            reportError(t("ErrorSendingResponse", {description}), e);
        });
        onClose(false);
    }, [task, responseType, onAddResponse, payload, onClose, reportError, startedOn, t]);

    const handlePayloadChange = useCallback(
        (state: Pick<JsonFormsCore, "data" | "errors">) => {
            if (state.errors && state.errors.length > 0) {
                translateValidationErrors(
                    state.errors.map(s => ({
                        ...s,
                        instancePath: s.dataPath,
                    }))
                );
            } else {
                setPayload(state.data);
            }
            errorsRef.current = clone(state.errors);
        },
        [setPayload]
    );

    const schema = useMemo(() => {
        if (!task || !contextType) {
            return undefined;
        }
        return getResponseJsonSchema(contextType, task.typeName, responseType.name);
    }, [task, contextType, responseType]);

    const uischema = useMemo(() => {
        if (!task || !contextType) {
            return undefined;
        }
        const schema = responseType ? getSettings(responseType)?.uiSchema : undefined;
        return schema ? (JSON.parse(schema) as UISchemaElement) : undefined;
    }, [task, contextType, responseType]);

    const context = useMemo(() => {
        if (!task || !payload) {
            return undefined;
        }
        return {task, payload};
    }, [task, payload]);

    useEffect(() => {
        if (!loading) setStartedOn(new Date());
    }, [loading]);

    const dialogWidth: DialogProps["maxWidth"] = useMemo(() => {
        if (!responseType) {
            return "md";
        }
        return getDialogMaxWidthForResponseType(contextType, task.typeName, responseType.name);
    }, [contextType, responseType, task.typeName]);

    const handleInteractiveClose = useCallback(
        (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (reason !== "backdropClick") {
                onClose(false);
            }
        },
        [onClose]
    );

    const uiSchemaLibrary = useMemo(() => {
        const uiSchemaStr = getSettings(contextType)?.uiSchemaCommon;
        if (!uiSchemaStr) {
            return undefined;
        }
        return JSON.parse(uiSchemaStr) as Record<string, UISchemaElement>;
    }, [contextType]);

    if (loading) {
        return <WaitSpinner className={classes.loading}>{t("Loading")}</WaitSpinner>;
    }

    if (error || !payload) {
        return null;
    }

    return (
        <Dialog
            open={open}
            onClose={handleInteractiveClose}
            maxWidth={dialogWidth}
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle disableTypography>
                <Typography component="h2" variant="h6">
                    {task?.title} {taskGroupSize > 1 ? t("AndNOtherResponses", {othersCount: taskGroupSize - 1}) : ""}
                </Typography>
                <Typography variant="subtitle1">{getActionInProgressText(responseType)}</Typography>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                {loading && <WaitSpinner>{t("Loading")}</WaitSpinner>}
                {!!task && !loading && !error && (
                    <JsonForms
                        schema={schema as any}
                        uischema={uischema}
                        data={payload}
                        onChange={handlePayloadChange}
                        context={context}
                        uiSchemaLibrary={uiSchemaLibrary}
                    />
                )}
            </DialogContent>
            <DialogActions>
                {!!task && (
                    <Button onClick={handleAcceptResponse} disabled={loading || !!error} color="primary">
                        {t("Accept")}
                    </Button>
                )}
                <Button onClick={() => onClose(false)} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
