import {
    UISchemaElement,
    VerticalLayout,
    HorizontalLayout,
    GroupLayout,
    LabelElement,
    ControlElement,
    Category,
    Categorization,
    Layout,
} from "@jsonforms/core";

import {ListWithDetailsElement} from "./list-with-details-element";
import {MarkdownElement} from "./markdown-element";
import {PdfElement} from "./pdf-element";
import {UseElement} from "./use-element";
import {ImageBatchRecognitionElement} from "./image-batch-recognition/image-batch-recognition-element";

export class UiSchemaVisitor {
    constructor(public schemaLibrary?: Record<string, UISchemaElement>) {}

    public visit(schema: UISchemaElement | undefined): UISchemaElement | undefined {
        if (!schema) {
            return undefined;
        }
        switch (schema.type) {
            case "VerticalLayout":
                return this.visitVerticalLayout(schema as VerticalLayout);
            case "HorizontalLayout":
                return this.visitHorizontalLayout(schema as HorizontalLayout);
            case "Group":
                return this.visitGroupLayout(schema as GroupLayout);
            case "Label":
                return this.visitLabelElement(schema as LabelElement);
            case "Control":
                return this.visitControlElement(schema as ControlElement);
            case "Category":
                return this.visitCategory(schema as Category);
            case "Categorization":
                return this.visitCategorization(schema as Categorization);
            case "ListWithDetails":
                return this.visitListWithDetails(schema as ListWithDetailsElement);
            case "Markdown":
                return this.visitMarkdownElement(schema as MarkdownElement);
            case "Pdf":
                return this.visitPdfElement(schema as PdfElement);
            case "Use":
                return this.visitUseElement(schema as UseElement);
            case "ImageBatchRecognition":
                return this.visitImageBatchRecognitionElement(schema as ImageBatchRecognitionElement);
        }
    }

    protected processDetailElement(element: UISchemaElement) {
        const detail = element.options?.detail;
        // Is it a valid UISchema?
        if (typeof detail === "object" && !!detail && typeof detail.type === "string") {
            this.visit(detail);
        }
    }

    protected processLayoutElements(layout: Layout) {
        for (const elem of layout.elements) {
            this.visit(elem);
        }
    }

    protected visitVerticalLayout(schema: VerticalLayout): UISchemaElement | undefined {
        this.processLayoutElements(schema);
        return schema;
    }

    protected visitHorizontalLayout(schema: HorizontalLayout): UISchemaElement | undefined {
        this.processLayoutElements(schema);
        return schema;
    }

    protected visitGroupLayout(schema: GroupLayout): UISchemaElement | undefined {
        this.processLayoutElements(schema);
        return schema;
    }

    protected visitLabelElement(schema: LabelElement): UISchemaElement | undefined {
        return schema;
    }

    protected visitControlElement(schema: ControlElement): UISchemaElement | undefined {
        this.processDetailElement(schema);
        return schema;
    }

    protected visitCategory(schema: Category): UISchemaElement | undefined {
        return schema;
    }

    protected visitCategorization(schema: Categorization): UISchemaElement | undefined {
        this.processLayoutElements(schema);
        return schema;
    }

    protected visitListWithDetails(schema: ListWithDetailsElement): UISchemaElement | undefined {
        return schema;
    }

    protected visitMarkdownElement(schema: MarkdownElement): UISchemaElement | undefined {
        return schema;
    }

    protected visitPdfElement(schema: PdfElement): UISchemaElement | undefined {
        return schema;
    }

    protected visitUseElement(schema: UseElement): UISchemaElement | undefined {
        const targetSchema = this.schemaLibrary?.[schema.name];
        if (targetSchema) {
            this.visit(targetSchema);
        }
        return schema;
    }

    protected visitImageBatchRecognitionElement(schema: ImageBatchRecognitionElement): UISchemaElement | undefined {
        return schema;
    }
}
