import {isEmpty, uniq, concat} from "ramda";

import {
    ContextType,
    ActionType,
    TaskType,
    ResponseType,
    ActionEvent,
    TaskResponseEvent,
    SimulationEvent,
    InitialStateEvent,
    Context,
    ContextTask,
    ActionDispatchedEventListener,
    TaskCreatedEventListener,
    MilestoneReachedEventListener,
    MilestoneClearedEventListener,
    TaskUpdatedEventListener,
    TaskArchivedEventListener,
    ResponseSentEventListener,
    ContextRecalculationErrorListener,
    ContextsRecalculationCompletedListener,
    RequestProcessedEventListener,
    CustomEventListener,
    Importance,
} from "./nimflow-api";
import {isPresent} from "ts-is-present";

export const requestProcessedEventType = "RequestProcessed";
export const actionDispatchedEventType = "ActionDispatched";
export const contextsRecalculationCompletedEventType = "ContextsRecalculationCompleted";
export const contextRecalculationErrorEventType = "ContextRecalculationError";
export const taskCreatedEventType = "TaskCreated";
export const taskUpdatedEventType = "TaskUpdated";
export const taskArchivedEventType = "TaskArchived";
export const responseSentEventType = "ResponseSent";
export const milestoneReachedEventType = "MilestoneReached";
export const milestoneClearedEventType = "MilestoneCleared";
export const customEventType = "CustomEvent";

export const milestoneEventTypes = [
    milestoneReachedEventType,
    milestoneClearedEventType,
];

export type MilestoneEventType = typeof milestoneEventTypes[number];

export const contextTaskEventTypes = [
    taskCreatedEventType,
    taskUpdatedEventType,
    taskArchivedEventType,
    responseSentEventType,
] as const;

export type ContextTaskEventType = typeof contextTaskEventTypes[number];

export const contextEventTypes = [
    actionDispatchedEventType,
    contextsRecalculationCompletedEventType,
    contextRecalculationErrorEventType,
    ...contextTaskEventTypes,
    ...milestoneEventTypes,
] as const;

export type ContextEventType = typeof contextEventTypes[number];

export const eventTypes = [
    requestProcessedEventType,
    ...contextEventTypes,
    customEventType,
] as const;

export type EventType = typeof eventTypes[number];

declare module "./nimflow-api" {
    export interface RequestProcessedEventListener {
        event: typeof requestProcessedEventType;
    }

    export interface ActionDispatchedEventListener {
        event: typeof actionDispatchedEventType;
    }

    export interface ContextsRecalculationCompletedListener {
        event: typeof contextsRecalculationCompletedEventType;
    }

    export interface ContextRecalculationErrorListener {
        event: typeof contextRecalculationErrorEventType;
    }

    export interface TaskCreatedEventListener {
        event: typeof taskCreatedEventType;
    }

    export interface TaskUpdatedEventListener {
        event: typeof taskUpdatedEventType;
    }

    export interface TaskArchivedEventListener {
        event: typeof taskArchivedEventType;
    }

    export interface ResponseSentEventListener {
        event: typeof responseSentEventType;
    }

    export interface MilestoneReachedEventListener {
        event: typeof milestoneReachedEventType;
    }

    export interface MilestoneClearedEventListener {
        event: typeof milestoneClearedEventType;
    }

    export interface CustomEventListener {
        event: typeof customEventType;
    }
}

export type MilestoneEventListener = MilestoneReachedEventListener | MilestoneClearedEventListener;

export type ContextTaskEventListener =
    | TaskCreatedEventListener
    | TaskUpdatedEventListener
    | TaskArchivedEventListener
    | ResponseSentEventListener;

export type ContextEventListener = 
    | ActionDispatchedEventListener 
    | ContextsRecalculationCompletedListener
    | ContextRecalculationErrorListener
    | ContextTaskEventListener 
    | MilestoneEventListener;

export type GenericEventListener =
    | RequestProcessedEventListener
    | ContextEventListener
    | CustomEventListener;

export const isMilestoneEventListener = (listener: GenericEventListener): listener is MilestoneEventListener => {
    return milestoneEventTypes.includes(listener.event as any);
};

export const isContextTaskEventListener = (listener: GenericEventListener): listener is ContextTaskEventListener => {
    return contextTaskEventTypes.includes(listener.event as any);
};

export const isContextEventListener = (listener: GenericEventListener): listener is ContextEventListener => {
    return contextEventTypes.includes(listener.event as any);
};

export interface ContextTypeSettings {
    uiSchemaCommon?: string;
}
export interface ActionTypeSettings {
    uiSchema?: string;
}
export interface TaskTypeSettings {
    taskDetailsTemplate?: string;
}

export const iconFamilies = ["Filled", "Outlined", "Rounded", "Two tone", "Sharp"] as const;
export type IconFamily = typeof iconFamilies[number];

export interface IconSpec {
    name: string;
    family?: IconFamily;
}

export interface ResponseTypeSettings {
    uiSchema?: string;
    commandText?: string;
    actionInProgressText?: string;
    actionCompletedText?: string;
    icon?: IconSpec;
}

export type ObjectWithSettings = {settings?: {[key: string]: any}};

export function getSettings(obj: ResponseType, create?: boolean): ResponseTypeSettings | undefined;
export function getSettings(obj: TaskType, create?: boolean): TaskTypeSettings | undefined;
export function getSettings(obj: ActionType, create?: boolean): ActionTypeSettings | undefined;
export function getSettings(obj: ContextType, create?: boolean): ContextTypeSettings | undefined;
export function getSettings(obj: ObjectWithSettings, create: boolean = false): any {
    let result = obj.settings;
    if (!result && create) {
        obj.settings = result = {};
    }
    return result;
}

export const isActionEvent = (event: SimulationEvent): event is ActionEvent => {
    return "action" in event;
};

export const isResponseEvent = (event: SimulationEvent): event is TaskResponseEvent => {
    return "taskTypeName" in event && "typeName" in event;
};

export const isInitialStateEvent = (event: SimulationEvent): event is InitialStateEvent => {
    return "initialState" in event;
};

export function getCommandText(obj: ResponseType) {
    const fromSettings = getSettings(obj)?.commandText;
    return !isEmpty(fromSettings) ? fromSettings : obj.name;
}

export function getActionInProgressText(obj: ResponseType) {
    const fromSettings = getSettings(obj)?.actionInProgressText;
    return fromSettings ? fromSettings : obj.name;
}

export function getActionPayloadDefinitionName(actionName: string) {
    return `${actionName}_payload`;
}

export function getResponsePayloadDefinitionName(taskName: string, responseName: string) {
    return `${taskName}_${responseName}_payload`;
}

type TypeName<T> = T extends string
    ? "string" | "dateTime"
    : T extends number
    ? "number"
    : T extends boolean
    ? "boolean"
    : T extends any[]
    ? "array"
    : T extends Object
    ? "record"
    : never;

type TypeDescriptor<T extends object> = {
    [K in keyof T]-?: TypeName<T[K]>;
};

export const contextTypeDescriptor: TypeDescriptor<Context> = {
    id: "string",
    typeName: "string",
    reference: "string",
    state: "record",
    dateCreated: "dateTime",
    dateModified: "dateTime",
    version: "string",
    contextTypeVersion: "number",
    milestones: "array",
    tags: "array",
    metrics: "array",
};

export const contextTaskTypeDescriptor: TypeDescriptor<ContextTask> = {
    id: "string",
    contextId: "string",
    contextTypeName: "string",
    contextReference: "string",
    typeName: "string",
    title: "string",
    data: "record",
    labels: "array",
    dateCreated: "dateTime",
    dateUpdated: "dateTime",
    assignedTo: "string",
    status: "string",
    archived: "boolean",
    dueDate: "string",
    importance: "number",
    focused: "boolean",
};

export const eventTypeDescriptor = {
    event: "string",
    timestamp: "dateTime",
    requestTypeName: "string",
    contextTypeName: "string",
    sentBy: "string",
    reference: "string",
    subject: "string",
    //duration: "string",
    contextIsNew: "boolean",
    action: "string",
    message: "string",
    dueDate: "dateTime",
    taskTypeName: "string",
    assignedTo: "string",
    importance: "number",
    responseTypeName: "string",
    oldImportance: "number",
    oldLabels: "array",
    oldDueDate: "dateTime",
    oldAssignedTo: "string",
    milestoneName: "string",
    value: "dateTime",
    tags: "array",
    metrics: "array",
} as const;

export const getCollectionPropertyType = () => {};

const getScalarPropertyTypes = <T extends string>(typeDescriptor: Record<T, string>) => {
    const scalarPropertyTypes = ["string", "number", "boolean", "dateTime"];
    return (Object.keys(typeDescriptor) as T[]).filter(s => scalarPropertyTypes.includes(typeDescriptor[s])).sort();
};

const getArrayPropertyTypes = <T extends string>(typeDescriptor: Record<T, string>) => {
    return (Object.keys(typeDescriptor) as T[]).filter(s => typeDescriptor[s] === "array").sort();
};

const getRecordPropertyTypes = <T extends string>(typeDescriptor: Record<T, string>) => {
    return (Object.keys(typeDescriptor) as T[]).filter(s => typeDescriptor[s] === "record").sort();
};

export const contextScalarPropertyNames: Array<keyof Context> = getScalarPropertyTypes(contextTypeDescriptor);

export const contextListPropertyNames: Array<keyof Context> = getArrayPropertyTypes(contextTypeDescriptor);

export const contextDictionaryPropertyNames: Array<keyof Context> = getRecordPropertyTypes(contextTypeDescriptor);

export const contextPropertyNames: Array<keyof Context> = [
    ...contextScalarPropertyNames,
    ...contextListPropertyNames,
    ...contextDictionaryPropertyNames,
];

export const contextTaskScalarPropertyNames: Array<keyof ContextTask> = getScalarPropertyTypes(
    contextTaskTypeDescriptor
);

export const contextTaskListPropertyNames: Array<keyof ContextTask> = getArrayPropertyTypes(contextTaskTypeDescriptor);

export const contextTaskDictionaryPropertyNames: Array<keyof ContextTask> = getRecordPropertyTypes(
    contextTaskTypeDescriptor
);

export const contextTaskPropertyNames: Array<keyof ContextTask> = [
    ...contextTaskScalarPropertyNames,
    ...contextTaskDictionaryPropertyNames,
];

export const eventScalarPropertyNames: Array<string> = getScalarPropertyTypes(eventTypeDescriptor);

export const eventListPropertyNames: Array<string> = getArrayPropertyTypes(eventTypeDescriptor);

export const eventPropertyNames: Array<string> = [...eventScalarPropertyNames, ...eventListPropertyNames];

export const inferStateNames = (actions: Partial<ActionType>[], tasks: Partial<TaskType>[]): string[] => {
    return uniq(
        concat(
            actions.flatMap(s => Object.keys(s.jsSetters ?? {})),
            tasks.flatMap(s => s.validResponses).flatMap(s => Object.keys(s?.jsSetters ?? {}))
        )
    ).filter(isPresent);
};

export const getImportanceString = (importance: Importance): string | undefined => {
    return Importance[importance];
}
