import {FC, useCallback, useMemo, useRef} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {JsonFormsCore, JsonFormsRendererRegistryEntry} from "@jsonforms/core";
import {JsonForms} from "@jsonforms/react";
import {equals} from "ramda";

import {useBatchProcessorService} from "./image-batch-recognition-service";
import {translateValidationErrors} from "../../ajv";
import {cells, renderers} from "../index";
import ImageRenderer, {imageRendererTester} from "./image-renderer";
import MaskedTextControlRenderer, {maskedTextControlRendererTester} from "./masked-text-control-renderer";
import {jsonFormsAjv} from "./ajv";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            display: "grid",
            gridTemplateColumns: `auto`,
            gridTemplateRows: "auto",
            rowGap: "5px",
            height: "100%",
            overflowY: "hidden",
        },
        editorPane: {
            gridRow: "1 / 2",
            gridColumn: "1 / 2",
            overflow: "auto",
        },
        listPane: {
            gridRow: "2 / 3",
            gridColumn: "1 / 2",
            overflow: "auto",
            borderTop: `solid 2px ${theme.palette.divider}`,
            paddingTop: 10,
        },
        listPaneHeader: {
            display: "flex",
            flexDirection: "row",
            placeItems: "center",
        },
        list: {
            display: "flex",
            flexDirection: "row",
            padding: 0,
        },
        listItem: {
            width: "inherit",
            marginTop: 10,
            marginLeft: 10,
            marginRight: 10,
            border: `solid 2px ${theme.palette.divider}`,
            height: 125,
        },
        imageItem: {},
        image: {
            maxWidth: 300,
            maxHeight: 100,
        },
        grow: {
            flexGrow: 1,
        },
    })
);

export interface DocumentEditorProps {}

export const DocumentEditor: FC<DocumentEditorProps> = props => {
    const {documentSchemas, currentDocumentEditionState, setCurrentDocumentEditionState} = useBatchProcessorService();
    const classes = useStyles();
    const documentDataRef = useRef(currentDocumentEditionState?.document.data);
    const hasErrorsRef = useRef(false);

    useMemo(() => {
        documentDataRef.current = currentDocumentEditionState?.document.data;
    }, [currentDocumentEditionState?.document.data]);

    const customRenderers: JsonFormsRendererRegistryEntry[] = useMemo(() => {
        return [
            ...renderers,
            {
                tester: imageRendererTester,
                renderer: ImageRenderer,
            },
            {
                tester: maskedTextControlRendererTester,
                renderer: MaskedTextControlRenderer,
            },
        ];
    }, []);

    const handleDocumentDataChange = useCallback(
        (state: Pick<JsonFormsCore, "data" | "errors">) => {
            hasErrorsRef.current = !!state.errors && state.errors.length > 0;
            const errors = hasErrorsRef.current
                ? translateValidationErrors(
                      state.errors!.map(s => ({
                          ...s,
                          instancePath: s.dataPath,
                      }))
                  )
                : undefined;
            if (!hasErrorsRef.current && documentDataRef.current !== state.data) {
                documentDataRef.current = state.data;
                hasErrorsRef.current = false;
            }
            setCurrentDocumentEditionState(s => {
                if (equals(state.data, s?.document) && equals(errors, s?.errors)) {
                    return s;
                }
                return {
                    document: {
                        ...s!.document,
                        data: state.data ?? s?.document?.data,
                    },
                    errors: errors,
                };
            });
        },
        [setCurrentDocumentEditionState]
    );

    if (!currentDocumentEditionState?.document) {
        return null;
    }

    const schema = documentSchemas?.find(s => s.$id === currentDocumentEditionState?.document.schemaId);

    return (
        <div className={classes.content}>
            <div className={classes.editorPane}>
                <JsonForms
                    data={currentDocumentEditionState?.document.data ?? {}}
                    schema={schema as any}
                    onChange={handleDocumentDataChange}
                    //uischema={uischema}
                    renderers={customRenderers}
                    cells={cells}
                    ajv={jsonFormsAjv}
                    readonly={currentDocumentEditionState?.document.action === "dontSend"}
                />
            </div>
        </div>
    );
};
