import {materialRenderers, materialCells} from "@jsonforms/material-renderers";

import MarkdownRenderer, {markdownRendererTester} from "./markdown-renderer";
import PdfRenderer, {pdfRendererTester} from "./pdf-renderer";
import UseRenderer, {useRendererTester} from "./use-renderer";
import DynamicSelectionRenderer, {dynamicSelectionRendererTester} from "./dynamic-selection-renderer";
import ImageBatchRecognitionRenderer, {imageBatchRecognitionRendererTester} from "./image-batch-recognition/image-batch-recognition-renderer";

export {JsonForms} from "./jsonforms";

export const renderers = [
    ...materialRenderers,
    {
        tester: markdownRendererTester,
        renderer: MarkdownRenderer,
    },
    {
        tester: pdfRendererTester,
        renderer: PdfRenderer,
    },
    {
        tester: useRendererTester,
        renderer: UseRenderer,
    },
    {
        tester: dynamicSelectionRendererTester,
        renderer: DynamicSelectionRenderer,
    },
    {
        tester: imageBatchRecognitionRendererTester,
        renderer: ImageBatchRecognitionRenderer,
    },
];

export const cells = materialCells;
