import {FC, useMemo, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import RefreshIcon from "@material-ui/icons/Refresh";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import {Action, Column} from "@material-table/core";
import Avatar from "@material-ui/core/Avatar";

import {ErrorBoundary} from "../../components/error-boundary";
import MaterialTable, {MaterialTableProps} from "../../libs/material-table";
import {InviteUserDialog} from "./invite-user-dialog";
import {SecurityServiceProvider, UserSecurity, useSecurityService} from "./security-context";
import {useOkCancelConfirm} from "../../components/confirm";
import {WarningIcon} from "../../components/icons/warning-icon";
import {useAuthenticationService} from "../auth/authentication-service";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        small: {
            width: theme.spacing(3),
            height: theme.spacing(3),
        },
    })
);

const buildInviteAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: PersonAddIcon,
        tooltip: i18next.t("InviteUser"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const UserRolesViewer: FC<{}> = () => {
    const {t} = useTranslation();
    const classes = useStyles();
    const {
        users,
        userLoadError: error,
        isRefreshingUsers: isRefreshing,
        refreshUsers,
        updateUserRoles,
    } = useSecurityService();
    const [inviting, setInviting] = useState(false);
    const {authenticatedUser} = useAuthenticationService();
    const confirm = useOkCancelConfirm();

    const columns = useMemo(() => {
        const columns: Column<UserSecurity>[] = [
            {
                field: "picture",
                title: "",
                width: 50,
                render: data => {
                    return <Avatar alt={data.fullName} src={data.picture} className={classes.small} />;
                },
                editable: "never",
            },
            {field: "fullName", title: i18next.t("FullName"), editable: "never"},
            {field: "email", title: i18next.t("Email"), editable: "never"},
            {field: "emailVerified", title: i18next.t("EmailVerified"), type: "boolean", editable: "never"},
            {field: "isWorker", title: i18next.t("IsWorker"), type: "boolean"},
            {field: "isMaker", title: i18next.t("IsMaker"), type: "boolean"},
            {
                field: "isOwner",
                title: i18next.t("IsOwner"),
                type: "boolean",
                validate: rowData => {
                    if (!rowData.isOwner) {
                        const currentOwners = users.filter(s => s.isOwner);
                        if (currentOwners.length === 1 && currentOwners[0].email === rowData.email) {
                            return t("ThereMustBeAtLeastOneOwnerInUnit") as string;
                        }
                    }
                    return true;
                },
            },
        ];
        return columns;
    }, [classes.small, t, users]);

    const refreshAction = useMemo(() => {
        return buildRefreshAction(refreshUsers);
    }, [refreshUsers]);

    const inviteAction = useMemo(() => {
        return buildInviteAction(() => setInviting(true));
    }, []);

    const editable = useMemo(() => {
        const editable: MaterialTableProps<UserSecurity>["editable"] = {
            onRowUpdate: async (newData, oldData) => {
                const authUserLosingOwnership = !newData.isOwner && authenticatedUser?.email === newData.email;
                if (authUserLosingOwnership) {
                    const result = await confirm({
                        title: (
                            <>
                                <WarningIcon style={{verticalAlign: "text-bottom"}} /> {t("LosingOwnershipTitle")}
                            </>
                        ),
                        description: t("LosingOwnershipDescription"),
                    });
                    if (!result) {
                        return;
                    }
                }
                await updateUserRoles(newData);
            },
        };
        return editable;
    }, [authenticatedUser, confirm, t, updateUserRoles]);

    return (
        <>
            <MaterialTable
                columns={columns}
                data={users}
                title={t("Users")}
                editable={editable}
                actions={[inviteAction, refreshAction]}
            />
            {!isRefreshing && !!error && <Alert severity="error">{error.toString()}</Alert>}
            {!!inviting && <InviteUserDialog open={!!inviting} onClose={result => setInviting(false)} />}
        </>
    );
};

export const Security: FC<{}> = () => {
    return (
        <ErrorBoundary>
            <SecurityServiceProvider>
                <UserRolesViewer />
            </SecurityServiceProvider>
        </ErrorBoundary>
    );
};
