import {useMemo} from "react";
import {atom, useRecoilValue} from "recoil";
import {AxiosInstance} from "axios";
import useSWRImmutable from "swr/immutable";

import {IClient, Client, Unit} from "./nimflow-business-directory-api";
import {hubApiUrlProp, studioAllowedPop} from "./nimflow-api-provider";
import {businessDirectoryServerUrl} from "../env-vars";
import {useAuthenticationService} from "../features/auth/authentication-service";

type NimflowBusinessDirectory = {[K in keyof IClient]: Client[K]} & {
    baseUrl: string;
};

export const getNimflowBusinessDirectory = (axios: AxiosInstance) => {
    const client = new Client(axios.defaults.baseURL, axios);
    const api: NimflowBusinessDirectory = {
        auth_SupportedSchemes: client.auth_SupportedSchemes.bind(client),
        auth_BearerSchemeSettings: client.auth_BearerSchemeSettings.bind(client),
        info_Get: client.info_Get.bind(client),
        organizations_FindById: client.organizations_FindById.bind(client),
        organizations_Update: client.organizations_Update.bind(client),
        organizations_Register: client.organizations_Register.bind(client),
        organizations_Delete: client.organizations_Delete.bind(client),
        organizations_Search: client.organizations_Search.bind(client),
        permissions_Grant: client.permissions_Grant.bind(client),
        permissions_Deny: client.permissions_Deny.bind(client),
        permissions_Set: client.permissions_Set.bind(client),
        permissions_Search: client.permissions_Search.bind(client),
        permissions_Any: client.permissions_Any.bind(client),
        permissions_SearchEntityUsers: client.permissions_SearchEntityUsers.bind(client),
        units_Register: client.units_Register.bind(client),
        units_Update: client.units_Update.bind(client),
        units_UpdateProps: client.units_UpdateProps.bind(client),
        units_Delete: client.units_Delete.bind(client),
        units_Search: client.units_Search.bind(client),
        units_SetUserRoles: client.units_SetUserRoles.bind(client),
        units_SearchUserRoles: client.units_SearchUserRoles.bind(client),
        users_Register: client.users_Register.bind(client),
        users_FindById: client.users_FindById.bind(client),
        users_FindByEmail: client.users_FindByEmail.bind(client),
        users_Search: client.users_Search.bind(client),
        userSession_GetUnits: client.userSession_GetUnits.bind(client),
        baseUrl: businessDirectoryServerUrl,
    };
    return api;
};

export const currentUnitIdState = atom<string | undefined>({
    key: "currentUnitIdState",
    default: undefined,
});

export const useNimflowBusinessDirectory = () => {
    const {getAuthorizedAxios} = useAuthenticationService();
    const api = useMemo(() => {
        let baseUrl = businessDirectoryServerUrl;
        for(let i = baseUrl.length - 1; i >= 0; i--) {
            if (baseUrl[i] !== "/") {
                baseUrl = baseUrl.substring(0, i + 1);
                break;
            }
        }
        const axios = getAuthorizedAxios({
            baseURL: baseUrl,
            transformResponse: [], // Disable default automatic json parsing.
        });
        return getNimflowBusinessDirectory(axios);
    }, [getAuthorizedAxios]);

    return api;
};

export const useCurrentUnitInfo = () => {
    const unitId = useRecoilValue(currentUnitIdState);
    const {userSession_GetUnits} =  useNimflowBusinessDirectory();
    const {isAuthenticated} = useAuthenticationService();
    const {data: unit} = useSWRImmutable([`unit/${unitId}`, isAuthenticated], async () => {
        if (!isAuthenticated)
            return undefined;
        const units = await userSession_GetUnits({withProps: [hubApiUrlProp, studioAllowedPop]}).catch(() => {
            return [] as Unit[];
        });
        if (units.length > 0) {
            return units.find(s => s.id === unitId) ?? units[0];
        } else {
            return undefined;
        }
    });
    const hubBaseUrl = useMemo(() => {
        if (!unit) {
            return undefined;
        }
        return (unit as Unit).props[hubApiUrlProp];
    }, [unit]);
    return {unit, hubBaseUrl};
};
