import React, {FC, useCallback, useState, useEffect, useRef, useMemo} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import RefreshIcon from "@material-ui/icons/Refresh";
import useSWR from "swr";
import {useTranslation} from "react-i18next";
import {format} from "date-fns";

import {DashboardWidgetVisualization} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetInstantiation} from "../../api/nimflow-dashboard-api";
import {MarkdownRenderer} from "./markdown-renderer";
import {VegaLiteRenderer} from "./vega-lite-renderer";
import {useDashboardContext} from "./dashboard-context";
import {ErrorViewer} from "../../components/error-viewer";
import CircularProgress from "@material-ui/core/CircularProgress";
import {TimeAgo} from "../../components/time-ago";
import { TabularRenderer } from "./tabular-renderer";

const useStyles = makeStyles(theme =>
    createStyles({
        widget: {
            display: "flex",
            overflow: "hidden",
            position: "relative",
            flexDirection: "column",
            zIndex: 0,
            backgroundColor: theme.palette.background.paper,
            borderWidth: 1,
            borderStyle: "solid",
            borderColor: theme.palette.divider,
            height: "100%",
            borderRadius: 10,
        },
        widgetHeader: {
            paddingLeft: 8,
            minHeight: 36,
            display: "flex",
            alignItems: "center",
        },
        widgetContent: {
            position: "relative",
            flex: "1 1",
            overflow: "auto",
            padding: "0 8px 8px",
            zIndex: 0,
        },
        widgetTitle: {
            flex: "0 1 auto",
            overflow: "hidden",
            maxWidth: "100%",
            textOverflow: "ellipsis",
            lineHeight: "20px",
        },
        widgetTimeago: {
            flex: "0 1 auto",
            overflow: "hidden",
            maxWidth: "100%",
            textOverflow: "ellipsis",
            lineHeight: "20px",
            opacity: 0.4,
        },
        spaceFiller: {
            flex: "1 1",
        },
    })
);

export interface DashboardWidgetRendererProps<T extends DashboardWidgetVisualization> {
    data: Record<string, any>;
    params: Record<string, any>;
    visualization: T;
}

export interface DashboardWidgetViewerProps {
    instantiation: DashboardWidgetInstantiation;
}
const rendererMap: Record<
    DashboardWidgetVisualization["type"],
    React.ComponentType<DashboardWidgetRendererProps<any>>
> = {
    markdown: MarkdownRenderer,
    "vega-lite": VegaLiteRenderer,
    tabular: TabularRenderer,
};

const timeagoOpts = {
    minInterval: 60,
};

const useWidgetData = (instantiationId: string) => {
    const [lastRefresh, setLastRefresh] = useState<Date>(new Date());
    const {
        getWidgetData,
        getWidgetVariables,
        registerWidgetRefreshTrigger,
        unregisterWidgetRefreshTrigger,
    } = useDashboardContext();
    const getWidgetDataRef = useRef(getWidgetData);
    const getWidgetVariablesRef = useRef(getWidgetVariables);

    const {data, error, isValidating, mutate} = useSWR(
        `/WidgetInstanceData/${instantiationId}`,
        async () => {
            const dataPromise = getWidgetDataRef.current(instantiationId);
            const paramsPromise = getWidgetVariablesRef.current(instantiationId);
            const [data, params] = await Promise.all([dataPromise, paramsPromise]);
            setLastRefresh(new Date());
            return {data, params};
        },
        {
            revalidateOnFocus: false,
        }
    );

    useEffect(() => {
        getWidgetDataRef.current = getWidgetData;
        getWidgetVariablesRef.current = getWidgetVariables;
    }, [getWidgetData, getWidgetVariables]);

    useEffect(() => {
        registerWidgetRefreshTrigger(instantiationId, mutate);
        return () => unregisterWidgetRefreshTrigger(instantiationId, mutate);
    }, [instantiationId, registerWidgetRefreshTrigger, mutate, unregisterWidgetRefreshTrigger]);

    return useMemo(() => {
        return {
            widgetData: data,
            isValidating,
            error,
            revalidate: mutate,
            lastRefresh,
        };
    }, [data, isValidating, error, mutate, lastRefresh]);
};

export const DashboardWidgetViewer: FC<DashboardWidgetViewerProps> = props => {
    const {instantiation} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {
        getWidgetDefinition,
        refreshWidgetData,
    } = useDashboardContext();
    const widget = getWidgetDefinition(instantiation.widgetName);
    const {widgetData, error, isValidating, lastRefresh} = useWidgetData(instantiation.instantiationId);

    const handleRefreshData = useCallback(async () => {
        await refreshWidgetData(instantiation.instantiationId);
    }, [instantiation.instantiationId, refreshWidgetData]);

    if (!widget) {
        return null;
    }
    const Renderer = rendererMap[widget.visualization.type];

    return (
        <Paper className={classes.widget} elevation={3}>
            <div className={classes.widgetHeader}>
                <Typography className={classes.widgetTitle} variant="subtitle2" noWrap display="block">
                    {widget?.title}
                </Typography>
                <div className={classes.spaceFiller} />
                <Tooltip title={format(lastRefresh, t("DATETIME_FORMAT")) as string}>
                    <Typography className={classes.widgetTimeago} variant="caption" noWrap display="block">
                        <TimeAgo datetime={lastRefresh} opts={timeagoOpts} />
                    </Typography>
                </Tooltip>
                {!isValidating && (
                    <Tooltip title={t("Refresh") as string}>
                        <IconButton size="small" onClick={handleRefreshData}>
                            <RefreshIcon />
                        </IconButton>
                    </Tooltip>
                )}
                {isValidating && <CircularProgress size={20} />}
            </div>
            <div className={classes.widgetContent}>
                {!!widgetData && Renderer && (
                    <Renderer visualization={widget.visualization} data={widgetData.data} params={widgetData.params} />
                )}
                {!!error && <ErrorViewer error={error} />}
            </div>
        </Paper>
    );
};
