import React, {FC, useState, useCallback} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Grid from "@material-ui/core/Grid";
import InputLabel from "@material-ui/core/InputLabel";
import Chip, {ChipProps} from "@material-ui/core/Chip";
import AssignmentIndIcon from "@material-ui/icons/AssignmentInd";
import {useTranslation} from "react-i18next";

import {ContextTask, DigitalWorker, User} from "../../api/nimflow-api";
import {
    TasksViewGroupServiceProvider,
    TasksViewGroupServiceConsumer,
    TaskGroupService,
} from "./tasks-view-group-service";
import {
    getTaskAssignmentId,
    TaskAssignment,
    TasksViewUserAssignmentEditor,
} from "./tasks-view-user-assignment-editor";
import {useReportError} from "../../utils/use-report-error";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        grow: {
            flexGrow: 1,
        },
        assignmentEditorContainer: {
            justifyContent: "center",
        },
    })
);

interface AssignUserDialogProps {
    taskGroupService: TaskGroupService;
    users?: User[];
    digitalWorkers?: DigitalWorker[];
    open: boolean;
    onClose: () => void;
    onChange?: (assignedTo: string | undefined) => void;
}

const AssignUserDialog: FC<AssignUserDialogProps> = props => {
    const {taskGroupService, users, digitalWorkers, open, onClose, onChange} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {updateAssignedTo} = taskGroupService;
    const [taskAssignment, setTaskAssignment] = useState<TaskAssignment>();
    const reportError = useReportError();

    const handleTaskAssignmentChange = useCallback((taskAssignment?: TaskAssignment) => {
        setTaskAssignment(taskAssignment);
    }, []);

    const handleAccept = useCallback(async () => {
        try {
            await updateAssignedTo(getTaskAssignmentId(taskAssignment));
        } catch (e) {
            reportError(t("CouldNotAssignUser"), e);
            return;
        }
        onChange?.(getTaskAssignmentId(taskAssignment));
        onClose();
    }, [onChange, onClose, reportError, taskAssignment, t, updateAssignedTo]);

    const handleClearAssignments = useCallback(async () => {
        try {
            await updateAssignedTo(undefined);
        } catch (e) {
            reportError(t("CouldNotClearAssignments"), e);
            return;
        }
        onChange?.(undefined);
        onClose();
    }, [onChange, onClose, reportError, t, updateAssignedTo]);

    const handleInteractiveClose = useCallback((event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog open={open} onClose={handleInteractiveClose} maxWidth="sm" fullWidth>
            <DialogTitle>{t("AssigningUser")}</DialogTitle>
            <DialogContent className={classes.assignmentEditorContainer}>
                <Grid container justifyContent="center" alignItems="center" spacing={2}>
                    <Grid item>
                        <InputLabel>{t("User")}</InputLabel>
                    </Grid>
                    <Grid item xs={6}>
                        <TasksViewUserAssignmentEditor
                            users={users}
                            digitalWorkers={digitalWorkers}
                            value={taskAssignment}
                            onChange={handleTaskAssignmentChange}
                        />
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClearAssignments} color="primary">
                    {t("ClearAssignments")}
                </Button>
                <div className={classes.grow}></div>
                <Button onClick={handleAccept} color="primary" disabled={!taskAssignment}>
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export type TasksViewUserAssignmentButtonProps = Omit<ChipProps, "onClick" | "clickable"> & {
    data: ContextTask[];
    users?: User[];
    digitalWorkers?: DigitalWorker[];
    onAssignmentChange?: (assignedTo: string | undefined) => void;
};

export const TasksViewUserAssignmentButton: FC<TasksViewUserAssignmentButtonProps> = props => {
    const {data, users, digitalWorkers, onAssignmentChange, ...rest} = props;
    const {t} = useTranslation();
    const [dialogOpen, setDialogOpen] = useState(false);

    const handleClick = useCallback(() => {
        setDialogOpen(true);
    }, []);

    const handleClose = useCallback(() => {
        setDialogOpen(false);
    }, []);

    const handleChange = useCallback(
        (assignedTo: string | undefined) => {
            onAssignmentChange?.(assignedTo);
        },
        [onAssignmentChange]
    );

    return (
        <TasksViewGroupServiceProvider tasks={data}>
            <TasksViewGroupServiceConsumer>
                {service => (
                    <>
                        <Chip
                            icon={<AssignmentIndIcon />}
                            label={t("AssignUser")}
                            clickable={true}
                            onClick={handleClick}
                            variant="outlined"
                            {...rest}
                        />
                        {dialogOpen && (
                            <AssignUserDialog
                                taskGroupService={service}
                                users={users}
                                digitalWorkers={digitalWorkers}
                                open={dialogOpen}
                                onClose={handleClose}
                                onChange={handleChange}
                            />
                        )}
                    </>
                )}
            </TasksViewGroupServiceConsumer>
        </TasksViewGroupServiceProvider>
    );
};
