import React, {FC} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Toolbar, {ToolbarProps} from "@material-ui/core/Toolbar";
import { StandardSectionTitle } from "./standard-section-title";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
    })
);

export type StandardSectionHeaderProp = {
    title?: string;
    actions?: React.ReactNode;
} & ToolbarProps;

export const StandardSectionHeader: FC<StandardSectionHeaderProp> = props => {
    const {title, actions, ...rest} = props;
    const classes = useStyles();
    return (
        <Toolbar variant="dense" {...rest}>
            {title && <StandardSectionTitle>{title}</StandardSectionTitle>}
            <div className={classes.grow} />
            {actions && <div className={classes.actionToolbarButtons}>{actions}</div>}
        </Toolbar>
    );
};
