import React, {FC, useMemo} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Chip, {ChipProps} from "@material-ui/core/Chip";

import {DashboardParameter} from "../../api/nimflow-dashboard-api";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        chip: {
            margin: "6px 4px",
        },
        parameterTitle: {
            display: "flex",
            flexDirection: "row",
        },
        parameterName: {
            display: "inline",
            flex: "1 1 auto",
            overflow: "hidden",
            textOverflow: "ellipsis",
        },
        parameterSeparator: {
            flex: "0 0 auto",
            padding: "0 4px",
        },
        parameterValueWidth: {
            maxWidth: 256,
        },
        parameterValue: {
            display: "inline-block",
            flex: "1 1 auto",
            overflow: "hidden",
            textOverflow: "ellipsis",
        },
    })
);

export interface DashboardParameterPillProps extends ChipProps {
    parameter: DashboardParameter;
    valueDescription: string;
}

export const DashboardParameterPill: FC<DashboardParameterPillProps> = props => {
    const {parameter, valueDescription, ...rest} = props;

    const classes = useStyles();

    const label = useMemo(() => {
        let paramValue = valueDescription;
        let valueClassName = classes.parameterValue;
        if (parameter.selectionType !== "timeRange") {
            valueClassName += " " + classes.parameterValueWidth;
        }
        return (
            <div className={classes.parameterTitle}>
                <span className={classes.parameterName}>{parameter.displayName}</span>
                <span className={classes.parameterSeparator}>:</span>
                <div className={valueClassName}>{paramValue}</div>
            </div>
        );
    }, [classes, parameter, valueDescription]);

    return <Chip label={label} variant="outlined" size="small" className={classes.chip} {...rest}/>;
};
